import * as React from 'react';
import * as PropTypes from 'prop-types';
import { Link } from 'react-router';
import glamorous from 'glamorous';
const { Div, A } = glamorous;
import { readCookie } from 'utils/helpers';
import { Button } from '../../atoms/Button';
import { CopyBlock } from '../../atoms/CopyBlock';
import { HeadlineTitle } from '../../atoms/HeadlineTitle';
import { Image } from '../../atoms/Image';
import { Media } from '../Media';
import { EyebrowTitle } from '../../atoms/EyebrowTitle';
import { DeviceMobile, IDeviceMobileProps } from '../../molecules/DeviceMobile';
import { ChillahTooltip } from '../../molecules/ChillahTooltip';
import { IToolTip } from 'utils/helpers/parseModules';
import { IKeys } from 'utils/helpers/parseModules';
import { connect } from 'react-redux';
import * as styles from './CopyWithImage-styles';

export interface IButtonContentProps {
  color?: string;
  cta?: string;
  outline?: boolean;
  filled?: boolean;
  onClick?: Function;
  link?: string;
  buttonStyle?: string;
  target?: string;
}

export interface IImageContentProps {
  imageUrl: string;
  imageUrl2x: string;
  title?: string;
  minHeight?: string;
  maxHeight?: string;
  fullWidth?: boolean;
  valign?: boolean;
  halign?: boolean;
  dimensions?: {
    height: string;
    width: string;
  };
}

export interface IVideoContentProps {
  mediaWidthRelativeToContainer?: string;
  ratioWidth?: number;
  ratioHeight?: number;
  video: {
    videoSrc: string;
    posterImageUrl?: string;
  };
}

export interface IDeviceContentProps {
  imageUrl?: string;
  imageUrl2x: string;
  videoUrl?: string;
}

export interface ICopyWithImageProps {
  headline?: string;
  copy?: string;
  eyebrow?: string;
  buttonContent?: IButtonContentProps;
  secondaryButtonContent?: IButtonContentProps;
  imageContent?: IImageContentProps;
  videoContent?: IVideoContentProps;
  deviceContent?: IDeviceContentProps;
  chillah?: IToolTip;
  reverse?: boolean;
  rounded?: boolean;
  blog?: boolean;
  link?: string;
  placeholder?: boolean;
  specsLink?: string;
  specsLabel?: string;
  routeData?: string;
  analyticsEvent?: Function;
  keys?: IKeys;
  locale?: string;
  isMobile?: boolean;
  isHighDensity?: boolean;
}

export const CopyWithImagePropTypes = {
  copy: PropTypes.string,
  headline: PropTypes.string,
  eyebrow: PropTypes.string,
  rounded: PropTypes.bool,
  placeholder: PropTypes.bool,
  specsLink: PropTypes.string,
  specsLabel: PropTypes.string,
  buttonContent: PropTypes.shape({
    color: PropTypes.string,
    cta: PropTypes.string,
    outline: PropTypes.bool,
    filled: PropTypes.bool,
    onClick: PropTypes.func,
  }),
  imageContent: PropTypes.shape({
    imageUrl: PropTypes.string,
    imageUrl2x: PropTypes.string,
    title: PropTypes.string,
    minHeight: PropTypes.string,
    maxHeight: PropTypes.string,
    fullWidth: PropTypes.bool,
    valign: PropTypes.bool,
    halign: PropTypes.bool,
    dimensions: PropTypes.shape({
      height: PropTypes.string,
      width: PropTypes.string,
    }),
    blog: PropTypes.bool,
  }),
  keys: PropTypes.object,
  locale: PropTypes.string,
};

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  keys: state.data.keys,
  locale: state.data.locale,
  isMobile: state.app.mobile,
  isHighDensity: state.app.isHighDensity,
});

export class CopyWithImageImpl extends React.Component<ICopyWithImageProps, void> {

  static propTypes = CopyWithImagePropTypes;

  onButtonClick() {
    const {
      routeData,
      buttonContent,
      analyticsEvent,
      keys,
    } = this.props;
    if (keys && buttonContent.cta === keys.signUp) {
      analyticsEvent({
        action: 'click',
        label: `sign-up`,
        category: `get-started:sign-up`,
      });
      this.sendTrackingPixel();
    }
    if (buttonContent.onClick) { buttonContent.onClick(); }
  }
  sendTrackingPixel() {
    if ((window as any).google_trackConversion) {
      (window as any).google_trackConversion({
        google_conversion_id: 845934552,
        google_conversion_language: 'en',
        google_conversion_format: '3',
        google_conversion_color: '#ffffff',
        google_conversion_label: 'pBjqCIec-nIQ2N-vkwM',
        google_remarketing_only: false,
      });
    }
  }
  addUTM(link) {
    if (typeof document === 'undefined') {
      return;
    }
    const cookies = document.cookie.split(';');
    const utm = [];
    cookies.map((cookie) => {
      const trimmed = cookie.trim();
      if (trimmed.indexOf('utm') > -1) {
        utm.push(trimmed);
      }
      if (trimmed.indexOf('cid') > -1) {
        utm.push(trimmed);
      }
    });
    return `${link}?${utm.join('&')}`;
  }
  render() {
    const {
      copy,
      imageContent,
      videoContent,
      deviceContent,
      buttonContent,
      secondaryButtonContent,
      headline,
      eyebrow,
      rounded,
      reverse,
      blog,
      link,
      placeholder,
      routeData,
      specsLabel,
      specsLink,
      keys,
      locale,
      isHighDensity,
    } = this.props;
    let buttonLink = buttonContent ? buttonContent.link : '';
    if (buttonContent && keys && buttonContent.cta === keys.signUp) {
      buttonLink = this.addUTM(buttonContent.link);
    }

    let isDeviceContent: boolean = false;

    if (deviceContent) {
      isDeviceContent = deviceContent.videoUrl ? true : false;
    }

    let isChillah: boolean = false;
    if (this.props.chillah) {
      isChillah = this.props.chillah.image ? true : false;
    }

    return (
      <Div css={styles.container(reverse)}>
        <Div css={[styles.imageBlock(reverse, blog)]}>
          {imageContent && imageContent.imageUrl ? (
            <Div css={styles.image(imageContent.dimensions && imageContent.dimensions.width, imageContent.dimensions && imageContent.dimensions.height, imageContent.minHeight)}>
              {link && !placeholder ? (
                <Link to={link}>
                  <Image
                    imageUrl={ imageContent.imageUrl }
                    imageUrl2x={ imageContent.imageUrl2x }
                    title={ imageContent.title || 'Image' }
                    valign={ imageContent.valign }
                    fullWidth={ imageContent.fullWidth }
                    natural
                    rounded={ rounded }
                    maxWidth={false}
                    noPadding
                  />
                </Link>
              ) : (
                <Image
                    imageUrl={ imageContent.imageUrl }
                    imageUrl2x={ imageContent.imageUrl2x }
                    title={ imageContent.title || 'Image' }
                    valign={ imageContent.valign }
                    fullWidth={ imageContent.fullWidth }
                    natural
                    rounded={ rounded }
                    maxWidth={false}
                    noPadding
                  />
              )}
            </Div>
          ) : ( null )}
          {videoContent && videoContent.video.videoSrc ? (
            <Div>
              <Media
                mediaWidthRelativeToContainer={ videoContent.mediaWidthRelativeToContainer }
                ratioWidth={ videoContent.ratioWidth }
                ratioHeight={ videoContent.ratioHeight }
                video={videoContent.video}
                mute
                autorun
                fullWidth
                roundedCorner
                noShadow
                loop
                autosize={false}
              />
            </Div>
          ) : ( null )}
          {deviceContent && deviceContent.videoUrl &&
            <Div css={styles.deviceContainer()}>
              <DeviceMobile
                imageUrl={this.props.deviceContent.imageUrl}
                imageUrl2x={this.props.deviceContent.imageUrl2x}
                videoUrl={this.props.deviceContent.videoUrl}
              />
              { !this.props.isMobile && isChillah && <ChillahTooltip iconUrl={this.props.chillah.image} animate delay={0} left={this.props.reverse ? '245px' : '-45px'} top={'-20px'} width={this.props.chillah.width} color={'black'}>{this.props.chillah.tip}</ChillahTooltip> }
            </Div>
          }
        </Div>
        <Div css={[styles.copyBlock(reverse, blog, copy)]}>
          <Div>
            { eyebrow &&
              <EyebrowTitle css={ styles.eyebrow() }>{this.props.eyebrow}</EyebrowTitle>
            }
            { headline &&
              <HeadlineTitle noMax noPadding css={ styles.headline(locale) }>{this.props.headline}</HeadlineTitle>
            }
            { copy &&
              <CopyBlock>{this.props.copy}</CopyBlock>
            }
          </Div>
          { buttonContent && !placeholder &&
            buttonContent.buttonStyle === 'primaryButton' &&
            <Div css={styles.buttonContainer(secondaryButtonContent && true)}>
              <Button onClick={ this.onButtonClick.bind(this) } color={ buttonContent.color } filled={ buttonContent.filled } outline={ buttonContent.outline } link={buttonLink} isSmall analyticsEvent={ this.props.analyticsEvent }>{ buttonContent.cta }</Button>
            </Div>
          }
          { buttonContent && !placeholder &&
            buttonContent.buttonStyle === 'secondaryButton' &&
            <A css={styles.specs()} href={buttonContent.link} target={buttonContent.target}>
              <span>
                { buttonContent.cta }
              </span>
            </A>
          }
          { secondaryButtonContent && !placeholder &&
            secondaryButtonContent.buttonStyle === 'primaryButton' &&
            <Div css={styles.buttonContainer(true)}>
              <Button onClick={ this.onButtonClick.bind(this) } color={ secondaryButtonContent.color } filled={ secondaryButtonContent.filled } outline={ secondaryButtonContent.outline } link={secondaryButtonContent.link} isSmall analyticsEvent={ this.props.analyticsEvent }>{ secondaryButtonContent.cta }</Button>
            </Div>
          }
          { secondaryButtonContent && !placeholder &&
            secondaryButtonContent.buttonStyle === 'secondaryButton' &&
            <A css={styles.specs()} href={secondaryButtonContent.link} target={secondaryButtonContent.target}>
              <span>
                { secondaryButtonContent.cta }
              </span>
            </A>
          }
          {specsLink && specsLabel &&
            <A css={styles.specs()} href={specsLink} target="_blank">
              <span>
                { specsLabel }
              </span>
            </A>
          }
        </Div>
      </Div>
    );
  }
}

export const CopyWithImage = connect(mapStateToProps)(CopyWithImageImpl);



