import * as React from 'react';
import * as glamorous from 'glamorous';
const { Div } = glamorous;

import { DeviceMobile, IDeviceMobileProps } from '../DeviceMobile';
import { EyebrowTitle, IEyebrowTitleProps } from '../../atoms/EyebrowTitle';
import { CopyBlock } from '../../atoms/CopyBlock';
import { Button, IButtonProps } from '../../atoms/Button';
import { ChillahTooltip } from '../ChillahTooltip';

import * as styles from './DeviceMobileLockup-styles';

export interface IDeviceMobileLockupProps {
  deviceMobile: IDeviceMobileProps;
  eyebrowTitle?: string;
  title?: string;
  copyBlock: React.ReactNode;
  primaryButton?: IButtonProps;
  secondaryButton?: IButtonProps;
  animationTiming?: number;
  color?: string;
  chillahData?: {
    iconUrl: string;
    imageAlt: string;
    label: string;
    left: any;
    top: any;
    content: string;
  };
  specLink?: {
    copy: string;
    link: string;
  };
  primaryOverview?: {
    title: string;
    body: string;
  };
  secondaryOverview?: {
    title: string;
    body: string;
  };
  video?: boolean;
  videoUrl?: string;
  videoPoster?: string;
}

export class DeviceMobileLockup extends React.Component<IDeviceMobileLockupProps, { animating: boolean; }> {
  static defaultProps = {
    animationTiming: 200,
  };
  state = { animating: false };
  componentWillReceiveProps(nextProps) {
    if (nextProps.copyBlock !== this.props.copyBlock) {
      this.setState({
        animating: true,
      });
      setTimeout(() => {
        this.setState({
          animating: false,
        });
      }, this.props.animationTiming);
    }
  }
  render() {
    const {
      eyebrowTitle,
      primaryButton,
      secondaryButton,
      animationTiming,
    } = this.props;
    const { animating } = this.state;
    const tipColor = this.props.color === 'Yellow' ? 'black' : this.props.color.toLowerCase();
    return (
      <Div css={styles.container()}>
        {this.props.chillahData ? (
           <ChillahTooltip
             iconUrl={this.props.chillahData.iconUrl}
             imageAlt={this.props.chillahData.imageAlt}
             label={this.props.chillahData.label}
             left={this.props.chillahData.left}
             top={this.props.chillahData.top}
             color={tipColor}>
             {this.props.chillahData.content}
          </ChillahTooltip>
        ) : (
          null
        )}
        <Div css={{ flex: 1, display: 'flex', alignItems: 'center', }}>
          <DeviceMobile {...this.props.deviceMobile} />
        </Div>
        <Div
          css={[
            styles.copyContainer(),
            animating && styles.animateIn(animationTiming),
          ]}
        >
          {eyebrowTitle &&
            <EyebrowTitle css={styles.eyebrow()}>
              {eyebrowTitle}
            </EyebrowTitle>
          }

          <CopyBlock>
            {this.props.copyBlock}
          </CopyBlock>

          <Div css={styles.buttonContainer()}>
            {primaryButton && <Button {...primaryButton} />}
            {secondaryButton && <Button {...secondaryButton} />}
          </Div>
        </Div>
      </Div>
    );
  }
}



