import * as React from 'react';
import * as glamorous from 'glamorous';
import * as PropTypes from 'prop-types';
import * as styles from './TabOrganism-styles';
import * as colors from 'utils/styles/colors';
import { autobind } from 'core-decorators';
import { ImageTab, ImageTabProps } from '../../molecules/TabArray';
import { DeviceMobileLockup, IDeviceMobileLockupProps } from '../../molecules/DeviceMobileLockup';
import { DeviceMobile, IDeviceMobileProps } from '../../molecules/DeviceMobile';
import { EyebrowTitle, IEyebrowTitleProps } from '../../atoms/EyebrowTitle';
import { CopyBlock } from '../../atoms/CopyBlock';
import { Button, IButtonProps } from '../../atoms/Button';
import { ChillahTooltip } from '../../molecules/ChillahTooltip';
import { connect } from 'react-redux';
import { Carousel } from '../../organisms/Carousel';
import { IToolTip } from 'utils/helpers/parseModules';

import { throttle } from 'lodash';

const {
  H4,
  A,
  Div,
  Span,
} = glamorous;

export interface ITabProps {
  tabIcon: {
    hoverImage?: string;
    activeImage?: string;
    image?: string;
    label: string;
    imageAlt?: string;
  };
  tabInfo: IDeviceMobileLockupProps;
}

export interface ITabOrganismProps {
  tabs?: Array<ITabProps>;
  animationTiming?: number;
  isMobile?: boolean;
  color?: string;
  chillah?: IToolTip;
  analyticsEvent?: any;
  routeData?: string;
  sectionType?: string;
}

export interface ITabOrganismState {
  selectedId: number;
  animating: boolean;
  clientWidth: number;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
});

export class TabOrganismImpl extends React.Component<ITabOrganismProps, ITabOrganismState> {
  static defaultProps = {
    animationTiming: 200,
  };

  static propTypes = {
    tabs: PropTypes.arrayOf(PropTypes.shape({
      tabIcon: PropTypes.shape({
        hoverImage: PropTypes.string,
        activeImage: PropTypes.string,
        image: PropTypes.string,
        label: PropTypes.string.isRequired,
      }),
      tabInfo: PropTypes.shape(),
    })),
  };

  state = {
    selectedId: 0,
    animating: false,
    clientWidth: (typeof document !== 'undefined') ? document.body.clientWidth : 1024,
  };

  carousel: any;
  updateDimensionsCallback: any;

  componentDidMount() {
    this.updateDimensionsCallback = throttle(this.updateDimensions, 50);
    window.addEventListener('resize', this.updateDimensions);
    if (this.carousel) {
      this.updateDimensions();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensionsCallback);
  }

  @autobind
  updateDimensions() {
    if (this.carousel && this.carousel.childRefs) {
      this.setState({clientWidth: this.carousel.childRefs.container.getBoundingClientRect().width});
    }
  }

  @autobind
  setClientWidth() {
    this.setState({clientWidth: document.body.clientWidth});
  }

  @autobind
  handleCarouselChange(index: number): void {
    const setCarousel = () => {
      this.setState({
        selectedId: index,
        animating: false,
      });
    };

    if (this.state.selectedId !== index) {
      this.setState({
        animating: true,
      });
      setTimeout(() => {
        setCarousel();
      }, this.props.animationTiming);
    } else {
      setCarousel();
    }
  }

  renderTabs() {
    const { tabs } = this.props;
    const { selectedId } = this.state;
    return tabs.map((tab, index) => {
      const selected = selectedId === index;
      return (
        <Div css={ styles.tabContainer(selected, this.props.color) } key={`${index}`}>
          <ImageTab
            css={styles.tabIconStyle(selected)}
            imageAlt={tab.tabIcon.imageAlt}
            selectedColor={'transparent'}
            selected={selected}
            onSelect={() => {
                        if ( this.props.analyticsEvent ) {
                          this.props.analyticsEvent({
                            action: 'click',
                            label: `${tab.tabIcon.label}-pane`,
                            category: `${this.props.routeData}:${tab.tabIcon.label}`,
                          });
                        }
                        this.setState({
                          animating: true,
                        });
                        setTimeout(() => {
                          this.setState({
                            selectedId: index,
                            animating: false,
                          });
                        }, this.props.animationTiming);
            }}
            {...tab.tabIcon}
          >
            <Div css={styles.tabLabelStyle(selected, tab.tabIcon.label)}>
              {tab.tabIcon.label}
            </Div>
          </ImageTab>
        </Div>
      );
    });
  }

  renderLockup(tabInfo: IDeviceMobileLockupProps, includeTabs: boolean = true) {
    const { tabs } = this.props;
    const {
      deviceMobile,
      eyebrowTitle,
      copyBlock,
      primaryButton,
      secondaryButton,
      title,
      specLink,
    } = tabInfo;
    return (
      <Div css={ this.props.isMobile && styles.content() }>
        { includeTabs &&
          <Div css={styles.tabsContainer()}>
            <Div css={styles.tabs()}>
              <Div css={styles.tabWrapperStyle()}>
                {(tabs.length > 0) &&
                  this.renderTabs()
                }
              </Div>
            </Div>
          </Div>
        }
        <Div css={styles.copyContainer(this.state.animating, this.props.animationTiming)}>
          {eyebrowTitle &&
            <EyebrowTitle css={styles.eyebrow()}>
              {eyebrowTitle}
            </EyebrowTitle>
          }
          {title &&
            <H4 css={styles.headline()}>{title}</H4>
          }
          <CopyBlock>
            {copyBlock}
          </CopyBlock>

          {
            (primaryButton || secondaryButton || specLink) &&
            <Div css={styles.buttonContainer(specLink)}>
              {primaryButton &&
                <Button {...primaryButton} css={ this.props.isMobile ? styles.buttonMobile() : null } sectionType={this.props.sectionType} analyticsEvent={ this.props.analyticsEvent }/>
              }
              {secondaryButton &&
                 <Button {...secondaryButton} css={ this.props.isMobile ? styles.buttonMobile() : null } sectionType={this.props.sectionType} analyticsEvent={ this.props.analyticsEvent }/>
               }
              {specLink &&
                <A css={styles.specsLink()} href={specLink.link} target="_blank">
                  <span>{ specLink.copy }</span>
                </A>
              }
            </Div>
          }
        </Div>
      </Div>
    );
  }

  render() {
    const { selectedId, animating, clientWidth } = this.state;
    const { tabs, animationTiming } = this.props;
    const {
      deviceMobile,
      eyebrowTitle,
      copyBlock,
      primaryButton,
      secondaryButton,
    } = this.props.tabs[selectedId].tabInfo;

    const baseWidth = Math.min((clientWidth - 160), 311);
    const tipColor = this.props.color === 'Yellow' ? 'black' : this.props.color.toLowerCase();

    return !this.props.isMobile ?
      (
        <Div css={styles.tabOrganismStyle()}>
          <Div css={styles.deviceContainer()}>
            <DeviceMobile {...tabs[selectedId].tabInfo.deviceMobile} animating={animating} timing={animationTiming} tabOrganism/>
            { this.props.chillah && <ChillahTooltip iconUrl={this.props.chillah.image} imageAlt={this.props.chillah.imageAlt} width={this.props.chillah.width} animate delay={0} left={'-20%'} top={'-20px'} color={tipColor} >{this.props.chillah.tip}</ChillahTooltip> }
          </Div>
          <Div css={ styles.rightColumnContainer() } >
            { this.renderLockup(tabs[selectedId].tabInfo) }
          </Div>
        </Div>
      ) :
      (
        <Div css={styles.tabOrganismStyle()}>
          <Carousel ref={(carousel) => {this.carousel = carousel; }} leftOffset={ Math.max(((clientWidth - baseWidth) * 0.5), 80) } padding={32} snapCenter isMobile onChange={this.handleCarouselChange} containerWidth={clientWidth}>
            {
              this.props.tabs.map((tab: any, index) => {
                return (
                  <Div css={styles.carouselContainer(clientWidth)} key={`organism-carousel-item-${index}`}>
                    <DeviceMobile {...tab.tabInfo.deviceMobile} inCarousel={true} isFocused={this.state.selectedId === index} />
                  </Div>
                );
              })
            }
          </Carousel>
          { this.renderLockup(tabs[selectedId].tabInfo, false) }
        </Div>
      );
    }
}

export const TabOrganism = connect(mapStateToProps)(TabOrganismImpl);



