import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as utilStyles from 'utils/styles';
import * as glamor from 'glamor';
import { Z_INDEXES } from '../../../../utils/constants';
const { mediaQueries } = utilStyles;

const fadeEnter = glamor.keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
});

const fadeLeave = glamor.keyframes({
  '0%': {
    opacity: 1,
  },
  '100%': {
    opacity: 0,
  },
});

export const modalBackground = (scrollOverflow: any = false) => ({
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
  backgroundColor: 'rgba(255, 255, 255, 0.95)',
  padding: '40px 0px',
  position: 'fixed',
  [mediaQueries.ie11]: {
    position: '-ms-page',
  },
  overflowY: 'auto',
  zIndex: Z_INDEXES.MODAL,
  [`${mediaQueries.phablet}`]: {
    padding: '80px 0px',
  },
  [`${mediaQueries.ie11}`]: {
    overflowY: scrollOverflow ? 'scroll' : 'hidden',
    overflowX: 'hidden',
  },
});

export const contentContainer = (video: any = false) => ({
  width: '100%',
  maxWidth: '870px',
  margin: '0 auto',
  padding: '40px',
  position: 'absolute',
  transform: video ? 'translateX(-50%)' : 'translate(-50%, -50%)',
  top: video ? 'initial' : '50%',
  left: '50%',
  [`${mediaQueries.phablet}`]: {
    width: '667px',
  },
  [`${mediaQueries.normalWithPadding}`]: {
      width: '100%',
  },
});

export const deviceContainer = () => ({
  display: 'block',
  width: '200px',
  position: 'relative',
  margin: '0 auto',
  backfaceVisibility: 'hidden',
  [`${mediaQueries.phablet}`]: {
    display: 'inline-block',
    width: '240px',
    position: 'relative',
    margin: '0 50px 0px 20px',
  },
  [`${mediaQueries.normalWithPadding}`]: {
      width: '270px',
      margin: '0 70px 0px 30px',
  },
});
export const videoContainer = (animating = false, timing = 300) => ({
  display: 'block',
  width: '200px',
  position: 'relative',
  margin: '0 auto',
  backfaceVisibility: 'hidden',
  opacity: '0',
  animation: animating ? `${fadeLeave} ${timing / 1000}s ease-in-out forwards` : `${fadeEnter} ${timing / 1000}s ease-in-out forwards ${timing / 1000}s`,
  [`${mediaQueries.phablet}`]: {
    display: 'inline-block',
    width: '100%',
    position: 'relative',
    margin: '0 0px 32px 0px',
  },
  [`${mediaQueries.normalWithPadding}`]: {
      width: '100%',
      margin: '0px 0px 32px 0px',
  },
});

export const copyContainer = (video: any = false) => ({
  display: 'block',
  width: '100%',
  position: 'relative',
  textAlign: 'center',
  backfaceVisibility: 'hidden',
  [`${mediaQueries.phablet}`]: {
    textAlign: 'left',
    display: 'inline-block',
    width: video ? '100%' : '294px',
    position: video ? 'relative' : 'absolute',
    top: '50%',
    transform: video ? 'initial' : 'translateY(-50%)',
  },
  [`${mediaQueries.normalWithPadding}`]: {
      width: video ? '100%' : '400px',
  },
});

export const modalCopyWrapper = (video: any = false) => ({
  [`${mediaQueries.phablet}`]: {
      width: video ? '400px' : 'intial',
  },
  [`${mediaQueries.normalWithPadding}`]: {
      width: video ? '496px' : 'intial',
  },
});

export const eyebrow = () => ({
  fontSize: '12px',
  marginBottom: '20px',
  fontFamily: 'AvenirNext-DemiBold',
  padding: '0px',
});

export const title = (video: any = false) => ({
  fontSize: '24px',
  marginBottom: '30px',
});

const modalBorder = {
  borderBottom: `1px solid ${colors.lightGray}`,
};

export const bodyCopy = (video: any = false) => ({
  fontSize: '16px !important',
  marginBottom: '20px',
  paddingBottom: '30px',
  lineHeight: '30px',
  ...modalBorder,
});

export const overviewContainer = (video: any = false) => ({
  fontSize: '20px',
  marginBottom: '20px',
  paddingBottom: '30px',
  lineHeight: '30px',
  ...modalBorder,
});

export const leftContainer = () => ({
  width: '100%',
  display: 'inline-block',
  marginBottom: '14px',
  [`${mediaQueries.normalWithPadding}`]: {
      width: '50%',
      verticalAlign: 'top',
      marginBottom: '0px',
  },
});

export const rightContainer = () => ({
  width: '100%',
  display: 'inline-block',
  [`${mediaQueries.normalWithPadding}`]: {
      width: '50%',
      verticalAlign: 'top',
  },
});

export const overviewTitle = () => ({
  fontSize: '12px',
  display: 'inline-block',
});

export const overviewBody = () => ({
  fontSize: '16px',
  display: 'block',
  lineHeight: '22px',
});

export const leftArrow = () => ({
  display: 'block',
  cursor: 'pointer',
  backgroundImage: `url(/icons/modal-chevron.svg)`,
  position: 'fixed',
  left: '32px',
  width: '40px',
  height: '40px',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  backgroundPosition: '50% 50%',
  transform: 'translateY(-50%)',
  top: '50%',
  zIndex: '20',
  [`${mediaQueries.normalWithPadding}`]: {
      left: '100px',
  },
});

export const rightArrow = () => ({
  display: 'block',
  cursor: 'pointer',
  backgroundImage: `url(/icons/modal-chevron.svg)`,
  position: 'fixed',
  right: '32px',
  width: '40px',
  height: '40px',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  backgroundPosition: '50% 50%',
  transform: 'rotate(180deg) translateY(-50%)',
  transformOrigin: 'top center',
  top: '50%',
  zIndex: '20',
  [`${mediaQueries.normalWithPadding}`]: {
      right: '100px',
  },
});

export const exit = () => ({
  display: 'block',
  cursor: 'pointer',
  backgroundImage: `url(/icons/x.svg)`,
  position: 'fixed',
  top: '40px',
  right: '40px',
  width: '20px',
  height: '20px',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  backgroundPosition: '50% 50%',
  zIndex: '20',
  [`${mediaQueries.normalWithPadding}`]: {
      right: '110px',
  },
});

export const carouselContainer = (clientWidth) => ({
  display: 'block',
  width: `calc(${clientWidth}px - 160px)`,
  maxWidth: '311px',
  height: 'auto',
});

export const tabContainer = (selected, color: any = false) => ({
  boxSizing: 'border-box',
  border: `${ selected ? 3 : 2}px solid ${ selected ? color === 'Yellow' ? colors.snapYellow : ((color && colors['medium' + color]) || colors.mediumBlue) : colors.lightGray}`,
  borderRadius: '8px',
  flexDirection: 'column',
  flex: '1 0 auto',
  width: '96px',
  marginRight: '16px',
  transition: 'border .2s ease-out',
  '&:last-child': {
    marginRight: 0,
  },
});

export const tabIconStyle = (selected) => ({
  padding: '20px 8px 8px 8px',
  '&::after': {
    backgroundColor: 'none',
  },
});

export const tabLabelStyle = (selected, text) => ({
  fontWeight: 600,
  fontSize: '12px',
  lineHeight: '16px',
  position: 'relative',
  color: 'rgba(0,0,0,0)',
  display: 'block',
  '&::after': {
    position: 'absolute',
    width: '100%',
    content: `"${text}"`,
    right: 0,
    top: 0,
    fontWeight: selected ? 600 : 400,
    color: 'black',
  },
});

export const content = () => ({
  display: 'block',
  width: `calc(100vw - 64px)`,
  maxWidth: '311px',
  margin: '0 auto',
  textAlign: 'center',
  marginTop: '-60px',
});

export const tabsContainer = () => ({
  display: 'block',
  position: 'relative',
  width: '100%',
  height: 'auto',
  zIndex: 4,
  marginTop: '48px',
  [`${utils.mediaQueries.normal}`]: {
    marginTop: '64px',
  },
});

export const tabs = () => ({
  display: 'block',
  position: 'relative',
});

export const tabWrapperStyle = () => ({
  textAlign: 'center',
  marginBottom: '32px',
  display: 'flex',
  [`${utils.mediaQueries.normal}`]: {
    marginBottom: '48px',
  },
});

export const copyContainerInner = (animating = false, timing = 300, video: any = false) => ({
  display: 'block',
  position: 'relative',
  width: '100%',
  maxWidth: video ? 'none' : '400px',
  opacity: 0,
  animation: animating ? `${fadeLeave} ${timing / 1000}s ease-in-out forwards` : `${fadeEnter} ${timing / 1000}s ease-in-out forwards ${timing / 1000}s`,
  [`${utils.mediaQueries.normalWithPadding}`]: {
    maxWidth: video ? 'none' : '496px',
  },
});

export const headline = () => ({
  fontFamily: '\'AvenirNext-DemiBold\', sans-serif',
  fontSize: '16px',
  lineHeight: '24px',
  [`${utils.mediaQueries.phablet}`]: {
    fontSize: '14px',
  },
  [`${utils.mediaQueries.normal}`]: {
    fontSize: '19px',
    lineHeight: '28px',
  },
});

export const buttonContainer = (video: any = false) => ({
  display: 'block',
  width: '100%',
  marginTop: '24px',
  '& > :first-child': {
    marginBottom: '24px',
  },
  [`${utils.mediaQueries.phablet}`]: {
    display: video ? 'block' : 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    marginTop: video ? 0 : 25,
    position: video ? 'absolute' : 'relative',
    top: video ? '0' : 'initial',
    right: video ? '0' : 'initial',
    width: video ? 'initial' : '100%',
    '& > :first-child': {
      marginBottom: 0,
      marginRight: video ? 0 : 15,
    },
  },
  [`@media all and (-ms-high-contrast: none) and (min-width:${utils.screenSizes.phablet}px), (-ms-high-contrast: active) and (min-width:${utils.screenSizes.phablet}px)`]: {
    right: video ? '-80%' : 'initial',
  },
});

export const buttonMobile = () => ({
  width: '100%',
});

export const specsLink = () => ({
  fontWeight: 600,
  textTransform: 'capitalize',
  color: colors.regularBlue,
  alignSelf: 'center',
  '& span': {
    borderBottom: `2px solid ${colors.regularBlue}`,
    verticalAlign: 'middle',
    fontFamily: 'AvenirNext-DemiBold',
    fontSize: '12px',
    lineHeight: '16px',
    letterSpacing: '0.2px',
    paddingBottom: '2px',
    [utils.mediaQueries.phabletMax]: {
      display: 'inline-block',
      margin: '24px auto 0',
    },
    [utils.mediaQueries.phablet]: {
      margin: '0',
      display: 'block',
    },
  },
});

export const tabOrganismStyle = () => ({
  width: '100%',
  margin: '0 auto',
  display: 'block',
  [`${utils.mediaQueries.phablet}`]: {
    padding: '0 32px',
    maxWidth: '704px',
  },
  [`${utils.mediaQueries.normal}`]: {
    maxWidth: `${utils.maxContentWidth - utils.padding.semi * 2}px`,
  },
});

export const rightColumnContainer = () => ({
  display: 'inline-block',
  width: utils.columnsByPercent(11),
  [`${utils.mediaQueries.normal}`]: {
    width: utils.columnsByPercent(7),
  },
});



