import { css } from 'glamor';
import { Z_INDEXES } from '../../../../utils/constants';
import * as colors from 'utils/styles/colors';
import * as utilStyles from 'utils/styles';

const { mediaQueries } = utilStyles;

export const defaultStyle = (gradientColor = 'Purple', id) => ({
  background: id !== 'home' && colors[`regular${gradientColor}Gradient`],
});

export const skipContent = () => ({
  padding: '1em',
  position: 'absolute',
  top: -1000,
  zIndex: 5000,
  backgroundColor: colors.white,
  '&:focus': {
    top: 0,
  },
});

export const pageContent = (id) => ({
  width: '100%',
  backgroundColor: colors.backgroundGray,
  borderRadius: id === 'home' ? 'none' : '24px 24px 0 0',
  position: 'relative',
  top: `${id === 'home' ? 0 : 96}px`,
  left: '0px',
  overflow: 'hidden',
});

export const navigationHeader = (gradientColor = 'Purple', height = 120, absolute: boolean = false) => ({
  zIndex: Z_INDEXES.PAGE_BACKGROUND,
  height: `${height}px`,
  width: '100%',
  background: colors[`regular${gradientColor}Gradient`],
  position: absolute ? 'absolute' : 'relative',
  top: 0,
  left: 0,
});

export const section = (backgroundColor: string = 'transparent') => ({
  backgroundColor,
  position: 'relative',
  boxSizing: 'border-box',
  width: '100%',
  '& button.outline-button': {
    backgroundColor: (backgroundColor === 'transparent' || !backgroundColor) ? colors.backgroundGray : colors.white,
    '&:hover': {
      backgroundColor: (backgroundColor === 'transparent' || !backgroundColor) ? colors.backgroundGray : colors.white,
    },
  },
});

export const sidePagination = () => ({
  width: 60,
  height: '100%',
  position: 'fixed',
  top: 0,
  zIndex: Z_INDEXES.SIDENAV,
  visibility: 'hidden',
  left: -1000,
  [`${mediaQueries.phabletBelow}`]: {
    visibility: 'visible',
    left: 0,
  },
});

export const formatHolder = (formatting: string = 'none', {...padding}) => ({
  boxSizing: 'border-box',
  position: 'relative',
  top: formatting === 'vertically-center' && '50%',
  marginTop: formatting === 'negative-top' ? '-80px' : formatting === 'half-padding' ? '-64px' : null,
  transform: formatting === 'vertically-center' ? 'translateY(-50%)' : null,
  paddingTop: formatting === 'no-padding' ? 0 : padding.top,
  paddingBottom: formatting === 'no-padding' ? 0 : formatting === 'negative-top' ? 0 : padding.bottom,
  paddingLeft: padding.left,
  paddingRight: padding.right,
  textAlign: padding.align,
});



