import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

const { mediaQueries } = utils;

export const container = (imageHeadline: any = false, miniBlock: boolean = false) => ({
  padding: imageHeadline ? `24px` : miniBlock  ? `30px 15px` : `40px 24px 20px 24px`,
  width: `100%`,
  textAlign: `center`,
  [`${mediaQueries.normal}`]: {
    padding: imageHeadline ? `24px` : miniBlock ? `32px 25px` : `80px 40px 40px 40px`,
    maxWidth: `none`,
    minHeight: miniBlock ? `none` : '264px',
  },
});

export const headline = (miniBlock: boolean = false) => ({
  fontSize: miniBlock ?  type.large.fontSize : `34px`,
  lineHeight: miniBlock ? `22px` : `56px`,
  letterSpacing: `-1.5px`,
  marginBottom: miniBlock ? `8px` : 0,
  [`${mediaQueries.normal}`]: {
    marginBottom: miniBlock ? `8px` : `20px`,
    fontSize: miniBlock ? `28px` : `48px`,
    letterSpacing: `-2px`,
  },
});

export const image = (bigImage: any = false) => ({
  width: 80,
  marginBottom: 10,
  [`${mediaQueries.phablet}`]: {
    width: bigImage ? 95 : 60,
  },
  [`${mediaQueries.normal}`]: {
    width: bigImage ? 120 : 60,
  },
});

export const markdownStyles = (boldSubhead: any = false, miniBlock: boolean = false, bodyCopy: boolean = false) => ({
  [' p']: {
    fontSize: boldSubhead ? `22px` : miniBlock ? type.small.fontSize : type.regular.fontSize,
    lineHeight: boldSubhead ? 1.2 : miniBlock ? type.small.lineHeight : `24px`,
    width: `100%`,
    letterSpacing: `0px`,
    marginBottom: miniBlock ? `0` : `20px`,
    fontFamily: boldSubhead ? type.BOLD_FONT : null,
    [`${mediaQueries.normal}`]: {
      fontSize: boldSubhead ? `36px` : miniBlock ? `14px` : type.regular.fontSize,
    },
  },
});



