import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';
import { css } from 'glamor';

export const chillahContainer = (left, top) => ({
    pointerEvents: 'all',
    zIndex: 100,
    position: 'absolute',
    top: `calc(${top} * 0.95)`,
    left,
    // left: `calc(${left} * 0.95)`,
    [`${utils.mediaQueries.phablet}`]: {
        top,
        left,
    },
});

const orbit = (css as any).keyframes({
    '0%': { transform: 'rotate(0deg) translateX(4px) rotate(0deg)' },
    '100%': { transform: 'rotate(360deg) translateX(4px) rotate(-360deg)' },
});

const reverseOrbit = (css as any).keyframes({
    '0%': { transform: 'rotate(360deg) translateX(4px) rotate(-360deg)' },
    '100%': { transform: 'rotate(0deg) translateX(4px) rotate(0deg)' },
});

export const chillahWrapper = (left, top, delay = 0, reverse, animate = false) => ({
    position: 'absolute',
    cursor: 'pointer',
    animation: animate ? !reverse ? `${orbit} 6s linear -${delay}s infinite` : `${reverseOrbit} 6s linear -${delay}s infinite` : 'none',
});

export const chillah = (width) => ({
    pointerEvents: 'none',
    maxWidth: '150px',
    width: width / 2,
});

export const label = () => ({
    textAlign: 'center',
    display: 'block',
    paddingTop: 10,
});

export const tooltip = (active, position) => ({
    position: 'absolute',
    pointerEvents: 'none',
    zIndex: 2,
    opacity: 0,
    transition: 'opacity 0.3s ease-out',
});

export const markdownStyles = () => ({
    [' p']: {
        ...type.stat,
    },
    [' sup']: {
        fontSize: '8px',
        verticalAlign: 'top',
        position: 'relative',
        top: '-3px',
    },
});



