import * as React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { Markdown } from '../../atoms/Markdown';
import { browserHistory } from 'react-router';

import { store } from '../../../../state/createStore';

import glamorous from 'glamorous';
const { Div, P, Img } = glamorous;

import * as styles from './ErrorPage-styles';

import { HeadlineTitle } from '../../atoms/HeadlineTitle';
import { Button } from '../../atoms/Button';

const chillah = require('./assets/404_chillah.svg');

export interface IErrorPageProps {
    pageData: {
        title: string;
        subTitle: string;
        buttonPrimaryLabel: string;
        buttonPrimaryLink: string;
        imageAlt?: string;
        image?: string;
    };
    imageAlt?: string;
}

export class ErrorPage extends React.Component<IErrorPageProps, void> {

    onClick(evt) {
        evt.preventDefault();
        const currentRoute = store.getState().app.previousRoute;

        if (currentRoute) {
            browserHistory.push(currentRoute);
        } else {
            browserHistory.push('/');
        }
    }

    render() {
        return (
            <Div css={styles.wrapper()}>
                <Img src={this.props.pageData.image || chillah} alt={this.props.pageData.imageAlt} css={styles.image()} />
                <HeadlineTitle primary>{this.props.pageData.title}</HeadlineTitle>
                <Markdown copy={this.props.pageData.subTitle} styleOverrides={ styles.subHeadline() } />
                <Link to={this.props.pageData.buttonPrimaryLink} onClick={((evt) => this.onClick(evt))}>
                    <Button filled isSmall color="regularYellow">{this.props.pageData.buttonPrimaryLabel}</Button>
                </Link>
            </Div>
        );
    }
}



