export const gaMiddleware = (store: any) => (next: any) => (action: any) => {
  if (action.event) {
    const state = store.getState();
    try {
      if (typeof window !== 'undefined') {
        (window as any).ga('send', {
          hitType: 'event',
          eventCategory: action.event.category,
          eventAction: action.event.action,
          eventLabel: action.event.label,
        });
      }
    } catch (ex) {
      // Ignore ex
    }
  }
  return next(action);
};



