export const sansFont = `'Avenir Next', sans-serif`;
export const maxContentWidth = 1440;
export const spacing = 24;
export const minLargeWidth = maxContentWidth + spacing * 2;
export const letterSpacing = '0.2px';

export const columns = {
  desktopColumnCount: 17,
  desktopColumnWidth: (1 / 17) * 100,
  desktopMaxWidth: 1264,
};

export const borderRadius = {
  desktopContainer: `32px`,
  tabletContainer: `24px`,
  mobileContainer: `12px`,
};

export const padding = {
  micro: 4,
  xtiny: 6,
  tiny: 8,
  small: 16,
  normal: 24,
  semi: 32,
  large: 48,
  xlarge: 64,
  xxlarge: 96,
  xxxlarge: 120,
};

export const screenSizes = {
  xxlarge: 1600,
  xlarge: 1280,
  large: 1120,
  normal: 1024,
  normalWithPadding: 1024 + padding.normal * 2,
  medium: 960,
  phablet: 768,
  desktopMenu: 1000,
  small: 321,
};

export const mediaQueries = {
  xxlarge: `@media (min-width:${screenSizes.xxlarge}px)`,
  xlarge: `@media (min-width:${screenSizes.xlarge}px)`,
  large: `@media (min-width:${minLargeWidth}px)`,
  normal: `@media (min-width:${screenSizes.normal}px)`,
  normalWithPadding: `@media (min-width:${screenSizes.normalWithPadding}px)`,
  medium: `@media (min-width:${screenSizes.medium}px)`,
  phablet: `@media (min-width:${screenSizes.phablet}px)`,
  phabletBelow: `@media (min-width:${screenSizes.phablet + 1}px)`,
  phabletNav: `@media (min-width:${screenSizes.desktopMenu}px)`,
  phabletMax: `@media (max-width:${screenSizes.phablet}px)`,
  small: `@media (max-width:${screenSizes.small}px)`,
  normalMax: `@media (max-width:${screenSizes.normal - 1}px)`,
  minHeight: `@media (min-height:590px)`,
  contentWrapBreak: `@media (min-width:${ 1048 + spacing * 2}px)`,
  ie11: `@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)`,
  ie11Phablet: `@media all and (-ms-high-contrast: none), (-ms-high-contrast: active), (min-width:${screenSizes.phablet}px)`,
  ie11normal: `@media all and (-ms-high-contrast: none), (-ms-high-contrast: active), (min-width:${screenSizes.normal}px)`,
  phabletToNormal: `@media (max-width:${screenSizes.normal}px) and (min-width:${screenSizes.phablet}px)`,
  phabletCardMax: `@media (max-width:${screenSizes.phablet - 1}px)`,
};

export const columnsTotal = 17;

export function columnsByPercent(cols: number) {
  return `${ (100 / columnsTotal) * cols }%`;
}

export const CHAR = {
  RSQUO: `\u2019`,
  RDQUO: `\u201D`,
  LDQUO: `\u201C`,
  PRIME: `\u0027`, // \u2032 actual prime unicode
  DPRIME: `\u0022`, // \u2033
  HELLIP: `\u2026`,
  NL: `\u000A`, // new line
  HYPHEN: `\u2010`,  // Hyphen
};



