import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as _ from 'lodash';
import glamorous from 'glamorous';
import * as colors from 'utils/styles/colors';
import * as Hammer from 'hammerjs';

import { autobind } from 'core-decorators';
const { Div } = glamorous;
import * as styles from './VideoProgressSection-styles';
import * as ReactDOM from 'react-dom';
const { findDOMNode } = ReactDOM;

import { CircleDeviceNavigation } from '../CircleDeviceNavigation';
import { DeviceLaptop } from '../DeviceLaptop';

import { Video } from '../../atoms/Video';
import { Media } from '../Media';

interface INavItemsProps {
    symbol: string;
    description: any;
    playhead: number;
}

export interface IVideoProgressSectionProps {
    color: string;
    navItems: Array<INavItemsProps>;
    video?: Object;
    mute?: boolean;
    autorun?: boolean;
    controls?: boolean;
    handlePlayback?: Function;
    roundedCorner?: boolean;
    ratioWidth: number;
    ratioHeight: number;
    analyticsEvent?: Function;
    routeData?: string;
    section?: string;
}

export const VideoProgressSectionPropTypes = {
    color: PropTypes.string.isRequired,
    navItems: PropTypes.arrayOf(PropTypes.shape({
        symbol: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
        playhead: PropTypes.number.isRequired,
    })),
    video: PropTypes.shape({
        videoSrc: PropTypes.string,
        posterImageUrl: PropTypes.string,
    }),
    mute: PropTypes.bool,
    autorun: PropTypes.bool,
    controls: PropTypes.bool,
    handlePlayback: PropTypes.func,
    roundedCorner: PropTypes.bool,
    ratioWidth: PropTypes.number.isRequired,
    ratioHeight: PropTypes.number.isRequired,
};

interface IVideoProgressSectionState {
  active: number;
  seekTo: number;
  touchCoords: {x: number; y: number; };
  dragging: boolean;
}

export class VideoProgressSection extends React.Component<IVideoProgressSectionProps, IVideoProgressSectionState> {

    static propTypes = VideoProgressSectionPropTypes;
    hammer;

    state = {
        active: 0,
        seekTo: 0,
        dragging: false,
        touchCoords: {
            x: null,
            y: null,
        },
    };

    handleSwipeCallback: any;

    componentDidMount() {
        const video = findDOMNode(this.refs.videoWrapper);
        this.handleSwipeCallback = this.handleSwipe.bind(this);
        this.hammer = new Hammer(video as HTMLElement, {
            domEvents: true,
        });
        this.hammer.on('swipe', this.handleSwipeCallback);
        const events = [
            'mousedown',
            'mouseup',
            'touchstart',
            'touchend',
            'mousemove',
        ];
        events.forEach((event) => {
            video.addEventListener(event, this.handleTouch.bind(this));
        });
    }
    componentWillUnmount() {
        this.hammer.off('swipe', this.handleSwipeCallback);
    }

    @autobind
    handleTouch(e) {
        if (e.type === 'mousedown' || e.type === 'touchstart') {
            this.setState({
                touchCoords: {
                    x: e.clientX,
                    y: e.clientY,
                },
            });
        } else if (e.type === 'mousemove' || e.type === 'touchmove') {
          if ( this.state.touchCoords.x && (Math.abs(e.clientX - this.state.touchCoords.x) > 5 ||
                         Math.abs(e.clientY - this.state.touchCoords.y) > 5)) {
            if (!this.state.dragging) {
              this.setState({
                dragging: true,
              });
            }
          }
        } else if (e.type === 'mouseup' || e.type === 'touchend') {
          if ( Math.abs(e.clientX - this.state.touchCoords.x) > 5 ||
               Math.abs(e.clientY - this.state.touchCoords.y) > 5) {
              e.preventDefault();
              e.stopPropagation();
              e.stopImmediatePropagation();
          }
          setTimeout(() => {
            this.setState({
                dragging: false,
                touchCoords: {
                    x: null,
                    y: null,
                },
            });
          }, 5);
        }
    }
    handleNavClick(event, i) {
        this.props.analyticsEvent({
            action: 'click',
            label: `${this.props.section}-${i}`,
            category: `${this.props.routeData}:button-${this.props.section}`,
        });
        this.setState({
            active: i,
            seekTo: this.props.navItems[i].playhead,
        });
    }

    handlePlayback(event) {
        const playback = _.filter(this.props.navItems, (o) => {
            return o.playhead <= event.playedSeconds;
        });

        if (playback.length) {
            if (playback.length - 1 !== this.state.active) {
                const progress = (playback.length - 1) / this.props.navItems.length * 100;
                this.props.analyticsEvent({
                    action: 'view',
                    label: `video-progress-${progress}%`,
                    category: `video-progress:${this.props.section}`,
                });
            }
            this.setState({active: playback.length - 1});
        }
    }
    handleSwipe(e) {
        const { direction } = e;
        const { navItems } = this.props;
        let index = this.state.active;
        if (direction === 2 && index < navItems.length - 1) {
            index++;
        } else if (direction === 4 && index > 0) {
            index--;
        } else {
            return false;
        }
        this.setState({
            active: index,
            seekTo: index !== this.state.active && navItems[index].playhead,
        });
    }
    render() {
        const {color, navItems, ...rest} = this.props;
        const { active, seekTo } = this.state;
        return (
            <Div ref="videoWrapper">
                <Div css={styles.desktopNavigation()}>
                    <CircleDeviceNavigation
                        color={color}
                        textColor={'black'}
                        navItems={navItems}
                        active={active}
                        onClick={this.handleNavClick.bind(this)} />
                </Div>
                <Div css={styles.mobileNavigation()}>
                    <CircleDeviceNavigation
                        color={color}
                        textColor={'black'}
                        navItems={[navItems[active]]}
                        active={0}
                        onClick={null} />
                </Div>
                <Media
                    {...rest}
                    mediaWidthRelativeToContainer={'100%'}
                    handlePlayback={this.handlePlayback.bind(this)}
                    seekTo={seekTo}
                    dragging={this.state.dragging}
                    isVideoProgress={true}
                />
                <Div css={styles.mobileNavigation()}>
                    <Div css={styles.dotNav(active + 1, color)}>
                        {navItems.map((item, index) => <span key={index} /> )}
                    </Div>
                </Div>
            </Div>
        );
    }
}



