import * as React from 'react';
import { throttle } from 'lodash';
import glamorous from 'glamorous';
import { VideoPlayer } from './components/Video.player';
import { autobind } from 'core-decorators';
import * as styles from './Video-styles';
import * as baseStyles from '../../../../utils/styles';
import { connect } from 'react-redux';
import { isMobile, notMobile } from '../../../../state/actionCreators/appCreator';
import { store } from '../../../../state/createStore';

const { Div } = glamorous;

export interface IVideoProps {
  videoSrc: string;
  videoSrcVertical?: string;
  posterImageVerticalUrl?: string;
  posterImageUrl: string;
  posterVideoSrc?: string;
  width?: number;
  height?: any;
  isMobile?: boolean;
  ctaText?: string;
  showCTA?: boolean;
  smallLayout?: boolean;
  posterWidth?: number;
  posterHeight?: number;
  heroMediaStyle?: boolean;
  animOnScroll?: boolean;
  mute?: boolean;
  autorun?: boolean;
  controls?: boolean;
  handlePlayback?: Function;
  roundedCorner?: boolean;
  seekTo?: number;
  dragging?: boolean;
  analyticsEvent?: Function;
  title?: string;
  fitHeight?: any;
  noShadow?: boolean;
  fullWidth?: boolean;
  isFocused?: boolean;
  loop?: boolean;
  autosize?: boolean;
  routeId?: string;
}

export interface IVideoState {
  playing: boolean;
  videoDuration: number;
  quarterPlayed: boolean;
  halfPlayed: boolean;
  threeQuarterPlayed: boolean;
  fullPlayed: boolean;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  routeId: state.data.current.id,
});

export class VideoImpl extends React.Component<IVideoProps, IVideoState> {
    static defaultProps = {
        width: '100%',
        height: '100%',
        showCTA: true,
        loop: false,
        isFocused: true,
    };

    state = {
      playing: this.props.autorun ? true : false,
      videoDuration: 0,
      quarterPlayed: false,
      halfPlayed: false,
      threeQuarterPlayed: false,
      fullPlayed: false,
    };

    handleResize: any;

    componentDidMount() {
      this.handleResize = throttle(this.resizeListener, 200);
      window.addEventListener('resize', this.handleResize);
    }

    componentWillReceiveProps(nextProps) {
      if (nextProps.isFocused !== this.props.isFocused) {
        const paused = !nextProps.isFocused; // if Feed is not focused, pause video
        if (paused) {
          this.setState({ playing: false });
        }
      }
    }

    componentWillUnmount() {
      window.removeEventListener('resize', this.handleResize);
    }

    @autobind
    resizeListener() {
      if (window.innerWidth < baseStyles.screenSizes.phablet && !store.getState().app.mobile) {
        store.dispatch(isMobile());
      } else if (window.innerWidth >= baseStyles.screenSizes.phablet && store.getState().app.mobile) {
        store.dispatch(notMobile());
      }
    }

    @autobind
    handlePosterTap() {
      const {playing} = this.state;
      if (this.props.analyticsEvent) {
        this.props.analyticsEvent({
          action: 'click',
          label: `video-${playing ? 'pause' : 'play'}`,
          category: `${this.props.routeId}:${this.props.videoSrc}`,
        });
      }
      if (this.props.isFocused ) {
        this.setState({
          playing: !playing,
        });
      }
    }

    @autobind
    handleVideoEnded() {
        setTimeout(() => {
            this.setState({
                playing: false,
            });
        }, 500);
    }

    @autobind
    handleVideoPause() {
      this.setState({
        ...this.state,
        playing: false,
      });
    }

    @autobind
    onKeyUp(key) {
      this.setState({playing: !this.state.playing});
    }

    triggerAnalytics(percent) {
      if (this.props.analyticsEvent) {
        this.props.analyticsEvent({
          action: 'view',
          label: `video-${percent}%`,
          category: `${this.props.routeId}-video`,
        });
      }
    }

    @autobind
    analyticsPlayback(evt, ref) {

      const playBackPercentage = ref.getCurrentTime() / this.state.videoDuration;

      if (playBackPercentage >= 0.25 && playBackPercentage <= 0.5 && !this.state.quarterPlayed) {
        this.state.quarterPlayed = true;
        this.triggerAnalytics(25);
      } else if (playBackPercentage >= 0.5 && playBackPercentage <= 0.75 && !this.state.halfPlayed) {
        this.state.halfPlayed = true;
        this.triggerAnalytics(50);
      } else if (playBackPercentage >= 0.75 && playBackPercentage <= 1 && !this.state.threeQuarterPlayed) {
        this.state.threeQuarterPlayed = true;
        this.triggerAnalytics(75);
      } else if (playBackPercentage === 1) {
        this.triggerAnalytics(100);
      }
    }

    @autobind
    onDuration(duration) {
      this.state.videoDuration = duration;
    }

    render() {

        const {
          height,
          isMobile,
          posterImageUrl,
          posterVideoSrc,
          posterImageVerticalUrl,
          videoSrc,
          videoSrcVertical,
          width,
          ctaText,
          smallLayout,
          posterWidth,
          posterHeight,
          heroMediaStyle,
          animOnScroll,
          mute,
          handlePlayback,
          roundedCorner,
          seekTo,
          controls,
          dragging,
          fullWidth,
          loop,
        } = this.props;
        const showCTA = this.props.showCTA;

        const handlePlaybackCallback = handlePlayback ? handlePlayback : this.analyticsPlayback;

        return (

          <Div css={styles.fullContainer(isMobile && videoSrcVertical)}>
            <Div css={styles.columnContainer(fullWidth)}>
              <Div className={'video-wrapper'} css={styles.videoWrapper(isMobile && videoSrcVertical, height, width)}>
                <VideoPlayer
                  {...this.props}
                  ctaText={ ctaText }
                  isMobile={ isMobile }
                  isVertical= { posterImageVerticalUrl && isMobile ? true : false }
                  posterImageUrl={ isMobile && posterImageVerticalUrl ? posterImageVerticalUrl : posterImageUrl }
                  posterVideoSrc={ posterVideoSrc }
                  showCTA={ showCTA }
                  videoSrc={ isMobile && videoSrcVertical ? videoSrcVertical : videoSrc}
                  width={ width }
                  height={ height }
                  posterWidth={ posterWidth }
                  posterHeight={ posterHeight }
                  smallLayout={ smallLayout }
                  heroMediaStyle={ heroMediaStyle }
                  animOnScroll={ animOnScroll }
                  mute={ mute }
                  handlePlayback={ handlePlaybackCallback }
                  onDuration={ this.onDuration }
                  roundedCorner={ roundedCorner }
                  seekTo={ seekTo }
                  controls={ controls }
                  playing={ this.state.playing }
                  dragging={ dragging }
                  onKeyUp={this.onKeyUp.bind(this)}
                  handlePosterTap={ this.handlePosterTap.bind(this) }
                  handleVideoEnded={ this.handleVideoEnded }
                  handleVideoPause={ this.handleVideoPause.bind(this) }
                  fitHeight={ this.props.fitHeight }
                  loop={ loop }
                  autosize={this.props.autosize}
                  isFocused={this.props.isFocused}
                  >
                </VideoPlayer>
              </Div>
            </Div>
          </Div>
        );
    }
}

export const Video = connect(mapStateToProps)(VideoImpl);



