import * as React from 'react';
import glamorous from 'glamorous';
const { Div, Ul, Li } = glamorous;

import * as styles from './HamburgerMenu-styles';

export interface IHamburgerMenuProps {
    active: boolean;
    onClick: Function;
    handleOnKeyUp: Function;
}

export class HamburgerMenu extends React.Component<IHamburgerMenuProps, void> {
    render() {
        const { active } = this.props;
        return (
            <Div
                css={styles.hamburgerWrapper()}
                tabIndex="0"
                onClick={this.props.onClick}
                onKeyUp={(evt) => {
                    if (evt.which === 32 || evt.which === 13) {
                        this.props.handleOnKeyUp();
                    }
                }}>
                <Div css={styles.hamburger(active)}>
                    <Ul>
                        <Li/>
                        <Li/>
                        <Li/>
                    </Ul>
                </Div>
            </Div>
        );
    }
}



