import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

export const containerStyle = () => ({
  fontSize: '16px',
  lineHeight: '24px',
  [`${utils.mediaQueries.phablet}`]: {
    fontSize: '14px',
  },
  [`${utils.mediaQueries.normal}`]: {
    fontSize: '19px',
    lineHeight: '28px',
  },
});

export const markdownStyles = () => ({
  [' h3']: {
    ...type.bold5,
    padding: `${utils.padding.small}px 0`,
  },
  [' p']: {
    ...type.large,
    padding: `${utils.padding.small}px 0`,
  },
});



