import * as action from '../constants/appConstants';

export function isMobile() {
  return {
    type: action.IS_MOBILE,
  };
}

export function notMobile() {
  return {
    type: action.NOT_MOBILE,
  };
}

export function hideCookie() {
  return {
    type: action.HIDE_COOKIE,
  };
}

export function showCookie() {
  return {
    type: action.SHOW_COOKIE,
  };
}

export function previousRoute(route: string) {
  return {
    type: action.PREVIOUS_ROUTE,
    route: route,
  };
}

export function navIndex(navIndex: number) {
  return {
    type: action.NAV_INDEX,
    navIndex: navIndex,
  };
}

export function blogIndex(blogIndex: number) {
  return {
    type: action.BLOG_INDEX,
    blogIndex: blogIndex,
  };
}

export function isLoaded() {
  return {
    type: action.IS_LOADED,
  };
}

export function modalIsOpen(isOpen: boolean) {
  return {
    type: action.MODAL_IS_OPEN,
    isOpen: isOpen,
  };
}

export function videoIsPlaying(videoIsPlaying: {state: boolean, ref: any}) {
  return {
    type: action.VIDEO_IS_PLAYING,
    videoIsPlaying: videoIsPlaying,
  };
}



