import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { hexToRGB } from 'utils/helpers';

const { mediaQueries } = utils;

export const logoCardWrapper = () => ({
  width: '128px',
  height: 70,
  display: 'inline-block',
  flex: '0 0 auto',
  position: 'relative',
  margin: '0 16px 16px 0',
  verticalAlign: 'top',
  [`${mediaQueries.phablet}`]: {
    margin: '0 60px 10px 0',
  },
});

export const logoWrapper = (retina = false) => ({
  width: retina ? '256px' : '100%',
  height: retina ? '140px' : '70px',
  transform: retina ? 'scale(.5, .5)' : 'none',
  transformOrigin: '0 0',
  whiteSpace: 'nowrap',
});

export const logoCardImage = () => ({
  position: 'relative',
  verticalAlign: 'middle',
  width: '100%',
});

export const verticalHelper = () => ({
  display: 'inline-block',
  height: '100%',
  verticalAlign: 'middle',
});

export const logoGardenWrapper = () => ({
  width: '100%',
  maxWidth: 1000,
  margin: '0 auto',
  textAlign: 'center',
  paddingLeft: 16,
  [`${mediaQueries.phablet}`]: {
    paddingLeft: 50,
  },
});



