import * as React from 'react';
import glamorous from 'glamorous';
import { autobind } from 'core-decorators';
import ReactDOM, { findDOMNode } from 'react-dom';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { browserHistory, Link } from 'react-router';
import { analyticsEvent } from '../../../../state/actionCreators/data';
import { filter } from 'lodash';

import { getLocationHref } from 'utils/helpers';
import * as colors from 'utils/styles/colors';
import * as pageStyles from '../PageTemplate/_styles';
import * as blogStyles from './_styles';
import { padding } from 'utils/styles';
import { languageGroup } from 'utils/constants';

import { SectionList } from '../../molecules/SectionList';

const { Div, Img, A } = glamorous;

import { GlobalNav } from '../../organisms/GlobalNav';
import { Footer } from '../../organisms/Footer';

import { PageHeading } from '../../organisms/PageHeading';

const windowLocationHref = (typeof window !== 'undefined') && window.location.href;

const mapStateToProps = (state) => {
  return {
    data: state.data.current,
    navData: state.data.entries.navigation,
    footerData: state.data.entries.footer,
    posts: state.data.entries.posts,
    locale: state.data.locale,
    isMobile: state.app.mobile,
    isLoaded: state.app.isLoaded,
  };
};

export interface IBlogPageTemplate {
  data: any;
  location: { [key: string]: any; };
  navData: any;
  footerData: any;
  params: any;
  posts: any;
  analyticsEvent: any;
  locale: string;
  isMobile: boolean;
  isLoaded: boolean;
}

export interface IBlogPageState {
  scrolled: boolean;
  articleFinished: boolean;
  pageFinished: boolean;
}

export class IBlogPageTemplate extends React.Component<IBlogPageTemplate, IBlogPageState> {
  sections = new Array();
  state = {
    scrolled: false,
    articleFinished: false,
    pageFinished: false,
  };
  frame;
  componentDidMount() {
    this.props.analyticsEvent({
      action: 'send',
      description: 'pageview',
      page: this.props.data.postTitle,
      label: `blog-pageview-${this.props.data.postTitle}`,
      category: 'blog:pageview',
    });
    this.frame = window.requestAnimationFrame(this.handleScroll.bind(this));
  }
  componentWillUnmount() {
    cancelAnimationFrame(this.frame);
  }
  handleScroll() {
    const body = (window as any).document.body;
    const pageOffset = (window as any).document.documentElement.scrollTop;
    const contentHeight = findDOMNode(this.refs.articleContent).clientHeight;
    const {
      scrolled,
      pageFinished,
      articleFinished,
    } = this.state;
    const { data } = this.props;
    if (this.props.isLoaded && pageOffset > 0 && !scrolled) {
      this.setState({
        scrolled: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        description: 'Start reading article',
        page: data.postTitle,
        label: `article-start-reading-${data.postTitle}`,
        category: 'blog:article-reading',
      });
    } else if (this.props.isLoaded && (contentHeight - pageOffset) / body.clientHeight < 1 && !articleFinished) {
      this.setState({
        articleFinished: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        description: 'Finish reading article',
        page: data.postTitle,
        label: `article-content-bottom-${data.postTitle}`,
        category: 'blog:article-reading',
      });
    } else if (this.props.isLoaded && (body.scrollHeight - pageOffset) === body.clientHeight && !pageFinished) {
      this.setState({
        pageFinished: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        description: 'Scroll to bottom of page',
        page: data.postTitle,
        label: `article-page-bottom-${data.postTitle}`,
        category: 'blog:article-reading',
      });
    }
    this.frame = window.requestAnimationFrame(this.handleScroll.bind(this));
  }

  onCTAClick(evt) {
    const categoryIndex = this.props.posts.categories.indexOf(this.props.data.tag);
    return evt;
  }

  renderNavigation() {
      const { data } = this.props;

      const tabItems = this.props.navData.filter((navItem) => {
        if ( navItem.type === 'link' || navItem.type === 'link-collection' && navItem.label === 'News') {
          return navItem;
        }
      });
      const subnavContent = this.props.navData.filter((navItem) => {
        if ( navItem.type === 'link-collection' && navItem.label !== 'News' ) {
          return navItem;
        }
      });
      const buttonContent = this.props.navData.filter((navItem) => {
        if ( navItem.type === 'special') {
          return navItem;
        }
      });

      const mobileMenuItems = {
        menuItems: tabItems,
        subMenuItems: subnavContent[0].links,
      };

      const subnavItems = subnavContent[0].links;
      const navButton = buttonContent[0];

      return <GlobalNav pageColor={'Yellow'} buttonTitle={ navButton.label } buttonLink={ navButton.link } tabItems={ tabItems } subnavItems={ subnavItems } navItemColor={ colors.black } scrollTransition={ 94 } menuItems={ mobileMenuItems } />;
  }

  @autobind
  handleSectionRef(node, index) {
    if (node) {
      this.sections[index] = node;
    }
  }

  renderHeading() {
    return (
      <PageHeading
        headline={ this.props.data.postTitle || this.props.data.postTitle_loc || this.props.data.title_loc }
        category={ this.props.data.entryTypeLabel }
        date={ (this.props.data.entryType === 'newsEntry' || this.props.data.entryType === 'insightsEntry') && this.props.data.postDate }
        image={ this.props.data.brandLogo && this.props.data.brandLogo.url }
        imageAltText= { this.props.data.title_loc }
      />
    );
  }

  render() {
      const { data, footerData, locale } = this.props;
      const footerContent = footerData.groups.map((group) => {
          return {
              headline: group.title,
              links: group.links.map((groupLink) => {
                  return {name: groupLink.label, path: groupLink.link};
              }),
          };
      });

      const policyTermsContent = [
          {label: footerData.privacyLabel, url: footerData.privacyLink},
          {label: footerData.termsLabel, url: footerData.termsLink},
      ];

      const pageTitle = data.title_loc ? data.title_loc : '';

      return (
          <Div css={ blogStyles.defaultStyle(data.color, data.id) }>
            <Helmet>
              <meta charSet="utf-8" />
              <title>Snapchat Ads{pageTitle && ` | ${pageTitle}`}</title>
              <meta name="title" content={data.title_loc} />
              <link rel="canonical" href={getLocationHref(this.props.location.pathname)} />
              <meta name="description" content={data.seoDescription_loc} />
              <meta name="keywords" content={data.keywords_loc} />
              <meta name="og:site_name" content="Snapchat Ads" />
              <meta name="og:title" content={data.ogTitle_loc} />
              <meta name="og:description" content={data.ogDescription_loc} />
              <meta name="og:type" content={data.pageType} />
              <meta name="og:image" content={data.seoImage && data.seoImage.url2x} />
              <meta name="og:url" content={getLocationHref(this.props.location.pathname)} />
              <meta name="twitter:card" content="summary_large_image" />
              <meta name="twitter:site" content="Snapchat Ads" />
              <meta name="twitter:url" content={getLocationHref(this.props.location.pathname)} />
              <meta name="twitter:title" content={data.ogTitle_loc} />
              <meta name="twitter:description" content={data.ogDescription_loc} />
              <meta name="twitter:image" content={data.seoImage && data.seoImage.url2x} />
              <script type="application/ld+json">
                {`{
                  "@context": "http://schema.org",
                  "@type": 'NewsArticle',
                  "mainEntityOfPage": {
                    "@type": "WebPage",
                    "@id": "${getLocationHref(this.props.location.pathname)}",
                  },
                  "headline": "${data.title_loc}",
                  "image": {
                    "@type": "ImageObject",
                    "url": "${data.seoImage && data.seoImage.url2x}",
                  },
                  "publisher": {
                    "@type": "Organization",
                    "name": "Snapchat",
                  },
                }`}
              </script>
            </Helmet>
            <A css={pageStyles.skipContent()} id="skip-to-content" href={`${this.props.location.pathname.replace(/\/+$/, '')}#content`}>skip to main content</A>
            <Div className="Header" ref="header">
              { this.renderNavigation() }
            </Div>
            <Div css={ pageStyles.pageContent(96)} id="content" role="main" ref="articleContent">
              { data &&
                <Div>
                  { data.id !== '*' &&
                    <Div>
                      <Div css={ blogStyles.blogHeadlineWrapper() }>
                        { this.renderHeading() }
                      </Div>
                    </Div>
                  }
                  <Div className={'page-content'} ref="pageContent">
                    <SectionList
                      sections={this.props.data.sections}
                      sectionRef={this.handleSectionRef}
                      routeId={this.props.data.id}
                      analyticsEvent={this.props.analyticsEvent}
                      pageColor={data.color}
                      isMobile={this.props.isMobile}
                      pageType={this.props.data.entryType}
                    />
                    <Footer
                      analyticsEvent={this.props.analyticsEvent}
                      adManagerContent={this.props.footerData.adManagerContent}
                      footerContent={footerContent}
                      annotationContent={data.annotations}
                      policyTermsContent={policyTermsContent}
                      languageContent={languageGroup}
                      showAnnotations={false}
                    />
                  </Div>
                </Div>
              }
            </Div>
          </Div>
      );
  }
}

export const BlogPageTemplate = connect(mapStateToProps, {analyticsEvent})(IBlogPageTemplate);



