import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

const { mediaQueries, screenSizes, padding, borderRadius } = utils;

const gridColumns = 3;
const gutterWidths = {
  tablet: 24,
  desktop: 48,
};

export const container = {
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  marginTop: 40,
  maxWidth: 705,

  [`${mediaQueries.phablet}`]: {
    maxWidth: 1200,
    marginLeft: 'auto',
    marginRight: 'auto',
  },
};

export const rowStyle = {
  display: 'flex',
  flexDirection: 'row',
  alignItems: 'center',
  justifyContent: 'center',
  flexWrap: 'wrap',
};

export const caption = {
  marginTop: padding.normal,
  marginBottom: 40,
  textAlign: 'center',
  maxWidth: 775,
  padding: padding.tiny,
  fontSize: 16,
  lineHeight: '17px',

  color: colors.regularCharcoal,
  [`${mediaQueries.phablet}`]: {
    marginTop: padding.normal,
    fontSize: 17,
    lineHeight: '24px',
  },
};

export const imageWrapper = () => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  borderRadius: borderRadius.desktopContainer,
  overflow: 'hidden',
  width: '100%',
  marginBottom: padding.tiny,

  ['&:last-child']: {
    marginRight: 0,
    marginBottom: 0,
  },

  [`${mediaQueries.phablet}`]: {
    width: `calc(${1 / gridColumns * 100}% - ${((gutterWidths.tablet * (gridColumns - 1)) / gridColumns)}px)`,
    marginRight: gutterWidths.tablet,
    marginBottom: 0,
    padding: 0,
  },

  [`${mediaQueries.normal}`]: {
    width: `calc(${1 / gridColumns * 100}% - ${((gutterWidths.desktop * (gridColumns - 1)) / gridColumns)}px)`,
    marginRight: gutterWidths.desktop,
    marginBottom: 0,
    padding: 0,
  },
});

export const image = () => ({
  borderRadius: borderRadius.desktopContainer,
  width: '100%',
});



