import * as React from 'react';
import * as glamorous from 'glamorous';
import * as styles from './SidePagination-styles';
import * as smoothScroll from 'utils/smoothScroll';

import { whereAmI } from 'utils/helpers';
import { SidePaginationItem } from './SidePaginationItem';
import { black, white } from 'utils/styles/colors';
import { screenSizes } from 'utils/styles';

import { throttle } from 'lodash';

const {
  Div,
  Ul,
} = glamorous;

export interface ISidePaginationProps {
  items: Array<any>;
  primaryColor: string;
  secondaryColor: string;
  analyticsEvent?: Function;
}

export interface ISidePaginationState {
  activeIndex: number;
  colorMode: boolean;
  scrolling: boolean;
  scrollTop: number;
}

export class SidePagination extends React.Component<ISidePaginationProps, ISidePaginationState> {
  state = {
    activeIndex: -1,
    scrolling: false,
    colorMode: false,
    scrollTop: 0,
  };

  scrollCallback: any;
  navRef: any;

  componentDidMount() {
    this.scrollCallback = throttle(this.handleScroll.bind(this), 300);
    document.addEventListener('scroll', this.scrollCallback);
    this.handleScroll();
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.scrollCallback);
  }

  handleScroll = () => {
    if (this.state.scrolling || (window as any).innerWidth < screenSizes.phablet ) {
      return false;
    }

    const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);
    const windowOffset: number = (window as any).innerHeight / 2;

    let activeIndex = this.state.activeIndex;
    let colorMode = this.state.colorMode;

    this.props.items.forEach((item) => {
      if (scrollTop >= item.ref.offsetTop + item.yOffset - (item.index === 0 ? 0 : windowOffset)) {
        activeIndex = item.index;
        colorMode = Boolean(item.color);
      }
    });

    if (document.documentElement.scrollTop > 5) {
      if (activeIndex !== this.state.activeIndex) {
        this.setState({ activeIndex, colorMode });
      }
    } else {
      this.setState({ activeIndex: 0, colorMode });
    }
  }

  handleClick(ref: any, yOffset: number, title: string, index: number) {
    if (smoothScroll) {
      smoothScroll.animateScroll(ref.offsetTop + yOffset, false, {
        before: () => {
          this.setState({
            scrolling: true,
          });
        },
        after: () => {
          this.setState({
            scrolling: false,
            scrollTop: ref.offsetTop + yOffset,
          });
          this.handleScroll();
        },
      });
    }
    console.log(ref);
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${whereAmI()}:sideNav-${index + 1}-${title}`,
        category: `${whereAmI()}:sideNav-${index + 1}-${title}`,
      });
    }
  }

  renderItems() {
    return this.props.items.map((item, index) => {
      return (
        <SidePaginationItem
          primaryColor={ this.state.colorMode ? black : this.props.primaryColor }
          secondaryColor={ this.state.colorMode ? white : this.props.secondaryColor }
          key={ `${item.title} ${index}` }
          handleClick={ this.handleClick.bind(this) }
          index={ item.index }
          title={ item.title }
          active={ this.state.activeIndex === item.index }
          sectionRef={ item.ref }
          yOffset={ item.yOffset } />
        );
    });
  }
  render() {
    const { activeIndex } = this.state;
    return (
      <Div>
        <Ul
          innerRef={(el) => { this.navRef = el; } }
          css={ styles.paginationList(activeIndex) }>
          {this.renderItems()}
        </Ul>
      </Div>
    );
  }
}



