import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { Z_INDEXES } from '../../../../utils/constants';
const { mediaQueries, screenSizes } = utils;

export const globalNav = ( color: string = '#ffffff' ) => ({
  zIndex: Z_INDEXES.NAVIGATION,
  position: 'relative',
  width: '100%',
});

export const globalNavContainer = ( isScrolled: boolean, navActive: boolean, navIsVisible: boolean ) => ({
  zIndex: Z_INDEXES.NAVIGATION,
  height: 98,
  width: '100%',
  position: 'fixed',
  textAlign: 'right',
  background: isScrolled ? colors.white : 'transparent',
  boxShadow: isScrolled && '0 2px 4px rgba(0,0,0,.05)',
  transition: !navIsVisible && 'background 0.2s ease-out',
  willChange: 'transform',
});

export const logoContainer = () => ({
  position: 'absolute',
  top: 25,
  left: 30,
  zIndex: Z_INDEXES.NAVIGATION,
});

export const rightContainer = (hideCTA, buttonWidth, transition = false, isLoaded) => ({
  opacity: isLoaded ? 1 : 0,
  transform: 'none',
  transition: `transform ${transition ? 0.4 : 0}s ease-out, transform ${transition ? 0.4 : 0}s ease-out`,
  display: 'flex',
  justifyContent: 'flex-end',
  willChange: 'transform',
  [`${mediaQueries.phabletNav}`]: {
    transform: `translateX(${hideCTA ? buttonWidth : 0}px)`,
  },
});

export const tabContainer = () => ({
  display: 'inline-block',
  verticalAlign: 'top',
});

export const subnavContainer = () => ({
  display: 'inline-block',
  top: 32,
  position: 'relative',
  marginRight: 30,
  verticalAlign: 'top',
  zIndex: Z_INDEXES.SUBNAV,
});

export const buttonMenuWrapper = () => ({
    display: 'flex',
    justifyContent: 'flex-end',
});

export const buttonContainer = () => ({
  display: 'inline-block',
  top: 24,
  position: 'relative',
  marginRight: 8,
  [`@media (max-width:390px)`]: {
    maxWidth: 175,
  },
  [`${mediaQueries.phablet}`]: {
    marginRight: 30,
  },
});

export const navWrapper = () => ({
  display: 'none',
  [`${mediaQueries.phabletNav}`]: {
    display: 'inline-flex',
  },
});

export const hamburgerContainer = () => ({
  display: 'inline-flex',
  top: 24,
  position: 'relative',
  marginRight: 10,
  [`${mediaQueries.phabletNav}`]: {
    display: 'none',
  },
});



