import * as React from 'react';
import { Link } from 'react-router';
import * as glamorous from 'glamorous';
import { TabArray, NavTab, DropdownTab } from '../TabArray';
import {
  globalNavStyle,
  globalNavWrapper,
  globalNavTab,
} from './GlobalNavTabArray-styles';

const {
  Component,
  PropTypes,
} = React;
const { Div } = glamorous;

export interface IGlobalNavTabArrayProps {
  tabs?: Array<{
    label: string;
    link?: string;
    onSelect?: any;
    selected?: boolean;
    links?: Array<any>;
  }>;
  bgColor?: string;
  fontColor?: string;
  analyticsEvent?: Function;
}

export class GlobalNavTabArray extends Component<IGlobalNavTabArrayProps, void> {
  static propTypes = {
    tabs: PropTypes.arrayOf(PropTypes.shape({
      label: PropTypes.string.isRequired,
      onSelect: PropTypes.func,
      selected: PropTypes.bool,
    })),
    bgColor: PropTypes.string,
    fontColor: PropTypes.string,
  };

  renderTabs() {
    const { tabs, fontColor } = this.props;
    return tabs.map((tab, index) => {
      return tab.links
        ? (
          <DropdownTab
            key={`tab-${index}`}
            css={globalNavTab(fontColor)}
            selected={tab.selected}
            selectedColor={fontColor}
            onSelect={tab.onSelect.bind(this, tab.link)}
            items={tab.links}
            analyticsEvent={ this.props.analyticsEvent }
            uri={tab.link}
          >
            {tab.label}
          </DropdownTab>
        )
        : (
          <NavTab
            key={`tab-${index}`}
            css={globalNavTab(fontColor)}
            selected={tab.selected}
            selectedColor={fontColor}
            onSelect={tab.onSelect.bind(this, tab.link)}
            uri={tab.link}
          >
            {tab.label}
          </NavTab>
        );
    });
  }
  render() {
    const {
      bgColor,
      fontColor,
    } = this.props;
    return (
      <Div css={globalNavWrapper(bgColor)}>
        <TabArray css={globalNavStyle(fontColor)}>
          {this.renderTabs()}
        </TabArray>
      </Div>
    );
  }
}



