import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries } = utils;

export const defaults = () => ({
  display: 'block',
  position: 'relative',
  left: '0px',
  minWidth: '100%',
  height: '100%',
  overflow: 'hidden',
  opacity: 0,
  [`${mediaQueries.phablet}`]: {
    paddingBottom: 0,
  },
});
export const centered = () => ({
  left: '50%',
  transform: 'translateX(-50%)',
});

export const container = () => ({
  display: 'block',
  position: 'relative',
  left: '0px',
  width: '100%',
  height: '100%',
  overflow: 'visible',
  whiteSpace: 'nowrap',
  boxSizing: 'border-box',
});

export const element = () => ({
  display: 'inline-block',
  position: 'relative',
  top: '0',
  left: '0px',
  height: '100%',
  verticalAlign: 'middle',
});

export const dotNav = () => ({
  display: 'none',
  [`${mediaQueries.normal}`]: {
    position: 'absolute',
    display: 'block',
    right: `${ 32 }px`,
    left: 'auto',
    bottom: `${ (32 - 8) + 4 }px`,
    transform: 'none',
  },
});

export const flex = () => ({
  display: 'flex',
});

export const flexItem = () => ({
  display: 'flex',
  flex: '0 0 auto',
  height: 'auto',
});

export const centerItem = () => ({
  left: '50%',
  transform: 'translateX(-50%)',
});

export const allowOverflow = () => ({
  overflow: 'visible',
});

export const alignTop = () => ({
    verticalAlign: 'top',
});



