import * as React from 'react';
import * as glamorous from 'glamorous';

import * as styles from './KeyLearningsBlock-styles';
import { MetricBlock } from '../MetricBlock';

const { Div } = glamorous;

export interface IKeyLearningsBlockProps {
  metrics: Array<any>;
}

export class KeyLearningsBlock extends React.Component<IKeyLearningsBlockProps, void> {

  countProps(prop) {
    if (prop.length === 4) {
      return ({
        container: styles.container,
        metricWrapper: styles.metricWrapper,
      });
    } else if (prop.length === 3) {
      return ({
        container: styles.container3Up,
        metricWrapper: styles.metricWrapper3Up,
      });
    } else if (prop.length === 2) {
      return ({
        container: styles.container2Up,
        metricWrapper: styles.metricWrapper2Up,
      });
    } else if (prop.length === 1) {
      return ({
        container: styles.container1Up,
        metricWrapper: styles.metricWrapper1Up,
      });
    }
  }

  computedStyles = this.countProps(this.props.metrics);

  items = this.props.metrics.map((metric, i) => (
    <Div
      key={i}
      css={this.computedStyles.metricWrapper()}
    >
      <MetricBlock
        imageAndBody={{
          imageUrl: metric.imageUrl,
          imageAlt: metric.imageAlt,
          copy: metric.copy,
        }}
      />
    </Div>
  ));

  render() {
    return (
      <Div css={this.computedStyles.container()} >
        {this.items}
      </Div>
    );
  }
}



