import glamorous from 'glamorous';
import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { hexToRGB } from 'utils/helpers';
import * as glamor from 'glamor';

const { mediaQueries, screenSizes, padding } = utils;
const fontSize = '19px';
const cardMargin = 8;
const cardMarginBottom = 8;

export const cardSetWrapper = ({ article = false, background = true, isThreeUp = false }) => ({
  display: 'flex',
  justifyContent: isThreeUp ? 'center' : 'left',
  alignItems: article ? 'flex-start' : 'normal',
  flexFlow: 'row wrap',
  position: 'relative',
  boxSizing: 'border-box',
  textAlign: 'left',
  marginRight: -(cardMargin * 2),
});

export const cardLayoutWrapper = () => ({
  maxWidth: utils.columns.desktopMaxWidth,
  margin: '0 auto',

  [`${mediaQueries.phablet}`]: {
    paddingRight: cardMargin,
  },
  [`${mediaQueries.normal}`]: {
    paddingRight: 0,
  },
});

export const cardSetChild = ({...imageHeight}, type, isMobile, isThreeUp = false) => ({
  maxWidth: '100%',
  width: '100%',
  paddingRight: cardMargin,
  '& .card-image': {
    paddingTop: type === 'creativeSpotlightDevice' && isMobile ? imageHeight.mobileSpacing : imageHeight.mobileBase,
    '& .card-featured-image': type === 'creativeSpotlightDevice' && isMobile && {
       position: 'relative',
       display: 'block',
       paddingTop: imageHeight.mobileFeatured,
    },
  },
  '& .card-content': {
    width: '100%',
  },
  '& .grid-card': {
    paddingBottom: `calc(50% + ${cardMargin / 2}px)`,
  },
  [`${mediaQueries.phabletCardMax}`]: {
    '& .grid-card': {
      paddingBottom: 50,
    },
    '& .card-content': {
      position: 'relative',
      bottom: 0,
    },
    '&:first-of-type, &:nth-of-type(5n+6)': !isThreeUp && {
      '& .mobile-logo-image': {
        display: 'block',
      },
      '& .card-image': {
        paddingTop: imageHeight.mobileSpacing,
        marginBottom: type === 'successStory' && -60,
        '& .card-featured-image': {
          backgroundPosition: 'center center',
          height: '60%',
          opacity: 1,
          transform: 'scale(1)',
          position: 'relative',
          paddingTop: imageHeight.mobileFeatured,
          display: 'block',
        },
        '& .card-logo-image': {
          opacity: type === 'successStory' && 0,
        },
      },
    },
  },
  [`${mediaQueries.phabletToNormal}`]: {
    maxWidth: '50%',
    flex: '0 0 50%',
    '& .card-image': {
      paddingTop: '75%',
      '& .card-featured-image': {
        height: '70%',
      },
    },
    '&:first-of-type, &:nth-of-type(5n+6)': {
      flex: '0 0 100%',
      maxWidth: '100%',
      '& .card-image': {
        paddingTop: '37.5%',
        '& .card-featured-image': {
          backgroundPosition: 'center right',
          height: '100%',
          opacity: 1,
          transform: 'scale(1)',
        },
      },
      '& .card-content': {
        width: '50%',
      },
      '& .grid-card': {
        paddingBottom: `25%`,
      },
    },
  },
  [`${mediaQueries.normal}`]: {
    maxWidth: '33%',
    flex: '0 0 33%',
    '& .card-image': {
      paddingTop: '65%',
    },
    '&:first-of-type, &:nth-of-type(10n + 1), &:nth-of-type(10n+7)': !isThreeUp && {
      flex: '0 0 66%',
      maxWidth: '66%',
      '& .card-image': {
        paddingTop: '32.5%',
        '& .card-featured-image': {
          backgroundPosition: 'center right',
          height: '100%',
          opacity: 1,
          transform: 'scale(1)',
        },
      },
      '& .card-content': {
        width: '50%',
      },
      '& .grid-card': {
        paddingBottom: `25%`,
      },
    },
  },
});



