import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries, screenSizes } = utils;

export const cardWrapper = () => ({
  paddingTop: 48,
});

export const noResults = () => ({
    textAlign: 'center',
    paddingTop: 168,
    paddingBottom: 216,
    [`${mediaQueries.phablet}`]: {
        padding: 152,
    },
});

export const selectWrapper = () => ({
    flex: '1 0 auto',
    alignSelf: 'auto',
    [`${mediaQueries.normal}`]: {
        '&.select-wrapper': {
            marginRight: 15,
            alignSelf: 'center',
            '&:last-of-type': {
                marginRight: 0,
            },
        },
    },
});

export const filtersWrapper = () => ({
    padding: '0 32px',
    [`${mediaQueries.phablet}`]: {
        padding: 0,
    },
});



