import * as React from 'react';
import { Markdown } from '../../atoms/Markdown';
import glamorous from 'glamorous';
import { autobind } from 'core-decorators';
const { Div, Img, Button } = glamorous;
import * as styles from './CookieNotice-styles';

export interface ICookieNoticeProps {
  copy: string;
  onClick?: Function;
  imageAlt?: string;
}

export class CookieNotice extends React.Component<ICookieNoticeProps, void> {
  @autobind
  closeHandler() {
    this.props.onClick();
  }

  render() {
    return (
      <Div css={styles.container()}>
        <Div css={styles.innerContainer()} >
          <Div css={styles.imageContainer()} >
            <Img css={styles.image()} src="/images/cookie-1.svg" alt={this.props.imageAlt} />
          </Div>
          <Div css={styles.content()}>
            <Markdown copy={ `${this.props.copy}` } styleOverrides={styles.markdownStyles()} />
          </Div>
          <Div css={styles.imageContainer()} >
            <Img css={styles.image()} src="/images/cookie-2.svg" alt={this.props.imageAlt} />
          </Div>
          <Button onClick={this.closeHandler} css={styles.close()}/>
        </Div>
      </Div>
    );
  }
}



