import { black } from 'utils/styles/colors';

export const tabStyles = ({ selected, selectedColor = black }) => ({
  display: 'inline-flex',
  userSelect: 'none',
  position: 'relative',
  padding: '16px',
  cursor: 'pointer',
  color: selected && selectedColor,
  fontWeight: selected ? 600 : 400,
  transition: 'all 0.2s',
  outline: '0 dashed rgba(0,0,0,0.2)',
  minHeight: '0',
  height: 'auto',
  '[data-whatinput="keyboard"] &:focus': {
    outlineWidth: '1px',
  },
  '&::after': {
    transition: 'all 0.2s ease-out',
    position: 'absolute',
    content: `''`,
    display: 'block',
    background: 'transparent',
    boxShadow: `0 2px 0 0 ${selectedColor}`,
    borderRadius: 2,
    width: '100%',
    transform: selected ? 'scale(1, 1)' : 'scale(0, 1)',
    height: 2,
    left: 0,
    right: 0,
    bottom: 0,
    margin: '0 auto',
  },
});

export const imageTabStyles = () => ({
  flexDirection: 'column',
  alignItems: 'center',
  display: 'block',
  '&:hover .image-tab-normal': {
    display: 'block',
  },
  '&:hover .image-tab-hover': {
    display: 'none',
  },
});

export const tabListStyles = () => ({
  display: 'flex',
  fontSize: 19,
});

const IMAGE_HEIGHT = 55;

export const imageTabNormalImage = () => ({
  width: '80%',
  display: 'block' ,
  margin: '0 auto',
});

export const imageTabHoverImage = () => ({
  width: '80%',
  display: 'none',
  margin: '0 auto',
});



