import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

function getTextColor(color, active, index, textColor) {
    if (textColor) { return textColor; }
    return active === index ? colors.white : colors[color];
}

export const navIcon = (color, active, index, textColor = null) => ({
    height: 42,
    width: 42,
    color: getTextColor(color, active, index, textColor),
    border: active !== index ? `2px solid ${colors[color]}` : 'none',
    borderRadius: 100,
    position: 'relative',
    backgroundImage: active === index ? colors[`${color}Gradient`] : 'none',
    backgroundColor: 'transparent',
    cursor: 'pointer',
    fontWeight: '600',
    fontSize: 15,
});

export const navIconDescription = (active, index, color) => ({
    fontWeight: 600,
    fontSize: 16,
    padding: '20px 8px 0px 8px',
    lineHeight: '20px',
    '&::after': {
        content: active === index ? `''` : null,
        borderStyle: 'solid',
        borderWidth: '2px 2px 0 0',
        display: 'block',
        height: '0.8em',
        width: '0.8em',
        position: 'absolute',
        left: '50%',
        transform: 'translateX(-50%)translateY(5px)rotate(135deg)',
        verticalAlign: 'top',
        color: colors[color],
    },
});

export const navIconWrapper = (description, active, index, color) => ({
    position: 'relative',
    flex: '1 1 100%',
    textAlign: 'center',
    maxWidth: 200,
});

export const navIconList = () => ({
    display: 'flex',
    justifyContent: 'center',
    padding: '25px 0',
    marginBottom: 30,
});



