import * as React from 'react';
import * as glamorous from 'glamorous';
import { connect } from 'react-redux';
import {
  logoCardWrapper,
  logoCardImage,
  logoWrapper,
  verticalHelper,
} from './LogoGarden-styles';

const { Component, PropTypes } = React;
const {
  Div,
  Img,
  Span,
  A,
} = glamorous;

export interface ILogoCardProps {
  title: string;
  image: string;
  imageAlt?: string;
  image2x?: string;
  link: string;
  isHighDensity?: boolean;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  isHighDensity: state.app.isHighDensity,
});

export class LogoCardClass extends Component<ILogoCardProps, void> {
  static propTypes = {
    title: PropTypes.string,
    image: PropTypes.string,
    image2x: PropTypes.string,
    link: PropTypes.string,
  };

  renderLogo(isRetina, image2x, image, imageAlt) {
    return (
      <Div css={logoWrapper(isRetina)}>
        <Span css={verticalHelper()}/>
        <Img src={(isRetina && image2x) ? image2x : image} alt={imageAlt} css={logoCardImage()}/>
      </Div>
    );
  }

  render() {
    const {
      title,
      image,
      image2x,
      link,
      imageAlt,
      isHighDensity,
    } = this.props;

    let imgSrc = image;

    if (isHighDensity && image2x) {
      imgSrc = image2x;
    }

    return (
      <Div css={logoCardWrapper()}>
        <A href={link} target="_blank" title={title}>
          <Div css={logoWrapper(isHighDensity)}>
            <Span css={verticalHelper()}/>
            <Img src={imgSrc} alt={imageAlt} css={logoCardImage()}/>
          </Div>
        </A>
      </Div>
    );
  }
}

export const LogoCard = connect(mapStateToProps)(LogoCardClass);



