import * as React from 'react';
import * as glamorous from 'glamorous';
import 'what-input';

const { Li } = glamorous;

import * as styles from './TabArray-styles';

export type TabProps = {
  css?: { [key: string]: any };
  onSelect: () => void;
  selected?: boolean;
  selectedColor?: string;
};

export const Tab: React.SFC<TabProps> =
  ({ selected, children, onSelect, selectedColor, css }) => {
    return (
      <Li
        css={[styles.tabStyles({ selected, selectedColor }), css]}
        role="tab"
        tabIndex="0"
        onClick={() => onSelect()}
        onKeyDown={(e) => {
          if (e.keyCode === 13 || e.keyCode === 32) {
            e.preventDefault();
            onSelect();
          }
        }}
      >
        {children}
      </Li>
    );
  };
Tab.displayName = 'Tab';



