import * as React from 'react';
import glamorous from 'glamorous';
import { Markdown } from '../../atoms/Markdown';
const { H2, Div } = glamorous;

import * as styles from './HeadlineTitle-styles';

export interface IHeadlineTitleProps {
  primary?: boolean;
  children: string;
  noMax?: boolean;
  css?: any;
  noPadding?: boolean;
  small?: boolean;
  isRichText?: boolean;
}

export class HeadlineTitle extends React.Component<IHeadlineTitleProps, void> {
    static defaultProps = {
        isRichText: false,
    };
    render() {
        let combinedStyles;
        const defaultStyles = styles.baseStyles(this.props.primary, this.props.noMax, this.props.small);
        if (this.props.css) {
            combinedStyles = Object.assign(defaultStyles, this.props.css);
        } else {
            combinedStyles = defaultStyles;
        }
        return (
            <Div css={styles.headlineWrapper(this.props.noPadding, this.props.small)}>
                { this.props.isRichText
                ?
                    <Markdown copy={ `${this.props.children}` } />
                :
                    <H2 css={combinedStyles}>{this.props.children}</H2>
                }
            </Div>
        );
    }
}



