import * as React from 'react';
import glamorous from 'glamorous';
const { Div } = glamorous;

import { Image } from '../../../atoms/Image';
import { LazyloadFade } from '../../../atoms/LazyloadFade';
import { Video } from '../../../atoms/Video';

import { IMediaProps } from '../';
import * as styles from '../Media-styles';

export class MediaElement extends React.Component<IMediaProps, void> {

  render() {
    if (this.props.image) {
      const { imageUrl, title, ctaText, natural } = this.props.image;
      const paddingBottom: string = `${ this.props.aspectRatio * 100 }%`;
      const modifiedStyles = {
        mediaContainer: {
          position: 'relative',
          width: `${ this.props.mediaWidthRelativeToContainer }`,
          ':after': {
            content: '""',
            display: 'block',
            paddingBottom,
          },
        },
      };
      return (
        <div style={this.props.containerStyle}>
          <Div
            style={this.props.containerStyle}
            css={modifiedStyles.mediaContainer}
          >
            <LazyloadFade>
              <Image
                imageUrl={imageUrl}
                title={title}
                natural={natural}
                maxHeight={this.props.maxHeight}
                fullWidth={this.props.fullWidth}
                rounded={this.props.roundedCorner}
              />
            </LazyloadFade>
          </Div>
        </div>
      );
    }

    const {
      videoSrc,
      posterImageUrl,
      posterVideoSrc,
      ctaText,
      smallLayout,
      heroMediaStyle,
    } = this.props.video;

    const { ratioWidth, ratioHeight, isVideoProgress } = this.props;
    return (
      <Div>
        <Video
          {...this.props}
          width={ isVideoProgress ? '100%' : ratioWidth }
          height={ isVideoProgress ? '100%' : ratioHeight }
          videoSrc={ videoSrc }
          posterImageUrl={ posterImageUrl }
          posterVideoSrc={ posterVideoSrc }
          smallLayout={ smallLayout }
          autosize={this.props.autosize}
        />
      </Div>
    );
  }
}



