import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { hexToRGB } from 'utils/helpers';
const { mediaQueries, screenSizes } = utils;

const shadowRGB = hexToRGB(colors.shadowGray);

export const formWrapper = () => ({
  margin: '0em auto',
  marginTop: -64,
  maxWidth: '494px',
  [utils.mediaQueries.phabletMax]: {
    fontSize: '14px',
  },
});

export const inputStyle = (error, active = false, type) => ({
  borderRadius: active ? '2em 2em 0 0' : '2em',
  margin: '0 0 8px',
  outline: 'none',
  border: (type === 'select') && `1px solid ${colors.lightGray}`,
  width: '100%',
  color: colors.black,
  boxShadow: (type === 'select') && `0 2px 4px rgba(${shadowRGB}, .5)`,
  borderColor: (type === 'select' && error && !active) && colors.regularRed,
  '& input': {
    border: error ? `1px solid ${colors.regularRed}` : `1px solid ${colors.lightGray}`,
    width: '100%',
    padding: '1rem',
    fontSize: '1em',
    borderRadius: '2em',
    appearance: 'none',
    fontFamily: utils.sansFont,
    lineHeight: 1,
    height: '48px',
    '&::placeholder': {
      color: colors.black,
      lineHeight: 1,
    },
    '&:focus, &:active': {
      outline: 'none',
      border: `2px solid ${colors.regularGray}`,
      borderColor: error && colors.regularRed,
    },
  },
  '&.firstName': {
    display: 'inline-block',
    width: 'calc(50% - .25em)',
    marginRight: '.5em',
  },
  '&.lastName': {
    display: 'inline-block',
    width: 'calc(50% - .25em)',
  },
  '& textarea': {
    border: `1px solid ${colors.lightGray}`,
    borderColor: error && colors.regularRed,
    appearance: 'none',
    width: '100%',
    fontSize: '1em',
    padding: '1em',
    borderRadius: '2em',
    color: colors.black,
    '&::placeholder': {
      color: colors.black,
    },
    '&:focus, &:active': {
      outline: 'none',
      border: `2px solid ${colors.regularGray}`,
      borderColor: error && colors.regularRed,
    },
  },
});

export const selectStyle = () => ({
  position: 'relative',
  borderRadius: '2em',
  border: `1px solid transparent`,
  '&:focus, &:active': {
    outline: 'none',
    border: `1px solid ${colors.lightGray}`,
    boxShadow: `0 2px 4px rgba(${shadowRGB}, .8)`,
  },
  '& select': {
    display: 'none',
  },
});

export const submitStyle = (valid) => ({
  color: colors.black,
  appearance: 'none',
  boxShadow: 'none',
  border: 'none',
  padding: '20px 50px',
  borderRadius: '2em',
  textTransform: 'uppercase',
  letterSpacing: '2px',
  fontWeight: 700,
  fontSize: '.8em',
  background: colors.regularYellowGradient,
  width: 'auto',
  marginTop: 15,
  display: 'inline-block',
  float: 'right',
  transition: 'background .2s ease-in-out',
  cursor: 'pointer',
  '&:hover': {
    background: valid && colors.regularYellowGradientHover,
  },
  [`${mediaQueries.phablet}`]: {
    marginLeft: '2%',
    width: '100%',
  },
});

export const optionsWrapper = (active) => ({
  maxHeight: active ? '300px' : 0,
  overflow: active ? 'auto' : 'hidden',
  visibility: !active && 'hidden',
  color: colors.black,
  borderLeft: `1px solid ${colors.regularGray}`,
  borderRight: `1px solid ${colors.regularGray}`,
  borderBottom: `1px solid ${colors.regularGray}`,
  borderRadius: '0 0 2em 2em',
  position: 'absolute',
  top: '2.8em',
  left: -2,
  right: -2,
  zIndex: active && 2,
  transition: 'max-height .2s ease-in-out',
  boxShadow: `0 2px 4px rgba(${shadowRGB}, .8)`,
});

export const selectOption = () => ({
  padding: '1rem',
  background: 'white',
  cursor: 'pointer',
  '&:hover': {
    backgroundColor: colors.lightGray,
  },
});

export const selectedOption = (value, active = false) => ({
  color: colors.black,
  fontWeight: 400,
  padding: '1em',
  background: 'white',
  borderRadius: active ? '2em 2em 0 0' : '2em',
  zIndex: '1',
  cursor: 'pointer',
  position: 'relative',
  '&::after': {
    position: 'absolute',
    right: '16px',
    top: '50%',
    transform: 'translateY(-50%)',
    content: `''`,
    width: '.25em',
    borderTop: '.4em solid black',
    borderLeft: '.25em solid transparent',
    borderRight: '.25em solid transparent',
  },
});

export const disclaimer = () => ({
  fontSize: '12px',
  color: colors.regularCharcoal,
  padding: '32px 0',
  textAlign: 'center',
  maxWidth: (5 / 17) * utils.maxContentWidth,
  width: '100%',
  lineHeight: '1rem',
  margin: '0 auto',
});

export const message = () => ({
  textAlign: 'center',
  paddingBottom: 20,
});

export const formFooter = () => ({
  width: '100%',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  flexDirection: 'column',
  '& .g-recaptcha': {
    display: 'inline-block',
  },
  [`${mediaQueries.phablet}`]: {
    flexDirection: 'row',
  },
});

export const formNode = (submitted) => ({
  visibility: submitted && 'hidden',
  opacity: submitted ? 0 : 1,
  transform: submitted && 'scaleY(0)',
  transition: 'all .2s ease-in-out',
  maxHeight: submitted ? 0 : 3000,
});



