import * as React from 'react';
import glamorous from 'glamorous';
import * as PropTypes from 'prop-types';
const { Div, Button } = glamorous;

import * as styles from './CircleDeviceNavigation-styles';

interface INavItemsProps {
    symbol: string;
    description: any;
}

export interface ICircleDeviceNavigationProps {
    navItems: Array<INavItemsProps>;
    color: string;
    active: number;
    onClick?: Function;
    textColor ?: string;
}

export const CircleDeviceNavigationPropTypes = {
    navItems: PropTypes.arrayOf(PropTypes.shape({
        symbol: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
    })),
    color: PropTypes.string.isRequired,
    active: PropTypes.number.isRequired,
    onClick: PropTypes.func,
    textColor: PropTypes.string,
};

export class CircleDeviceNavigation extends React.Component<ICircleDeviceNavigationProps, void> {

    static propTypes = CircleDeviceNavigationPropTypes;

    createNavIcon(item, i) {
        const {
            color,
            active,
            textColor,
            onClick,
        } = this.props;
        return (
            <Div key={i} css={styles.navIconWrapper(item.description, active, i, color)}>
                <Button css={styles.navIcon(color, active, i, textColor)} key={i} onClick={((evt) => onClick(evt, i))}>{item.symbol}</Button>
                <Div css={styles.navIconDescription(active, i, color)}>{item.description}</Div>
            </Div>
        );
    }

    render() {

        return (
            <Div css={styles.navIconList()}>
                {this.props.navItems.map(this.createNavIcon.bind(this))}
            </Div>
        );
    }
}



