import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';

import { Header } from '../../atoms/Header';
import { Markdown } from '../../atoms/Markdown';
import * as styles from './MetricBlock-styles';
import { Image } from '../../atoms/Image';

const {
  Div,
  H3,
  P,
  Img,
} = glamorous;

export interface IMetricBlockProps {
  numbMetricStat?: InumbMetricStatProps;
  imageAndBody?: IImageAndBody;
  imageAndHeader?: IImageAndHeader;
  miniBlock?: boolean;
  css?: object;
}

export interface InumbMetricStatProps {
  headline?: string;
  subhead?: string;
}

export interface IImageAndBody {
  imageUrl?: string;
  imageAlt?: string;
  copy?: string;
}

export interface IImageAndHeader {
  imageUrl?: string;
  imageAlt?: string;
  copy?: string;
}

export class MetricBlock extends React.Component<IMetricBlockProps, void> {

  static propTypes = {
    numbMetricStat: PropTypes.object,
    imageAndBody: PropTypes.object,
    imageAndHeader: PropTypes.object,
    miniBlock: PropTypes.bool,
  };

  renderScene() {
    if (this.props.numbMetricStat) {
      return (
        <Div className="metric-block-container" css={[styles.container(false, this.props.miniBlock), this.props.css]}>
          <Header tag="h3" css={styles.headline(this.props.miniBlock)}>{this.props.numbMetricStat.headline}</Header>
          <Markdown copy={`${this.props.numbMetricStat.subhead}`} styleOverrides={styles.markdownStyles(false, this.props.miniBlock)} />
        </Div>
      );
    } else if (this.props.imageAndBody) {
        return (
          <Div className="metric-block-container" css={[styles.container(this.props.imageAndBody, true), this.props.css]}>
            <Div>
              <Img css={styles.image()} src={this.props.imageAndBody.imageUrl} alt={this.props.imageAndBody.imageAlt} />
            </Div>
            <Markdown copy={`${this.props.imageAndBody.copy}`} styleOverrides={styles.markdownStyles(false, false, true)} />
          </Div>
        );
    } else if (this.props.imageAndHeader) {
      return (
        <Div className="metric-block-container" css={[styles.container(this.props.imageAndHeader), this.props.css]}>
          <Div>
            <Img css={styles.image(this.props.imageAndHeader)} src={this.props.imageAndHeader.imageUrl} alt={this.props.imageAndHeader.imageAlt} />
          </Div>
          <Markdown copy={`${this.props.imageAndHeader.copy}`} styleOverrides={styles.markdownStyles(this.props.imageAndHeader)} />
        </Div>
      );
    }

  }

  render() {
    return (
      this.renderScene()
    );
  }
}



