import * as React from 'react';
import * as PropTypes from 'prop-types';
import { Link, browserHistory } from 'react-router';
import glamorous from 'glamorous';
import { connect } from 'react-redux';
import { writeCookie } from 'utils/helpers';
const { Div, Ul, Li, Form, Select } = glamorous;

import * as styles from './Footer-styles';

import { AnnotationSection } from '../../atoms/AnnotationSection';
import { PolicyTerms } from '../../atoms/PolicyTerms';

import { PUBLIC_PATHS } from 'utils/constants';

interface IFooterLinksProps {
    name: any;
    path: any;
}

interface IFooterContentProps {
    headline: string;
    links: Array<IFooterLinksProps>;
}

export interface IFooterProps {
  footerContent?: Array<IFooterContentProps>;
  annotationContent: Array<string>;
  policyTermsContent: Array<{
      label: string;
      url: string;
  }>;
  adManagerContent: {
    copy: string;
    link: string;
    linkLabel: string;
  };
  languageContent: {
    title: string;
    languages: Array<{
      language: string;
      key: string;
    }>
  };
  analyticsEvent?: Function;
  locale: string;
  showAnnotations: boolean;
}

export const FooterpropTypes = {
    footerContent: PropTypes.arrayOf(PropTypes.shape({
        headline: PropTypes.string.isRequired,
        links: PropTypes.arrayOf(PropTypes.shape({
            name:  PropTypes.string.isRequired,
            path: PropTypes.string.isRequired,
        })),
    })),
    annotationContent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

const mapStateToProps = (state: any, ownProps) => {
  return {
    ...ownProps,
    locale: state.data.locale,
  };
};

export class FooterClassImpl extends React.Component<IFooterProps, void> {

    static propTypes = FooterpropTypes;

    defaultProps = {
      showAnnotations: true,
    };

    languageSelect: any;

    sendAnalytics(item) {
      this.props.analyticsEvent({
        action: 'click',
        label: `footer:button-${item.path}-${item.name}`,
        category: `footer:button-${item.path}-${item.name}`,
      });
    }

    generateItem(item, i) {
        return (
            <Li key={i} css={styles.listItem()} onClick={(e) => this.sendAnalytics(item)}>
                {item.path.indexOf('http') > -1 ? (
                  <a href={item.path} target="_blank">{item.name}</a>
                ) : (
                  <Link to={item.path}>{item.name}</Link>
                )}
            </Li>
        );
    }

    generateItemList(item, i) {
        return (
            <Ul key={i} css={styles.list()}>
                <Li css={styles.header()}>{item.headline}</Li>
                {item.links.map(this.generateItem.bind(this))}
            </Ul>
        );
    }

    onLangaugeSelect() {
      const locale = this.languageSelect.options[this.languageSelect.selectedIndex].value;

      const uri = document.location.pathname;

      const uriArray = uri.slice(1).split('/');

      let newUri = '/';
      if (locale !== 'en-us') {
        if (uri.indexOf('/l/') > -1) {
          uriArray[1] = locale;
          newUri = '/' + uriArray.join('/');
        } else {
          newUri = '/l/' + locale + '/' + uriArray[0];
        }

      } else {
        if (uri.indexOf('/l/') > -1) {
          newUri = '/' + uriArray.slice(2, uriArray.length).join('/');
        }
      }
      writeCookie('sc-langauage', locale, null, '.snapchat.com');
      window.location.assign(window.location.origin + newUri);

    }

    render() {
      const {
        copy,
        link,
        linkLabel,
      } = this.props.adManagerContent;

      return (
        <Div>
            <Div css={styles.adManager()}>
              <span className="ad-manager-copy">{copy}</span>
              <a onClick={() => this.sendAnalytics({name: 'ad-manager-log-in'})} className="ad-manager-link" href={`${link}`} target="_blank">
                <span>
                {linkLabel}
                </span>
              </a>
            </Div>
            <Div css={styles.footerWrapper()}>
              <Div css={styles.footerContainer()}>
                <Div css={styles.logoWrapper()}>
                    <a href="https://www.snapchat.com/" target="_blank">
                      <img src={`${PUBLIC_PATHS.IMAGES}/snap-logo-footer.svg`} alt="Snapchat" />
                    </a>
                </Div>
                <Div css={styles.linkWrapper()}>
                    <Div css={styles.innerWrapper()}>
                      { this.props.footerContent.map(this.generateItemList.bind(this)) }
                      <Ul css={[ styles.list(), styles.localeSelectWrapper() ]}>
                        <Li css={styles.header()}>{this.props.languageContent.title}</Li>
                        <Li>
                          <Div css={ styles.localeSelectContainer() }>
                            <Select
                              css={styles.languageSelect}
                              innerRef={(select) => { this.languageSelect = select; }}
                              name="locale-select"
                              onChange={this.onLangaugeSelect.bind(this)}
                              value={this.props.locale === '' ? 'en-us' : this.props.locale}
                            >
                                {this.props.languageContent.languages.map((language, index) => {
                                  return (
                                    <option
                                      key={index}
                                      value={language.key}
                                    >
                                      {language.language}
                                    </option>
                                  );
                                })}
                            </Select>
                            <Div css={ styles.upArrow() } />
                            <Div css={ styles.downArrow() } />
                          </Div>
                        </Li>
                      </Ul>
                    </Div>
                    { this.props.annotationContent && this.props.annotationContent.length > 0 &&
                        <AnnotationSection items={this.props.annotationContent} css={styles.annotationSection()}/>
                    }
                </Div>
              </Div>
            </Div>
            <PolicyTerms content={this.props.policyTermsContent} />
        </Div>
      );
  }
}

export const Footer = connect(mapStateToProps)(FooterClassImpl);



