import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

const visibleState = {
  visibility: 'visible',
  opacity: 1,
  transform: 'scale(1)',
  transition: 'all .2s ease-in-out',
};

export const subNavWrapper = (active, hideCTA) => ({
  backgroundColor: 'white',
  boxShadow: '0 8px 24px rgba(0,0,0,.2)',
  borderRadius: '1em',
  padding: '1.5em',
  display: 'inline-block',
  minWidth: '145px',
  maxWidth: '220px',
  textAlign: 'center',
  visibility: active ? 'visible' : 'hidden',
  opacity: active ? 1 : 0,
  transform: active ? 'scale(1)' : 'scale(1.05)',
  transition: 'all .2s ease-in-out',
  margin: '1em auto',
  position: 'absolute',
  top: 23,
  right: hideCTA ? 0 : -60,
  overflow: 'hidden',
  '& li': {
    display: 'block',
    '&:not(:last-child) a': {
      marginBottom: 16,
    },
  },
  '&': active && {...visibleState},
});

export const subNavItem = (activeColor = colors.lightBlue) => ({
  '& a': {
    display: 'block',
    cursor: 'pointer',
    fontSize: '12px',
    fontWeight: 400,
    textTransform: 'capitalize',
    transition: 'opacity .2s ease-in-out',
    '&:hover, &:focus': {
      opacity: 0.5,
    },
  },
  outline: '0 dashed rgba(0,0,0,0.2)',
  '[data-whatinput="keyboard"] &:focus': {
    outlineWidth: '1px',
  },
});

export const triggerWrapper = () => ({
  top: 0,
  width: 25,
  height: 45,
  textAlign: 'center',
  margin: '0 auto',
  '&:hover:focus + ul': {
    visibility: 'visible',
    opacity: 1,
    transform: 'scale(1)',
    transition: 'all .3s ease-in-out',
  },
  '&:hover:focus > div': {
    visibility: 'visible',
    opacity: 1,
    transform: 'rotate(180deg)',
    transition: 'all .3s ease-in-out',
  },
  outline: '0 dashed rgba(0,0,0,0.2)',
  '[data-whatinput="keyboard"] &:focus': {
    outlineWidth: '1px',
  },
});

export const triggerDots = (active) => ({
  width: '100%',
  height: '100%',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  backgroundPosition: '50% 50%',
  top: -5,
  paddingBottom: 5,
  position: 'relative',
  margin: '0 auto',
  backgroundImage: `url(/icons/arrow.svg)`,
  transition: 'all .3s ease-in-out',
  transform: active && 'rotate(180deg)',
  transformOrigin: '50% 50%',
});



