import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
import { connect } from 'react-redux';
import { autobind } from 'core-decorators';

import { Header } from '../../atoms/Header/';
import { BodyCopyBlock } from '../../molecules/BodyCopyBlock';
import { Media } from '../../molecules/Media';
import { InlineMetricsBlock, IInlineMetricsBlockProps } from '../../molecules/InlineMetricsBlock';
import { Carousel } from '../../organisms/Carousel';
import * as styles from './SuccessStoryAdProduct-styles';
import { MobileVideo } from './_/MobileVideo';
import { Markdown } from '../../atoms/Markdown';

const { Div, Section, Img, H4, P} = glamorous;

export interface IVideoProps {
  src?: string;
  posterUrl?: string;
  height?: number;
  width?: number;
}

export interface IProductProps {
  image?: {
    src?: string,
    alt?: string,
  };
  video?: IVideoProps;
  title: string;
  description: string;
}

export interface ISuccessStoryAdProductProps {
  content: string;
  products: Array<IProductProps>;
  copyRight?: boolean;
  header?: string;
  metrics?: Array<any>;
  isMobile?: boolean;
  animationTiming?: number;
}

const mapStateToProps = (state: any, ownProps) => ({
   ...ownProps,
   isMobile: state.app.mobile,
 });

export class SuccessStoryAdProductImpl extends React.Component<ISuccessStoryAdProductProps, { selectedId: number; animating: boolean; }> {

  static defaultProps = {
    copyRight: false,
    animationTiming: 200,
  };

  state = {
    selectedId: 0,
    animating: false,
  };

  @autobind
  handleCarouselChange(index: number): void {
    if ( index !== this.state.selectedId ) {
      this.setState({
        animating: true,
      });
      setTimeout(() => {

        this.setState({
          selectedId: index,
          animating: false,
        });
      }, this.props.animationTiming);
    }
  }

  renderAdProductBlocks(clientWidth: number) {
    return this.props.products.map( (product, i) => {
      let asset;
      if ( product.image ) {
        asset = (
          <Img
            src={product.image.src}
            alt={product.image.alt}
            css={ styles.productImage() }
          />
        );
      } else if ( this.props.isMobile ) {
        asset = (
          <MobileVideo
            video={product.video}
            isFocused={this.state.selectedId === i}
          />
        );
      } else if (product.video) {
        asset = (
          <Media
            ratioWidth={9}
            ratioHeight={16}
            fullWidth
            roundedCorner
            controls
            mediaWidthRelativeToContainer={'100%'}
            video={{
              videoSrc: product.video.src,
              posterImageUrl: product.video.posterUrl,
            }}
          />
        );
      }

      return (
        <Div
          key={`adProduct-${i}`}
          css={ [ styles.halfWidthColMd(false, 2), styles.productContainer(clientWidth) ] }
        >
          <Div
            css={ styles.assetWrapper() }
          >
            {asset}
          </Div>
          <Header
            tag="h4"
            css={ styles.productHeader() }
          >
            {product.title}
          </Header>
          <P
            css={ styles.productCopy() }
          >
            <Markdown copy={ `${product.description}` }/>
          </P>
        </Div>
      );
    });
  }

  render() {
    const clientWidth = (typeof document !== 'undefined') ? document.body.clientWidth : 1024;
    const baseWidth = Math.min((clientWidth - 160), 311);

    return (
      <Section
        css={ styles.container(this.props.copyRight, this.props.products.length) }
      >
        <Div
          className={'ad-product-wrapper'}
          css={styles.contentCol(this.props.copyRight, this.props.products.length)}
        >
          <BodyCopyBlock
            fullWidth
            marginTop={'0px'}
            headerCopy={this.props.header}
            tag={'h3'}
          >
            {this.props.content}
          </BodyCopyBlock>
          <Div
            css={ styles.metricContainer() }
          >
          {this.props.metrics.length > 0 &&
            <InlineMetricsBlock
              fullWidth
              metrics={this.props.metrics}
            />
          }
          </Div>
        </Div>
          {this.props.isMobile && this.props.products.length > 1 ? (
          <Carousel
              alignTop
              isMobile
              snapCenter
              leftOffset={ Math.max(((clientWidth - baseWidth) * 0.5), 80) }
              padding={32}
              onChange={ this.handleCarouselChange }
            >
            {this.renderAdProductBlocks(clientWidth)}
            </Carousel>
          ) : (
            <Div
              css={styles.productCol(this.props.copyRight, this.props.products.length) }
            >
              <Div
                css={ styles.productsGroup(this.props.copyRight) }
              >
                {this.renderAdProductBlocks(clientWidth)}
              </Div>
            </Div>
          )}
      </Section>
    );
  }
}

export const SuccessStoryAdProduct = connect(mapStateToProps)(SuccessStoryAdProductImpl);



