import glamorous from 'glamorous';
import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';
import { hexToRGB } from 'utils/helpers';
import * as glamor from 'glamor';

const { mediaQueries, screenSizes, padding } = utils;
const fontSize = '19px';
const cardMargin = padding.xlarge;
const cardWidth = `${utils.columns.desktopColumnWidth * 5}%`;

const slideFade = glamor.keyframes({
  '100%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
});

const opacityFade = glamor.keyframes({
  '0%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
  '100%': {
    transform: 'translateY(0)',
    opacity: 0,
  },
});

export const cardTitle = (article: boolean) => ({
  padding: !article ? '1rem 2rem 0' : '1.5rem 2rem 0.5rem',
  fontSize: !article ? type.regularBold.fontSize : type.large.fontSize,
  lineHeight: !article ? type.regularBold.lineHeight : type.regular.lineHeight,
  letterSpacing: !article ? type.regularBold.letterSpacing : type.regular.letterSpacing,
  width: '100%',
  fontFamily: !article && type.regularBold.fontFamily,
  [`${mediaQueries.phablet}`]: !article ? {
    ...type.largeBold,
  } : {
    ...type.headline3,
    letterSpacing: type.LETTER_SPACE,
  },
});

function getExploreColor(color: string) {
  switch (color) {
    case 'purple':
      return colors.explorePurple;
    case 'blue':
      return colors.exploreBlue;
    case 'green':
      return colors.exploreGreen;
    case 'red':
      return colors.exploreRed;
    case 'yellow':
      return colors.exploreYellow;
    case 'pink':
      return colors.explorePink;
    default:
      return colors.white;
  }
}

export const cardWrapper = ({ background = true, article = false, animate = false, isUpdating = false }) => ({
  borderRadius: utils.borderRadius.desktopContainer,
  width: `100%`,
  position: 'relative',
  overflow: 'hidden',
  fontSize: article ? type.regular.fontSize : fontSize,
  lineHeight: article ? '32px' : null,
  display: 'flex',
  alignItems: `center`,
  flexFlow: `column wrap`,
  transition: 'all .2s linear',
  letterSpacing: utils.letterSpacing,
  marginBottom: background ? padding.semi : '0px',
  background: background ? colors.white : 'transparent',
  boxShadow: background && `0 14px 24px rgba(${hexToRGB(colors.shadowGray)}, 0.5)`,
  transform: animate ? 'translateY(1em)' : 'initial',
  opacity: animate ? 0 : 1,
  animation: animate ? isUpdating ? `${opacityFade} 0.5s ease-in-out forwards` : `${slideFade} 0.3s ease-in-out forwards` : 'none',
  zIndex: -1,
  paddingBottom: !article && background ? 30 : 0,
  [`${mediaQueries.phablet}`]: {
    marginBottom: background ? cardMargin : '0',
  },
});

export const cardSubhead = () => ({
  padding: '1rem 2rem 0',
  width: `100%`,
  textAlign: `left`,
  color: colors.regularCharcoal,
  ...type.smallBold,
});

export const cardImageContainer = ({image, article = false, color, background = true}) => ({
  width:  '100%',
  display: `flex`,
  justifyContent: `center`,
  alignItems: `center`,
  overflow: `hidden`,
  padding: color ? `${utils.padding.semi}px 0 ${utils.padding.semi}px 0` : `${utils.padding.semi}px 0 0 0`,
  backgroundSize: color ? '50%' : article ? 'cover' : 'contain',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'center center',
  backgroundColor: color ? getExploreColor(color) : 'initial',
  [`${mediaQueries.phablet}`]: {
    padding: color ? `${utils.padding.xlarge}px 0 ${utils.padding.semi}px 0` : `${utils.padding.large}px 0 0 0`,
  },
});

export const imageWrapper = () => ({
  maxWidth: `64%`,
});

export const cardImage = () => ({
  width: `100%`,
});

export const cardCopy = (article: boolean = false, background: boolean = true) => ({
  color: background ? colors.regularCharcoal : colors.black,
  ...type.regular,
  padding: background ? '0.5rem 2rem 0' : '0.5rem 0 0',
  boxSizing: 'border-box',
  paddingBottom: article && 42,
  textAlign: article ? `left-align` : `center` ,
  [`${mediaQueries.phablet}`]: {
    fontSize: !article && background && type.large.fontSize,
    lineHeight: !article && background ? type.large.lineHeight : type.regular.lineHeight,
  },
});

export const superscript = () => ({
  transform: 'translateY(-20%)',
  display: 'inline-block',
  fontSize: '50%',
  verticalAlign: 'top',
});

/*************************
 * CardSet Styles Start *
**************************/

export const cardLayoutWrapper = () => ({
  maxWidth: utils.columns.desktopMaxWidth,
  margin: '0 auto',
  [`${mediaQueries.phablet}`]: {
    padding: `0 ${padding.xlarge}px`,
  },
});

export const cardSetChild = (background = true, link = false) => ({
  maxWidth: '100%',
  width: '100%',
  zIndex: 0,
  display: `flex`,
  '& > a': {
    display: 'inline-block',
    width: '100%',
  },
  '&:focus': {
    outline: !link && 0,
  },
  [`${mediaQueries.phablet}`]: {
    flex: '1 1 50%',
    maxWidth: '50%',
    width: '100%',
    boxSizing: 'border-box',
    padding: `0 ${utils.columns.desktopColumnWidth / 2}%`,
  },
  [`${mediaQueries.phabletMax}`]: {
    maxWidth: '100%',
    width: '100%',
  },
  [`${mediaQueries.normal}`]: {
    flex: background ? '1 1 33%' : '1 1 25%',
    maxWidth: '33%',
    width: '100%',
    boxSizing: 'border-box',
    padding: background ? `0 ${utils.columns.desktopColumnWidth / 2}%` : `0 ${utils.columns.desktopColumnWidth / 3}%`,
  },
});

export const cardSetWrapper = ({ article = false, background = true }) => ({
  display: 'flex',
  justifyContent: 'center',
  alignItems: article ? 'stretch' : 'normal',
  flexFlow: 'row wrap',
  position: 'relative',
  boxSizing: 'border-box',
  textAlign: article ? 'left' : 'center',
  [`${mediaQueries.phablet}`]: {
    margin: `0 -${utils.columns.desktopColumnWidth / 2}%`,
  },
});

export const markdownStyles = (article: boolean = false, background: boolean = true) => ({
  [' p']: {
    color: background ? colors.regularCharcoal : colors.black,
    padding: background ? '0.5rem 2rem 0' : '0.5rem 0 0',
    boxSizing: 'border-box',
    paddingBottom: article && utils.padding.semi,
    [`${mediaQueries.phablet}`]: {
      fontSize: !article && background && type.large.fontSize,
      lineHeight: !article && background && type.large.lineHeight,
    },
  },
});

export const bottomContainer = () => ({
  padding: '2rem 0rem 0rem',
  width: '100%',
  marginTop: 'auto',
});

export const ctaStyle = (article) => ({
  padding: '0.5rem 2rem 0.5rem',
  width: `100%`,
  textAlign: `center`,
  textTransform: 'uppercase',
  color: colors.black,
  letterSpacing: '1.4px',
  fontFamily: type.BOLD_FONT,
  fontSize: '12px',
  lineHeight: '16px',
  marginTop: 'auto',
  marginBottom: article && '30px',
  hyphens: 'auto',
});

export const partnerTypeStyle = () => ({
  ...type.regular,
  padding: '2rem 2rem 0.5rem',
  width: `100%`,
  textAlign: `center`,
  color: colors.regularCharcoal,
});



