import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

import { SnapchatRoutes } from './components/Shared/routes';
import { store } from './state/createStore';
import axios from 'axios';
import * as glamor from 'glamor';
import { loadJson, setLocale } from './state/actionCreators/data';
import { isMobile, notMobile, showCookie } from './state/actionCreators/appCreator';
import { readCookie } from 'utils/helpers';
import * as styles from './utils/styles';
import { languages } from 'utils/constants';

// *****************
// Client
// *****************
async function init() {
  const glam = (window as any)._glam;
  if (glam) {
    glamor.rehydrate(glam);
  }

  const locale = readCookie('sc-langauge');

  const uri = (document.location.pathname.slice(1)).split('/');
  const lang = uri[0] === 'l' ? uri[1] : 'en-us';

  const isLanguageMatch = languages.filter((language) => {
    return language === lang;
  });

  if (lang !== 'en-us' && isLanguageMatch.length) {
    store.dispatch(setLocale(lang));
  }

  const setLang = lang; // .replace('-', '_');
  const dateQuery = `${new Date().getMonth()}${new Date().getDate()}${new Date().getFullYear()}`;
  const jsonUrl = (lang !== 'en-us') ? `/locales/${setLang}/data.json?v=${dateQuery}` : `/locales/data.json?v=${dateQuery}`;

  if (window['pageData']) {
    store.dispatch(loadJson(window['pageData']));
  } else {
    const jsonData = await axios.get(jsonUrl);
    store.dispatch(loadJson(jsonData.data));
  }
  window.addEventListener('resize', () => {
    if (window.innerWidth < styles.screenSizes.phablet && !store.getState().app.mobile) {
      store.dispatch(isMobile());
    } else if (window.innerWidth >= styles.screenSizes.phablet && store.getState().app.mobile) {
      store.dispatch(notMobile());
    }
  });
  let showPolicy;
  try {
    showPolicy = await axios.get('https://www.snapchat.com/api/is_cookie_popup_eligible');
  } catch (e) {
    showPolicy = { data: 'true' };
  }

  if (showPolicy && String(showPolicy.data) === 'true') {
    if (readCookie('snapchatpolicynotice') !== 'true') {
      store.dispatch(showCookie());
    }
  }

  ReactDOM.render(
    <Provider store={store}>
      {SnapchatRoutes}
    </Provider>,
    document.getElementById('app'),
  );
}

// If window is not undefined, we can render to client
if (typeof window !== 'undefined') {
  init();
}



