import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries, screenSizes } = utils;

export const navWrapperStyle = () => ({
    borderBottom: `1px solid ${colors.borderGray}`,
});

export const tabStyle = () => ({
    [`${mediaQueries.phabletMax}`]: {
        padding: '18px 0',
        margin: '0 25px',
        fontSize: '19px',
        letterSpacing: '.5px',
        lineHeight: '16px',
    },
});

export const tabArrayStyle = () => ({
  justifyContent: 'center',
});

export const navWrapper = () => ({});



