import * as React from 'react';
import * as PropTypes from 'prop-types';

export { Carousel } from './Carousel-full';

export interface ICarouselProps {
  children?: Array<any>; // Array<HTMLElement>;
  padding?: number;
  snapCenter?: boolean;
  leftOffset?: number;
  dotNav?: boolean;
  isMobile?: boolean;
  allowOverflow?: boolean;
  alignTop?: boolean;
  flex?: boolean;
  trackingCategory?: string;
  trackingCarouselLabel?: string;
  color?: string;
  initialIndex?: number;
  containerWidth?: number;
  onChange?(index: number): void;
}

export const CarouselProptypes = {
  children: PropTypes
    .oneOfType([PropTypes
      .arrayOf(PropTypes.element),
      PropTypes.element]),
  padding: PropTypes.number,
  snapCenter: PropTypes.bool,
  leftOffset: PropTypes.number,
  dotNav: PropTypes.bool,
  isMobile: PropTypes.bool,
  allowOverflow: PropTypes.bool,
  flex: PropTypes.bool,
  trackingCategory: PropTypes.string,
  trackingCarouselLabel: PropTypes.string,
  color: PropTypes.string,
  onChange: PropTypes.func,
};

export interface ICarouselBaseProps {
  refsReady(refs: ICarouselRefs): void;
  children?: Array<any> | any;
  snapCenter?: boolean;
  leftOffset?: number;
  dotNav?: boolean;
  active?: number;
  padding?: number;
  isMobile?: boolean;
  allowOverflow?: boolean;
  alignTop?: boolean;
  flex?: boolean;
  trackingCategory?: string;
  trackingCarouselLabel?: string;
  color?: string;
  containerWidth?: number;
}

export interface ICarouselItemPosition {
  left: number;
  center: number;
  width: number;
}

export interface ICarouselRefs {
  container: HTMLElement;
  containerBounds: ClientRect;
  containerWidth: number;
  outerContainer: HTMLElement;
  outerContainerBounds: ClientRect;
  positions: Array<ICarouselItemPosition>;
}

export const CarouselBaseProptypes = {
  refsReady: PropTypes.func,
  padding: PropTypes.number,
  snapCenter: PropTypes.bool,
  leftOffset: PropTypes.number,
  dotNav: PropTypes.bool,
  active: PropTypes.number,
  isMobile: PropTypes.bool,
  allowOverflow: PropTypes.bool,
  children: PropTypes
    .oneOfType([PropTypes
      .arrayOf(PropTypes.any),
      PropTypes.any]),
  flex: PropTypes.bool,
  color: PropTypes.string,
};



