
export const subNavWrapper = (isActive) => ({
  position: 'absolute',
  top: 23,
  left: '50%',
  margin: '1em auto',
  padding: '1.5em',
  minWidth: '145px',
  overflow: 'hidden',
  textAlign: 'center',
  backgroundColor: 'white',
  boxShadow: '0 8px 24px rgba(0,0,0,.2)',
  borderRadius: '1em',
  visibility: isActive ? 'visible' : 'hidden',
  opacity: isActive ? 1 : 0,
  transform: isActive ? 'translateX(-50%) scale(1)' : 'translateX(-50%) scale(1.05)',
  transition: 'transform .2s ease-in-out, visibility .2s ease-in-out, opacity .2s ease-in-out',
  '& li': {
    display: 'block',
    '&:not(:last-child) a': {
      marginBottom: 16,
    },
  },
});

export const subNavItem = () => ({
  '& a': {
    color: 'black',
    whiteSpace: 'nowrap',
    display: 'block',
    cursor: 'pointer',
    fontSize: '12px',
    fontWeight: 400,
    textTransform: 'capitalize',
    transition: 'opacity .2s ease-in-out',
    '&:hover, &:focus': {
      opacity: 0.5,
    },
  },
  outline: '0 dashed rgba(0,0,0,0.2)',
  '[data-whatinput="keyboard"] &:focus': {
    outlineWidth: '1px',
  },
});

export const triggerWrapper = () => ({
  position: 'relative',
  paddingRight: 16,
  height: '100%',
});

export const dropdownTabStyles = (isActive) => ({
  '&::before': {
    content: `''`,
    position: 'absolute',
    top: 4,
    left: '100%',
    marginLeft: 6,
    width: 10,
    height: 10,
    backgroundImage: `url('/icons/arrow-down.svg')`,
    backgroundRepeat: 'no-repeat',
    backgroundSize: '100%',
    backgroundPosition: '50% 50%',
    transform: isActive && 'rotate(180deg)',
    transition: 'transform .3s ease-in-out',
  },
});



