import * as React from 'react';
import glamorous from 'glamorous';
const { Div, Img } = glamorous;
import * as PropTypes from 'prop-types';
import { connect } from 'react-redux';
const ReactPlayer = require('react-player');
import { LazyloadFade } from '../../atoms/LazyloadFade';
import { autobind } from 'core-decorators';
import{ throttle } from 'lodash';
import { Markdown } from '../../atoms/Markdown';

const deviceImage = require('./assets/mobile-device@2x.png');

import * as styles from './DeviceMobile-styles';

export interface IDeviceMobileProps {
  imageUrl: string;
  imageUrl2x: string;
  imageAlt?: string;
  title?: string;
  copy?: string;
  videoUrl?: string;
  isMobile?: boolean;
  isFocused?: boolean;
  inCarousel?: boolean;
  animating?: boolean;
  timing?: number;
  hideDevice?: boolean;
  deviceImageAlt?: string;
  playsInline?: boolean;
  tabOrganism?: boolean;
  isHighDensity?: boolean;
}

export interface IDeviceMobileState {
  playing: boolean;
  shouldShowPoster: boolean;
  initialPlay: boolean;
  shouldAutoPlay: boolean;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isHighDensity: state.app.isHighDensity,
});

export class DeviceMobileClass extends React.Component<IDeviceMobileProps, IDeviceMobileState> {

  player: any;
  container: any;
  videoContainer: any;
  scrollThrottle: any;

  static defaultProps = {
    isFocused: true,
    playsInline: true,
  };

  state = {
    playing: false,
    shouldShowPoster: !this.shouldAutoPlay(),
    initialPlay: false,
    shouldAutoPlay: this.shouldAutoPlay(),
  };

  componentDidMount() {
    if (this.state.shouldAutoPlay) {
      window.setTimeout(() => {
        this.scrollThrottle = throttle(this.scrollCallback, 100);
        window.addEventListener('scroll', this.scrollThrottle);
        this.scrollCallback();
      }, 100);
    }

  }

  componentWillReceiveProps(nextProps) {
    // Stop media when it looses focus, particularly for carousels.
    if ( nextProps.isFocused !== this.props.isFocused) {
      this.setState({
        playing: nextProps.isFocused && this.props.playsInline && this.state.shouldAutoPlay,
        initialPlay: nextProps.isFocused && this.props.playsInline && this.state.shouldAutoPlay,
      });
    }

    // Reset component for tabOrganism.
    if ( nextProps.videoUrl !== this.props.videoUrl && this.props.tabOrganism && !this.shouldAutoPlay()) {
      this.setState({
        playing: false,
        initialPlay: true,
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.scrollCallback);
  }

  @autobind
  scrollCallback() {
    if (this.container && this.videoContainer) {
      const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight - 60;
      const videoTop = this.container.getBoundingClientRect().top;
      if (videoTop <= (this.videoContainer.offsetHeight * 2) - 98 && videoTop >= -(this.videoContainer.offsetHeight + 98)) {
        if (!this.state.playing && this.player) {
          if (this.props.isFocused && this.props.isMobile) {
            this.setState({playing: true});
            this.player.player.play();
          } else if (!this.props.isMobile) {
            this.setState({playing: true});
            this.player.player.play();
          }
        }
      } else {
        if (this.state.playing && this.player) {
          this.setState({playing: false});
          this.player.player.pause();
        }
      }
    }

  }
  onTap() {
    // used to trigger Modal fullscreen device video. Video plays if you direct link but not navigate to it.
    if ( this.props.isFocused ) {
      this.setState({playing: !this.state.playing}, () => {
        if (this.state.playing) {
          this.player.player.play();
        }
      });
    }
  }

  shouldAutoPlay() {
    if (typeof window === 'undefined') {
      return false;
    }

    const deviceOSList = ['iPhone OS 10_2', 'iPhone OS 10_1', 'iPhone OS 10_0', 'iPhone OS 9_', 'iPad; CPU OS 10_2', 'iPad; CPU OS 10_1', 'iPad; CPU OS 10_0', 'iPad; CPU OS 9_', 'Android 5', 'Android 6', 'Android 7.0'];
    const userAgent = navigator.userAgent;

    const deviceMatch = deviceOSList.filter((OS) => {
      return userAgent.indexOf(OS) > -1;
    });

    return deviceMatch.length > 0 ? false : this.props.playsInline ? true : false;

  }

  render() {
    const {
      animating,
      timing,
      playsInline,
      imageUrl,
      imageUrl2x,
      isHighDensity,
    } = this.props;
    const {
      playing,
      shouldShowPoster,
      initialPlay,
    } = this.state;

    let imgSrc = imageUrl;

    if (isHighDensity && imageUrl2x) {
      imgSrc = imageUrl2x;
    }

    return (
      <Div className={'device-mobile'} innerRef={(container: any) => { this.container = container; }} css={ styles.container(this.props.isFocused, this.props.isMobile) }>
        <div>
          <Div css={styles.defaultStyles(this.props.inCarousel, this.props.hideDevice)} data-id="deviceMobile wrapper" tabIndex={this.props.videoUrl ? '0' : null}>
            {!this.props.hideDevice && <Img css={ styles.device() } src={deviceImage} alt={this.props.deviceImageAlt} />}
            <LazyloadFade>
              <Div
                className={'video-wrapper'}
                innerRef={(videoContainer: any) => { this.videoContainer = videoContainer; }}
                css={ styles.deviceScreen( this.props.videoUrl, animating, timing, playing, shouldShowPoster, initialPlay, this.props.hideDevice) }
                onClick={this.onTap.bind(this)}
              >
                { !this.props.videoUrl &&
                  <Img css={styles.defaultImage()} src={imgSrc} alt={this.props.imageAlt} />
                }
                { this.props.videoUrl &&
                  <ReactPlayer
                    ref={(player: any) => { this.player = player; }}
                    url={ this.props.videoUrl}
                    width={ '100%' }
                    height={ '100%' }
                    volume={ 0 }
                    playing={this.state.playing}
                    loop={ playsInline ? true : false }
                    fileConfig={{
                      attributes: {
                        muted: 'muted',
                        playsInline: playsInline ? true : false,
                        autoPlay: this.shouldAutoPlay() === true ? 'autoplay' : false,
                        poster: imgSrc,
                      },
                    }}
                    vimeoConfig={{
                      iframeParams: {
                        loop: playsInline ? true : false,
                        autoplay: playsInline ? true : false,
                        background: true,
                      },
                    }}
                    onPlay={() => {
                      // Used to set poster image for mobile devices.
                      this.setState({initialPlay: true});
                    }}
                    onPause={() => {
                      this.setState({playing: false});
                    }}
                  />
                }
              </Div>
            </LazyloadFade>
          </Div>
        </div>
        { ( this.props.title || this.props.copy ) &&
          <Div css={ styles.copyLockup(this.props.inCarousel) }>
            { this.props.title &&
              <Div css={ styles.deviceTitle() }>{ this.props.title }</Div>
            }
            { this.props.copy &&
              <Div css={ styles.deviceCopy() }>
                <Markdown copy={ `${this.props.copy}` } />
              </Div>
            }
          </Div>
        }
      </Div>
    );
  }
}

export const DeviceMobile = connect(mapStateToProps)(DeviceMobileClass);



