import * as React from 'react';
import * as glamorous from 'glamorous';
import * as PropTypes from 'prop-types';
import * as styles from './Modal-styles';
import { store } from '../../../../state/createStore';
import { modalIsOpen } from '../../../../state/actionCreators/appCreator';
import { autobind } from 'core-decorators';
import { Button } from '../../atoms/Button';
import { ImageTab, ImageTabProps } from '../../molecules/TabArray';
import { CopyBlock } from '../../atoms/CopyBlock';
import { DeviceMobile } from '../../molecules/DeviceMobile';
import { DeviceMobileLockup, IDeviceMobileLockupProps } from '../../molecules/DeviceMobileLockup';
import { EyebrowTitle, IEyebrowTitleProps } from '../../atoms/EyebrowTitle';
import { Video } from '../../atoms/Video';
import { connect } from 'react-redux';
import { throttle } from 'lodash';
import { Carousel } from '../../organisms/Carousel';

// gsap uses dom, it is ignored via webpack.
import { TweenMax, Power2 } from 'gsap';

const {
  H4,
  A,
  Div,
  Span,
} = glamorous;

export interface IModalProps {
  isMobile?: boolean;
  tabs?: Array<IDeviceMobileLockupProps>;
  animationTiming?: number;
  color?: string;
  analyticsEvent?: any;
  routeData?: string;
  isOpen?: boolean;
  closeModal?: Function;
  activeTab?: number;
  activeChange?: Function;
}

export interface IModalState {
  selectedId: number;
  animating: boolean;
  isRendered: boolean;
  clientWidth: number;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  isOpen: state.app.modalIsOpen,
});

export class ModalImpl extends React.Component<IModalProps, IModalState> {
  static defaultProps = {
    animationTiming: 200,
  };

  state = {
    selectedId: this.props.activeTab,
    animating: false,
    isRendered: false,
    clientWidth: (typeof document !== 'undefined') ? document.body.clientWidth : 1024,
  };

  carousel: any;
  updateDimensionsCallback: any;

  componentDidMount() {
    this.updateDimensionsCallback = throttle(this.updateDimensions, 50);
    window.addEventListener('resize', this.updateDimensions);
    if (this.carousel) {
      this.updateDimensions();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensionsCallback);
  }

  componentWillReceiveProps(nextProps) {
    if ( this.props.isOpen !== nextProps.isOpen ) {
      if ( nextProps.isOpen ) {
        this.setState(
          {
            isRendered: true,
          }, () => {
            if ( this.modalElement ) {
              TweenMax.fromTo(this.modalElement, 0.6, { opacity: 0, ease: Power2.easeInOut, }, { opacity: 1 });
            }
          },
        );
      } else {
        TweenMax.fromTo(this.modalElement, 0.4, { opacity: 1, ease: 'linear', }, { opacity: 0,
          onComplete: () => {
            this.setState({
              isRendered: false,
            });
          },
        });
      }
    }

    this.setState({selectedId: nextProps.activeTab});

  }

  modalElement: HTMLElement;
  modalRef = (node: HTMLElement) => {
    this.modalElement = node;
  }

  @autobind
  updateDimensions() {
    if (this.carousel && this.carousel.childRefs) {
      this.setState({clientWidth: this.carousel.childRefs.container.getBoundingClientRect().width});
    }
  }

  @autobind
  handleCarouselChange(index: number): void {
    const setCarousel = () => {
      this.setState({
        selectedId: index,
        animating: false,
      });
    };

    if (this.state.selectedId !== index) {
      this.setState({
        animating: true,
      });
      setTimeout(() => {
        const last = this.state.selectedId;
        setCarousel();
        if (this.props.activeChange) {
          this.props.activeChange(last, this.state.selectedId);
        }
      }, this.props.animationTiming);
    } else {
      setCarousel();
    }
  }

  @autobind
  handleClickNext(): void {
    this.setState({
      animating: true,
    });
    if (this.state.selectedId !== this.props.tabs.length - 1) {
      setTimeout(() => {
        const last = this.state.selectedId;
        this.setState({
          selectedId: this.state.selectedId + 1,
          animating: false,
        });
        if (this.props.activeChange) {
          this.props.activeChange(last, this.state.selectedId);
        }
      }, this.props.animationTiming);
    } else {
      setTimeout(() => {
        const last = this.state.selectedId;
        this.setState({
          selectedId: 0,
          animating: false,
        });
        if (this.props.activeChange) {
          this.props.activeChange(last, this.state.selectedId);
        }
      }, this.props.animationTiming);
    }
    this.props.analyticsEvent({
      action: 'click',
      label: `modal-next`,
      category: `creative-spotlight:modal-next-arrow`,
    });
  }

  @autobind
  handleClickPrev(): void {
    this.setState({
      animating: true,
    });
    if (this.state.selectedId !== 0) {
      setTimeout(() => {
        const last = this.state.selectedId;
        this.setState({
          selectedId: this.state.selectedId - 1,
          animating: false,
        });
        if (this.props.activeChange) {
          this.props.activeChange(last, this.state.selectedId);
        }
      }, this.props.animationTiming);
    } else {
      setTimeout(() => {
        const last = this.state.selectedId;
        this.setState({
          selectedId: this.props.tabs.length - 1,
          animating: false,
        });
        if (this.props.activeChange) {
          this.props.activeChange(last, this.state.selectedId);
        }
      }, this.props.animationTiming);
    }
    this.props.analyticsEvent({
      action: 'click',
      label: `modal-previous`,
      category: `creative-spotlight:modal-previous-arrow`,
    });
  }

  @autobind
  hideModal() {
    TweenMax.fromTo(this.modalElement, 1, { opacity: 1 }, { opacity: 0,
      onComplete: () => {
        setTimeout(() => {
          store.dispatch(modalIsOpen(false));
        }, 0);
      },
    });
  }

  @autobind
  handleKeyPress(e: KeyboardEvent) {
    if (e.key === 'Escape') {
      store.dispatch(modalIsOpen(false));
      document.body.style.overflow = 'scroll';
    } else if (e.keyCode === 37) {
      this.handleClickPrev();
    } else if (e.keyCode === 39) {
      this.handleClickNext();
    }
  }

  listenForEscape() {
    if (typeof document !== 'undefined') {
      document.addEventListener('keydown', this.handleKeyPress);
    }
  }

  removeEscapeListener() {
    if (typeof document !== 'undefined') {
      document.removeEventListener('keydown', this.handleKeyPress);
    }
  }

  renderLockup(tabInfo: IDeviceMobileLockupProps, includeTabs: boolean = true) {
    const { tabs } = this.props;
    const {
      deviceMobile,
      eyebrowTitle,
      copyBlock,
      primaryButton,
      secondaryButton,
      title,
      specLink,
      primaryOverview,
      secondaryOverview,
      video,
      videoUrl,
    } = tabInfo;
    return (
      <Div css={ this.props.isMobile && styles.content() }>
        <Div className={'copy-container'} css={styles.copyContainerInner(this.state.animating, this.props.animationTiming, videoUrl)}>
          <Div css={styles.modalCopyWrapper(videoUrl)}>
            {eyebrowTitle &&
              <EyebrowTitle css={styles.eyebrow()}>
                {eyebrowTitle}
              </EyebrowTitle>
            }
            {title &&
              <Div css={styles.title(videoUrl)}>{title}</Div>
            }
            <CopyBlock css={ styles.bodyCopy(videoUrl) }>
              {copyBlock}
            </CopyBlock>
            {primaryOverview &&
              <Div className={'overview-container'} css={ styles.overviewContainer(videoUrl) }>
                <Div css={ styles.leftContainer() }>
                  <Div css={ styles.overviewTitle() }>
                    {primaryOverview.title}
                  </Div>
                  <Div css={ styles.overviewBody() }>
                    {primaryOverview.body}
                  </Div>
                </Div>
                <Div css={ styles.rightContainer() }>
                  <Div css={ styles.overviewTitle() }>
                    {secondaryOverview.title}
                  </Div>
                  <Div css={ styles.overviewBody() }>
                    {secondaryOverview.body}
                  </Div>
                </Div>
              </Div>
            }
          </Div>
          {
            (primaryButton || secondaryButton || specLink) &&
            <Div css={styles.buttonContainer(videoUrl)}>
              {primaryButton &&
                <Button {...primaryButton} css={ this.props.isMobile ? styles.buttonMobile() : null } analyticsEvent={ this.props.analyticsEvent }/>
              }
              {secondaryButton &&
                 <Button {...secondaryButton} css={ this.props.isMobile ? styles.buttonMobile() : null } analyticsEvent={ this.props.analyticsEvent }/>
               }
              {specLink &&
                <A css={styles.specsLink()} href={specLink.link} target="_blank">
                  <span>{ specLink.copy }</span>
                </A>
              }
            </Div>
          }
        </Div>
      </Div>
    );
  }

  render() {
    const { selectedId, animating, clientWidth } = this.state;
    const { tabs, animationTiming } = this.props;
    const baseWidth = Math.min((clientWidth - 160), 311);
    const {
      deviceMobile,
      eyebrowTitle,
      copyBlock,
      primaryButton,
      secondaryButton,
      primaryOverview,
      secondaryOverview,
      video,
      videoUrl,
      videoPoster,
    } = this.props.tabs[selectedId];

    this.state.isRendered ? this.listenForEscape() : this.removeEscapeListener();

    return this.state.isRendered && (!this.props.isMobile ?
      (
        <Div
          css={styles.tabOrganismStyle() && styles.modalBackground(videoUrl)}
          innerRef={ this.modalRef }
        >

          {this.props.tabs.length > 1 &&
            <Div
              css={styles.leftArrow()}
              onClick={this.handleClickPrev}
            >
            </Div>
          }
          {this.props.tabs.length > 1 &&
              <Div
                css={styles.rightArrow()}
                onClick={this.handleClickNext}
              >
              </Div>
          }
          <Div css={ styles.exit() } onClick={ this.props.closeModal } />
          { !videoUrl && (deviceMobile.videoUrl || deviceMobile.imageUrl) &&
            <Div css={styles.contentContainer()}>
              <Div css={styles.deviceContainer()}>
                <DeviceMobile {...tabs[selectedId].deviceMobile} animating={animating} timing={animationTiming} />
              </Div>
              <Div css={ styles.copyContainer() } >
                { this.renderLockup(tabs[selectedId]) }
              </Div>
            </Div>
          }
          { videoUrl &&
            <Div css={styles.contentContainer(videoUrl)}>
              <Div css={styles.videoContainer(this.state.animating, this.props.animationTiming)}>
                <Video
                  videoSrc={ videoUrl }
                  posterImageUrl = { videoPoster }
                  roundedCorner
                  controls
                  fullWidth
                  analyticsEvent={ this.props.analyticsEvent }
                />
              </Div>
              <Div css={ styles.copyContainer(videoUrl) } >
                { this.renderLockup(tabs[selectedId]) }
              </Div>
            </Div>
          }
        </Div>
      ) :
      (
        <Div
          css={styles.tabOrganismStyle() && styles.modalBackground()}
          innerRef={ this.modalRef }
        >
          <Div css={ styles.exit() } onClick={ this.props.closeModal }>
          </Div>
          <Carousel
            ref={(carousel) => {this.carousel = carousel; }}
            leftOffset={Math.max(((clientWidth - baseWidth) * 0.5), 80)}
            padding={0}
            snapCenter
            isMobile
            onChange={this.handleCarouselChange}
            initialIndex={this.state.selectedId}
            containerWidth={clientWidth}
          >
            {
              this.props.tabs.map((tab: any, index) => {
                return (
                  <Div className={'device-mobile-container'} css={styles.carouselContainer(clientWidth)} key={`organism-carousel-item-${index}`}>
                    <DeviceMobile
                      {...tab.deviceMobile}
                      hideDevice
                      inCarousel={true}
                      isFocused={this.state.selectedId === index}
                    />
                  </Div>
                );
              })
            }
          </Carousel>
          { this.renderLockup(tabs[selectedId], false) }
        </Div>
      ));
  }
}

export const Modal = connect(mapStateToProps)(ModalImpl);



