import { css } from 'glamor';
import * as utilStyles from 'utils/styles';

const { mediaQueries } = utilStyles;

export const defaultStyles = () => ({
  backgroundSize: 'cover',
  backgroundPosition: 'center center',
  backgroundRepeat: 'no-repeat',
  position: 'absolute',
  width: '100%',
  height: '100%',
});

export const naturalImageContainer = (noPadding: boolean = false, blog: boolean = false, isLast: boolean = false, noMargin: boolean = false, type: string) => ({
  padding: noPadding && !blog ? 0 : blog ? `0 0 ${isLast ? 0 : `${utilStyles.padding.normal}px`} 0` : '0px',
  display: type !== 'downloads' ? 'block' : 'none',
  [`${mediaQueries.phablet}`]: {
    maxWidth: 1135,
    margin: noMargin ? null : '0 auto',
    boxSizing: 'border-box',
    padding: noPadding && !blog ? 0 : blog ? `0 0 ${isLast ? 0 : `${utilStyles.padding.xlarge}px`} 0` : `0 ${utilStyles.padding.xlarge}px`,
  },
  [`${mediaQueries.normal}`]: {
    display: 'block',
  },
});

export const maxWidth = ( useSmallMaxWidth: boolean = true, noPadding: boolean = false, width) => ({
  maxWidth: width && width,
  margin: '0 auto',
});

export const maxHeight = (height, fullWidth, valign, halign = false) => ({
  backgroundSize: fullWidth ? 'cover' : 'contain',
  backgroundPosition: valign ? `${halign ? 'center' : 'left'} center` : 'left top',
  backgroundRepeat: 'no-repeat',
  position: 'absolute',
  maxHeight: `${height}`,
  width: '100%',
  height: '100%',
  transform: valign ? 'translateY(-50%)' : 'initial',
  top: valign ? '50%' : 'initial',
});



