import * as React from 'react';
import * as glamorous from 'glamorous';
import * as Router from 'react-router';
const { Link } = Router;
const { Div, A } = glamorous;

import * as styles from './PolicyTerms-styles';

export interface IPolicyTermsProps {
    content: Array<{
        label: string;
        url: string;
    }>;
}

export class PolicyTerms extends React.Component<IPolicyTermsProps, void> {
  render() {
    return (
      <Div css={styles.wrapper()}>
        <Div css={styles.linkWrapper()}>
          <Div css={styles.linkInnerWrapper()}>
            {this.props.content.map((item, index) => {
              return (
                <Div css={styles.anchorWrapper()} key={index}>
                  {item.url.indexOf('http') > -1 ? (
                    <a href={item.url} target="_blank">{item.label}</a>
                  ) : (
                    <Link to={item.url}>{item.label}</Link>
                  )}
                </Div>
              );
            })}
          </Div>
        </Div>
      </Div>
    );
  }
}



