import * as React from 'react';
import { IDotNavProps, IDotNavProptypes } from './';
import * as styles from './DotNav-styles';
import { css } from 'glamor';

export class DotNav extends React.Component<IDotNavProps, void> {
  static propTypes = IDotNavProptypes;

  render() {
    return (
      <div className={ css(styles.container()).toString() }>
        {
            Array.from(Array(this.props.length)).map((_, index) => {
              const style = css(
                styles.defaultStyles(),
                this.props.active !== null && index === this.props.active ? styles.active(this.props.color) : null,
              ).toString();

              return <li data-active={ index === this.props.active } key={ `dotnav-${ index }` } className={ style } />;
            })
        }
      </div>
    );
  }
}



