import * as React from 'react';
import * as glamorous from 'glamorous';

import * as styles from './InlineMetricsBlock-styles';
import  { MetricBlock }  from '../MetricBlock';

const { Div } = glamorous;

export interface IInlineMetricsBlockProps {
  metrics: Array<any> ;
  fullWidth?: boolean;
}

export class InlineMetricsBlock extends React.Component<IInlineMetricsBlockProps, void> {
  static defaultProps = {
    fullWidth: false,
  };

  isSingleMetric = this.props.metrics.length === 1;

  items = this.props.metrics.map((metric, index, array) => {
    const isFirst = index === 0;
    const isLast = index === array.length - 1;
    return (
      <Div
        css={styles.metricContainer(isFirst, isLast, this.props.fullWidth, this.isSingleMetric)}
        key={index}
      >
        <MetricBlock
          miniBlock
          numbMetricStat={{
            headline: metric.headline,
            subhead: metric.subhead,
          }}
        />
      </Div>
    );
  });

  render() {
    return (
      <Div css={styles.container(this.props.fullWidth, this.isSingleMetric)}>
        {this.items}
      </Div>
    );
  }
}



