import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { paddingStyle } from '../../../atoms/Select/Select-styles';

const activeYellowBorder = `inset 0 0 0 2px ${colors.snapYellow}`;

export const select = (contentString, isDefaultValue) => ({
  // Need an `is-focused` state.
  '&:not(.is-open) > .Select-control, &.is-focused:not(.is-open) > .Select-control': {
    fontWeight: !isDefaultValue && 'bold',
    boxShadow: !isDefaultValue && activeYellowBorder,

    '&:hover': {
      // boxShadow: !isDefaultValue ? `0 1px 0 rgba(0, 0, 0, 0.06), ${activeYellowBorder}` : `0 1px 0 rgba(0, 0, 0, 0.06)`,
      boxShadow: !isDefaultValue ? activeYellowBorder : `0 1px 0 rgba(0, 0, 0, 0.06)`,
      outline: 0,
    },
  },
  '& .Select-multi-value-wrapper .Select-placeholder': {
    color: colors.black,
  },
  '& .Select-multi-value-wrapper::after': {
    content: `'${contentString}'`,
    fontWeight: 'normal',
    opacity: 0,
    ...paddingStyle,
  },
});



