import * as React from 'react';
import * as glamorous from 'glamorous';
import * as styles from './SubpageNav-styles';
import * as colors from 'utils/styles/colors';
import { TabArray, NavTab } from '../TabArray';

const { Div } = glamorous;
const { Component, PropTypes } = React;

export interface ISubpageNavProps {
  tabs?: Array<{
    onSelect?: () => void;
    selected?: boolean;
    selectedColor?: string;
    label: string;
    link?: string;
  }>;
  color?: string;
  activeId?: number;
  onTabSelect?: Function;
}

export class SubpageNav extends Component<ISubpageNavProps, void> {

  static propTypes = {
    color: PropTypes.string,
    activeId: PropTypes.number,
    onTabSelect: PropTypes.func,
    tabs: PropTypes.arrayOf(PropTypes.shape({
      onSelect: PropTypes.func,
      selected: PropTypes.bool,
      selectedColor: PropTypes.string,
      label: PropTypes.string,
      link: PropTypes.string,
    })),
  };

  static defaultProps = {
    color: colors.regularOrange,
  };
  renderTabs() {
    const { tabs, color, activeId, onTabSelect } = this.props;

    return tabs.map((tab, index) => {
      return (
        <NavTab
          key={index}
          css={styles.tabStyle()}
          selected={activeId === index}
          selectedColor={color && color}
          onSelect={() => onTabSelect(index)}
          uri={tab.link}
          >
          {tab.label}
        </NavTab>
      );
    });
  }
  render() {
    return (
      <Div css={styles.navWrapper()}>
        <Div css={styles.navWrapperStyle()}>
          <TabArray css={styles.tabArrayStyle()}>
            { this.renderTabs() }
          </TabArray>
        </Div>
      </Div>
    );
  }
}



