import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries } = utils;

export const container = {
  marginTop: 8,
  display: `none`,
  [`${mediaQueries.normal}`]: {
    display: 'flex',
    flexDirection: 'row',
  },
};

export const caption = {
  marginTop: 16,
  marginRight: 8,
  fontSize: 16,
  lineHeight: '17px',
  letterSpacing: '-0.5px',
  color: colors.regularCharcoal,

  [`${mediaQueries.phablet}`]: {
    marginTop: 8,
    fontSize: 19,
    lineHeight: '32px',
  },
};

export const downloadButtonContainer = {
  position: 'relative',
  marginLeft: 'auto',
};



