import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

export const container = (copyRight: boolean, prodCount) => ({
  width: '100%',
  margin: '0 auto',
  [`${utils.mediaQueries.phablet}`]: {
    maxWidth: utils.columnsByPercent(11),
  },
  [`${utils.mediaQueries.normal}`]: {
    display: 'flex',
    maxWidth: '1200px',
    flexDirection: copyRight ? 'row-reverse' : 'row',
  },
});

export const halfWidthColMd = (reversed: boolean, gutter: number = 1) => ({
  [`${utils.mediaQueries.phablet}`]: {
    flex: 1,
    width: utils.columnsByPercent(8),
    marginRight: utils.columnsByPercent(1),
    '&:first-of-type': {
      marginRight: reversed ?  0 : utils.columnsByPercent(1),
    },
    '&:last-of-type': {
      marginRight: reversed ? utils.columnsByPercent(1) : 0,
    },
  },
  [`${utils.mediaQueries.normal}`]: {
    marginRight: utils.columnsByPercent(gutter),
    '&:first-of-type': {
      marginRight: reversed ?  0 : utils.columnsByPercent(gutter),
    },
    '&:last-of-type': {
      marginRight: reversed ? utils.columnsByPercent(gutter) : 0,
    },
  },
});

export const contentCol = (reversed: boolean, prodCount: number) => {
  let marginRight = reversed ? 0 : utils.columnsByPercent(1);
  let marginLeft = reversed ? utils.columnsByPercent(1) : 0;
  if ( prodCount === 1 ) {
    marginRight = reversed ? utils.columnsByPercent(2) : utils.columnsByPercent(1);
    marginLeft = reversed ? utils.columnsByPercent(1) : utils.columnsByPercent(2);
  }
  return ({
    maxWidth: '375px',
    margin: '0 auto',
    padding: '0 32px',
    [`${utils.mediaQueries.phablet}`]: {
      maxWidth: 'none',
      margin: 0,
      padding: 0,
    },
    [`${utils.mediaQueries.normal}`]: {
      width: utils.columnsByPercent(7),
      marginRight: marginRight,
      marginLeft: marginLeft,
    },
  });
};

export const productCol = (reversed: boolean, prodCount: number) => {
  let marginRight;
  let marginLeft;
  if ( prodCount === 1 ) {
    marginRight = reversed ? 0 : utils.columnsByPercent(3);
    marginLeft = reversed ? utils.columnsByPercent(3) : 0;
  }
  return({
    [`${utils.mediaQueries.normal}`]: {
      width: prodCount === 1 ? utils.columnsByPercent(4) : utils.columnsByPercent(9),
      marginRight: marginRight,
      marginLeft: marginLeft,
    },
  });
};

export const metricContainer = () => ({
  display: 'flex',
  marginTop: '24px',
  marginBottom: '42px', // 6px short of spec as other 6px is in productContainer
  [`${utils.mediaQueries.phablet}`]: {
    marginTop: '32px',
  },
  [`${utils.mediaQueries.normal}`]: {
    marginTop: '36px',
  },
});

export const productsGroup = (copyRight: boolean) => ({
  display: 'flex',
  justifyContent: 'center',
  [`${utils.mediaQueries.phablet}`]: {
    justifyContent: 'flex-start',
  },
  [`${utils.mediaQueries.normal}`]: {
    justifyContent: copyRight ? 'flex-end' : 'flex-start',
  },
});

export const productContainer = (clientWidth) => ({
  textAlign: 'center',
  display: 'block',
  width: `calc(${clientWidth}px - 160px)`,
  maxWidth: '311px',
  height: 'auto',
  marginTop: '6px', // Margin to prevent outline from being overflow hidden on carousel
  [`${utils.mediaQueries.phablet}`]: {
    marginTop: 0,
  },
});

export const productImage = () => ({
  width: '100%',
  borderRadius: '24px',
  [`${utils.mediaQueries.phablet}`]: {
    borderRadius: '32px',
  },
});

export const assetWrapper = () => ({
  marginBottom: '16px',
});

export const productHeader = () => ({
  fontSize: '12px',
  lineHeight: '16px',
  marginBottom: '8px',
  whiteSpace: 'normal',
});

export const productCopy = () => ({
  fontSize: '12px',
  lineHeight: '16px',
  whiteSpace: 'normal',
});



