import * as React from 'react';
import * as glamorous from 'glamorous';
import * as styles from './SidePagination-styles';

const {
  Li,
  Div,
} = glamorous;

interface IPaginationItemProps {
  title: string;
  index: number;
  sectionRef: any;
  yOffset: number;
  handleClick?: Function;
  active?: boolean;
  primaryColor: string;
  secondaryColor: string;
  navRef?: any;
}

interface IPaginationItemState {
  titleWidth: number;
}

export class SidePaginationItem extends React.Component<IPaginationItemProps, IPaginationItemState> {
  state = {
    titleWidth: 0,
    isTouchEnabled: (typeof window !== 'undefined') ? ('ontouchstart' in window) : false,
  };

  itemTitle: any;

  componentDidMount() {
    window.setTimeout(() => {
      if (this.itemTitle) {
        this.setState({
          titleWidth: this.itemTitle.offsetWidth,
        });
      }
    }, 1000);
  }

  render() {
    const {
      title,
      index,
      handleClick,
      active,
      primaryColor,
      secondaryColor,
      sectionRef,
      yOffset,
    } = this.props;
    return (
      <Li
        onClick={ () => handleClick(sectionRef, yOffset, title, index) }
        css={ styles.listItemContainer(primaryColor, active, this.state.titleWidth, this.state.isTouchEnabled) }
      >
        <Div css={ styles.dotHolder(active) }>
          <Div className="dot" />
        </Div>
        <Div className={`list-item ${active && 'active'}`}
             css={ styles.listItem(secondaryColor, active, this.state.titleWidth, this.state.isTouchEnabled) }>
          <Div innerRef={(input) => { this.itemTitle = input; }}
               className="item-title"
               css={ styles.listItemTitle(active) }>
               {title}
          </Div>
        </Div>
      </Li>
    );
  }
}



