import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';

import * as styles from './Header-styles';

type TagTypes = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';

export interface IHeaderProps {
  children: React.ReactNode;
  tag?: TagTypes;
  css?: Object;
}

const {
  H1,
  H2,
  H3,
  H4,
  H5,
  H6,
} = glamorous;

export class Header extends React.Component<IHeaderProps, void> {
  static propTypes = {
    tag: PropTypes.string,
    css: PropTypes.object,
  };

  static defaultProps = {
    tag: 'h2',
  };

  renderHeader = (tag: string, styles: object) => {
    switch (this.props.tag) {
      case 'h1':
        return <H1 css={ styles } >{this.props.children}</H1>;
      case 'h2':
        return <H2 css={ styles } >{this.props.children}</H2>;
      case 'h3':
        return <H3 css={ styles } >{this.props.children}</H3>;
      case 'h4':
        return <H4 css={ styles } >{this.props.children}</H4>;
      case 'h5':
        return <H5 css={ styles } >{this.props.children}</H5>;
      case 'h6':
        return <H6 css={ styles } >{this.props.children}</H6>;
      default:
        return <H2 css={ styles } >{this.props.children}</H2>;
    }
  }

  render() {
    let combinedStyles;
    const defaultStyles = styles.baseStyles();
    if (this.props.css) {
        combinedStyles = Object.assign(defaultStyles, this.props.css);
    } else {
        combinedStyles = defaultStyles;
    }

    return (
      this.renderHeader(this.props.tag, combinedStyles)
    );
  }
}



