import { findIndex } from 'lodash';

import * as React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { browserHistory } from 'react-router';
import { autobind } from 'core-decorators';

import { store } from '../../../../state/createStore';
import { navIndex, isLoaded } from '../../../../state/actionCreators/appCreator';
import { analyticsEvent } from '../../../../state/actionCreators/data';

import * as glamorous from 'glamorous';
import * as styles from './GlobalNav-styles';
import * as colors from 'utils/styles/colors';

import { StatefulContainer } from 'utils/helpers';
import { GlobalNavTabArray } from '../../molecules/GlobalNavTabArray';
import { GlobalNavSubNav } from '../../molecules/GlobalNavSubNav';
import { Button } from '../../atoms/Button';
import { HamburgerMenu } from '../../atoms/HamburgerMenu';
import { MobileMenu } from '../../atoms/MobileMenu';

import { PUBLIC_PATHS } from 'utils/constants';

const {
  Div,
  Ul,
} = glamorous;

export interface IGlobalNavProps {
  hideCTA?: boolean;
  scrollTransition: number;
  navItemColor?: string;
  tabItems: Array<any>;
  subnavItems: Array<any>;
  buttonTitle?: string;
  buttonLink?: string;
  pageColor?: string;
  menuItems: {
    menuItems: Array<{
        label: string;
        link: string;
    }>;
    subMenuItems: Array<{
        label: string;
        link: string;
    }>;
  };
  selectedId: number;
  analyticsEvent?: Function;
  page?: string;
  locale?: string;
  isLoaded?: boolean;
}

export interface IGlobalNavState {
  scrolled: boolean;
  hideCTA?: boolean;
  buttonWidth: number;
  allowTransition: boolean;
  navActive: boolean;
  navIsVisible: boolean;
}

const mapStateToProps = (state, ownProps) => {
  return {
     ...ownProps,
    selectedId: state.app.navIndex,
    page: state.data.current.id,
    locale: state.data.locale,
    isLoaded: state.app.isLoaded,
  };
};

let waiting = false;

export class GlobalNavClass extends React.Component<IGlobalNavProps, IGlobalNavState> {
  state = {
    scrolled: false,
    hideCTA: this.props.hideCTA,
    buttonWidth: 0,
    allowTransition: false,
    navActive: false,
    navIsVisible: false,
  };

  waitInterval: any;
  button;
  freezeVpCallback: any;
  bodyEl: any;
  page: any;
  scrollPostion: number;
  navTimeout: any;

  componentDidMount() {

    document.addEventListener('scroll', this.throttleScroll);
    this.handleScroll();

    this.bodyEl = document.querySelector('body');
    this.page = document.querySelector('.page-content');

    window.setTimeout(() => {
      if ( this.button ) {
        this.setState({
          buttonWidth: this.button.offsetWidth + 32,
        }, () => {
          this.handleScroll();
        });

        setTimeout(() => {
          this.setState({
            allowTransition: true,
          }, () => {
            store.dispatch(isLoaded());
          });
        }, 100);

      }
    }, 1000);

    browserHistory.listen((location) => {
      if (this.state.navActive) {
        this.setState((state) => {
          return {
            ...state,
            navActive: false,
            navIsVisible: false,
          };
        }, () => {
          this.enableBodyScrolling();
        });
      }
      const index = findIndex(this.props.tabItems, { link: location.pathname });
      store.dispatch(navIndex(index));
    }).bind(this);

  }

  componentDidUpdate(lastProps, lastState) {
    if (lastProps.hideCTA !== this.props.hideCTA) {
      this.setState({
        hideCTA: this.props.hideCTA,
      });
      this.handleScroll();
    }
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.throttleScroll);
    clearTimeout(this.waitInterval);
  }

  // Freeze momentum scroll callback
  freezeVp(e) {
    e.preventDefault();
  }

  throttleScroll = () => {
    const limit = 100;
    clearTimeout(this.waitInterval);
    if (!waiting) {
      this.handleScroll();
      waiting = true;
      setTimeout(() => {
        waiting = false;
      }, limit);
    } else {
      this.waitInterval = setTimeout(() => { this.throttleScroll(); }, limit + 1);
    }
  }

  handleScroll = () => {
    const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);

    if (scrollTop > this.props.scrollTransition && !this.state.scrolled) {
      this.setState({
        scrolled: true,
        hideCTA: false,
      });
    }
    else if (scrollTop <= this.props.scrollTransition && this.state.scrolled) {
      this.setState({
        scrolled: false,
        hideCTA: this.props.hideCTA,
      });
    }
  }

  makeTabs = (id) => {
    return this.props.tabItems.map((tab, index) => {
      return {
        ...tab,
        selected: id === index,
        onSelect: (link) => {
          this.props.analyticsEvent({
            action: 'click',
            label: `nav-${tab.link}-${tab.label}`,
            category: `nav:button-${tab.link}-${tab.label}`,
          });
          browserHistory.push(link);
        },
      };
    });
  }

  stopBodyScrolling() {
    this.scrollPostion = this.bodyEl.scrollTop;
    this.bodyEl.style.overflow = 'hidden';
    this.bodyEl.style.touchAction = 'none';
    this.page.style.bottom = '200px';
  }

  enableBodyScrolling() {
    this.bodyEl.style.overflow = 'visible';
    this.bodyEl.style.touchAction = 'initial';
    this.page.style.bottom = 'inherit';
    this.bodyEl.scrollTop = this.scrollPostion;
  }

  handleNavMount() {
    const { navActive } = this.state;
    clearTimeout(this.navTimeout);
    // setTimouts are used to handle discrepancy of component mounting and css transitions.
    if (!navActive) {
      this.setState((state) => {
        return ({
          ...state,
          navIsVisible: true,
          navActive: true,
        });
      }, () => {
        this.navTimeout = setTimeout(() => {
          this.stopBodyScrolling();
        }, 200);
      });
    } else {
      this.setState((state) => {
        return ({
          ...state,
          navActive: false,
        });
      }, () => {
        this.enableBodyScrolling();
        this.navTimeout = setTimeout(() => {
          this.setState({navIsVisible: false});
        }, 200);
      });
    }
  }

  handleMobileNavClick(evt, route) {
    evt.preventDefault();
    this.setState((state) => {
      return {
        ...state,
        navIsVisible: false,
        navActive: false,
      };
    }, () => {
      this.enableBodyScrolling();
      if (route.indexOf('http') > -1) {
        window.location.href = route;
      } else {
        browserHistory.push(route);
      }
    });
  }

  @autobind
  logoAnalytics(evt) {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${this.props.page}-nav-logo`,
        category: `${this.props.page}-nav-logo`,
      });
    }
  }

  render() {
    const {
      scrolled,
      hideCTA,
      buttonWidth,
      allowTransition,
      navActive,
      navIsVisible,
    } = this.state;

    const {
      navItemColor,
      subnavItems,
      buttonTitle,
      buttonLink,
      menuItems,
      pageColor,
      isLoaded,
    } = this.props;

    const buttonColor = (pageColor === 'Yellow' && !scrolled) && !navActive ? 'regularWhite' : 'regularYellow';
    const homeLink = (this.props.locale !== '' && this.props.locale !== 'en-us') ? `/l/${this.props.locale}/` : '/';

    return (
      <Div css={ styles.globalNav(colors.regularBlueGradient) }>
        <Div css={ styles.globalNavContainer(scrolled, navActive, navIsVisible) } >
          <Div css={ styles.logoContainer() }>
            <Link to={homeLink} onClick={ this.logoAnalytics }>
              <img src={'/images/snap-logo.svg'} alt="Snapchat" />
            </Link>
          </Div>
          <Div css={ styles.rightContainer(hideCTA, buttonWidth, allowTransition, isLoaded) }>
            <Div css={styles.navWrapper()}>
              <Div css={ styles.tabContainer() }>
                <GlobalNavTabArray
                  fontColor={navItemColor}
                  tabs={this.makeTabs(this.props.selectedId)}
                  analyticsEvent={ this.props.analyticsEvent }
                />
              </Div>
              <Div css={ styles.subnavContainer() }>
                <GlobalNavSubNav
                  hideCTA={this.state.hideCTA}
                  items={subnavItems}
                  analyticsEvent={this.props.analyticsEvent}
                  dotColor={navItemColor}
                />
              </Div>
            </Div>
            <Div css={ styles.buttonMenuWrapper() }>
              { buttonTitle && buttonLink &&
                <Div innerRef={(ref) => { this.button = ref; }} css={ styles.buttonContainer() }>
                  <a href={ buttonLink } onClick={(e) => this.props.analyticsEvent({
                    action: 'click',
                    label: `${this.props.page}-nav-get-started`,
                    category: `${this.props.page}:nav-get-started`,
                  })}>
                    <Button color={buttonColor} filled={true} isSmall>{buttonTitle}</Button>
                  </a>
                </Div>
              }
              <Div css={styles.hamburgerContainer()}>
                <HamburgerMenu
                  active={navActive}
                  onClick={this.handleNavMount.bind(this)}
                  handleOnKeyUp={this.handleNavMount.bind(this)}
                />
              </Div>
            </Div>
          </Div>
        </Div>
        <MobileMenu selected={this.props.selectedId} pageColor={pageColor} active={navActive} isVisible={navIsVisible} menuItems={menuItems} onClick={this.handleMobileNavClick.bind(this)} />
      </Div>
    );
  }
}

export const GlobalNav = connect(mapStateToProps, {navIndex, analyticsEvent})(GlobalNavClass);



