import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div, A } = glamorous;
import * as colors from 'utils/styles/colors';
import { Markdown } from '../../atoms/Markdown';
import { HeadlineTitle } from '../../atoms/HeadlineTitle';
import { EyebrowTitle } from '../../atoms/EyebrowTitle';

import { Button } from '../../atoms/Button';
import { Image } from '../../atoms/Image';
import * as utilStyles from 'utils/styles';
const { mediaQueries, padding } = utilStyles;

import * as styles from './Headline-styles';

interface IButtonContentProps {
  color: string;
  cta: string;
  outline?: boolean;
  filled?: boolean;
  onClick?: Function;
  link?: string;
  isSmall?: boolean;
  buttonStyle?: string;
}

interface IEyebrowContentProps {
  color?: string;
  content: string;
}

export interface IImageContentProps {
  imageUrl: string;
  imageUrl2x?: string;
  title?: string;
  natural?: boolean;
  noPadding?: boolean;
  dimensions?: {
    height: number;
    width: number;
  };
}

export interface IHeadlineProps {
  eyebrowContent?: IEyebrowContentProps;
  titleContent: string;
  subheadContent?: string;
  primary?: boolean;
  buttonContent?: Array<IButtonContentProps>;
  headlineBodyCopy?: string;
  imageContent?: IImageContentProps;
  specsLabel?: string;
  specsLink?: string;
  color?: string;
  isRichText?: boolean;
  analyticsEvent?: Function;
  noMax?: boolean;
  sectionType?: string;
}

export const HeadlinepropTypes = {
  color: PropTypes.string,
  eyebrowContent: PropTypes.shape({
    color: PropTypes.string,
    content: PropTypes.string.isRequired,
  }),
  titleContent: PropTypes.string.isRequired,
  primary: PropTypes.bool,
  buttonContent: PropTypes.arrayOf(PropTypes.shape({
    color: PropTypes.string.isRequired,
    cta: PropTypes.string.isRequired,
    outline: PropTypes.bool,
    filled: PropTypes.bool,
    onClick: PropTypes.func,
    link: PropTypes.string,
    isSmall: PropTypes.bool,
  })),
  headlineBodyCopy: PropTypes.string,
  imageContent: PropTypes.shape({
    imageUrl: PropTypes.string.isRequired,
    imageUrl2x: PropTypes.string,
    title: PropTypes.string,
    natural: PropTypes.bool,
    dimensions: PropTypes.shape({
      height: PropTypes.number,
      width: PropTypes.number,
    }),
  }),
};

export class Headline extends React.Component<IHeadlineProps, void> {
    static propTypes = HeadlinepropTypes;
    render() {
        return (
          <Div css={styles.headerBlock()}>

            {this.props.imageContent &&
              <Div className="header-image-wrapper" css={styles.headlineImageWrapper(this.props.imageContent.dimensions.height, this.props.imageContent.dimensions.width)}>
                <Image
                  imageUrl={this.props.imageContent.imageUrl}
                  title={this.props.imageContent.title}
                  natural={this.props.imageContent.natural}
                  noPadding={this.props.imageContent.noPadding}
                  width={this.props.imageContent.dimensions.width}
                  height={this.props.imageContent.dimensions.height} />
              </Div>
            }

            {this.props.eyebrowContent &&
               <EyebrowTitle color={this.props.eyebrowContent.color}>
                 {this.props.eyebrowContent.content}
               </EyebrowTitle>
            }

            <HeadlineTitle primary={this.props.primary} isRichText={this.props.isRichText} noMax={ this.props.noMax }>
              {this.props.titleContent}
            </ HeadlineTitle>

            { this.props.primary && this.props.subheadContent &&
              <Markdown copy={`${this.props.subheadContent}`} styleOverrides={styles.markdownStylesSubHead()} />
            }

            { !this.props.primary && this.props.subheadContent &&
              <Markdown copy={`${this.props.subheadContent}`} styleOverrides={styles.markdownStyles()} />
            }

            {this.props.buttonContent && this.props.buttonContent.length ? (
              <Div css={styles.headerButtonBlockWrapper(this.props.primary)}>
                {this.props.buttonContent.map((button, i) => {
                  if (button.buttonStyle === 'primaryButton') {
                    return button && <Button css={(i === 1 && this.props.buttonContent[i - 1].buttonStyle === 'primaryButton') ? { display: 'block', margin: `${padding.semi}px auto`, [`${mediaQueries.phablet}`]: { display: 'inline-block', marginTop: 0, marginLeft: `${padding.semi}px`, }, } : {}} color={button.color} isSmall={button.isSmall === false ? false : true} filled={button.filled} outline={button.outline} onClick={button.onClick} link={button.link} key={i} analyticsEvent={ this.props.analyticsEvent }>{button.cta}</Button>;
                  } else if (button.buttonStyle === 'secondaryButton') {
                    return (
                      <Div css={styles.specLinkHolder()} key={i}>
                        <A css={styles.specsLink(this.props.color ? this.props.color : colors.black)} href={button.link} target="_blank">
                          <span>{ button.cta }</span>
                        </A>
                      </Div>
                    );
                  }
                })}
              </Div>
            ) : (
              null
            )}
          </Div>
        );
    }
}



