import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

const { mediaQueries } = utils;

export const buttonStyles = () => ({
  position: 'relative',
  width: 50,
  height: 50,
  padding: 0,
  backgroundColor: colors.white,
  border: 0,
  borderRadius: '50%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  cursor: 'pointer',
  outline: '0 dashed rgba(0,0,0,0.2)',
  transition: 'box-shadow .4s ease',
  boxShadow: '0 12px 65px 0 rgba(0,0,0,0.05)',

  '&::before': {
    content: `''`,
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    borderRadius: '50%',
    opacity: 0,
    zIndex: 1,
    boxShadow: '0 12px 43px 0 rgba(0,0,0,0.05)',
    transition: `opacity 400ms ease`,
  },

  '&:hover, &:focus': {
    '&::before': {
      opacity: 1,
    },
  },

  '[data-whatinput="keyboard"] &:focus': {
    outlineWidth: '1px',
  },

  [`${mediaQueries.phablet}`]: {
    width: 50,
    height: 50,
  },
});



