import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

const { padding, mediaQueries } = utils;

export const blockWrapper = (hasImage: any = false) => ({
  backgroundColor: colors.snapYellow,
  minHeight: 232,
  padding: padding.semi,
  display: 'flex',
  flexFlow: `column wrap`,
  alignItems: `stretch`,
  justifyContent: `center`,
  borderRadius: 20,
  letterSpacing: `-.40px`,
  textAlign: `center`,
  [`${mediaQueries.phablet}`]: {
    textAlign: hasImage ? `left` : 'center',
    flexFlow: `row wrap`,
    letterSpacing: `-.60px`,
    padding: padding.large,
  },
  [`${mediaQueries.normal}`]: {
    padding: padding.xlarge,
    letterSpacing: `-1.2px`,
  },

});

export const quoteMainCopy = (hasAtt: any = false) => ({
  fontSize: 22,
  fontFamily: '\'AvenirNext-Bold\', sans-serif',
  fontWeight: `700`,
  lineHeight: 1.2,
  marginBottom: hasAtt ? 24 : 0,
  [`${mediaQueries.phablet}`]: {
    fontSize: 30,
    lineHeight: 1.3,
    width: `100%`,
    marginBottom: hasAtt ? padding.semi : 0,
  },
  [`${mediaQueries.normal}`]: {
    fontSize: 36,
    marginBottom: hasAtt ? padding.xlarge : 0,
  },
});

export const quoteMainCopyWrapper = (hasImage: any = false) => ({
  width: '100%',
  paddingTop: `12`,
  order: 2,
  [`${mediaQueries.phablet}`]: {
    display: `flex`,
    justifyContent: `center`,
    flexFlow: `column wrap`,
    order: 1,
    width: hasImage ? `60%` : `75%`,
    paddingTop: 0,
    paddingRight: 12,
  },
  [`${mediaQueries.normal}`]: {
    width: hasImage ? `50%` : `80%` ,
  },
});

export const sourceCopy = () => ({
  fontSize: 16,
  [`${mediaQueries.normal}`]: {
    fontSize: 19,
  },
});

export const imageWrapper = () => ({
  order: 1,
  display: 'flex',
  flexDirection: `column`,
  alignItems: 'stretch',
  justifyContent: 'center',
  paddingBottom: 12,
  width: `100%`,
  [`${mediaQueries.phablet}`]: {
    order: 2,
    width: `40%`,
    padding: 24,
    alignItems: `center`,
  },
  [`${mediaQueries.normal}`]: {
    width: `50%`,
    padding: 48,
  },
});

export const imageBox = () => ({
  width: `50%`,
  alignSelf: `center`,
});

export const image = () => ({
  maxHeight: `80px`,
  [`${mediaQueries.phablet}`]: {
    flexGrow: 1,
    maxHeight: `none`,
    width: `100%`,
  },
});

export const imageContainer = () => ({
  width: `100%`,
  height: `100%`,
});

export const markdownStyles = (hasAtt: any = false) => ({
  [' p']: {
    display: `inline`,
    fontSize: 22,
    fontFamily: '\'AvenirNext-Bold\', sans-serif',
    fontWeight: `700`,
    lineHeight: 1.2,
    [`${mediaQueries.phablet}`]: {
      fontSize: 30,
      lineHeight: 1.3,
      width: `100%`,
    },
    [`${mediaQueries.normal}`]: {
      fontSize: 36,
    },
  },
  [' h2']: {
    fontSize: 22,
    fontFamily: '\'AvenirNext-Bold\', sans-serif',
    fontWeight: `700`,
    lineHeight: 1.2,
    [`${mediaQueries.phablet}`]: {
      fontSize: 30,
      lineHeight: 1.3,
      width: `100%`,
    },
    [`${mediaQueries.normal}`]: {
      fontSize: 36,
    },
  },
  [' h3']: {
    fontSize: 22,
    fontFamily: '\'AvenirNext-Bold\', sans-serif',
    fontWeight: `700`,
    lineHeight: 1.2,
    [`${mediaQueries.phablet}`]: {
      fontSize: 30,
      lineHeight: 1.3,
      width: `100%`,
    },
    [`${mediaQueries.normal}`]: {
      fontSize: 36,
    },
  },
  [' cite']: {
    display: `none`,
  },
});

export const markdownWrapper = (hasAtt: any = false) => ({
  marginBottom: hasAtt ? 24 : 0,
  [`${mediaQueries.phablet}`]: {
    marginBottom: hasAtt ? padding.semi : 0,
  },
  [`${mediaQueries.normal}`]: {
    marginBottom: hasAtt ? padding.xlarge : 0,
  },
});



