import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

export const container = (fullWidth: boolean) => ({
  maxWidth: fullWidth ? `none` : `635px`,
});

export const header = (marginTop: string) => ({
  margin: `0 0 30px 0`,
  fontSize: `20px`,
  lineHeight: `32px`,
  [`${utils.mediaQueries.phablet}`]: {
    marginTop: marginTop,
  },
});

export const markdownStyles = (marginTop: string) => ({
  [' p']: {
    ...type.large,
  },
  [' h3']: {
    fontFamily: type.BOLD_FONT,
    margin: `0 0 30px 0`,
    fontSize: `20px`,
    lineHeight: `32px`,
    [`${utils.mediaQueries.phablet}`]: {
      marginTop: marginTop,
    },
  },
});



