import * as React from 'react';
import * as glamorous from 'glamorous';
import * as PropTypes from 'prop-types';
import { Link } from 'react-router';
import { autobind } from 'core-decorators';
import {Button} from '../../atoms/Button' ;
import { whereAmI } from 'utils/helpers';

import * as styles from './HeadlineCTA-styles';

const {Div, H2} = glamorous;

export interface IHeadlineCTAProps {
  headlineCopy: React.ReactNode | string;
  buttonCopy: React.ReactNode | string;
  link?: string;
  onButtonClick: any;
  analyticsEvent?: Function;
}

export class HeadlineCTA extends React.Component<IHeadlineCTAProps, void> {

  static propTypes = {
    headlineCopy: PropTypes.string,
    buttonCopy: PropTypes.string,
    onButtonClick: PropTypes.func,
    link: PropTypes.string,
  };

  @autobind
  analytics() {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${whereAmI()}-button-${ this.props.headlineCopy }-${ this.props.buttonCopy }`,
        category: `${whereAmI()}-button-${ this.props.headlineCopy }-${ this.props.buttonCopy }`,
      });
    }
  }

  render() {
    return (
      <Div css={styles.container()} >
        <H2 css={styles.header()} >{this.props.headlineCopy}</H2>
        <Div css={styles.buttonWrapper()} >
          <Link to={this.props.link}>
            <Button color="black" outline isSmall onClick={ this.analytics }>{this.props.buttonCopy}</Button>
          </Link>
        </Div>
      </Div>
    );
  }
}



