import * as React from 'react';
import * as glamorous from 'glamorous';
import * as Router from 'react-router';

const { Link } = Router;

import {
  subNavWrapper,
  subNavItem,
  triggerDots,
  triggerWrapper,
} from './GlobalNavSubNav-styles';
const { Component, PropTypes } = React;
const {
  Div,
  A,
  Ul,
  Li,
} = glamorous;

export interface IGlobalNavSubNavProps {
  items: Array<{
    label: string;
    link: string;
  }>;
  dotColor?: string;
  css?: Function;
  analyticsEvent?: Function;
  hideCTA?: boolean;
  isTouchEnabled?: boolean;
}

export interface IGlobalNavSuvNavState {
  active: boolean;
}

export class GlobalNavSubNav extends Component<IGlobalNavSubNavProps, IGlobalNavSuvNavState> {

  state = {
    active: false,
    isTouchEnabled: (typeof window !== 'undefined') ? ('ontouchstart' in window) : false,
  };

  static propTypes = {
    items: PropTypes.arrayOf(PropTypes.shape({
      label: PropTypes.string.isRequired,
      link: PropTypes.string.isRequired,
    })),
    css: PropTypes.func,
    isMobile: PropTypes.bool,
  };

  el: any;

  handleClick(e) {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      active: !this.state.active,
    }, () => {
      if (this.state.active) {
        this.el.focus();
      } else {
        this.el.blur();
      }
    });
  }
  setActive(e) {
    e.preventDefault();
    this.el.focus();
    this.setState({
      active: true,
    });
  }
  closeNav = (e) => {
    this.setState({
      active: false,
    });
    this.el.blur();
  }
  renderItems() {
    const { items } = this.props;
    return items.map((item, index) => {
      const externalLink = item.link ? item.link.indexOf('http') : -1;
      return (
        <Li
          tabIndex="0"
          key={ item.label }
          css={subNavItem()}
          onClick={() => this.props.analyticsEvent({
            action: 'click',
            label: `subNav-${item.link}-${item.label}`,
            category: `subNav:button-${item.link}-${item.label}`,
          })}
          >
          { externalLink !== -1 ?
            <a href={item.link} onClick={this.closeNav} key={index} target="_blank" role="menutitem" >
              {item.label}
            </a>
            :
            <Link to={item.link} onClick={this.closeNav} key={index} role="menuitem" >
              {item.label}
            </Link>
          }
        </Li>
      );
    });
  }

  render() {
    const { active, isTouchEnabled } = this.state;
    const { css, dotColor, hideCTA } = this.props;
    return (
      <Div css={css && css()}>
        <Div
          innerRef={(el) => {
            this.el = el;
          }}
          tabIndex="0"
          aria-haspopup="true"
          onKeyUp={(e) => {
            if (e.which === 32 || e.which === 13) {
              if (!active) {
                this.setActive(e);
              } else {
                this.closeNav(e);
              }
            }
          }}
          css={triggerWrapper()}>
          <Div
            className="trigger-dots"
            css={triggerDots(active)}
            onClick={(e) => this.handleClick(e)}
            onMouseOver={(evt) => {
              if (!isTouchEnabled) {
                this.setActive(evt);
              }
            }}
            onMouseLeave={(evt) => {
              if (!isTouchEnabled) {
                this.closeNav(evt);
              }
            }}
            />
          </Div>
          <Ul
            css={subNavWrapper(active, hideCTA)}
            onMouseOver={this.setActive.bind(this)}
            onMouseLeave={this.closeNav}
            aria-expanded={this.state.active ? 'true' : 'false'}
            >
            {this.renderItems()}
          </Ul>
      </Div>
    );
  }
}



