import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
import 'what-input';

const { Div, Li, Img } = glamorous;

import { Tab, TabProps } from './Tab';
import * as styles from './TabArray-styles';

export type ImageTabProps = TabProps & {
  image?: any;
  imageAlt?: string;
  hoverImage?: any;
  activeImage?: any;
  label?: string;
};

const propTypes = {
  image: PropTypes.string,
  imageAlt: PropTypes.string,
  hoverImage: PropTypes.string,
  activeImage: PropTypes.string,
};

export const ImageTab: React.SFC<ImageTabProps> =
  ({ children, image, activeImage, hoverImage, css, imageAlt, ...rest }) => {
    const { selected } = rest;
    activeImage = activeImage || image;
    hoverImage = hoverImage || image;
    image = selected ? activeImage : image;

    return (
      <Tab
        css={[styles.imageTabStyles(), css]}
        {...rest}
      >
        <Div>
          <Img
            className="image-tab-normal"
            css={styles.imageTabNormalImage()}
            alt={imageAlt}
            src={image}
          />
          <Img
            className="image-tab-hover"
            css={styles.imageTabHoverImage()}
            alt={imageAlt}
            src={hoverImage}
            aria-hidden
          />
        </Div>
        <div style={{ marginTop: 10 }}>
          {children}
        </div>
      </Tab>
    );
  };
ImageTab.displayName = 'ImageTab';
ImageTab.propTypes = propTypes;



