import * as React from 'react';
import glamorous from 'glamorous';
import { connect } from 'react-redux';
import { isMobile } from '../../../../state/actionCreators/appCreator';

import * as styles from './BannerImage-styles';

const { Div, Img } = glamorous;

export interface IBannerImageProps {
  desktopUrl: string;
  mobileUrl: string;
  alt: string;
  desktopHeight?: number;
  mobileHeight?: number;
  isMobile?: boolean;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
});

export class BannerImageImpl extends React.Component<IBannerImageProps, void> {
  render() {
    const url = this.props.isMobile ? this.props.mobileUrl : this.props.desktopUrl;
    return (
      <Div
        className="ie-scroll-bar"
        css={styles.bannerContainer()}
      >
        <Img
          src={url}
          css={styles.image()}
          key={`banner-img${url}`}
          alt={this.props.alt}
        />
      </Div>
    );
  }
}

export const BannerImage = connect(mapStateToProps)(BannerImageImpl);



