import * as utilStyles from 'utils/styles';

const { borderRadius } = utilStyles;

const secondLastChildDesktop = (cardCount, radiusValue) => {

  if (
    (cardCount - 3) % 10 === 0 ||
    (cardCount - 8) % 10 === 0 ||
    (cardCount - 6) % 10 === 0 ||
    (cardCount - 1) % 10 === 0) {
     return cardCount === 3 ? `0 ${radiusValue} ${radiusValue} 0` : `0 0 ${radiusValue} 0`;
  } else if (cardCount === 2) {
    return `${radiusValue} 0 0 ${radiusValue}`;
  } else if (
    (cardCount - 4) % 10 === 0 ||
    (cardCount - 9) % 10 === 0 ||
    (cardCount - 7) % 10 === 0 ||
    (cardCount - 2) % 10 === 0) {
    return `0 0 0 ${radiusValue}`;
  } else {
    return 0;
  }

};

const thirdLastChildDesktop = (cardCount, radiusValue) => {

  if (
    (cardCount - 4) % 10 === 0 ||
    (cardCount - 9) % 10 === 0) {
    return cardCount === 4 ? `0 ${radiusValue} ${radiusValue} 0` : `0 0 ${radiusValue} 0`;
  } else if (cardCount % 5 === 0) {
    return `0 0 0 ${radiusValue}`;
  } else if (cardCount === 3) {
    return `${radiusValue} 0 0 0`;
  } else {
    return 0;
  }

};

const lastChildDesktop = (cardCount, radiusValue) => {

  if (
    (cardCount - 3) % 10 === 0 ||
    (cardCount - 8) % 10 === 0 ||
    (cardCount - 6) % 10 === 0 ||
    (cardCount - 1) % 10 === 0) {
    return `0 0 ${radiusValue} ${radiusValue}`;
  } else if (cardCount === 2) {
    return `0 ${radiusValue} ${radiusValue} 0`;
  } else {
    return `0 0 ${radiusValue} 0`;
  }

};

const secondLastChildTablet = (cardCount, radiusValue) => {

  if (
    (cardCount - 3) % 10 === 0 ||
    (cardCount - 5) % 10 === 0 ||
    (cardCount - 8) % 10 === 0 ||
    (cardCount - 10) % 10 === 0) {
    return `0 0 0 ${radiusValue}`;
  } else if (
    (cardCount - 9) % 10 === 0 ||
    (cardCount - 7) % 10 === 0 ||
    (cardCount - 4) % 10 === 0 ||
    (cardCount - 2) % 10 === 0) {
    return cardCount === 2 ? `${radiusValue} ${radiusValue} ${radiusValue} 0` : `0 0 ${radiusValue} 0`;
  } else {
    return 0;
  }

};

const lastChildTablet = (cardCount, radiusValue) => {

  if (
    (cardCount - 6) % 10 === 0 ||
    (cardCount - 1) % 10 === 0 ||
    (cardCount - 9) % 10 === 0 ||
    (cardCount - 7) % 10 === 0 ||
    (cardCount - 4) % 10 === 0 ||
    (cardCount - 2) % 10 === 0) {
    return `0 0 ${radiusValue} ${radiusValue}`;
  } else {
    return `0 0 ${radiusValue} 0`;
  }

};

export const desktopGridRadius = (cardCount, isThreeUp = false) => ({
  '& .grid-card-wrapper:first-of-type .grid-card': {
    borderRadius: isThreeUp ? `${borderRadius.desktopContainer} 0 0 ${borderRadius.desktopContainer}` : `${borderRadius.desktopContainer} 0 0 0`,
  },
  '& .grid-card-wrapper:only-of-type .grid-card': {
    borderRadius: `${borderRadius.desktopContainer}`,
  },
  '& .grid-card-wrapper:nth-of-type(2) .grid-card': {
    borderRadius: !isThreeUp && `0 ${borderRadius.desktopContainer} 0 0`,
  },
  '& .grid-card-wrapper:nth-last-child(2) .grid-card': {
    borderRadius: !isThreeUp && secondLastChildDesktop(cardCount, borderRadius.desktopContainer),
  },
  '& .grid-card-wrapper:nth-last-child(3) .grid-card': {
    borderRadius: !isThreeUp && thirdLastChildDesktop(cardCount, borderRadius.desktopContainer),
  },
  '& .grid-card-wrapper:last-of-type:not(:only-of-type) .grid-card': {
    borderRadius: isThreeUp ? `0 ${borderRadius.desktopContainer} ${borderRadius.desktopContainer} 0` : lastChildDesktop(cardCount, borderRadius.desktopContainer),
  },
});

export const tabletGridRadius = (cardCount) => ({
  '& .grid-card-wrapper:first-of-type .grid-card': {
    borderRadius: `${borderRadius.tabletContainer} ${borderRadius.tabletContainer} 0 0`,
  },
  '& .grid-card-wrapper:only-of-type .grid-card': {
    borderRadius: `${borderRadius.tabletContainer}`,
  },
  '& .grid-card-wrapper:nth-last-child(2) .grid-card': {
    borderRadius: secondLastChildTablet(cardCount, borderRadius.tabletContainer),
  },
  '& .grid-card-wrapper:last-of-type:not(:only-of-type) .grid-card': {
    borderRadius: lastChildTablet(cardCount, borderRadius.tabletContainer),
  },
});



