import { css } from 'glamor';
import * as utilStyles from 'utils/styles';
import * as glamor from 'glamor';

const play = require('../../atoms/Video/assets/play.svg');
const pause = require('../../atoms/Video/assets/pause.svg');

const { mediaQueries } = utilStyles;

const phone = {
  height: 548,
  width: 270,
};

const screen = {
  padSides: `${18 / phone.width * 100}%`,
  padBottom: `${77 / phone.height * 100}%`,
  padTop: `${55 / phone.height * 100}%`,
  longCorner: `${45 / phone.height * 100}%`,
  shortCorner: `${45 / phone.width * 100}%`,
};

const phoneRatio = `${(phone.height / phone.width) * 100}%`;

const fadeEnter = glamor.keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
});

const fadeLeave = glamor.keyframes({
  '0%': {
    opacity: 1,
  },
  '100%': {
    opacity: 0,
  },
});

export const container = (isFocused = true, isMobile = false) => ({
  width: '100%',
  display: 'block',
  opacity: isMobile ? isFocused ? 1 : 0.5 : 1,
  transition: 'opacity 300ms ease-in-out',
});

export const defaultStyles = (inCarousel, hideDevice = false) => ({
  position: 'relative',
  display: 'block',
  width: '100%',
  height: 'auto',
  [`${mediaQueries.phablet}`]: {
    width: '100%',
  },
  '::before': {
    content: `''`,
    display: hideDevice ? 'none' : 'block',
    borderTopLeftRadius: ` ${screen.shortCorner} ${screen.longCorner}`,
    borderTopRightRadius: ` ${screen.shortCorner} ${screen.longCorner}`,
    borderBottomLeftRadius: ` ${screen.shortCorner} ${screen.longCorner}`,
    borderBottomRightRadius: ` ${screen.shortCorner} ${screen.longCorner}`,
    background: 'rgba(0,0,0,0.05)',
    boxShadow: '0 10px 15px rgba(0,0,0,0.05)',
    position: 'absolute',
    height: '100%',
    width: '100%',
  },
  '::after': {
    content: '""',
    display: 'block',
    paddingBottom: `calc(${phoneRatio})`,
  },
});

export const device = () => ({
  height: '100%',
  position: 'absolute',
  width: '100%',
});

export const deviceScreen = (videoUrl, animating = false, timing = 300, playing, shouldShowPoster = false, initialPlay = false, hideDevice = false) => ({
  position: 'absolute',
  top: screen.padTop,
  bottom: screen.padBottom,
  left: screen.padSides,
  right: screen.padSides,
  boxShadow: hideDevice ? 'none' : 'inset 0 0 3px rgba(0,0,0,0.12)',
  borderRadius: hideDevice ? '24px' : 'none',
  overflow: `hidden`,
  display: `flex`,
  justifyContent: `center`,
  '&::after': {
    content: '""',
    boxShadow: 'inset 0 0 3px rgba(0,0,0,0.12)',
    display: 'block',
    position: 'absolute',
    top: 0,
    width: '100%',
    height: '100%',
    borderRadius: hideDevice ? '24px' : null,
    zIndex: shouldShowPoster && !initialPlay ? 1 : videoUrl ? -1 : 1,
    opacity: 0,
    animation: animating ? `${fadeLeave} ${timing / 1000}s ease-in-out forwards` : `${fadeEnter} ${timing / 1000}s ease-in-out forwards ${timing / 1000}s`,
  },
  '&:before': {
    content: shouldShowPoster ? `''` : null,
    width: 100,
    height: 100,
    backgroundImage: !playing ? `url(${play})` : '',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'contain',
    zIndex: 3,
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translateY(-50%)translateX(-50%)',
  },
  '& video': {
    opacity: 0,
    animation: animating ? `${fadeLeave} ${timing / 1000}s ease-in-out forwards` : `${fadeEnter} ${timing / 1000}s ease-in-out forwards ${timing / 1000}s`,
    borderRadius: hideDevice ? '24px' : 'none',
  },
});

export const defaultImage = () => ({
  height: `100%`,
});

export const deviceTitle = () => ({
  fontFamily: '\'AvenirNext-DemiBold\', sans-serif',
  textAlign: 'center',
  fontSize: '19px',
  lineHeight: '28px',
  letterSpacing: '0.2px',
});

export const deviceCopy = () => ({
  textAlign: 'center',
  fontSize: '16px',
  lineHeight: '24px',
  marginTop: '8px',
  maxWidth: 265,
  marginLeft: 'auto',
  marginRight: 'auto',
});

export const copyLockup = (inCarousel) => ({
  display: 'block',
  position: 'relative',
  bottom: 0,
  marginTop: '24px',
  width: '100%',
  [mediaQueries.phablet]: {
    width: '100%',
  },
  [mediaQueries.phabletMax]: {
    whiteSpace: 'normal',
  },
});



