import glamorous from 'glamorous';
import { css } from 'glamor';

import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';
import { hexToRGB } from 'utils/helpers';

export const storyStyles = {
    default: {
        display: 'inline-block',
        verticalAlign: 'top',
        textAlign: 'center',
        position: 'relative',
        margin: '0.5em',
    },
    h1: {
        color: '#f00',
    },
    p: {
        marginTop: '100px',
        ':first-child': {
            marginTop: 0,
        },
    },
    a: {
        textDecoration: 'none',
        color: '#0f0',
    },
    sup: {
        fontSize: '8px',
        verticalAlign: 'top',
    },
};

export const defaultStyles = () => ({
    [' h1']: {
        ...type.headline1Mobile,
        [`${utils.mediaQueries.phablet}`]: {
          ...type.headline1,
        },
    },
    [' h2']: {
        ...type.headline2,
    },
    [' h3']: {
        ...type.headline3,
    },
    [' h4']: {
        ...type.headline3,
    },
    [' h5']: {
        ...type.headline3,
    },
    [' h6']: {
        ...type.headline3,
    },
    [' p']: {
        ...type.regular,
    },
    [' a']: {
        color: colors.regularBlue,
    },
    [' li']: {
        ...type.regular,
    },
    [' b']: {
        fontFamily: type.BOLD_FONT,
    },
    [' strong']: {
        fontFamily: type.BOLD_FONT,
    },
    [' sup']: {
        ...type.superscript,
    },
    [' sub']: {
        ...type.subscript,
    },
    [' cite']: {
        color: colors.darkGray,
    },
    [' i']: {
        fontFamily: type.ITALIC_REGULAR_FONT,
    },
});

export const overideStyles = (styleOveride?: object) => ({
    ...styleOveride,
});



