import * as action from '../constants/data';

interface IAnalyticsEvent {
  action: string;
  description: string;
  page: string;
  label: string;
  category: string;
}

export function analyticsEvent(event: IAnalyticsEvent) {
  return {
    type: action.ANALYTICS_EVENT,
    event,
  };
}

export function setLocale(locale: string) {
  return {
    type: action.SET_LOCALE,
    locale,
  };
}

export function dataRequest() {
  return {
    type: action.DATA_REQUEST,
  };
}

export function dataReceived(data: { [key: string]: any; }) {
  return {
    type: action.DATA_RECEIVED,
    data: data,
  };
}

export function getData(route: string) {
  return {
    type: action.GET_DATA,
    route: route,
  };
}

export function loadJson(json: any) {
  return {
    type: action.LOAD_JSON,
    data: json,
  };
}

export function requestError(err: string) {
  return {
    type: action.REQUEST_ERROR,
    error: err,
  };
}

export function setCurrentEntry(route: string) {
  return {
    type: action.SET_CURRENT_ENTRY,
    route: route,
  };
}



