import { css } from 'glamor';
import * as utilStyles from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

const { mediaQueries, screenSizes, columns } = utilStyles;

export const container = (reverse, isBlog: boolean = false) => ({
  maxWidth: columns.desktopMaxWidth,
  margin: '0 auto',
  [`${mediaQueries.phablet}`]: {
    padding: '0 32px',
    flexDirection: reverse ? 'row-reverse' : 'row',
    flex: '1 0 auto',
    display: 'flex',
    alignItems: 'center',
  },
  [`${mediaQueries.phabletMax}`]: {
    flex: 'none',
  },
});

export const imageBlock = (reverse, isBlog: boolean = false) => ({
  flex: '0 0 100%',
  [`${mediaQueries.phablet}`]: {
    flex: `0 0 ${isBlog ? utilStyles.columnsByPercent(11) : utilStyles.columnsByPercent(9)}`,
  },
});

export const copyBlock = (reverse, isBlog: boolean = false, copy: any) => ({
  flex: '0 0 100%',
  marginTop: 30,
  textAlign: 'center',
  [`${mediaQueries.phablet}`]: {
    paddingLeft: reverse && !copy ? `30px` : null,
    textAlign: 'left',
    flex: `0 0 ${isBlog ? utilStyles.columnsByPercent(5) : utilStyles.columnsByPercent(7)}`,
    marginTop: 0,
    '&:last-child': {
      marginLeft: !reverse ? utilStyles.columnsByPercent(1) : 0,
      marginRight: reverse ? utilStyles.columnsByPercent(1) : 0,
    },
  },
  [`${mediaQueries.normal}`]: {
    paddingLeft: reverse && !copy ? `195px` : null,
  },
});

export const buttonContainer = (secondaryButton = false) => ({
  marginTop: 15,
  display: secondaryButton ? 'block' : 'inline-block',
  '& button': secondaryButton && {
    width: '100%',
    maxWidth: 280,
  },
  [`${mediaQueries.phablet}`]: {
    display: secondaryButton ? 'block' : 'inline-block',
    marginTop: 24,
    '& button': {
      width: secondaryButton ? '239px' : 'auto',
    },
  },
});

export const image = (width = '100%', height = 'auto', minHeight = 'none') => ({
  position: 'relative',
  width,
  height,
  minHeight: minHeight,
});

export const headline = (locale) => ({
  paddingBottom: 4, // remaining 16px comes from paragraph below it.
  [utilStyles.mediaQueries.phablet]: {
    ...type.headline2,
    wordBreak: locale === 'de-de' ? 'break-word' : 'normal',
  },
});

export const eyebrow = () => ({
  color: colors.regularCharcoal,
});

export const specs = () => ({
  fontWeight: 600,
  color: colors.black,
  display: 'inline-block',
  flexDirection: 'column',
  justifyContent: 'center',
  marginTop: 30,
  [utilStyles.mediaQueries.phabletMax]: {
    display: 'block',
    marginTop: 0,
  },
  '& span': {
    display: 'inline-block',
    borderBottom: `2px solid ${colors.black}`,
    verticalAlign: 'middle',
    fontFamily: 'AvenirNext-DemiBold',
    fontSize: '12px',
    lineHeight: '16px',
    letterSpacing: '0.2px',
    paddingBottom: '2px',
    margin: '0 24px',
    [utilStyles.mediaQueries.phabletMax]: {
      display: 'inline-block',
      paddingBottom: '.5em',
      margin: '24px',
    },
  },
});

export const deviceContainer = () => ({
  width: 'calc(100% - 64px)',
  margin: '0 auto',
  position: 'relative',
  [`${utilStyles.mediaQueries.phablet}`]: {
    width: '300px',
  },
});



