import * as React from 'react';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { findDOMNode } from 'react-dom';
import { Dispatch } from '@types/react-redux';
import { analyticsEvent} from '../../../../state/actionCreators/data';
import { hideCookie } from '../../../../state/actionCreators/appCreator';
import * as styles from './_styles';
import * as colors from 'utils/styles/colors';
import { whereAmI, getLocationHref } from 'utils/helpers';
import { padding } from 'utils/styles';
import { Footer } from '../../organisms/Footer';
import { Helmet } from 'react-helmet';
import { GlobalNav } from '../../organisms/GlobalNav';
import { SidePagination, ISidePaginationProps } from '../../molecules/SidePagination';
import { CookieNotice } from '../../molecules/CookieNotice';
import { SectionList } from '../../molecules/SectionList';
import { writeCookie } from 'utils/helpers';
import { languageGroup } from 'utils/constants';
import { PageHeading } from '../../organisms/PageHeading';

const { Div, A } = glamorous;

const mapStateToProps = (state) => ({
  data: state.data.current,
  navData: state.data.entries.navigation,
  footerData: state.data.entries.footer,
  cookieHidden: state.app.cookieHidden,
  isMobile: state.app.mobile,
  isLoaded: state.app.isLoaded,
});

const windowLocationHref = (typeof window !== 'undefined') && window.location.href;

export interface IPageTemplate {
  data: any;
  location: { [key: string]: any; };
  setCurrentEntry: any;
  hideCookie: any;
  navData: any;
  analyticsEvent: any;
  footerData: any;
  cookieHidden: boolean;
  isMobile: boolean;
  isLoaded: boolean;
  params?: any;
}

export interface IPageTemplateState {
  refsReady: boolean;
  scrolled: boolean;
  contentFinished: boolean;
  pageFinished: boolean;
}

export class PageTemplateClass extends React.Component<IPageTemplate, IPageTemplateState> {
  sections = new Array();
  state = {
    refsReady: false,
    contentFinished: false,
    pageFinished: false,
    scrolled: false,
  };
  frame;
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        refsReady: true,
      });
    }, 0);
    this.props.analyticsEvent({
      action: 'send',
      description: 'pageview',
      page: this.props.data.title_loc,
      label: `${whereAmI()}-pageview-${this.props.data.title_loc}`,
      category: `${whereAmI()}:pageview`,
    });
    this.frame = requestAnimationFrame(this.handleScroll.bind(this));
  }
  componentWillUpdate(nextProps) {
    if (nextProps.data !== this.props.data) {
      this.setState({refsReady: false}, () => {
        setTimeout(() => {
          this.setState({
            refsReady: true,
            scrolled: false,
          });
        }, 0);
      });
      this.sections = [];
    }
  }
  componentDidUpdate(prevProps) {
    if (prevProps.data !== this.props.data) {
      this.props.analyticsEvent({
        action: 'send',
        description: 'pageview',
        page: this.props.data.title_loc,
        label: `${whereAmI()}-pageview-${this.props.data.title_loc}`,
        category: `${whereAmI()}:pageview`,
      });
    }
  }
  componentWillUnmount() {
    cancelAnimationFrame(this.frame);
  }
  handleScroll() {
    const body = (window as any).document.body;
    const pageOffset = (window as any).document.documentElement.scrollTop;
    const contentHeight = findDOMNode(this.refs.pageContent).clientHeight;
    const {
      scrolled,
      pageFinished,
      contentFinished,
    } = this.state;
    const { data } = this.props;
    if (this.props.isLoaded && pageOffset > 0 && !scrolled) {
      this.setState({
        scrolled: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        page: data.postTitle,
        label: `${whereAmI()}-start-reading`,
        category: `${whereAmI()}:reading`,
      });
    } else if (this.props.isLoaded && (contentHeight - pageOffset) / body.clientHeight < 1 && !contentFinished) {
      this.setState({
        contentFinished: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        page: data.postTitle,
        label: `${whereAmI()}-content-end`,
        category: `${whereAmI()}:reading`,
      });
    } else if (this.props.isLoaded && (body.scrollHeight - pageOffset) === body.clientHeight && !pageFinished) {
      this.setState({
        pageFinished: true,
      });
      this.props.analyticsEvent({
        action: 'scroll',
        page: data.postTitle,
        label: `${whereAmI()}-page-end`,
        category: `${whereAmI()}:reading`,
      });
    }
    this.frame = requestAnimationFrame(this.handleScroll.bind(this));
  }

  renderNavigation() {
    const { data } = this.props;

    const tabItems = this.props.navData.filter((navItem) => {
      if ( navItem.type === 'link' || navItem.type === 'link-collection' && navItem.label === 'News') {
        return navItem;
      }
    });
    const subnavContent = this.props.navData.filter((navItem) => {
      if ( navItem.type === 'link-collection' && navItem.label !== 'News' ) {
        return navItem;
      }
    });
    const buttonContent = this.props.navData.filter((navItem) => {
      if ( navItem.type === 'special') {
        return navItem;
      }
    });

    const mobileMenuItems = {
      menuItems: tabItems,
      subMenuItems: subnavContent[0].links,
    };

    const subnavItems = subnavContent[0].links;
    const navButton = buttonContent[0];

    return (
      <GlobalNav
        pageColor={data.color}
        buttonTitle={navButton.label}
        buttonLink={navButton.link}
        hideCTA={data.id === 'home'}
        tabItems={tabItems}
        subnavItems={subnavItems}
        navItemColor={colors.black}
        scrollTransition={94}
        menuItems={mobileMenuItems}
      />
    );
  }

  @autobind
  closeCookieNotice() {
    this.props.hideCookie();
    writeCookie('snapchatpolicynotice', 'true');
  }

  @autobind
  handleSectionRef(node, index) {
    if (node) {
      this.sections[index] = node;
    }
  }

  renderHeading() {
    return (
      <PageHeading
        headline={ this.props.data.title || this.props.data.title_loc }
        category={ this.props.data.eyebrowLoc }
        date={ null }
        image={ this.props.data.brandAvatar && this.props.data.brandAvatar.url }
        imageAltText= { this.props.data.title_loc }
      />
    );
  }

  render() {
    const { data, footerData } = this.props;
    let items = [];

    if (this.state.refsReady && this.sections && data.sidenav) {
      const header = document.getElementsByClassName('Header')[0].clientHeight;
      let negativeTop = false;
      items = this.sections.map((section, index) => {
        const ref = this.sections[index];
        let yOffset = 0;
        if (data.sections[index].elements[0].formatting) {
          if (data.sections[index].elements[0].formatting === 'negative-top' || data.id === 'home') {
            negativeTop = true;
            yOffset += padding.xxlarge * 3;
          }
        }
        if ( negativeTop ) { yOffset -= header / 2; }
        if (data.id === 'home') { yOffset = -96; }
        const sectionsObj = {
          title: data.sections[index].title,
          yOffset: yOffset,
          ref: ref,
          index,
          color: (index === 0 || index === this.sections.length - 1) ? true : false,
        };
        return sectionsObj;
      });
    }

    const sidePagnitationData: ISidePaginationProps = {
      items,
      primaryColor: colors.black,
      secondaryColor: data.color === 'Yellow' ? colors.snapYellow : colors[`regular${data.color}`],
    };

    const footerContent = footerData.groups.map((group) => {
      return {
        headline: group.title,
        links: group.links.map((groupLink) => {
          return {name: groupLink.label, path: groupLink.link};
        }),
      };
    });

    const policyTermsContent = [
        {label: footerData.privacyLabel, url: footerData.privacyLink},
        {label: footerData.termsLabel, url: footerData.termsLink},
    ];

    const pageTitle = data.title_loc ? data.title_loc : '';

    return (
      <Div css={ styles.defaultStyle(data.color, data.id) }>
        <Helmet>
            <meta charSet="utf-8" />
            <title>Snapchat Ads{pageTitle && ` | ${pageTitle}`}</title>
            <meta name="title" content={data.meta.title} />
            <meta name="description" content={data.meta.description} />
            <meta name="keywords" content={data.meta.keywords} />
            <meta name="og:site_name" content="Snapchat Ads" />
            <meta name="og:title" content={data.meta.ogTitle} />
            <meta name="og:description" content={data.meta.ogDescription} />
            <meta name="og:type" content={data.meta.ogType} />
            <meta name="og:image" content={data.meta.image} />
            <meta name="og:url" content={`${getLocationHref(this.props.location.pathname)}`} />
            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:site" content="Snapchat Ads" />
            <meta name="twitter:url" content={`${getLocationHref(this.props.location.pathname)}`} />
            <meta name="twitter:title" content={data.meta.ogTitle} />
            <meta name="twitter:description" content={data.meta.ogDescription} />
            <meta name="twitter:image" content={data.meta.image} />
            { data.id === 'odg' &&
              <meta name="robots" content="noindex, nofollow" />
            }
        </Helmet>
        { this.state.refsReady && data.sidenav &&
          <Div className={'side-pagination'} css={ styles.sidePagination() }>
            <SidePagination {...sidePagnitationData} analyticsEvent={this.props.analyticsEvent}/>
          </Div>
        }
        <A css={styles.skipContent()} id="skip-to-content" href={`${this.props.location.pathname.replace(/\/+$/, '')}#content`}>skip to main content</A>
        <Div className="Header">
          { this.renderNavigation() }
        </Div>
        <Div id="content" role="main" className={'page-content'} css={styles.pageContent(data.id)} ref="pageContent">
          {(data.typeLabel === 'successStory') &&
            this.renderHeading()
          }
          <SectionList
            sections={this.props.data.sections}
            sectionRef={this.handleSectionRef}
            routeId={this.props.data.id}
            analyticsEvent={this.props.analyticsEvent}
            pageColor={data.color}
            isMobile={this.props.isMobile}
            pageType={this.props.data.typeLabel}
            params={this.props.params}
          />
          <Footer
            analyticsEvent={this.props.analyticsEvent}
            adManagerContent={this.props.footerData.adManagerContent}
            footerContent={footerContent}
            annotationContent={data.annotations}
            policyTermsContent={policyTermsContent}
            languageContent={languageGroup}
          />
        </Div>
        { !this.props.cookieHidden && <CookieNotice copy={footerData.cookiePolicy} onClick={this.closeCookieNotice}/> }
      </Div>
    );
  }
}

export const PageTemplate = connect(mapStateToProps, { analyticsEvent, hideCookie })(PageTemplateClass);



