import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries, screenSizes } = utils;

export const filterWrapper = () => ({
  display: 'flex',
  flexDirection: 'column',
  maxWidth: utils.columns.desktopMaxWidth,
  margin: '0 auto',
  [`${mediaQueries.normal}`]: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    padding: '0 64px',
  },
  [`${mediaQueries.normalMax}`]: {
    flexDirection: 'column',
    padding: '0 0px',
    '& .Select': {
      flex: '1 0 100%',
      marginBottom: '8px',
    },
  },
  [`${mediaQueries.phablet}`]: {
    padding: '0 64px',
  },
});

export const tabList = () => ({
  display: 'none',
  [`${mediaQueries.normal}`]: {
    display: 'inline-flex',
  },
});

export const tabSelectWrapper = () => ({
  display: 'initial',
  flex: '1 0 auto',
  [`${mediaQueries.normal}`]: {
    display: 'none',
  },
});

export const tab = () => ({
  '&::after': {
    width: 'calc(100% - 16px)',
    left: 8,
    right: 8,
  },
});



