import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div, Ul, Li } = glamorous;

import * as styles from './BulletedList-styles';

export interface IBulletedListProps {
  items: Array<string>;
  css?: any;
}

export const BulletedListPropTypes = {
  items: PropTypes.array.isRequired,
  css: PropTypes.object,
};

export class BulletedList extends React.Component<IBulletedListProps, void> {

  static propTypes = BulletedListPropTypes;

  render() {
    return (
      <Ul css={[styles.listStyle(), this.props.css]}>
        {this.props.items.map((item, i) => {
          return <Li key={i} css={styles.listItem(i + 1)}>{item}</Li>;
        })}
      </Ul>
    );
  }
}



