import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
import * as _ from 'lodash';
import { browserHistory } from 'react-router';
import { connect } from 'react-redux';
const { Div } = glamorous;

import { autobind } from 'core-decorators';

import * as styles from './StoriesGrid-styles';

import * as utils from '../../../../utils/styles';

import { SelectFilters } from '../../molecules/SelectFilters';
import { GridCardSet } from '../../atoms/GridCard/GridCardSet';
import { CardSet } from '../../atoms/Card/CardSet';
import { Select } from '../../atoms/Select';
import { BlogPageFilters } from '../BlogPageFilters';
import { CopyBlock } from '../../atoms/CopyBlock';
import { Modal } from '../../organisms/Modal';
import { Button } from '../../atoms/Button';
import { StatefulContainer, whereAmI } from 'utils/helpers';
import { IDeviceMobileLockupProps } from '../../molecules/DeviceMobileLockup';

import { modalIsOpen } from '../../../../state/actionCreators/appCreator';
import { store } from '../../../../state/createStore';

export interface IStoriesCard {
    type: string;
    subhead: string;
    date?: string;
    cta: string;
    title: string;
    logoImage?: string;
    logoImage2x?: string;
    featuredImage?: string;
    featuredImage2x?: string;
    image?: string;
    image2x?: string;
    color?: string;
    lightText?: boolean;
    tags: Array<string>;
    link?: string;
    modalData?: IDeviceMobileLockupProps;
    id?: string;
}

interface IStoriesDataProps {
    tabItems?: Array<{
        value: string;
        label: string;
    }>;
    tabSelectOptions?: {
        placeholder: string,
        options: Array<{
            value: string;
            label: string;
        }>;
    };
    selectOptions: Array<{
        placeholder: string;
        options: Array<{
            label: string;
            value: string;
        }>;
    }>;
    stories: Array<IStoriesCard>;
    hideFilters?: boolean;
    noResults?: string;
    loadMoreCta?: string;
    isGridCard?: boolean;
}

export interface IStoriesGridProps {
    data: IStoriesDataProps;
    color: string;
    analyticsEvent?: Function;
    isMobile?: boolean;
    store?: any; // For storybook testing only
    isThreeUp?: boolean;
    paginationNumerator?: number;
    params?: any;
    cards?: Array<{
      image: string;
      subhead: string;
      title: string;
      copy: string;
      link?: string;
      color?: string;
      cardCta?: string;
    }>;
}

export interface IStoriesGridState {
    isLoaded: boolean;
    selectedId: number;
    filters: Array<string>;
    filteredResults: any;
    paginatedResults: any;
    modalData: Array<IDeviceMobileLockupProps>;
    activeModalIndex: number;
    isUpdating: boolean;
    stories: Array<{
        subhead: string;
        title: string;
        cta: string;
        logoImage: string;
        featuredImage: string;
        color?: string;
        lightText?: boolean;
        tags: Array<string>;
        link?: string;
        id?: string;
    }>;
    isCleared: boolean;
    isTablet: boolean;
    viewableResults: number;
    wrapperMinHeight: number;
}

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  noResults: state.data.keys['noResults'],
  isMobile: state.app.mobile,
});

const isTablet = (typeof window !== 'undefined') && window.innerWidth < utils.screenSizes.normal && window.innerWidth > utils.screenSizes.phablet;

export class StoriesGridImpl extends React.Component<IStoriesGridProps, IStoriesGridState> {

    static propTypes = {
        selectOptions: PropTypes.arrayOf(PropTypes.shape({
            placeholder: PropTypes.string,
            options: PropTypes.arrayOf(PropTypes.shape({
                label: PropTypes.string.isRequired,
                value: PropTypes.string.isRequired,
            })),
            stories: PropTypes.arrayOf(PropTypes.shape({
                subhead: PropTypes.string,
                title: PropTypes.string,
                copy: PropTypes.string,
                image: PropTypes.string,
                tags: PropTypes.array,
                link: PropTypes.string,
            })),
        })),
    };

    static defaultProps = {
        paginationNumerator: 12, // News and Insights has a numerator of 17 and is set in the parser. Default is 12 for other grid instances.
    };

    state = {
        isLoaded: false,
        selectedId: 0,
        filters: ['all'],
        filteredResults: [],
        paginatedResults: [],
        viewableResults: this.props.paginationNumerator,
        modalData: [],
        activeModalIndex: 0,
        isUpdating: false,
        stories: this.parseStories(this.props.data.stories),
        isCleared: true,
        isTablet: isTablet,
        wrapperMinHeight: 1000,
    };

    detectScreenSizeCallback: any;
    cardWrapper: HTMLElement;

    // Filter on willMount will allow for server render of cards.
    componentWillMount() {
        this.filter();
    }

    componentDidMount() {
        // this.filter();
        this.detectScreenSizeCallback = _.throttle(this.detectScreenSize, 300);
        window.addEventListener('resize', this.detectScreenSizeCallback);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.detectScreenSizeCallback);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.data !== this.props.data) {
            this.setState(() => {
                return {
                    selectedId: 0,
                    filters: ['all'],
                    filteredResults: [],
                    paginatedResults: [],
                    viewableResults: nextProps.paginationNumerator,
                    modalData: [],
                    activeModalIndex: 0,
                    isUpdating: false,
                    stories: this.parseStories(nextProps.data.stories),
                    isCleared: true,
                    isLoaded: false,
                    wrapperMinHeight: this.cardWrapper ? this.cardWrapper.getBoundingClientRect().height : undefined,
                };
            }, () => {
                this.filter();
            });
        }
    }

    @autobind
    detectScreenSize() {
        if (this.state.isTablet !== window.innerWidth < utils.screenSizes.normal && window.innerWidth > utils.screenSizes.phablet) {
            this.setState({ isTablet: window.innerWidth < utils.screenSizes.normal && window.innerWidth > utils.screenSizes.phablet });
        }
    }

    parseStories(stories) {
        stories.map((story) => {
            story.tags.push('all');
            return story;
        });
        return stories;
    }

    filter() {
        const results = _.filter(this.state.stories, (story: any) => {

            const isMatch = this.state.filters.map((filter) => {
                return story.tags.indexOf(filter);
            });
            return isMatch.indexOf(-1) > -1 ? false : true;
        });

        const modalData = [];
        results.forEach((item, index) => {
            if (item.modalData) {
               modalData.push(item.modalData);
               item.modalId = modalData.length - 1;
            }
        });

        const paginatedResults = results.slice(0, this.state.viewableResults);

        this.setState({
            filteredResults: results,
            paginatedResults: paginatedResults,
            modalData: modalData,
            activeModalIndex: 0,
            isLoaded: true,
            wrapperMinHeight: 0,
        }, () => {
            if (this.props.params && this.props.params.spotName) {
                let found = false;
                let modalIndex = -1;

                for (let i = 0; i < this.state.filteredResults.length && !found; i++) {
                    if (this.state.filteredResults[i].id === this.props.params.spotName) {
                        found = true;
                        modalIndex = i;
                    }
                }

                if (modalIndex > -1) {
                    this.openModal(modalIndex, this.state.filteredResults[modalIndex].type);
                }
            }
        });

        if (typeof window !== 'undefined') {
            if ((window.location.href.indexOf('draftpreview') > -1) && !(window.location.href.indexOf('draftpreviewblog') > -1)) {
                const storeRef = this.props.store ? this.props.store : store;
                if (storeRef.getState().data.current.entryType && (storeRef.getState().data.current.entryType === 'creativeSpotlightDevice' || storeRef.getState().data.current.entryType === 'creativeSpotlightVideo')) {
                    this.openModal(0, this.state.filteredResults[0].type);
                }
            }
        }
    }

    setFilter(prevOption, nextOption) {
        const { analyticsEvent } = this.props;
        const filters = [...this.state.filters];
        if (prevOption !== nextOption) {
            const index = this.state.filters.indexOf(prevOption);
            if (index > -1) { filters.splice(index, 1); }
            if (nextOption) { filters.push(nextOption); }
            analyticsEvent({
                action: 'click',
                page: whereAmI(),
                label: `filter-${nextOption ? nextOption : 'clear'}`,
                category: `${whereAmI()}:button-filter`,
            });
            this.setState({
                isUpdating: true,
                wrapperMinHeight: this.cardWrapper ? this.cardWrapper.getBoundingClientRect().height : undefined,
            }, () => {
                setTimeout(() => {
                    this.setState((state) => {
                        return {
                            ...state,
                            filters: filters,
                            filteredResults: [],
                            isUpdating: false,
                            isLoaded: false,
                            viewableResults: this.props.paginationNumerator,
                        };
                    }, () => {
                        this.filter();
                    });
                }, 500);
            });
        }
    }

    @autobind
    loadMore() {
        this.setState({viewableResults: this.state.viewableResults + (this.props.paginationNumerator - (this.props.data.isGridCard ? 2 : 0))}, () => {
            this.filter();
        });
    }

    @autobind
    closeModal() {
        // For local testing
        const storeRef = this.props.store ? this.props.store : store;

        storeRef.dispatch(modalIsOpen(false));
        document.body.style.overflow = 'scroll';
        if (browserHistory.getCurrentLocation().pathname.indexOf(`/${this.state.filteredResults[this.state.activeModalIndex].id}`) !== -1) {
            const newPathName = browserHistory.getCurrentLocation().pathname.replace(`/${this.state.filteredResults[this.state.activeModalIndex].id}`, '');
            const newUrl = `${newPathName}`;
            browserHistory.push(newUrl);
        }
        if (this.props.analyticsEvent) {
          this.props.analyticsEvent({
            action: 'click',
            label: `${this.state.filteredResults[this.state.activeModalIndex].id}-close-modal`,
            category: `${whereAmI()}-close-modal`,
          });
        }
    }

    @autobind
    openModal(index, type) {
        // For local testing
        const storeRef = this.props.store ? this.props.store : store;
        this.setState({activeModalIndex: index}, () => {
            storeRef.dispatch(modalIsOpen(true));
        });
        document.body.style.overflow = 'hidden';

        if (browserHistory.getCurrentLocation().pathname.indexOf('/creative-spotlight') !== -1 || browserHistory.getCurrentLocation().pathname.indexOf('/inspiration') !== -1) {
            if (browserHistory.getCurrentLocation().pathname.indexOf(`/${this.props.params.spotName}`) === -1) {
                const newPathName = `${browserHistory.getCurrentLocation().pathname}/${this.state.filteredResults[index].id}`;
                const newUrl = `${newPathName}`;
                browserHistory.push(newUrl);
            }
        }
        if (this.props.analyticsEvent) {
          this.props.analyticsEvent({
            action: 'click',
            label: `${this.state.filteredResults[index].id}-open-${type}-modal`,
            category: `${whereAmI()}-open-${type}-modal`,
          });
        }
    }

    makeTabs = (id) => {
        return this.props.data.tabItems.map((tab, index) => {
            return {
                ...tab,
                selected: id === index,
                onSelect: () => {
                    const tabData = this.props.data.tabItems[this.state.selectedId].value;
                    this.setState((state) => {
                        return {
                            ...state,
                            selectedId: index,
                            isCleared: index === 0 ? true : false,
                        };
                    }, () => {
                        this.setFilter(tabData, this.props.data.tabItems[index].value);
                    });
                },
            };
        });
    }

    makeMobileTabSelect() {
        const { tabSelectOptions } = this.props.data;
        return (
            <Select
                placeholder={tabSelectOptions.placeholder}
                options={tabSelectOptions.options}
                value={this.state.isCleared ? null : tabSelectOptions.options[this.state.selectedId - 1].value}
                onChange={(selectOption) => {
                    const currentValue = this.state.selectedId === 0 ? 'all' : tabSelectOptions.options[this.state.selectedId - 1].value;
                    const nextIndex = selectOption ? tabSelectOptions.options.indexOf(selectOption) : 0;
                    this.setFilter(currentValue, selectOption ? selectOption.value : 'all');
                    this.setState((state) => {
                        return {
                            ...state,
                            selectedId: selectOption ? nextIndex + 1 : nextIndex,
                            isCleared: selectOption ? false : true,
                        };
                    });
                }}
            />
        );
    }
    @autobind
    handleChange(lastIndex, newIndex) {
        const storeRef = this.props.store ? this.props.store : store;
        if (browserHistory.getCurrentLocation().pathname.indexOf(`/${this.state.filteredResults[lastIndex].id}`) !== -1) {
            this.setState({activeModalIndex: newIndex});
            const newPathName = browserHistory.getCurrentLocation().pathname.replace(`/${this.state.filteredResults[lastIndex].id}`, `/${this.state.filteredResults[newIndex].id}`);
            const newUrl = `${newPathName}`;
            browserHistory.push(newUrl);
        }
    }

    @autobind
    setCardWrapperRef(node) {
        if (node) {
            this.cardWrapper = node;
        }
    }

    render() {
        const { filteredResults, modalData, activeModalIndex, paginatedResults, isLoaded, wrapperMinHeight } = this.state;

        // Used to alphabetize cards by title on Partners page
        if (this.props.data.isGridCard === false) {
          paginatedResults.sort((a, b) => {
            return a.title.localeCompare(b.title);
          });
        }
        return (
            <div>
                <Div css={styles.filtersWrapper()}>
                    { !this.props.data.tabItems && !this.props.data.hideFilters && <SelectFilters selects={this.props.data.selectOptions} centered onChange={this.setFilter.bind(this)} /> }
                    { this.props.data.tabItems && !this.props.data.hideFilters && <BlogPageFilters tabs={this.makeTabs(this.state.selectedId)} selects={this.props.data.selectOptions} tabSelect={this.makeMobileTabSelect()} onChange={this.setFilter.bind(this)} />}
                </Div>
                <Div
                    innerRef={this.setCardWrapperRef}
                    css={styles.cardWrapper()}
                    style={{ minHeight: wrapperMinHeight }}
                >
                    {this.props.data.isGridCard && filteredResults.length > 0 &&
                        <GridCardSet
                            cards={paginatedResults}
                            animate
                            isUpdating={this.state.isUpdating}
                            color={this.props.color}
                            openModal={this.openModal}
                            analyticsEvent={ this.props.analyticsEvent }
                            isThreeUp={this.props.isThreeUp}
                        />
                    }
                    {!this.props.data.isGridCard &&
                        <CardSet
                            cards={paginatedResults}
                            //  titleStyle={articleCardTitle}
                            animate
                            isUpdating={this.state.isUpdating}
                            color={this.props.color}
                        />
                    }
                    {filteredResults.length > 0 && this.state.viewableResults < filteredResults.length &&
                        <div style={{ textAlign: 'center', paddingTop: 40, paddingBottom: this.props.isMobile ? 48 : 0 }}>
                            <Button
                                color="black"
                                outline
                                isSmall
                                onClick={this.loadMore}>
                                {this.props.data.loadMoreCta}
                            </Button>
                        </div>
                    }
                    {filteredResults.length === 0 && isLoaded &&
                        <CopyBlock css={styles.noResults()}>
                            { this.props.data.noResults }
                        </CopyBlock>
                    }
                </Div>
                {modalData.length > 0 &&
                    <Modal
                        tabs={modalData}
                        closeModal={ this.closeModal }
                        activeTab={activeModalIndex}
                        activeChange={this.handleChange}
                        analyticsEvent={ this.props.analyticsEvent }
                    />
                }
            </div>
        );
    }
}

export const StoriesGrid = connect(mapStateToProps)(StoriesGridImpl);



