import * as React from 'react';
import { connect } from 'react-redux';
import { throttle } from 'lodash';
import { autobind } from 'core-decorators';

import { PropTypes } from 'prop-types';
import * as glamorous from 'glamorous';
import { DeviceMobile, IDeviceMobileProps } from '../DeviceMobile';
import { Carousel } from '../../organisms/Carousel';
import * as styles from './DevicesCollage-styles';
import * as colors from 'utils/styles/colors';
import { ChillahTooltip } from '../../molecules/ChillahTooltip';
import * as utils from 'utils/styles';

const { Div } = glamorous;

export interface IDevicesCollageProps {
  devices: Array<IDeviceMobileProps>;
  tooltip?: any;
  tooltipLocation?: string;
  isMobile?: any;
  color?: any;
  animationTiming?: number;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  color: state.data.current.color || ownProps.color,
});

export interface IDeviceCollageState {
  clientWidth: number;
  animating: boolean;
  selectedId: number;
}

export class DeviceCollageClass extends React.Component<IDevicesCollageProps, IDeviceCollageState> {

  static defaultProps = {
    animationTiming: 200,
  };

  state = {
    clientWidth: (typeof document !== 'undefined') ? document.body.clientWidth : 1024,
    animating: false,
    selectedId: 0,
  };

  static propTypes = {
    devices: PropTypes.arrayOf(PropTypes.shape({
      imageUrl: PropTypes.string,
      title: PropTypes.string,
      videoUrl: PropTypes.string,
      isMobile: PropTypes.boolean,
    })),
  };

  setClientWidthCallback: any;

  componentDidMount() {
    this.setClientWidthCallback = throttle(this.setClientWidth, 50);
    window.addEventListener('resize', this.setClientWidthCallback);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.setClientWidthCallback);
  }

  @autobind
  setClientWidth() {
    this.setState({clientWidth: document.body.clientWidth});
  }

  @autobind
  handleCarouselChange(index: number, clientWidth?: number): void {
    const setCarousel = () => {
      this.setState({
        selectedId: index,
        animating: false,
        clientWidth: clientWidth ? clientWidth : this.state.clientWidth,
      });
    };

    if (this.state.selectedId !== index) {
      this.setState({
        animating: true,
      });
      setTimeout(() => {
        setCarousel();
      }, this.props.animationTiming);
    } else {
      setCarousel();
    }
  }

  renderDevices(inCarousel) {
    const { devices } = this.props;
    const numberOfDevices = this.props.devices.length;
    const twoDevices = this.props.devices.length === 2;
    return devices.map((device, index) => {
      return (
        <Div css={styles.carouselContainer(this.state.clientWidth, twoDevices)} key={index}>
          {this.props.isMobile ? (
            <DeviceMobile {...device} key={`${index}`} inCarousel={inCarousel} isFocused={this.state.selectedId === index} />
          ) : (
            <DeviceMobile {...device} key={`${index}`} inCarousel={inCarousel} />
          )}
        </Div>
      );
    });
  }
  render() {
    const numberOfDevices = this.props.devices.length;
    const twoDevices = this.props.devices.length === 2;
    const tipColor = this.props.color === 'Yellow' ? 'black' : this.props.color.toLowerCase();
    let chillahLocation;
    if (this.props.tooltipLocation && this.props.tooltipLocation === 'topRight') {
      chillahLocation = `${utils.columns.desktopColumnWidth * 14.8}%`;
    } else if (this.props.tooltipLocation === 'topLeft') {
      chillahLocation = `${utils.columns.desktopColumnWidth * 1}%`;
    } else if (this.props.tooltipLocation === 'insetTopLeft') {
      chillahLocation = `${utils.columns.desktopColumnWidth * 5}%`;
    }
    const baseWidth = Math.min((this.state.clientWidth - 160), 311);
    return (
      <Div css={styles.devicesCollageStyle(twoDevices)}>
        { this.props.isMobile &&
          <Carousel
            leftOffset={Math.max(((this.state.clientWidth - baseWidth) * 0.5), 80) }
            padding={36}
            snapCenter
            dotNav
            isMobile
            color={colors[`medium${this.props.color}`]}
            onChange={this.handleCarouselChange} >
              { this.renderDevices(true) }
          </Carousel>
        }
        { !this.props.isMobile && this.renderDevices(false) }
        {this.props.tooltip && this.props.tooltip.image && !this.props.isMobile &&
          <ChillahTooltip
            animate
            iconUrl={this.props.tooltip.image}
            imageAlt={this.props.tooltip.imageAlt}
            left={chillahLocation}
            top={'-20px'}
            width={ this.props.tooltip.width }
            color={tipColor}>
            {this.props.tooltip.tip}
          </ChillahTooltip>
        }
      </Div>
    );
  }
}

export const DevicesCollage = connect(mapStateToProps)(DeviceCollageClass);



