import { css } from 'glamor';
import * as utilStyles from 'utils/styles';
import * as colors from 'utils/styles/colors';

const { mediaQueries, borderRadius } = utilStyles;
const play = require('./assets/play.svg');
const pause = require('./assets/pause.svg');

export const fullContainer = (isVertical) => ({
  [`${mediaQueries.phablet}`]: {
    maxWidth: isVertical ? `${utilStyles.columns.desktopMaxWidth / 2}` : `${utilStyles.columns.desktopMaxWidth}`,
    margin: '0 auto',
  },
});

export const columnContainer = (fullWidth) => ({
  [`${mediaQueries.phablet}`]: {
    maxWidth: fullWidth ? '100%' : `${utilStyles.columns.desktopColumnWidth * 11}%`,
    margin: '0 auto',
  },
});

export const videoWrapper = (isVertical, height, width) => ({
  width: '100%',
  paddingTop: isVertical ? '178.25%' : height !== '100%' && width !== '100%' ? `${(height / width) * 100}%` : '56.25%',
  position: 'relative',
});

export const wrapper = (rounded, playing, controls, noShadow) => ({
  position: 'relative',
  overflow: 'hidden',
  borderRadius: rounded ? borderRadius.mobileContainer : 0,
  WebkitBackfaceVisibility: 'hidden',
  WebkitTransform: 'translateZ(0)',
  boxShadow: !noShadow ? `0 8px 32px 0 rgba(0,0,0,0.08)` : 'none',
  [`${mediaQueries.phablet}`]: {
    borderRadius: rounded ? borderRadius.tabletContainer : 0,
  },
  [`${mediaQueries.normal}`]: {
    borderRadius: rounded ? borderRadius.desktopContainer : 0,
  },
  '&:after': {
    content: controls ? `''` : null,
    width: 100,
    height: 100,
    backgroundImage: !playing ? `url(${play})` : '',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'contain',
    zIndex: 3,
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translateY(-50%)translateX(-50%)',
  },
  '&:hover,&:focus': {
    cursor: 'pointer',
    '&:after': {
        content: controls ? `''` : null,
        width: 100,
        height: 100,
        backgroundImage: playing ? `url(${pause})` : `url(${play})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'contain',
        zIndex: 3,
        position: 'absolute',
        left: '50%',
        top: '50%',
        transform: 'translateY(-50%)translateX(-50%)',
        pointerEvents: 'none',
    },
  },
  '&:focus': {
    outline: 'none',
    boxShadow: '0 0 3pt 2pt #FFFE00',
    '&:after': {
      visibility: playing ? 'hidden' : 'visible',
    },
  },
});

export const child = () => ({
  position: 'absolute',
  left: 0,
  top: 0,
});

export const poster = () => ({
  zIndex: 1,
  width: '100%',
  height: '100%',
  backgroundPosition: 'center',
  backgroundSize: 'cover',
});



