import * as React from 'react';
import glamorous from 'glamorous';
const { Div } = glamorous;
import { autobind } from 'core-decorators';
import * as styles from './DynamicSelect-styles';
import { Select } from '../../../atoms/Select';

export interface ISelectProps {
  value: string | number;
  label: string;
}

export interface IDynamicSelectProps {
  options: Array<ISelectProps>;
  onChange?: Function;
  placeholder: string;
  css?: Object;
  pageId?: string;
  instanceId?: string;
}

export interface IDynamicSelectState {
  value: any;
}

const getLongestString = (placeholder, options) => {
  const longestString =
    options
      .map((option) => option.label)
      .concat(placeholder)
      .reduce((a, b) => {
        return a.length > b.length ? a : b;
      });
  return longestString;
};

export class DynamicSelect extends React.Component<IDynamicSelectProps, IDynamicSelectState> {

  state = {
    value: null,
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.pageId !== this.props.pageId) {
      this.setState({value: null});
    }
  }

  @autobind
  onChange(selectedOption) {
    if (this.props.onChange) {
      this.props.onChange(this.state.value, selectedOption ? selectedOption.value : null);
    }
    this.setState({value: selectedOption ? selectedOption.value : null});
  }

  render() {
    return (
      <Select
        css={[
          // Longest string is used as psuedo element to give the Select box a default width.
          styles.select(
            getLongestString(this.props.placeholder, this.props.options),
            this.state.value === null,
          ),
          this.props.css,
        ]}
        placeholder={this.props.placeholder}
        options={this.props.options}
        value={this.state.value}
        onChange={this.onChange}
        instanceId={this.props.instanceId}
      />
    );
  }
}



