import * as React from 'react';
import * as glamorous from 'glamorous';
import * as colors from 'utils/styles/colors';

import { Button } from '../../atoms/Button';
import { Markdown } from '../../atoms/Markdown';

import { ConfettiMetricsBlock } from '../../molecules/ConfettiMetricsBlock';

import * as styles from './CopyWithStat-styles';

const { Div, A } = glamorous;

export interface ICopyWithStatProps {
  color?: string;
  headline?: string;
  paragraph?: string;
  primaryButtonContent?: IButtonContentProps; // remove
  secondaryButtonContent?: IButtonContentProps; // remove
  confettiAnimation?: boolean;
  stats?: Array<IStatMetric>;
  buttonContent?: Array<IButtonContentProps>;
}

export interface IStatMetric {
  headline: string;
  subhead: string;
}

export interface IButtonContentProps {
  cta?: string;
  link?: string;
  color?: string;
  filled?: boolean;
  outline?: boolean;
  buttonStyle?: string;
  isSmall?: boolean;
  onClick?: Function;
}

export class CopyWithStat extends React.Component<ICopyWithStatProps, void> {
  render() {
    return (
      <Div className="copy-stat-container" css={styles.container()} >
        <Div className="copy-container" css={styles.copyContainer()} >
          {
            this.props.headline &&
            <Div className="headline-wrapper" css={styles.headlineWrapper()} >
              <Markdown
                copy={this.props.headline}
                styleOverrides={styles.headlineMarkdown()}
              />
            </Div>
          }
          {
            this.props.paragraph &&
            <Div className="paragraph-wrapper" css={styles.paragraphWrapper()} >
              <Markdown
                copy={this.props.paragraph}
              />
            </Div>
          }
          {this.props.buttonContent && this.props.buttonContent.length ? (
            <Div className="button-wrapper" css={styles.buttonWrapper()} >
              {this.props.buttonContent.map((button, i) => {
                if (button.buttonStyle === 'primaryButton') {
                  if (i > 0) {
                    console.log('dam - ' + i + ' : ' + this.props.buttonContent[i - 1].buttonStyle);
                  }
                  return button && <Button css={(i === 1 && this.props.buttonContent[i - 1].buttonStyle === 'primaryButton') ? { display: 'block', marginTop: '32px' } : {}} color={button.color} isSmall={button.isSmall === false ? false : true} filled={button.filled} outline={button.outline} onClick={button.onClick} link={button.link} key={i}>{button.cta}</Button>;
                } else if (button.buttonStyle === 'secondaryButton') {
                  return (
                    <Div css={styles.specLinkHolder()} key={i}>
                      <A css={styles.specsLink(this.props.color ? this.props.color : colors.black)} href={button.link} target="_blank">
                        <span>{ button.cta }</span>
                      </A>
                    </Div>
                  );
                }
              })}
            </Div>
          ) : (
            null
          )}
        </Div>
        <Div className="confetti-conatiner" css={styles.confettiStatContainer()} >
          <ConfettiMetricsBlock
            metrics={this.props.stats}
            confettiAnimation={this.props.confettiAnimation}
          />
        </Div>
      </Div>
    );
  }
}



