import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import * as styles from './Tooltip-styles';

const { Component } = React;
const { Div, Img } = glamorous;
const { findDOMNode } = ReactDOM;
const {
  tooltipStyle,
} = styles;

export interface ITooltipProps {
  corner?: string;
  direction?: string;
  color?: string;
  css?: any;
  innerRef?: Function;
}

export class Tooltip extends Component<ITooltipProps, void> {

  static propTypes = {
    corner: PropTypes.string,
    direction: PropTypes.string,
    color: PropTypes.string,
    css: PropTypes.any,
  };

  static defaultProps = {
    corner: 'right',
    direction: 'bottom',
    color: 'purple',
  };

  render() {
    const { corner, direction, color, css } = this.props;
    return (
      <Div
        innerRef={this.props.innerRef}
        css={[tooltipStyle(corner, direction, color), css]}
      >
        <Div>
          {this.props.children}
        </Div>
      </Div>
    );
  }
}



