import * as React from 'react';
import * as glamorous from 'glamorous';
import * as glamor from 'glamor';
import 'what-input';

const { Ul } = glamorous;

import * as styles from './TabArray-styles';

export type TabArrayProps = {
  css?: { [key: string]: any };
};

export class TabArray extends React.Component<TabArrayProps, void> {
  render() {
    return (
      <Ul
        css={[styles.tabListStyles(), this.props.css]}
        role="tablist"
      >
        {this.props.children}
      </Ul>
    );
  }
}



