import * as React from 'react';
import * as PropTypes from 'prop-types';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
const { A } = glamorous;

import * as styles from './IconButton-styles';

export interface IIconButtonProps {
  iconUrl: string;
  css?: object;
  onClick?: Function;
  onHover?: Function;
  downloadUrl?: string;
}

export const IconButtonPropTypes = {
  iconUrl: PropTypes.string.isRequired,
  css: PropTypes.object,
  onClick: PropTypes.func,
  onHover: PropTypes.func,
  downloadUrl: PropTypes.string,
};

export class IconButton extends React.Component<IIconButtonProps, void> {

  static propTypes = IconButtonPropTypes;

  @autobind
  onClick(evt) {
    if (this.props.onClick) {
      this.props.onClick(evt);
    }
  }

  @autobind
  handleMouseEnter() {
    if (this.props.onHover) {
      this.props.onHover(true);
    }
  }

  @autobind
  handleMouseLeave() {
    if (this.props.onHover) {
      this.props.onHover(false);
    }
  }

  render() {
    return (
      <A
        css={[styles.buttonStyles(), this.props.css]}
        onClick={this.onClick}
        href={this.props.downloadUrl}
        target="_blank"
        download={Boolean(this.props.downloadUrl)}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
      >
        <img src={this.props.iconUrl} />
      </A>
    );
  }
}



