import * as React from 'react';
import * as glamorous from 'glamorous';
import { throttle } from 'lodash';
import { autobind } from 'core-decorators';

import { MetricBlock } from '../../molecules/MetricBlock';

import * as styles from './ConfettiMetricsBlock-styles';

const { Div, Img } = glamorous;

export interface IConfettiMetricsBlockProps {
  metrics: Array<IStatMetric>;
  confettiAnimation?: boolean;
}

export interface IConfettiMetricsBlockState {
  confettiClassName: string;
}

export interface IStatMetric {
  headline: string;
  subhead: string;
}

export class ConfettiMetricsBlock extends React.Component<IConfettiMetricsBlockProps, IConfettiMetricsBlockState> {

  confettiElement;
  handleScroll;

  state = {
    confettiClassName: '',
  };

  componentDidMount() {
    this.handleScroll = throttle(this.elementView, 200);
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  @autobind
  elementView() {
    const browserHeight = window.innerHeight;
    const browserCenter = browserHeight / 2;
    const topThreshold = browserCenter - 500;
    const bottomThreshold = browserCenter + 500;

    const confettiAttr = this.confettiElement.getBoundingClientRect();
    const confettiCenter = confettiAttr.top + (confettiAttr.height / 2);

    if (confettiCenter > topThreshold && confettiCenter < bottomThreshold && this.state.confettiClassName !== 'animate' ) {
      this.setState({
        confettiClassName: 'animate',
      });
    }
    if (confettiAttr.bottom < 0 || confettiAttr.top > browserHeight && this.state.confettiClassName !== '' ) {
      this.setState({
        confettiClassName: '',
      });
    }
  }

  countProps(prop) {
    if (prop.length === 4) {
      return ({
        container: styles.container,
        metricWrapper: styles.metricWrapper,
      });
    } else if (prop.length === 3) {
      return ({
        container: styles.container3Up,
        metricWrapper: styles.metricWrapper3Up,
      });
    } else if (prop.length === 2) {
      return ({
        container: styles.container2Up,
        metricWrapper: styles.metricWrapper2Up,
      });
    } else if (prop.length === 1) {
      return ({
        container: styles.container1Up,
        metricWrapper: styles.metricWrapper1Up,
      });
    }
  }

  computedStyles = this.countProps(this.props.metrics);

  items = this.props.metrics.map((metric, i) => (
    <Div
      key={i}
      css={this.computedStyles.metricWrapper()}
    >
      <MetricBlock
        numbMetricStat={{
          headline: metric.headline,
          subhead: metric.subhead,
        }}
      />
    </Div>
  ));

  render() {
    return (
      <Div css={styles.anniWrapper()} >
        <Div
          css={this.computedStyles.container()}
          className="inner-container"
          innerRef={(confettiBlock) => {this.confettiElement = confettiBlock; } }
        >
          {this.items}
        </Div>
        {
          this.props.confettiAnimation &&
          <Div css={styles.confettiContainer()} className={`${this.state.confettiClassName}`}>
            <Div css={styles.confettiQuadrent()}  >
              <Img className={`confetti-item eye-purple`} src={require(`./confetti-eye-purple.svg`)} />
              <Img className={`confetti-item top-heart`} src={require(`./confetti-heart.svg`)} />
              <Img className={`confetti-item star`} src={require(`./confetti-star.svg`)} />
            </Div>
            <Div css={styles.confettiQuadrent()} >
              <Img className={`confetti-item bubble`} src={require(`./confetti-bubble.svg`)} />
              <Img className={`confetti-item replay`} src={require(`./confetti-replay.svg`)} />
              <Img className={`confetti-item square`} src={require(`./confetti-square.svg`)} />
            </Div>
            <Div css={styles.confettiQuadrent()} >
              <Img className={`confetti-item eye-orange`} src={require(`./confetti-eye-orange.svg`)} />
              <Img className={`confetti-item shared-message`} src={require(`./confetti-shared-message.svg`)} />
            </Div>
            <Div css={styles.confettiQuadrent()} >
              <Img className={`confetti-item send`} src={require(`./confetti-send.svg`)} />
              <Img className={`confetti-item heart`} src={require(`./confetti-heart.svg`)} />
            </Div>
          </Div>
        }
      </Div>
    );
  }
}



