export const container = () => ({
  display: 'block',
  fontSize: '0',
  lineHeight: '0',
});

export const defaultStyles = () => ({
  width: `4px`,
  height: `4px`,
  display: 'inline-block',
  marginRight: `8px`,
  verticalAlign: 'middle',
  backgroundColor: '#000',
  borderRadius: `${ 4 * 0.5 }px`,
  ':last-child': {
    marginRight: '0',
  },
});
export const active = (color) => ({
  width: `8px`,
  height: `8px`,
  borderRadius: `${ 8 * 0.5 }px`,
  backgroundColor: 'none',
  border: `2px solid ${color ? color : '#ff0000'}`,
});



