import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as glamor from 'glamor';

const opacityFadeIn = glamor.keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
});

const opacityFadeOut = glamor.keyframes({
  '0%': {
    opacity: 1,
  },
  '100%': {
    opacity: 0,
  },
});

export const navWrapper = (active, isVisible) => ({
    backgroundColor: colors.white,
    // position: active ? 'fixed' : 'relative',
    position: 'fixed',
    // top: 98,
    top: 0,
    paddingTop: 98,
    bottom: 0,
    width: '100%',
    textAlign: 'center',
    // opacity: active ? 1 : 0,
    // transition: 'opacity .2s ease-out',
    zIndex: 999,
    flexDirection: 'column',
    display: isVisible ? 'flex' : 'none',
    animation: `${opacityFadeOut} 0.2s ease-in-out forwards`,
    '&.active': {
        animation: `${opacityFadeIn} 0.2s ease-in-out forwards`,
    },
});

export const innerNavContainer = () => ({
    overflow: 'scroll',
});

export const navContainer = () => ({
    fontSize: 32,
    flex: 1,
    alignContent: 'center',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '48px 0 48px 0',
    '& li': {
        paddingBottom: 32,
        '&:last-of-type': {
            paddingBottom: 0,
        },
    },
});

export const navItem = (id, selectedId, color) => ({
    color: id === selectedId ? colors[`${'regular' + color}`] : 'initial',
});

export const subNavContainer = () => ({
    backgroundColor: colors.backgroundGray,
    fontSize: 16,
    padding: '48px 0 48px 0',
    '& li': {
        paddingBottom: 24,
        '&:last-of-type': {
            paddingBottom: 0,
        },
    },
});



