import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

export const container = () => ({
  borderRadius: utils.borderRadius.mobileContainer,
  overflow: `hidden`,
  margin: '0 auto',
  position: `relative`,
  width: `100%`,
  zIndex: `5`,
  [`${utils.mediaQueries.phablet}`]: {
    borderRadius: utils.borderRadius.tabletContainer,
    display: `flex`,
    flexFlow: `row wrap`,
    justifyContent: `space-between`,
    alignContent: `space-between`,
    alignItems: `stretch`,
    maxWidth: 777,
  },
  [`${utils.mediaQueries.normal}`]: {
    borderRadius: utils.borderRadius.desktopContainer,
  },
});

export const anniWrapper = () => ({
  overflow: `visible`,
  margin: '0 auto',
  position: `relative`,
  display: `flex`,
  justifyContent: `center`,
  alignItems: `center`,
  [`${utils.mediaQueries.phablet}`]: {
    maxWidth: 777,
  },
});

export const metricWrapper = () => ({
  backgroundColor: '#fff',
  margin: `4px 0`,
  flex: 1,
  [':first-child']: {
    marginTop: 0,
  },
  [':last-child']: {
    marginBottom: 0,
  },
  [`${utils.mediaQueries.phablet}`]: {
    flexBasis: 'calc(50% - 8px)',
    [':first-child']: {
      margin: `0 4px 4px 0`,
    },
    [':last-child']: {
      margin: `4px 0 0 4px `,
    },
    [':nth-child(2)']: {
      margin: `0 0 4px 4px `,
    },
    [':nth-child(3)']: {
      margin: `4px 4px 0 0 `,
    },
  },
});

