export const container1Up = () => ({
  borderRadius: 20,
  overflow: `hidden`,
  margin: '0 auto',
  maxWidth: `635px`,
});

export const metricWrapper1Up = () => ({
  backgroundColor: '#fff',
});

const rotate = {
  upLeft: `rotate(-45deg)`,
  upRight: `rotate(45deg)`,
  bottomLeft: `rotate(-135deg)`,
  bottomRight: `rotate(135deg)`,
};

export const confettiContainer = () => ({
  width: `110%`,
  height: `105%`,
  position: `absolute`,
  display: `flex`,
  flexFlow: `row wrap`,
  [`${utils.mediaQueries.phablet}`]: {
    height: `115%`,
    width: `115%`,
  },
  ['&.animate']: {
    [' .confetti-item']: {
      opacity: `1`,
    },
    [' .star']: {
      transform: `translate(-98%, -848%) ${rotate.upLeft}`,
    },
    [' .top-heart']: {
      transform: `translate(-384%, -750%) ${rotate.upLeft}`,
    },
    [' .eye-purple']: {
      transform: `translate(-234%, -562%) ${rotate.upLeft}`,
    },
    [' .bubble']: {
      transform: `translate(-20%, -754%) ${rotate.upRight}`,
    },
    [' .replay']: {
      transform: `translate(359%, -803%) ${rotate.upRight}`,
    },
    [' .square']: {
      transform: `translate(327%, -551%) ${rotate.upRight}`,
    },
    [' .eye-orange']: {
      transform: `translate(-293%, 679%) ${rotate.bottomLeft}`,
    },
    [' .shared-message']: {
      transform: `translate(-142%, 920%) ${rotate.bottomLeft}`,
    },
    [' .send']: {
      transform: `translate(225%, 960%) ${rotate.bottomRight}`,
    },
    [' .heart']: {
      transform: `translate(360%, 679%) ${rotate.bottomRight}`,
    },
    [`${utils.mediaQueries.phablet}`]: {
      [' .star']: {
        transform: `translate(-98%, -570%) ${rotate.upLeft}`,
      },
      [' .top-heart']: {
        transform: `translate(-413%, -420%) ${rotate.upLeft}`,
      },
      [' .eye-purple']: {
        transform: `translate(-283%, -104%) ${rotate.upLeft}`,
      },
      [' .bubble']: {
        transform: `translate(1%, -524%) ${rotate.upRight}`,
      },
      [' .replay']: {
        transform: `translate(400%, -480%) ${rotate.upRight}`,
      },
      [' .square']: {
        transform: `translate(401%, 0) ${rotate.upRight}`,
      },
      [' .eye-orange']: {
        transform: `translate(-353%, 340%) ${rotate.bottomLeft}`,
      },
      [' .shared-message']: {
        transform: `translate(-142%, 610%) ${rotate.bottomLeft}`,
      },
      [' .send']: {
        transform: `translate(225%, 660%) ${rotate.bottomRight}`,
      },
      [' .heart']: {
        transform: `translate(388%, 179%) ${rotate.bottomRight}`,
      },
    },
    [`${utils.mediaQueries.normal}`]: {
      [' .star']: {
        transform: `translate(-191%, -683%) ${rotate.upLeft}`,
      },
      [' .top-heart']: {
        transform: `translate(-633%, -600%) ${rotate.upLeft}`,
      },
      [' .eye-purple']: {
        transform: `translate(-500%, -104%) ${rotate.upLeft}`,
      },
      [' .bubble']: {
        transform: `translate(46%, -570%) ${rotate.upRight}`,
      },
      [' .replay']: {
        transform: `translate(624%, -600%) ${rotate.upRight}`,
      },
      [' .square']: {
        transform: `translate(675%, 0) ${rotate.upRight}`,
      },
      [' .eye-orange']: {
        transform: `translate(-535%, 200%) ${rotate.bottomLeft}`,
      },
      [' .shared-message']: {
        transform: `translate(-142%, 730%) ${rotate.bottomLeft}`,
      },
      [' .send']: {
        transform: `translate(225%, 800%) ${rotate.bottomRight}`,
      },
      [' .heart']: {
        transform: `translate(600%, 379%) ${rotate.bottomRight}`,
      },
    },
  },
  [`${utils.mediaQueries.ie11}`]: {
    top: `-2.5%`,
    left: `-5%`,
  },
  [`${utils.mediaQueries.ie11Phablet}`]: {
    top: `-7.5%`,
    left: `-7.5%`,
  },
});

export const confettiQuadrent = () => ({
  display: `flex`,
  justifyContent: `center`,
  alignItems: `center`,
  width: `50%`,
  transistion: ``,
  [' .confetti-item']: {
    opacity: `0`,
    transition: ` transform 1s ease, opacity .5s ease .15s`,
  },
});



