import * as React from 'react';
import * as glamorous from 'glamorous';
import {Link} from 'react-router';
import 'what-input';

const { Li } = glamorous;

import * as styles from './TabArray-styles';

export type NavTabProps = {
  css?: { [key: string]: any };
  onSelect?: () => void;
  selected?: boolean;
  selectedColor?: string;
  uri: string;
};

export const NavTab: React.SFC<NavTabProps> =
  ({ selected, children, onSelect, selectedColor, css, uri, }) => {
    return (
      <Li
        css={[styles.tabStyles({ selected, selectedColor }), css]}
        role="tab"
        tabIndex="0"
        onClick={() => onSelect()}
        onKeyDown={(e) => {
          if (e.keyCode === 13 || e.keyCode === 32) {
            e.preventDefault();
            onSelect();
          }
        }}
      >
        <Link
          to={uri}
          onClick={(e) => {
            e.preventDefault();
            }}
        >
          {children}
        </Link>
      </Li>
    );
  };

NavTab.displayName = 'NavTab';



