import * as React from 'react';
import { connect } from 'react-redux';
import { css } from 'glamor';
import glamorous from 'glamorous';
const { Div } = glamorous;
import{ throttle } from 'lodash';
import { videoIsPlaying } from '../../../../../state/actionCreators/appCreator';
import { store } from '../../../../../state/createStore';

const ReactPlayer = require('react-player');
import * as styles from '../Video-styles';
import { IVideoProps } from '../Video';
import { autobind } from 'core-decorators';

interface IVideoInnerProps extends IVideoProps {
  width: any;
  height?: any;
  playing: boolean;
  handlePosterTap: React.EventHandler<React.MouseEvent<HTMLDivElement>>;
  handleVideoEnded: Function;
  handleVideoPause: Function;
  isMobile: boolean;
  isVertical?: boolean;
  ctaText?: string;
  showCTA: boolean;
  smallLayout?: boolean;
  heroMediaStyle?: boolean;
  animOnScroll?: boolean;
  dragging: boolean;
  handlePlayback?: Function;
  onDuration?: Function;
  roundedCorner?: boolean;
  seekTo?: number;
  controls?: boolean;
  onKeyUp?: Function;
  fitHeight?: any;
  noShadow?: boolean;
  loop?: boolean;
  autosize?: boolean;
  videoIsPlaying: {
    state: boolean;
    ref: any;
  };
  isFocused?: boolean;
}

interface IVideoState {
  transitioningPoster: boolean;
  isReady: boolean;
  width: number;
  height: number;
  playing: boolean;
  showNativeControls: boolean;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  videoIsPlaying: state.app.videoIsPlaying,
});

export class VideoPlayerClass extends React.Component<IVideoInnerProps, IVideoState> {

  posterElement: HTMLElement;
  container: any;
  videoContainer: any;
  player: any;
  deviceMatch: any;
  deviceOSList = ['iPhone', 'iPad', 'Android 5', 'Android 6', 'Android 7.0'];
  allowsMultipleVideos = ['OS 11', 'OS 10_3'];
  userAgent = (typeof navigator !== 'undefined') ? navigator.userAgent : '';
  scrollThrottle: any;

  state = {
    transitioningPoster: false,
    isReady: false,
    width: 160,
    height: 90,
    playing: this.shouldAutoPlay(),
    showNativeControls: this.showNativeControls(),
  };

  componentDidMount() {
    window.addEventListener('resize', this.resize);

    /*
    ** Required to get iPad to load poster image. Autoplaying videos also will not replay after navigating between routes despite the component unmounting.
    */
    this.deviceMatch = this.deviceOSList.filter((OS) => {
      return this.userAgent.indexOf(OS) > -1;
    });

    if (this.deviceMatch.length && this.player) {
      if (this.canPlayInline() && !this.props.isVertical && this.userAgent.indexOf('iPad; CPU OS 9_') < 0) { // iPad has issues initially preloading videos without user interaction
        setTimeout(() => {
          this.player.player.play();
        }, 0);
      }
    }

    if (this.canPlayInline() && !this.props.isMobile) {
      window.setTimeout(() => {
        this.scrollThrottle = throttle(this.scrollCallback, 100);
        window.addEventListener('scroll', this.scrollThrottle);
        this.scrollCallback();
      }, 100);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resize);
    if (this.canPlayInline()) {
      window.removeEventListener('scroll', this.scrollThrottle);
    }
  }

  componentWillReceiveProps(newProps: IVideoInnerProps) {
    if (newProps.seekTo !== this.props.seekTo) {
      this.player.seekTo(newProps.seekTo / this.player.getDuration());
    }

    if (newProps.videoIsPlaying !== this.props.videoIsPlaying) {
      if (newProps.videoIsPlaying.state === true && newProps.videoIsPlaying.ref !== this.player && !this.props.playing) { // Using this props.playing instead of state.playing to get original intended playback
        this.setState({playing: false});
      }
    }

    if (newProps.isFocused !== this.props.isFocused) {
      const paused = !newProps.isFocused;
      if (paused) {
        this.setState({ playing: false });
      }
    }
  }

  componentWillUpdate(nextProps: IVideoInnerProps, nextState: IVideoState) {
    if (nextState.playing !== this.state.playing) {
      if (nextState.playing === true ) {
        store.dispatch(videoIsPlaying({state: true, ref: this.player}));
      } else {
        store.dispatch(videoIsPlaying({state: false, ref: this.player}));
      }
    }
  }

  @autobind
  resize() {
    if ( this.state.isReady ) {
      this.setState({width: this.player.player.player.videoWidth, height: this.player.player.player.videoHeight});
    }
  }

  @autobind
  scrollCallback() {
    if (this.container && this.player) {
      const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight - 60;
      const videoTop = this.container.getBoundingClientRect().top;

      if (videoTop - window.innerHeight >= this.videoContainer.offsetHeight || videoTop <= 0) {
         if (this.state.playing && this.player) {
          this.setState({playing: false});
          this.player.player.pause();
        }
      } else {
        if (!this.state.playing && this.player && this.props.playing) { // Using this props.playing instead of state.playing to get original intended playback
          this.setState({playing: true});
          this.player.player.play();
        }
      }
    }
  }

  isReady(event) {
    if (this.deviceMatch.length) {

      const playableDevice = this.allowsMultipleVideos.filter((OS) => {
        return this.userAgent.indexOf(OS) > -1;
      });

      if (playableDevice.length < 1) {
        if (this.props.isMobile && this.props.isVertical) { // for full screen videos
          this.player.player.play();
        } else {
          if (this.userAgent.indexOf('iPad; CPU OS 9_') < 0) { // iPad has issues initially preloading videos without user interaction
            this.player.player.pause();
          }
        }
      } else {
        if (!this.state.playing && !this.props.isVertical) {
          this.player.player.pause();
        } else if (!this.state.playing && this.props.isVertical) { // for full screen videos
          this.player.player.play();
        }
      }

    }

    this.setState({isReady: true, width: this.player.player.player.videoWidth, height: this.player.player.player.videoHeight});
  }

  @autobind
  handleTap(event: React.MouseEvent<HTMLDivElement>) {
    if (this.props.dragging) {
      event.preventDefault();
    } else {
      this.setState((state) => {
        return {
          ...state,
          playing: !this.state.playing,
        };
      });
    }
  }

  handleKeyEvent(event) {
    event.persist();
    if (this.props.dragging) {
      event.preventDefault();
    } else {
      if (event.which === 32 || event.which === 13) {
        this.setState((state) => {
          return {
            ...state,
            transitioningPoster: true,
          };
        }, () => {
            this.props.onKeyUp(event.which);
        });
      }
    }
  }

  showNativeControls() {
    const deviceOSList = ['Phone OS 11_', 'iPad; CPU OS 11_'];

    const deviceMatch = deviceOSList.filter((OS) => {
      return this.userAgent.indexOf(OS) > -1;
    });
    return deviceMatch.length > 0 && !this.shouldAutoPlay() && this.props.isVertical ? true : false;
  }

  canPlayInline() {
    const excludedDeviceOSList = ['iPhone OS 9_']; // 'iPad; CPU OS 9_'

    const deviceMatch = excludedDeviceOSList.filter((OS) => {
      return this.userAgent.indexOf(OS) > -1;
    });
    return deviceMatch.length > 0 ? false : true;
  }

  shouldAutoPlay() {
    if (typeof navigator === 'undefined') {
      return false;
    }

    const deviceOSList = ['iPhone OS 10_2', 'iPhone OS 10_1', 'iPhone OS 10_0', 'iPhone OS 9_', 'iPad; CPU OS 10_2', 'iPad; CPU OS 10_1', 'iPad; CPU OS 10_0', 'iPad; CPU OS 9_', 'Android 5', 'Android 6', 'Android 7.0'];
    const userAgent = navigator.userAgent;

    const deviceMatch = deviceOSList.filter((OS) => {
      return userAgent.indexOf(OS) > -1;
    });
    return deviceMatch.length > 0 ? false : this.props.playing ? true : false;
  }

  render() {
    /*
    ** Leaving in case of reversion
    */
    // const rat = this.props.width / this.state.width;
    // const calcWidth = String(this.props.width).indexOf('%') === -1 ? `${ this.state.width * rat }px` : this.props.width;
    // const calcHeight = String(this.props.height).indexOf('%') === -1 ? `${ this.state.height * rat }px` : this.props.height;
    const calcWidth = this.props.width;
    const calcHeight = this.props.height;
    const componentStyles = [
      styles.wrapper,
      {
        width: calcWidth,
        height: calcHeight,
      },
    ];
    const controls = !this.state.showNativeControls ? this.props.controls ? true : !this.shouldAutoPlay() ? true : false : false;
    return (
      <Div innerRef={(container: any) => { this.container = container; }}>
        <Div
          className={'player-wrapper'}
          innerRef={(videoContainer: any) => { this.videoContainer = videoContainer; }}
          css={styles.wrapper(this.props.roundedCorner, this.state.playing, controls, this.props.noShadow)}
          style={{
            width: this.props.autosize ? '100%' : calcWidth,
            height: calcHeight,
            top: 0,
            left: 0,
            position: this.props.autosize ? 'relative' : 'absolute',
          }}
          onClick={this.handleTap}
          onKeyUp={this.handleKeyEvent.bind(this)}
          tabIndex="0"
        >
          <ReactPlayer
            ref={(player: any) => { this.player = player; }}
            preload
            className={css(styles.child()).toString()}
            width={ calcWidth }
            height={ calcHeight }
            url={ this.props.videoSrc }
            playing={ this.state.showNativeControls && !this.props.playing && this.props.isVertical ? true : this.state.playing }
            onReady= { this.isReady.bind(this) }
            onEnded={ this.props.handleVideoEnded }
            volume={ this.props.mute ? 0 : 1 }
            loop={ this.props.loop }
            fileConfig={{
              attributes: {
                muted: this.props.mute ? 'muted' : false,
                playsInline: this.props.isMobile && this.props.isVertical ? false : true,
                autoPlay: this.shouldAutoPlay() === true ? 'autoplay' : false,
                poster: this.props.posterImageUrl,
                controls: this.state.showNativeControls,
            }}}
            onProgress={((evt) => this.props.handlePlayback ? this.props.handlePlayback(evt, this.player) : null)}
            onDuration={(evt) => this.props.onDuration(evt)}
            onPause={() => {
              this.setState({playing: false});
            }}
          />
        </Div>
      </Div>
    );
  }
}

export const VideoPlayer = connect(mapStateToProps)(VideoPlayerClass);



