/*
** Parse and generate react elements from data
*/
function generateElements(data: any, pageColor: string, sectionColor: string, isHome: boolean = false, sectionIndex: number, isBlog: boolean = false, general: any, footer?: any): Array<any> {
  let lastElement;
  let currentElement;
  const elements = data.map((element, index) => {
    lastElement = currentElement || null;
    currentElement = element;
    switch (element.type) {
      case 'home-page-headline': // ported
        element.headline_loc = element.headline_loc.replace(new RegExp('<p>', 'g'), '<h1>');
        element.headline_loc = element.headline_loc.replace(new RegExp('</p>', 'g'), '</h1>');
        return generateHeadline(element, pageColor, sectionColor, isHome, sectionIndex, index);
      case 'sub-header': // ported
        return generateHeadline(element, pageColor, sectionColor, isHome, sectionIndex, index);
      case 'cards': // ported
        return generateCardsSection(element, pageColor, sectionColor, lastElement);
      case 'tab-devices': // ported small minor update to add poster image to video
        return generateTabDevicesSection(element, pageColor, sectionColor);
      case 'image-with-copy':
        return generateImageWithCopy(element, pageColor, sectionColor);
      // case 'chillas':
      //   return generateChillas(element, pageColor, sectionColor);
      case 'device-collage': // ported
        return generateDeviceCollage(element, pageColor, sectionColor);
      case 'device-lockup-mobile':
        return generateDeviceLockup(element, pageColor, sectionColor, 'mobile');
      case 'stories':
        return generateStoriesSection(element, pageColor, sectionColor, general);
      case 'logo-garden': // partial ported : incomplete waiting for craft update
        return generateLogoGarden(element, pageColor, sectionColor);
      // case 'lead-gen-form':
      //   return generateLeadGenForm(element);
      case 'progress-video': // partial ported: need poster image from cms
        return generateProgressVideo(element, pageColor, sectionColor);
      case 'pre-footer':
        return generatePreFooter(element); // , pageColor, sectionColor);
      // case 'sub-nav':
      //   return generateSubNav(element, pageColor, sectionColor);
      case 'image': // partial ported : waiting on craft updates
        return generateImageSection(element, pageColor, sectionColor, (index === data.length - 1), isBlog);
      case 'video': // partial ported : need craft updates
        return generateVideoSection(element, pageColor, sectionColor);
      case 'button':
        return generateButtonSection(element, pageColor, sectionColor);
      case 'error-page':
        return generateErrorPageSection(element, pageColor, sectionColor);
      case 'blog-grid':
        return generateBlogGridSection(element, pageColor, sectionColor, general, footer);
      case 'annotations':
        return generateBlogAnnotations(element);
      case 'copy':
        return generateCopyBlock(element);
      case 'infographic':
        return generateInfographic(element);
      case 'headline':
        return generateHeadlineTitle(element);

      // new below
      case 'textLockup':
        return generateHeadline(element, pageColor, sectionColor, isHome, sectionIndex, index);
      case 'centeredTextLockup':
        return generateHeadline(element, pageColor, sectionColor, isHome, sectionIndex, index);
      case 'imageCardGrid':
        return generateCardsSection(element, pageColor, sectionColor, lastElement);
      case 'mobileDeviceGrid':
        return generateDeviceCollage(element, pageColor, sectionColor);
      case 'logoGrid':
        return generateLogoGarden(element, pageColor, sectionColor);
      case 'centeredImage':
        return generateImageSection(element, pageColor, sectionColor, (index === data.length - 1), isBlog);
      case 'imageBesideText':
        return generateImageWithCopy(element, pageColor, sectionColor);
      case 'statsBesideText':
        return generateStatsWithCopy(element, pageColor, sectionColor);
      case 'videoBesideText':
        return generateImageWithCopy(element, pageColor, sectionColor);
      case 'centeredVideo':
        return generateVideoSection(element, pageColor, sectionColor);
      case 'progressiveVideo':
        return generateProgressVideo(element, pageColor, sectionColor);
      case 'deviceTabs':
        return generateTabDevicesSection(element, pageColor, sectionColor);
      case 'blockQuote':
        return generateBlockQuote(element, pageColor, sectionColor);
      case 'blockQuoteWithImage':
        return generateBlockQuote(element, pageColor, sectionColor);
      case 'statCards':
        return generateStats(element, pageColor, sectionColor);
      case 'textList':
        return generateList(element, pageColor, sectionColor);
      case 'leadInCopyBlock':
        return generateLeadIn(element);
      case 'multiImage':
        return generateMultiImage(element, pageColor, sectionColor);
      case 'successStoriesGrid':
        return generateSuccessGrid(element, pageColor, sectionColor, general);
      case 'gridCardSet':
        return generateGridCardSet(element, general);
      case 'headlineCTA':
        return generateHeadlineCTA(element);
      case 'heroImage':
        return generateBannerImage(element);
      case 'downloads':
        return generateImageSection(element, pageColor, sectionColor, (index === data.length - 1), true);
      case 'textBlock':
        return generateCopyBlock(element);
      case 'keyLearnings':
        return generateKeyLearnings(element);
      case 'imageAboveText':
        return generateHeadline(element, pageColor, sectionColor, isHome, sectionIndex, index);
      case 'adProductBlock':
        return generateAdProductBlock(element);
      case 'metricCards':
        return generateMetricCards(element);
      case 'metrics':
        return generateMetricCards(element);
      default:
        return null;
    }
  });
  // this block is used to merge cards elements when they appear back to back;
  const mergedElements = [];
  lastElement = null;
  currentElement = null;
  elements.forEach((element) => {
    lastElement = currentElement;
    currentElement = element;
    // if (lastElement && lastElement.type === 'cards' && element && element.type === 'cards') {
    //   mergedElements.pop();
    // }
    mergedElements.push(element);
  });
  return mergedElements;
}

/*
** Interface for 404 Page
*/
export interface IErrorPage {
  type?: string;
  props: IErrorPageProps;
}

function generateErrorPageSection(data: any, pageColor: string, sectionColor: string): IErrorPage {
  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';
  return {
    type: data.type,
    props: {
      pageData: {
        title: data.titleLoc,
        subTitle: data.subTitleLoc,
        buttonPrimaryLabel: data.buttonPrimaryLabelLoc,
        buttonPrimaryLink: `${langPrefix}${data.buttonPrimaryLink}`,
        image: data.image && data.image.url,
        imageAlt: data.titleLoc,
      },
    },
  };
}

