function formatDate(dateRef, general) {
  if (!dateRef) {
    dateRef = '0';
  }
  dateRef += '000';
  dateRef = Number(dateRef);
  const d = new Date(dateRef);
  const loc = store.getState().data.locale || 'en-us';
  const month = general.months[d.getMonth()].title_loc;
  const year = d.getFullYear();
  return `${month} ${year}`;
}

function parsePost(post, general, footer) {
  const elements = [];
  // Map Entries to Sections
  if (post.entryBuilder && post.entryBuilder.length > 0) {
    post.entryBuilder.map((section) => {
      elements.push(section.elements[0]);
    });
  }

  // Set Annotations Section
  if (post.pageAnnotations && post.pageAnnotations.length) {

    const obj = {
      type: 'annotations',
      annotations: post.pageAnnotations,
    };

    elements.push(obj);
  }

  // Generate Related Entries headline
  if (post.relatedContentHeadline_loc) {
    const relatedEntriesHeadline = {
      headline_loc: post.relatedContentHeadline_loc,
      ctaButtons: post.relatedContentButton && [
        {
          buttonStyle: 'primaryButton',
          button: {
            customText_loc: post.relatedContentButton.customText_loc,
            filled: false,
            outline: true,
            type: post.relatedContentButton.type,
            uri: post.relatedContentButton.uri,
          },
        },
      ],
      type: 'sub-header',
    };
    elements.push(relatedEntriesHeadline);
  }

  // Set Related Entries
  if (post.relatedEntries) {
    if (post.relatedEntries.length > 0) {
      post.relatedEntries.unshift([]);
      const related = post.relatedEntries.reduce((a, entry) => {
        const story = entry.entry && entry.entry.data && entry.entry.data[0] && entry.entry.data[0].data;
        if (story) {
          a.push(story);
        }
        return a;
      });

      const obj = {
        stories: related,
        hideFilters: true,
        selectOptions: [],
        isThreeUp: true,
        type: 'successStoriesGrid',
      };

      if (related.length > 0) {
        elements.push(obj);
      }
    }
  }

  // Generate pre-footer section
  if (!post.noPreFooter) {
    let preFooter;
    if (post.customPreFooter && post.customPreFooter.length > 0) {
      preFooter = {
        type: 'pre-footer',
        ...post.customPreFooter[0],
      };
    } else {
      preFooter = {
        type: 'pre-footer',
        ...footer[0].preFooterHeadline[0],
      };
    }
    elements.push(preFooter);
  }

  // const elements = post.entryBuilder; // TODO see if this works for everything
  post.id = 'detailPage';
  post.postTitle = post.title_loc;
  post.postDate = formatDate(post.postDate, general); // TODO come back and update this call to pass in the locale
  post.returnCTA = post.returnCTALoc; // TODO figure out what this should be and can it be generated
  post.meta = {
    description: post.seoDescription, //  || post.metafields.descriptionLoc,
    image: post.seoImage && post.seoImage.url, // || post.metafields.image,
    keywords: post.keywords, // post.metafields.keywordsLoc,
    title: post.seoTitle, // post.metafields.titleLoc,
    type: post.pageType, // post.metafields.type,
    ogTitle: post.ogTitle, // post.metafields.og.titleLoc,
    ogDescription: post.ogDescription, // post.metafields.og.descriptionLoc,
    ogType: post.pageType, // post.metafields.og.type,
  },
    post.sections = [{
      title: post.title_loc,
      elements: generateElements(elements, 'Yellow', 'white', false, 0, false, general, footer),
    }]; // TODO once homepage is generated make sure uri compare is correct] // generateSections(sections, 'Yellow', 'blog');
  delete post.postTitleLoc;
  delete post.postDateLoc;
  delete post.returnCTALoc;
  delete post.metafields;
  delete post.pageAnnotations;
  return post;
}

