export interface IMultiImage {
  type: string;
  props: IMultiImageProps;
}

function generateMultiImage(data: any, pageColor: string, sectionColor: string): IMultiImage {
  return {
    type: 'multiImage',
    props: {
      caption: data.caption_loc,
      images: data.multiImage.map((img) => {
        return {
          imageUrl: img.imageAsset && img.imageAsset.url,
          altText: img.imageAltText_loc,
        };
      }),
    },
  };
}

