/*****
* LeadInCopyBlock
******/
function generateLeadIn(data: any): ILeadIn {
  data.leadInCopyBlock_loc = data.leadInCopyBlock_loc.replace(new RegExp('<p>', 'g'), '<h2>');
  data.leadInCopyBlock_loc = data.leadInCopyBlock_loc.replace(new RegExp('</p>', 'g'), '</h2>');
  return {
    type: 'leadInCopyBlock',
    props: {
      copy: data.leadInCopyBlock_loc,
    },
  };
}

