export interface IQuoteBlock {
  type: string;
  props: IQuoteBlockProps;
}

function generateBlockQuote(data: any, pageColor: string, sectionColor: string): IQuoteBlock {
  if (!data.blockQuote && !data.blockQuote.length) { return null; }
  return {
    type: 'blockQuote',
    props: {
      attributionSource: data.blockQuote[0].attribution_loc,
      quoteCopy: data.blockQuote[0].quoteText_loc,
      imageContent: data.image ? {
        imageUrl: data.image.url,
        imageUrl2x: data.image.url, // TODO update with 2x vs 1x
        title: data.imageAltText_loc,
        dimensions: {
          width: data.image.width / 2,
          height: data.image.height / 2,
        },
      } : null,
    },
  };
}

function generateBlogGridCards(data: any): Array<IStory> {
  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';

  return data.map((card) => {
    return {
      id: card.id,
      subhead: null,
      title: card.title_loc,
      copy: card.copyLoc,
      image: card.image,
      image2x: card.image2x,
      link: `${langPrefix}${card.link + card.id}`,
      placeholder: card.placeholder || false,
      tags: card.tags,
      rounded: card.rounded,
    };
  });
}

