/*
** Image with Copy
*/
export interface IImageWithCopy {
  type: string;
  formatting?: string;
  props: ICopyWithImageProps;
}

function generateImageWithCopy(data: any, pageColor: string, sectionColor: string): IImageWithCopy {
  let primaryButton;
  let secondaryButton;
  if (data.ctaButtons && data.ctaButtons.length > 0) {
    data.ctaButtons.forEach((button) => {
      if (button.buttonStyle === 'primaryButton' && !primaryButton) {
        primaryButton = button;
      } else if (button.buttonStyle === 'secondaryButton' || button.buttonStyle === 'primaryButton') {
        if (!primaryButton) {
          primaryButton = button;
        } else {
          secondaryButton = button;
        }
      }
    });
  }
  if (data.primaryCta) {
    primaryButton = {
      button: {
        ...data.primaryCta,
      },
      buttonStyle: 'primaryButton',
      outline: true,
    };
  }
  if (data.secondaryCta) {
    secondaryButton = {
      button: {
        ...data.secondaryCta,
      },
      buttonStyle: secondaryButton.buttonStyle, // 'secondaryButton',
      outline: true,
    };
  }
  const isGettingStarted = primaryButton && primaryButton.buttonType === 'gettingStarted';

  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';

  return {
    type: 'image-with-copy', // data.type,
    formatting: data.formatting,
    props: {
      headline: data.headline || data.headline_loc,
      copy: data.paragraph || data.paragraph_loc,
      eyebrow: data.eyebrow || data.eyebrowLoc,
      reverse: data.imageLocation ? data.imageLocation === 'right' : data.desktopVideoLocation && data.desktopVideoLocation === 'right', // data.flip || false,
      specsLabel: data.specsLabelLoc,
      specsLink: data.specsLink,
      buttonContent: primaryButton && primaryButton.button && {
        cta: primaryButton.button.customText_loc,
        link: primaryButton.button.type === 'entry' ? `${langPrefix}/${primaryButton.button.uri}` : primaryButton.button.custom,
        color: isGettingStarted ? buttonColor('Yellow', true, true) : buttonColor(pageColor, true),
        filled: data.filled ? data.filled : isGettingStarted ? true : false,
        outline: data.outline ? data.outline : isGettingStarted ? false : true,
        buttonStyle: primaryButton.buttonStyle,
        target: primaryButton.button.target,
      },
      secondaryButtonContent: secondaryButton && secondaryButton.button && {
        cta: secondaryButton.button.customText_loc,
        link: secondaryButton.button.type === 'entry' ? (primaryButton && `${langPrefix}/${primaryButton.button.uri}`) : (primaryButton && primaryButton.button.custom),
        color: isGettingStarted ? buttonColor('Yellow', true, true) : buttonColor(pageColor, true),
        filled: data.filled ? data.filled : isGettingStarted ? true : false,
        outline: data.outline ? data.outline : isGettingStarted ? false : true,
        buttonStyle: secondaryButton.buttonStyle,
        target: secondaryButton.button.target,
      },
      imageContent: data.image && {
        imageUrl: data.image.url,
        imageUrl2x: data.image.url, // TODO come back and handle 2x vs 1x images // data.image2x,
        title: data.image.title_loc,
        fullWidth: true,
        valign: true,
      },
      videoContent: data.video && {
        mediaWidthRelativeToContainer: '100%',
        ratioWidth: data.video[0].posterImage.width,
        ratioHeight: data.video[0].posterImage.height,
        video: {
          videoSrc: data.video && !data.video[0].videoInMobileDevice && data.video[0] && data.video[0].videoFileMp4 && data.video[0].videoFileMp4.url,
          posterImageUrl: data.video && data.video[0] && data.video[0].posterImage && data.video[0].posterImage.url2x,
        },
      },
      deviceContent: {
        imageUrl: data.video && data.video[0].videoInMobileDevice && data.video[0] && data.video[0].posterImage.url,
        imageUrl2x: data.video && data.video[0].videoInMobileDevice && data.video[0] && data.video[0].posterImage.url2x,
        videoUrl: data.video && data.video[0].videoInMobileDevice && data.video[0] && data.video[0].videoFileMp4 && data.video[0].videoFileMp4.url,
      },
      chillah: data.chillahTooltip && data.chillahTooltip[0] && {
        image: data.chillahTooltip[0].chillah.url,
        tip: data.chillahTooltip[0].tooltipText_loc,
        width: data.chillahTooltip[0].chillah.width,
      },
    },
  };
}

