function generateDeviceModal(data: any, general: any): IDeviceMobileLockupProps {
  let imageUrl;
  let imageUrl2x;
  let imageAlt;
  let videoUrl;
  let videoPoster;
  let playsInline = true;
  const link = {
    link: null,
    copy: null,
  };
  if (data.spotlightDeviceAsset && data.spotlightDeviceAsset[0]) {
    if (data.spotlightDeviceAsset[0].handle === 'spotlightDeviceAsset') {
      if (data.spotlightDeviceAsset[0].imageAsset) {
        imageUrl = data.spotlightDeviceAsset[0].imageAsset.url;
        imageUrl2x = data.spotlightDeviceAsset[0].imageAsset.url2x;
      }
      else if (data.spotlightDeviceAsset[0].videoAssetMp4 && data.spotlightDeviceAsset[0].videoPosterImage) {
        imageUrl = data.spotlightDeviceAsset[0].videoPosterImage.url;
        imageUrl2x = data.spotlightDeviceAsset[0].videoPosterImage.url2x;
      }
      imageAlt = data.spotlightDeviceAsset[0].imageAltText_loc;
      videoUrl = data.spotlightDeviceAsset[0].videoAssetMp4 && data.spotlightDeviceAsset[0].videoAssetMp4.url;
    } else if (data.spotlightDeviceAsset[0].handle === 'spotlightVideo') {
      imageUrl = data.spotlightDeviceAsset.videoPosterImage && data.spotlightDeviceAsset.videoPosterImage.url;
      imageUrl2x = data.spotlightDeviceAsset.videoPosterImage && data.spotlightDeviceAsset.videoPosterImage.url2x;
      videoUrl = data.spotlightDeviceAsset.videoMp4 && data.spotlightDeviceAsset.videoMp4.url;
    }
  }

  if (data.entryType === 'creativeSpotlightVideo') {
    videoUrl = data.spotlightVideo && data.spotlightVideo[0] && data.spotlightVideo[0].videoMp4 && data.spotlightVideo[0].videoMp4.url;
    imageUrl = data.spotlightVideoPortrait && data.spotlightVideoPortrait[0] && data.spotlightVideoPortrait[0].videoPosterImage && data.spotlightVideoPortrait[0].videoPosterImage.url;
    imageUrl2x = data.spotlightVideoPortrait && data.spotlightVideoPortrait[0] && data.spotlightVideoPortrait[0].videoPosterImage && data.spotlightVideoPortrait[0].videoPosterImage.url2x;
    videoPoster = data.spotlightVideo && data.spotlightVideo[0] && data.spotlightVideo[0].videoPosterImage && data.spotlightVideo[0].videoPosterImage && data.spotlightVideo[0].videoPosterImage.url2x;
    playsInline = false;
  }

  if (data.spotlightProductUsed && data.spotlightProductUsed.length > 0) {
    switch (data.spotlightProductUsed[0].slug) {
      case 'lenses':
        link.copy = general.modalLensCta.customText_loc;
        link.link = general.modalLensCta.custom;
        break;
      case 'filters':
        link.copy = general.modalFilterCta.customText_loc;
        link.link = general.modalFilterCta.custom;
        break;
      default:
        link.copy = general.modalSnapAdCta.customText_loc;
        link.link = general.modalSnapAdCta.custom;
    }
  }

  return {
    deviceMobile: {
      imageUrl,
      imageUrl2x,
      imageAlt,
      videoUrl,
      playsInline,
    },
    videoUrl: data.spotlightVideo && data.spotlightVideo[0] && data.spotlightVideo[0].videoMp4 && data.spotlightVideo[0].videoMp4.url,
    videoPoster: videoPoster,
    eyebrowTitle: general.creativeSpotlightTileTitle_loc,
    title: data.title_loc,
    copyBlock: data.spotlightSummary_loc,
    primaryOverview: {
      title: general.productUsedTitle_loc,
      body: data.snapProduct && data.snapProduct[0] && data.snapProduct[0].title_loc,
    },
    secondaryOverview: {
      title: general.overviewlabel_loc,
      body: (data.entryObjective && data.entryObjective[0]) && (data.entryObjective[0].longTitle_loc || data.entryObjective[0].title_loc),
    },
    primaryButton: link.link && {
      link: link.link,
      color: 'black',
      children: link.copy,
      outline: true,
      isSmall: true,
    },
  };
}

function generateSuccessCard(data: any, general: any): IStoriesCard {
  let subhead = general.successTileTitle_loc;
  let cta = general.storyTitleCta_loc;
  let tags = [];
  let featuredImage;
  let featuredImage2x;
  let modalData;
  let image;
  let image2x;

  if (data.entryType === 'successStory') {
    tags = generateBlogTags([['successStory'], data.snapProduct, data.entryObjective, data.entryIndustry, data.entryRegion]);
    cta = general.storyTileCta_loc;
    featuredImage = data.cardDeviceImage && data.cardDeviceImage.url;
    featuredImage2x = data.cardDeviceImage && data.cardDeviceImage.url2x;
    modalData = null;
  } else if (data.entryType === 'creativeSpotlightDevice') {
    tags = generateBlogTags([['creativeSpotlightDevice', 'successStory'], data.snapProduct, data.entryObjective, data.entryIndustry, data.entryRegion]);
    cta = general.spotlightDeviceTileCta_loc;
    subhead = general.creativeSpotlightTileTitle_loc;
    featuredImage = data.spotlightCardDevice && data.spotlightCardDevice.url;
    featuredImage2x = data.spotlightCardDevice && data.spotlightCardDevice.url2x;
    modalData = generateDeviceModal(data, general);
  } else if (data.entryType === 'creativeSpotlightVideo') {
    tags = generateBlogTags([['creativeSpotlightVideo', 'successStory'], data.snapProduct, data.entryObjective, data.entryIndustry, data.entryRegion]);
    cta = general.spotlightVideoTileCta_loc;
    subhead = general.creativeSpotlightTileTitle_loc;
    featuredImage = false;
    image = data.videoCardImage.url1x;
    image2x = data.videoCardImage.url2x;
    modalData = generateDeviceModal(data, general);
  }

  return {
    type: data.entryType,
    subhead,
    title: data.entryType === 'successStory' ? data.cardHeadline_loc : data.title_loc,
    cta,
    logoImage: data.brandLogo && data.brandLogo.url,
    logoImage2x: data.brandLogo && data.brandLogo.url2x,
    featuredImage,
    featuredImage2x,
    image,
    image2x,
    color: data.brandColor,
    tags,
    modalData,
    lightText: data.lightText,
    link: data.entryType === 'successStory' && `/success-stories/${data.slug}`,
    id: data.slug,
  };
}

function generateSuccessGrid(data: any, pageColor: string, sectionColor: string, general: any): ISuccessGrid {
  const stories = generateGridCards(data.stories, general);
  const options = data.selectOptions.map((filter) => {
    filter.options.map((option) => {
      option.label = option.title;
      option.value = option.slug;
    });
  });
  return {
    props: {
      data: {
        isGridCard: data.isGridCard ? false : true,
        stories,
        selectOptions: data.selectOptions,
        hideFilters: data.hideFilters,
        noResults: general.noResults_loc,
        loadMoreCta: general.loadMoreButtonText_loc,
      },
      isThreeUp: data.isThreeUp,
      color: pageColor,
    },
    type: 'storiesGrid',
  };
}
