function calculateImageWidth(data, isWide) {
  let width = null;

  if (data.type === 'centeredImage') {
    if (isWide) {
      width = (columns.desktopMaxWidth / 17) * 12;
    } else {
      width = (columns.desktopMaxWidth / 17) * 9;
    }
  } else {
    width = data.image.width / 2;
  }

  return width;
}

function calculateImageHeight(data, isWide) {
  let height = null;

  const resizeRatio = ((columns.desktopMaxWidth / 17) * 12) / (data.image.width / 2);

  if (data.type === 'centeredImage') {
    height = (data.image.height / 2) * resizeRatio;
  } else {
    height = data.image.width / 2;
  }

  return height;
}

function generateImageSection(data: any, pageColor: string, sectionColor: string, isLast: boolean, isBlog: boolean): IImage {
  let alt = data.imageAltText_loc || '';
  let download;
  let caption = data.imageCaption_loc || '';
  let isWide = false;
  let type;

  if (data.type === 'centeredImage') {
    download = data.downloadableFile && data.downloadableFile.length ? data.downloadableFile[0].asset : null;
    alt = data.webImageAltText_loc;
    isWide = data.wideImage || data.assetWidth === 'wide';
    type = data.type;
  } else if (data.type === 'downloads' && data.downloadableAsset.length) {
    download = data.downloadableAsset[0].downloadableAsset;
    caption = data.downloadableAsset[0].caption_loc;
    alt = data.downloadableAsset[0].siteImage.title;
    isWide = data.wideImage || data.assetWidth === 'wide';
    type = data.type;
  } else if (data.type === 'heroImage' && data.heroImage && data.heroImage.length) {
    alt = data.heroImage[0].altText;
    type = data.type;
  }

  return {
    type: 'image',
    rounded: data.roundedCorners || false,
    formatting: data.formatting || null,
    props: {
      imageUrl: data.url ||
      (data.imageAsset && data.imageAsset.url) ||
      (data.image && data.image.url) || (data.webImage && data.webImage.url) ||
      (data.downloadableAsset && data.downloadableAsset.length && data.downloadableAsset[0].siteImage.url) ||
      (data.heroImage && data.heroImage.length && data.heroImage[0].image.url),
      imageUrl2x: data.url2x ||
      (data.imageAsset && data.imageAsset.url) ||
      (data.image && data.image.url) || (data.webImage && data.webImage.url) ||
      (data.downloadableAsset && data.downloadableAsset.length && data.downloadableAsset[0].siteImage.url),
      mobileImageUrl: data.mobileImage && data.mobileImage.url || null,
      title: alt,
      fullWidth: isWide,
      maxWidth: data.assetWidth === 'wide',
      natural: true,
      noPadding: data.assetWidth === 'full' || data.type === 'centeredImage',
      isBlog: isBlog,
      isLast: isLast,
      rounded: data.roundedCorners || data.type === 'downloads' || false,
      caption: (caption && caption.length > 0) ? caption : null,
      downloadUrl: download ? download.url : null,
      isWide: isWide,
      width: data.image && data.type && calculateImageWidth(data, isWide), // divided to reflect 1x width
      height: data.image && data.type && calculateImageHeight(data, isWide), // divided to reflect 1x width
      type: type,
    },
  };
}

export interface IBanner {
  type: string;
  props: IBannerImageProps;
}

function generateBannerImage(data: any): IBanner {
  const images = data.heroImage[0];
  return {
    type: data.type,
    props: {
      desktopUrl: images && images.image.url ? images.image.url : null,
      mobileUrl: images && images.mobileImage.url ? images.mobileImage.url : images && images.image.url ? images.image.url : null,
      alt: images.altText_loc,
    },
  };
}

/*
** Sections
*/
export interface ISection {
  title: string;
  color?: string;
  elements: Array<IHeadline | ICards | ITabDevices | IImageWithCopy | IChillahs | IDeviceCollage | ITabDevice | IStories | ILogoGarden | IProgressVideo | IPreFooter | ISubNav | IImage | IVideo | IButton>;
}

function generateSections(data: any, pageColor: string, uri: string, general: any, footer?: any): Array<ISection> {
  return data.map((section: any, index: number) => {
    const sectionColor = section.color || (section.sectionBackgroundColor && section.sectionBackgroundColor !== 'gray' && section.sectionBackgroundColor) || null;
    return {
      title: section.sectionTitle_loc,
      color: sectionColor,
      elements: generateElements(section.elements, pageColor, sectionColor, uri === '' || uri === 'home', index, uri === 'blog', general, footer), // TODO once homepage is generated make sure uri compare is correct
    };
  });
}

/*
** Annotations
*/
function generateAnnotations(data: any): Array<string> {
  data.sort((a, b) => {
    return parseFloat(a.number) - parseFloat(b.number);
  });
  return data.map((annotation: any) => {
    return annotation.text_loc;
  });
}

export interface IBlogAnnotations {
  type: string;
  props: {
    items: Array<string>;
  };
}

function generateBlogAnnotations(data: any): IBlogAnnotations {

  data.annotations.sort((a, b) => {
    return parseFloat(a.number) - parseFloat(b.number);
  });

  return {
    type: data.type,
    props: {
      items: data.annotations.map((annotation: any) => {
        return annotation.text_loc;
      }),
    },
  };
}

/*
** Navigation
*/
export interface INavigationItem {
  type: NavLinkTypes;
  label?: string; // if blank use ellipsis svg
  labelKey?: string;
  link?: string;
  links?: Array<ILink>;
}

function generateSubNavigation(data: any): Array<INavigationItem> {
  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';

  return data.map((item: any) => {

    return {
      type: 'link',
      label: (item.linkInfo.type === 'entry' ? item.linkInfo.customText_loc : item.linkInfo.customText_loc), // item.labelLoc || item.linkInfo.customText_loc || null,
      labelKey: item.label || null,
      link: (item.linkInfo.type === 'entry' ? `${langPrefix}/${item.linkInfo.uri}` : item.linkInfo.custom), // || item.elements[0].link || null,
      links: null,
    };
  });
}

function generateNavigation(data: any): Array<INavigationItem> {
  if (!data[0]) {
    data = [data];
  }
  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';
  const nav = data[0].primaryNavigation.map((item: any) => {

    return {
      type: 'link',
      label: item.elements[0].navItem.customText_loc, // item.elements[0].labelLoc || item.elements[0].navItem.customText_loc || null,
      labelKey: item.elements[0].label || null,
      link: (item.elements[0].navItem.type === 'entry' ? `${langPrefix}/${item.elements[0].navItem.uri}` : item.elements[0].navItem.custom), // || item.elements[0].link || null,
      links: item.elements[0].subnavItems ? generateSubNavigation(item.elements[0].subnavItems) : null,
    };
  });
  const subNav = data[0].secondaryNavigation.map((item: any) => {
    if (item.linkInfo_loc) { // TODO remove once linkInfo_loc label is updated to just linkInfo;
      item.linkInfo = item.linkInfo_loc;
    }
    return {
      type: item.type || 'link',
      label: (item.linkInfo.type === 'entry' ? item.linkInfo.customText_loc : item.linkInfo.customText_loc), // item.labelLoc || item.linkInfo.customText_loc || null,
      labelKey: item.label || null,
      link: (item.linkInfo.type === 'entry' ? `${langPrefix}/${item.linkInfo.uri}` : item.linkInfo.custom), // || item.link || null,
      links: item.links ? generateLinks(item.links) : null,
    };
  });
  nav.push({
    type: 'link-collection',
    label: '',
    links: subNav,
  });
  if (data[0].mainNavigationCta_loc) {
    data[0].mainNavigationCta = data[0].mainNavigationCta_loc;
  }
  nav.push({
    type: 'special',
    label: data[0].mainNavigationCta.customText_loc,
    link: data[0].mainNavigationCta.custom || `${langPrefix}/${data[0].mainNavigationCta.uri}`,
  });
  return nav;
}

/*
** Links
*/
export interface ILink {
  link: string;
  label: string;
  labelKey?: string;
  target?: string;
}

function generateLinks(data: any): Array<ILink> {
  const lang = store.getState().data.locale;
  const langPrefix = lang.length > 0 ? `/l/${lang}` : '';

  return data.map((link: any) => {
    return {
      link: link.link || (link.footerLink && (link.footerLink.type === 'entry' ? `${langPrefix}/${link.footerLink.uri}` : link.footerLink.custom)) || (link.footerLink_loc && (link.footerLink_loc.type === 'entry' ? `${langPrefix}/${link.footerLink_loc.uri}` : link.footerLink_loc.custom)) || link.slug,
      label: link.labelLoc || link.footerLink && link.footerLink.customText_loc || link.footerLink_loc && link.footerLink_loc.customText_loc || link.title,
      target: link.footerLink && link.footerLink.target || null,
      labelKey: link.label || link.slug || null,
    };
  });
}

/*
** Ad Product Block
*/

export interface ISuccessStoryAdProduct {
  type: string;
  props: ISuccessStoryAdProductProps;
}

function generateAdProductBlock(data: any): ISuccessStoryAdProduct {
  return {
    type: data.type,
    props: {
      content: data.paragraph_loc,
      products: data.adProductDevices.map((product: any) => {
        return {
          image: product.imageAsset && {
            src: product.imageAsset.url,
            alt: product.imageAsset.title,
          },
          video: product.videoAssetMp4 && {
            src: product.videoAssetMp4.url,
            posterUrl: product.posterImage && product.posterImage.url,
            height: product.posterImage && product.posterImage.height,
            width: product.posterImage && product.posterImage.width,
          },
          title: product.title_loc,
          description: product.caption_loc,
        };
      }),
      copyRight: data.flipContent,
      header: data.headline_loc,
      metrics: data.storyMetrics.map((metric: any) => {
        return {
          headline: metric.largeNumber_loc,
          subhead: metric.smallText_loc,
        };
      }),
    },
  };
}

/*
** Metric Cards
*/

export interface IMetricCards {
  type: string;
  props: IMetricCollectionProps;
}

export interface IMetricCollectionProps {
  headline: string;
  metrics: Array<IStatMetric>;
}

export interface IStatMetric {
  headline: string;
  subhead: string;
}

function generateMetricCards(data: any): IMetricCards {
  return {
    type: data.type,
    props: {
      headline: data.headline_loc,
      metrics: data.metricCards.map((card: any) => {
        return {
          headline: card.largeNumber,
          subhead: card.smallText_loc,
        };
      }),
    },
  };
}



