import { css } from 'glamor';
import * as utilStyles from 'utils/styles';
const { mediaQueries } = utilStyles;

export const baseStyles = (primary: boolean, noMax: boolean, small: boolean) => ({
    fontWeight: '400',
    fontSize: primary ? 32 : 24,
    lineHeight: primary ? '36px' : '32px',
    letterSpacing: '0',
    [`${mediaQueries.phablet}`]: {
        maxWidth: noMax ? 'none' : primary ? `${utilStyles.columns.desktopColumnWidth * 11}%` : `${!small ? (utilStyles.columns.desktopColumnWidth * 9) : (utilStyles.columns.desktopColumnWidth * 15)}%`,
        margin: '0 auto',
        fontSize: primary ? 48 : 32,
        lineHeight: primary ? '56px' : '36px',
    },
    [`${mediaQueries.normal}`]: {
        maxWidth: noMax && primary && `${utilStyles.columns.desktopColumnWidth * 11}%`,
    },
});

export const headlineWrapper = (noPadding, small = false) => ({
    maxWidth: utilStyles.columns.desktopMaxWidth,
    margin: '0 auto',
    padding: `0`,
    boxSizing: 'border-box',
    [`${mediaQueries.normalMax}`]: !noPadding && {
       padding: `0 ${small ? 0 : utilStyles.padding.semi}px`,
    },
     [`${mediaQueries.phabletMax}`]: !noPadding && {
       padding: `0 ${small ? utilStyles.padding.normal : utilStyles.padding.semi}px`,
    },
});

export const paragraph = () => ({
    color: 'black',
});

export const superscript = () => ({
  transform: 'translateY(-20%)',
  display: 'inline-block',
  fontSize: '50%',
  verticalAlign: 'top',
});



