import * as React from 'react';
import glamorous from 'glamorous';
const { Div, Img, H1 } = glamorous;

import * as styles from './PageHeading-styles';

export interface IPageHeadingProps {
  headline: string;
  image?: string;
  imageAltText?: string;
  category?: string;
  date?: string;
}

export class PageHeading extends React.Component<IPageHeadingProps, void> {
  render() {
    const isArticle = this.props.date; // We can change this logic later
    return (
      <Div css={styles.container(isArticle)}>

        {
          this.props.image && !isArticle && (
            <Img
              src={this.props.image}
              alt={this.props.imageAltText}
              css={styles.image()}
            />
          )
        }

        <Div css={styles.subtext(isArticle)}>
          { isArticle
            ? `${this.props.category} / ${this.props.date}`
            : this.props.category
          }
        </Div>

        <H1 css={styles.headline(isArticle)}>
          {this.props.headline}
        </H1>
      </Div>
    );
  }
}



