import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries } = utils;

export const wrapper =  () => ({
    backgroundColor: colors.black,
    color: colors.white,
    padding: '20px 24px',
    [`${mediaQueries.phabletMax}`]: {
        padding: '24px 32px',
    },
});

export const anchorWrapper = () => ({
    fontSize: 19,
    lineHeight: '32px',
    display: 'block',
    [`${mediaQueries.phablet}`]: {
        fontSize: 16,
        lineHeight: '16px',
        display: 'inline-block',
    },
});

export const linkWrapper = () => ({
    maxWidth: utils.columns.desktopMaxWidth,
    margin: '0 auto',
    textAlign: 'center',
    '& a:first-of-type': {
        paddingRight: 0,
        paddingBottom: 20,
    },
    '& >a': {
        display: 'block',
    },
    [`${mediaQueries.phablet}`]: {
        textAlign: 'left',
        '& >a': {
            display: 'initial',
        },
        '& a:first-of-type': {
            paddingRight: 32,
            paddingBottom: 0,
        },
    },
});

export const linkInnerWrapper = () => ({
    paddingLeft: 0,
    [`${mediaQueries.phablet}`]: {
        paddingLeft: '20%',
    },
});



