import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
const { Div } = glamorous;

import * as styles from './BlogPageFilters-styles';

import { TabArray, Tab } from '../../molecules/TabArray';
import { SelectFilters } from '../../molecules/SelectFilters';

export interface IBlogPageFiltersProps {
  tabs?: Array<{
    label: string;
    onSelect: any;
    selected?: boolean;
  }>;
  fontColor?: string;
  selects?: Array<any>;
  tabSelect?: Object;
  onChange?: Function;
}

export class BlogPageFilters extends React.Component<IBlogPageFiltersProps, void> {

  static propTypes = {
    tabs: PropTypes.arrayOf(PropTypes.shape({
      label: PropTypes.string.isRequired,
      onSelect: PropTypes.func.isRequired,
      selected: PropTypes.bool,
    })),
    fontColor: PropTypes.string,
    selects: PropTypes.array,
    tabSelect: PropTypes.object,
  };

  renderTabs() {
    const { tabs, fontColor } = this.props;
    return tabs.map((tab, index) => {
      return (
        <Tab
          css={styles.tab()}
          selected={tab.selected}
          onSelect={tab.onSelect}
          key={`tab-${index}`}>
          {tab.label}
        </Tab>
      );
    });
  }

  render() {
    return (
      <Div css={styles.filterWrapper()}>
        <TabArray css={styles.tabList()}>
          {this.renderTabs()}
        </TabArray>
        <Div css={styles.tabSelectWrapper()}>
          {this.props.tabSelect}
        </Div>
        <SelectFilters
          selects={this.props.selects}
          onChange={this.props.onChange}
        />
      </Div>
    );
  }
}



