import * as React from 'react';
import { browserHistory } from 'react-router';
import { connect } from 'react-redux';
import { Dispatch } from '@types/react-redux';
import { writeCookie } from 'utils/helpers';
import { Helmet } from 'react-helmet';
import { setCurrentEntry } from '../../../../state/actionCreators/data';

// Load up the global styles for the entire app
import '../../globalStyles';

export interface IRouterLocation {
  pathname: string;
}

export interface IRouter {
  location: IRouterLocation;
}

export interface IApp {
  router: IRouter;
  route: {
    path: string;
  };
  data: any;
  location: any;
  setCurrentEntry: any;
}

const mapStateToProps = (state: any) => ({
  data: state.data,
});

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
  setCurrentEntry: (route) => dispatch(setCurrentEntry(route)),
});

export class AppImpl extends React.Component<IApp, {}> {

  componentWillMount() {
    if (typeof window === 'undefined') {
      return;
    }
    this.props.setCurrentEntry(this.props.location.pathname);
    if (this.props.location.query) {
      this.storeUTM(this.props.location.query);
    }
  }
  componentDidMount() {
    this.sendRemarketing();
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.location !== this.props.location) {
      this.props.setCurrentEntry(nextProps.location.pathname);
      this.sendRemarketing();
    }
  }
  sendRemarketing() {
    if ((window as any).google_trackConversion) {
      (window as any).google_trackConversion({
        google_conversion_id: 845934552,
        google_custom_params: (window as any).google_tag_params,
        google_remarketing_only: true,
      });
    }
  }
  storeUTM(query) {
    Object.keys(query).map((param) => {
      if (param.indexOf('utm') > -1) {
        writeCookie(param, query[param]);
      }
      if (param.indexOf('cid') > -1) {
        writeCookie(param, query[param]);
      }
    });
    writeCookie('utm_date', Date.now().toString());
  }
  render() {
    return (
      <div>
        <Helmet>
          <script type="text/javascript" src="https://www.googleadservices.com/pagead/conversion_async.js"></script>
        </Helmet>
        {this.props.children}
      </div>
    );
  }
}

export const App = connect(mapStateToProps, mapDispatchToProps)(AppImpl);



