import * as React from 'react';
import { connect } from 'react-redux';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
const { Div } = glamorous;
import { whereAmI } from 'utils/helpers';

import { isMobile } from '../../../../state/actionCreators/appCreator';
import { IconButton } from '../../atoms/IconButton';
import * as styles from './CaptionAndDownload-styles';

export interface ICaptionAndDownloadProps {
  caption?: string;
  downloadUrl?: string;
  onDownloadClick?: Function;
  isMobile?: boolean;
  analyticsEvent?: Function;
  routeId?: string;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  routeId: state.data.current.id,
});

export class CaptionAndDownloadClass extends React.Component<ICaptionAndDownloadProps, void> {

  @autobind
  handleDownloadClick() {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${whereAmI()}-download-asset-${this.props.downloadUrl}`,
        category: `${this.props.routeId}-download-asset`,
      });
    }
    if (this.props.onDownloadClick) {
      this.props.onDownloadClick();
    }
  }

  render() {
    return (
      <Div css={styles.container}>
        {this.props.caption && (
          <Div css={styles.caption}>
            {this.props.caption}
          </Div>
        )}
        {this.props.downloadUrl && (
          <Div css={styles.downloadButtonContainer}>
            <IconButton
              iconUrl="/icons/download-2.svg"
              downloadUrl={this.props.downloadUrl}
              onClick={this.handleDownloadClick}
            />
          </Div>
        )}
      </Div>
    );
  }
}

export const CaptionAndDownload = connect(mapStateToProps)(CaptionAndDownloadClass);



