import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

function getArrow(color: string) {
  if (color === 'yellow') {
    color = 'black';
  }
  const arrow = require(`./tooltip-arrow-${color}.svg`);
  return `url(${arrow})`;
}

function getGradient(color: string) {
  switch (color) {
    case 'purple':
      return colors.regularPurpleGradient;
    case 'blue':
      return colors.regularBlueGradient;
    case 'green':
      return colors.regularGreenGradient;
    case 'orange':
      return colors.regularOrangeGradient;
    case 'red':
      return colors.regularRedGradient;
    case 'yellow':
      return colors.black;
    default:
      return colors.black;
  }
}

function determineTransform(corner: string, direction: string) {
  if (corner === 'right' && direction === 'top') {
    return 'rotateX(180deg)';
  } else if (corner === 'left' && direction === 'top') {
    return 'rotate(180deg)';
  } else if (corner === 'left' && direction === 'bottom') {
    return 'rotateY(180deg)';
  } else {
    return 'none';
  }
}

function getBorderRadius(corner: string, direction: string, inner?: boolean) {
  if (corner === 'right' && direction === 'top') {
    if (inner) {
      return '14px 0rem 14px 14px';
    }
    return '1rem 0rem 1rem 1rem';
  } else if (corner === 'left' && direction === 'top') {
    if (inner) {
      return '0rem 14px 14px 14px';
    }
    return '0rem 1rem 1rem 1rem';
  } else if (corner === 'left' && direction === 'bottom') {
    if (inner) {
      return '14px 14px 14px 0rem';
    }
    return '1rem 1rem 1rem 0rem';
  } else {
    if (inner) {
      return '14px 14px 0 14px';
    }
    return '1rem 1rem 0 1rem';
  }
}

export const tooltipStyle = (corner: string, direction: string, color: string) => ({
  display: 'inline-block',
  padding: '2rem',
  position: 'absolute',
  width: '300px',
  border: '2px solid transparent',
  borderRadius: getBorderRadius(corner, direction),
  backgroundClip: 'padding-box',
  marginTop: direction === 'top' && '60px',
  marginBottom: direction === 'bottom' && '60px',
  textAlign: 'left',
  lineHeight: '120%',
  letterSpacing: '0.01em',
  transform: 'rotateX(0deg)',
  background: getGradient(color),
  '&::after': {
    content: `''`,
    position: 'absolute',
    top: 0,
    right: 0,
    left: 0,
    bottom: 0,
    borderRadius: getBorderRadius(corner, direction, true),
    boxShadow: `0 7px 21px rgba(0,0,0,.2)`,
    zIndex: -1,
    backgroundColor: 'white',
  },
  '&::before': {
    content: `''`,
    position: 'absolute',
    top: direction === 'bottom' && '100%',
    bottom: direction === 'top' && '100%',
    [`${corner}`]: '-19px',
    width: '80px',
    height: '75px',
    background: getArrow(color),
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '0 0',
    transform: determineTransform(corner, direction),
    zIndex: 1,
  },
});



