import * as React from 'react';
import * as glamorous from 'glamorous';
import * as Router from 'react-router';
import * as utils from 'utils/styles';
import { whereAmI } from 'utils/helpers';
import { max } from 'lodash';
import { autobind } from 'core-decorators';
import { connect } from 'react-redux';
import { Card } from './Card';
import {
  cardSetWrapper,
  cardLayoutWrapper,
  cardSetChild,
} from './Card-styles';

import { ChillahTooltip } from '../../molecules/ChillahTooltip';

const { Div, A } = glamorous;
const { Link } = Router;

export interface ICardProps {
  id?: string;
  subhead?: string;
  title?: string;
  image: string;
  imageAlt?: string;
  image2x?: string;
  copy: string;
  link?: string;
  target?: string;
  chillah?: {
    iconUrl: string;
    imageAlt?: string;
    label: string;
    left: any;
    right: any;
    top: any;
    content: string;
    width?: number;
  };
}

export interface ICardSetProps {
  cards?: Array<ICardProps>;
  rows?: Array<Array<ICardProps>>;
  chillah?: {
    tip: string;
    iconUrl: string;
    imageAlt?: string;
    width?: number;
  };
  background?: boolean;
  article?: boolean;
  titleStyle?: any;
  animate?: boolean;
  isUpdating?: boolean;
  color: string;
  isMobile?: boolean;
  locale?: string;
  tooltipLocation?: string;
  analyticsEvent?: Function;
}
export interface ICardSetState {
}

const mapStateToProps = (state: any, ownProps) => {
  return {
    ...ownProps,
    isMobile: state.app.mobile,
    locale: state.data.locale,
  };
};

export class CardSetImpl extends React.Component<ICardSetProps, ICardSetState> {

  renderIndividualCard = (card, index) => {
    const {
      cards,
      background,
      article,
      titleStyle,
      animate,
      isUpdating,
      color,
    } = this.props;

    return (
      <Card
        {...card}
        titleStyle={titleStyle}
        article={article}
        key={`card-${index}`}
        background={background}
        animate={animate}
        isUpdating={isUpdating} />
    );
  }

  handleClick(card) {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${whereAmI()}-card-${card.link}-${card.title}`,
        category: `${whereAmI()}-card-${card.title}`,
      });
    }
  }

  renderCardLink = (card, index, renderCard) => {

    let externalLink = -1;
    if (card.link) {
      if (card.link.indexOf('http') || card.link && card.link.indexOf('mailto')) {
        externalLink = 0;
      }
    }

    if (externalLink === -1) {
      return (
        <Link to={ `${(this.props.locale === '' || this.props.locale === 'en-us') ? card.link : '/l/' + this.props.locale + card.link }`  }  target={ card.target || '' } style={{display: `flex`}} onClick={ (evt) => this.handleClick(card) } >
          { renderCard(card, index) }
        </Link>
      );
    } else {
      return (
        <A href={ card.link } target="_blank" style={{display: `flex`, }} onClick={ (evt) => this.handleClick(card) }>
          { renderCard(card, index) }
        </A>
      );
    }
  }

  renderCards (cards) {
    const renderCard = this.renderIndividualCard;
    const renderLink = this.renderCardLink;
    return cards.map((card, index) => {
      return (
        <Div css={cardSetChild(this.props.background, card.link)} key={index} tabIndex={ card.link ? '0' : null }>
          { card.link ?
            renderLink(card, index, renderCard)
            :
            renderCard(card, index)
          }
        </Div>
      );
    });
  }

  renderRows () {
    const renderCard = this.renderIndividualCard;
    const renderLink = this.renderCardLink;
    const {
      rows,
      article,
      background,
    } = this.props;
    const tipColor = (this.props.color && this.props.color === 'Yellow') ? 'black' : (this.props.color && this.props.color.toLowerCase()) || 'black';
    return rows.map((cards, index) => {
      return (
        <Div key={`cardWrapper-${index}`} css={cardSetWrapper({article, background})}>
          { this.renderCards(cards) }
          {(this.props.chillah && index === 0) && !this.props.isMobile &&
              <ChillahTooltip
                animate
                iconUrl={this.props.chillah.iconUrl}
                imageAlt={this.props.chillah.imageAlt}
                left={`${utils.columns.desktopColumnWidth * 15.5}%`}
                top={-50}
                width={ this.props.chillah.width }
                color={tipColor}>
                {this.props.chillah.tip}
              </ChillahTooltip>
            }
        </Div>
      );
    });
  }

  render () {
    const {
      background,
      article,
      rows,
      cards,
    } = this.props;
    const tipColor = (this.props.color && this.props.color === 'Yellow') ? 'black' : (this.props.color && this.props.color.toLowerCase()) || 'black';
    let chillahLocation;
    if (this.props.tooltipLocation && this.props.tooltipLocation === 'topRight') {
      chillahLocation = `${utils.columns.desktopColumnWidth * 15.5}%`;
    } else if (this.props.tooltipLocation === 'topLeft') {
      chillahLocation = `-${utils.columns.desktopColumnWidth / 4}%`;
    } else if (this.props.tooltipLocation === 'insetTopLeft') {
      chillahLocation = `${utils.columns.desktopColumnWidth * 3.8}%`;
    }
    return (
      <Div css={cardLayoutWrapper()}>
        { rows ?
          this.renderRows()
          :
          <Div css={cardSetWrapper({article, background})}>
            { this.renderCards(cards) }
            {this.props.chillah && !this.props.isMobile &&
              <ChillahTooltip
                animate
                iconUrl={this.props.chillah.iconUrl}
                imageAlt={this.props.chillah.imageAlt}
                left={chillahLocation}
                top={-50}
                width={ this.props.chillah.width }
                color={tipColor}>
                {this.props.chillah.tip}
              </ChillahTooltip>
            }
          </Div>
        }
      </Div>
    );
  }
}
export const CardSet = connect(mapStateToProps)(CardSetImpl);



