import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as glamor from 'glamor';
const { mediaQueries, screenSizes } = utils;

const firstLine = glamor.keyframes({
    '0%': {
        transform: 'rotate(0)',
        top: '0%',
        marginLeft: 0,
    },
    '35%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '40%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '60%': {
        transform: 'rotate(55deg)',
        top: '50%',
        marginLeft: 1,
    },
    '100%': {
        transform: 'rotate(45deg)',
        top: '50%',
        marginLeft: 1,
    },
});

const firstLineReverse = glamor.keyframes({
    '0%': {
        transform: 'rotate(45deg)',
        top: '50%',
        marginLeft: 1,
    },
    '30%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '40%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '70%': {
        transform: 'rotate(0)',
        top: '-10%',
        marginLeft: 0,
    },
    '100%': {
        transform: 'rotate(0)',
        top: '0%',
        marginLeft: 0,
    },
});

const lastLine = glamor.keyframes({
    '0%': {
        transform: 'rotate(0)',
        top: '100%',
        marginLeft: 0,
    },
    '35%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '40%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '60%': {
        transform: 'rotate(-55deg)',
        top: '50%',
        marginLeft: -2,
    },
    '100%': {
        transform: 'rotate(-45deg)',
        top: '50%',
        marginLeft: -2,
    },
});

const lastLineReverse = glamor.keyframes({
    '0%': {
        transform: 'rotate(-45deg)',
        top: '50%',
        marginLeft: -2,
    },
    '30%': {
        transform: 'rotate(0)',
        top: '50%',
        marginLeft: 0,
    },
    '40%': {
        transform: 'rotate(0)',
        top: '50%',
    },
    '70%': {
        transform: 'rotate(0)',
        top: '110%',
    },
    '100%': {
        transform: 'rotate(0)',
        top: '100%',
    },
});

const middleLine = glamor.keyframes({
    '0%': {
        opacity: 1,
    },
    '30%': {
        opacity: 1,
    },
    '31%': {
        opacity: 0,
    },
    '100%': {
        opacity: 0,
    },
});

export const hamburger = (active) => ({
    height: 12,
    width: 15,
    marginTop: 3,
    position: 'relative',
    '& li': {
        background: 'transparent',
        boxShadow: `0 2px 0 0 ${colors.black}`,
        borderRadius: 2,
        height: 2,
        width: !active ? 15 : 16,
        position: 'absolute',
        transition: 'all .2s ease',
        marginTop: -2,
        '&:first-of-type': {
            marginBottom: 4,
            top: '0%',
            animation: active ? `${firstLine} 0.6s ease-out 1` : `${firstLineReverse} 0.4s ease-out 1`,
            animationFillMode: 'forwards',
        },
        '&:nth-of-type(2)': {
            top: '50%',
            animation: active ? `${middleLine} 0.6s ease-out 1` : 'none',
            animationFillMode: 'forwards',
        },
        '&:last-of-type': {
            marginBottom: 4,
            top: '100%',
            animation: active ? `${lastLine} 0.6s ease-out 1` : `${lastLineReverse} 0.4s ease-out 1`,
            animationFillMode: 'forwards',
        },
    },

});

export const hamburgerWrapper = () => ({
    cursor: 'pointer',
    padding: 16,
    outline: '0 dashed rgba(0,0,0,0.2)',
    '[data-whatinput="keyboard"] &:focus': {
        outlineWidth: '1px',
    },
});



