import * as React from 'react';
import * as marked from 'marked';
import * as glamorous from 'glamorous';

import { css } from 'glamor';

import * as styles from './Markdown-styles';

const {Div} = glamorous;

export interface IMarkdownProps {
    copy: string;
    styleOverrides?: Object;
}

const TAG_NAMES = [
    'h1',
    'h2',
    'h3',
    'h4',
    'h5',
    'h6',
    'a',
    'p',
    'span',
    'blockquote',
    'ul',
    'ol',
    'li',
    'pre',
    'code',
    'b',
    'i',
    'em',
    'strong',
    'sup',
    'sub',
    'cite',
    'br',
];

export class Markdown extends React.Component<IMarkdownProps, void> {

    getMarkdownText(copy: any): any {
        const rawMarkup = this.processMarkdown(copy);
        return { __html: rawMarkup };
    }

    processMarkdown(md: string): string {
      let processed: string = md;
      const keys = TAG_NAMES;
      keys.map((key) => {
        if (key && key !== 'copy' && key !== 'id') {
            const reg = new RegExp(`<${key}`, 'g');
            processed = processed.replace(reg, `<${key} `);
        }
      });
      return processed;
    }

    isTag(s: string): any {
        const cleanString = s.replace(new RegExp('  ', 'g'), ' ');
        let isTag = false;
        const keys = TAG_NAMES;
        let tagType = '';
        let closing = false;
        keys.map((key) => {
            if (key && key !== 'copy' && key !== 'id') {
                if ((key === cleanString.slice(0, key.length) && (((cleanString.slice(key.length, key.length + 1) === ' '))) || (key === cleanString.slice(1, key.length + 1) && (cleanString.slice(0, 1) === '/') ))) {
                    isTag = true;
                    tagType = key;
                    closing = cleanString.slice(0, 1) === '/';
                }
            }
        });

        return {
            isTag,
            tagType: isTag ? tagType : null,
            closing: closing,
        };
    }

    nestTags(elements: Array<any>): any {
        const tags = [];

        let inTag = false;
        let tagType = '';
        const tempArr = [];
        let tagStart = 0;
        for (let i = 0; i < elements.length; i++ ) {
            if (elements[i].isTag && !elements[i].closing && !elements[i].selfClosing && !inTag) {
                tagStart = i;
                tagType = elements[i].tagType;
                inTag = true;
            }
            if (elements[i].isTag && elements[i].closing && inTag && elements[i].tagType === tagType) {
                inTag = false;
                tags.push({
                    ...elements[tagStart],
                    children: this.nestTags(elements.slice(tagStart + 1, i)),
                });
            }
            if (!elements[i].isTag && !inTag) {
                tags.push(elements[i]);
            }
        }
        return tags;
    }

    renderElement(element, ind): any {
        let children = [];
        if (element.children) {
            children = element.children.map((child, index) => {
                return (this.renderElement(child, index));
            });
        }
        if (!element.isTag) {
            return element.value;
        } else {
            return React.createElement(
              element.tagType,
              this.generateAttributes(element.value, ind),
              children,
            );
        }
    }

    generateAttributes(props, ind): any {
        const attributes = {};
        const propsClean = props.replace(new RegExp('  ', 'g'), ' ');
        const split = propsClean.split('" ');
        split.forEach((spl) => {
            if (spl.slice(0, 1) === ' ') {
                spl = spl.slice(1);
            }
            const split2 = spl.split('=');
            if (split2[0] === 'class') {
                split2[0] = 'className';
            }
            if (split2[0] !== '') {
                attributes[split2[0]] = split2[1].replace(new RegExp('"', 'g'), ''); // remove quotes to prevent double quotes on href urls
            }
        });
        attributes['key'] = ind;
        if (attributes['href']) {
            if (attributes['href'].indexOf('http') > -1) {
                attributes['target'] = '_blank';
            }
        }
        return attributes;
    }

    render() {
        const md = this.getMarkdownText(this.props.copy);
        md.__html = md.__html.replace(new RegExp('<b r />', 'g'), '\n');
        const elements: Array<any> = [];
        const contents: Array<any> = [];
        const allElements: Array<any> = [];
        const split1 = md.__html.split('<');
        split1.forEach((str) => {
            const split2 = str.split('>');
            split2.forEach((str2, index) => {
                str2 = str2.replace(new RegExp('  ', 'g'), ' ');
                if (str2 !== '\n') {
                    const tagInfo = this.isTag(str2);
                    const modValue = tagInfo.isTag ? str2.slice(tagInfo.tagType.length + 1) : str2;
                    allElements.push({value: modValue, isTag: tagInfo.isTag, closing: tagInfo.closing, selfClosing: tagInfo.selfClosing, tagType: tagInfo.tagType });
                }
                if (index === 0) {
                    elements.push(str2);
                } else {
                    if (str2 !== '\n') {
                        contents.push(str2);
                    }
                }
            });
        });
        const nested: Array<any> = this.nestTags(allElements);
        return (
            <Div  css={styles.defaultStyles()} style={{whiteSpace: 'pre-line'}}>
                <Div css={styles.overideStyles(this.props.styleOverrides)} >
                    {
                        nested.map((element, index) => {
                            if (!element.isTag) {
                                return element.value;
                            } else {
                                return this.renderElement(element, index);
                            }
                        })
                    }
                </Div>
            </Div>
        );
    }
}



