import * as React from 'react';
import * as PropTypes from 'prop-types';
import * as _ from 'lodash';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
import { css } from 'glamor';
const { Div, Span } = glamorous;

import * as styles from './ChillahTooltip-styles';

import { Tooltip } from '../../atoms/Tooltip';
import { Markdown } from '../../atoms/Markdown';

export interface IChillahTooltipProps {
    children: any;
    iconUrl: string;
    imageAlt?: string;
    label?: string;
    left: any;
    top: any;
    delay?: number;
    reverse?: boolean;
    animate?: boolean;
    color?: string;
    width?: number;
    isMobile?: boolean;
}

export const ChillahTooltipTypes = {
    children: PropTypes.any.isRequired,
    iconUrl: PropTypes.string.isRequired,
    imageAlt: PropTypes.string,
    label: PropTypes.string,
    left: PropTypes.any.isRequired,
    top: PropTypes.any.isRequired,
    delay: PropTypes.number,
    reverse: PropTypes.bool,
    color: PropTypes.string,
};

export interface IChillahTooltipState {
    active: boolean;
    corner: string;
    direction: string;
    tooltipPostion: Object;
}

export class ChillahTooltip extends React.Component<IChillahTooltipProps, IChillahTooltipState> {

    static propTypes = ChillahTooltipTypes;

    state = {
        active: false,
        tooltip: null,
        chillah: null,
        corner: null,
        direction: null,
        tooltipPostion: {
            top: 0,
            left: 0,
        },
    };

    onResizeCallback: any;
    scrollCallback: any;
    tooltip: any;
    chillah: any;

    componentDidMount() {
        this.scrollCallback = _.throttle(this.setPosition, 300);
        this.onResizeCallback = _.throttle(this.onResize, 300);
        window.addEventListener('resize', this.onResizeCallback);
        window.addEventListener('scroll', this.scrollCallback);

        // Timeout to ensure that the chillah element has a width.
        setTimeout(() => {
            this.setPosition();
        }, 400);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.onResizeCallback);
        window.removeEventListener('scroll', this.scrollCallback);
    }

    @autobind
    onResize() {
        if (this.chillah) {
            this.setPosition();
        }
    }

    @autobind
    setPosition(callback?: Function) {
        const chillah = this.chillah.getBoundingClientRect();
        const tooltip = this.tooltip.getBoundingClientRect();
        const tooltipDimensions = {
            height: tooltip.height,
            width: tooltip.width,
            top: tooltip.top,
        };

        let top = this.state.tooltipPostion.top;
        let left = this.state.tooltipPostion.left;
        let direction = this.state.direction;
        let corner = this.state.direction;

        const chillahShouldBeTop = (chillah.top - 160) - tooltipDimensions.height <= 0;

        if (chillahShouldBeTop) {
            top = chillah.height;
            direction = 'top';
        } else {
            top = 0 - tooltipDimensions.height - 60;
            direction = 'bottom';
        }

        const chillahShouldBeLeft = chillah.left < window.innerWidth - tooltipDimensions.width - chillah.width;

        if (chillahShouldBeLeft) {
            left = chillah.width - 45;
            corner = 'left';
        } else {
            left = 0 - tooltipDimensions.width + 45;
            corner = 'right';
        }

        const chillahShouldUpdateDirection = direction !== this.state.direction;
        const chillahShouldUpdateCorner = corner !== this.state.corner;

        if (chillahShouldUpdateDirection || chillahShouldUpdateCorner) {
            this.setState({
                direction: direction,
                corner: corner,
                tooltipPostion: {
                    top: top,
                    left: left,
                },
            }, () => {
                if (callback && typeof callback === 'function') {
                    callback();
                }
            });
        }
        else if (callback && typeof callback === 'function') {
            callback();
        }
    }

    @autobind
    handleMouseEnter() {
        this.setState({ active: true });
    }

    @autobind
    handleMouseLeave() {
        this.setState({active: false});
    }

    @autobind
    handleClick() {
        this.setState({active: !this.state.active});
    }

    setTooltipRef = (tooltip) => {
        this.tooltip = tooltip;
    }

    setChillahRef = (chillah) => {
        this.chillah = chillah;
    }

    render() {
        return (
            <Div css={styles.chillahContainer(this.props.left, this.props.top)}>
                <Div
                    css={styles.tooltip(this.state.active, this.state.tooltipPostion)}
                    style={{
                        left: this.state.tooltipPostion.left,
                        top: this.state.tooltipPostion.top,
                        opacity: this.state.active ? 1 : 0,
                    }}
                >
                    <Tooltip
                        innerRef={this.setTooltipRef}
                        corner={this.state.corner}
                        direction={this.state.direction}
                        color={this.props.color || null}
                    >
                        <Markdown
                            copy={this.props.children}
                            styleOverrides={styles.markdownStyles()}
                        />
                    </Tooltip>
                </Div>
                <Div
                    innerRef={this.setChillahRef}
                    css={styles.chillahWrapper('0', '0', this.props.delay, this.props.reverse, this.props.animate)}
                    onMouseEnter={this.handleMouseEnter}
                    onMouseLeave={this.handleMouseLeave}
                    onClick={ this.handleClick }>

                    <img className={css(styles.chillah(this.props.width)).toString()} src={this.props.iconUrl} alt={this.props.imageAlt} />
                    { this.props.label && <Span css={styles.label()}>{this.props.label}</Span> }
                </Div>
            </Div>
        );
    }
}



