import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';

import { Markdown } from '../../atoms/Markdown';
import { CHAR } from 'utils/styles';

import * as styles from './QuoteBlock-styles';

const { Div, A, H1, P, Img } = glamorous;

export interface IQuoteBlockProps {
  imageContent?: IImageContentProps;
  attributionSource?: string;
  quoteCopy: string;
}

export interface IImageContentProps {
  imageUrl?: string;
  imageUrl2x?: string;
  title?: string;
  dimensions?: {
    height: number;
    width: number;
  };
}

export const QuoteBlockPropTypes = {
  imageContent: PropTypes.shape({
    imageUrl: PropTypes.string,
    imageUrl2x: PropTypes.string,
    title: PropTypes.string,
    dimensions: PropTypes.shape({
      height: PropTypes.number,
      width: PropTypes.number,
    }),
  }),
  quoteCopy: PropTypes.string.isRequired,
  attributionSource: PropTypes.string,
};

const openQuote: string = CHAR.RDQUO;
const closeQuote: string = CHAR.LDQUO;

export class QuoteBlock extends React.Component<IQuoteBlockProps, void> {
  render() {
    return (
      <Div css={styles.blockWrapper(this.props.imageContent)} >
        <Div css={styles.quoteMainCopyWrapper(this.props.imageContent)} >
          <Div css={styles.markdownWrapper(this.props.attributionSource)} >
            <Markdown copy={`<p>${this.props.attributionSource ? openQuote : ''}</p>${this.props.quoteCopy}<p>${this.props.attributionSource && closeQuote}</p>`} styleOverrides={styles.markdownStyles(this.props.attributionSource)} />
          </Div>
          {
            this.props.attributionSource &&
            <P css={styles.sourceCopy()} > {CHAR.HYPHEN} {this.props.attributionSource} </P>

          }
        </Div>
        {
          this.props.imageContent &&
          <Div css={styles.imageWrapper()} >
            <Div css={styles.imageBox()} >
              <Img src={this.props.imageContent.imageUrl} alt={this.props.imageContent.title} css={styles.image()} />
            </Div>
          </Div>
        }
      </Div>
    );
  }
}



