import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div } = glamorous;

import { Headline } from '../../molecules/Headline';

import * as styles from './PreFooter-styles';

export interface IPreFooterProps {
  backgroundColor: string;
  color?: string;
  height?: string;
  primary?: boolean;
  titleContent?: string;
  eyebrowContent?: {
    content: string;
  };
  analyticsEvent?: Function;
  buttonContent?: Array<{
    cta: string;
    color: string;
    outline?: boolean;
    filled?: boolean;
    link?: string;
    isSmall?: boolean;
    buttonStyle?: string;
  }>;
  sectionType?: string;
}

export class PreFooter extends React.Component<IPreFooterProps, void> {

    static propTypes = {
      backgroundColor: PropTypes.string.isRequired,
      color: PropTypes.string,
      height: PropTypes.string,
      titleContent: PropTypes.string,
      eyebrowContent: PropTypes.shape({
        content: PropTypes.string,
      }),
      buttonContent: PropTypes.arrayOf(PropTypes.shape({
        cta: PropTypes.string,
        color: PropTypes.string,
        outline: PropTypes.bool,
        filled: PropTypes.bool,
        link: PropTypes.string,
        isSmall: PropTypes.bool,
      })),
    };

    render() {
        return (
            <Div css={styles.colorWashBlock(this.props.backgroundColor, this.props.color, this.props.height)}>
              <Headline
                primary={this.props.primary}
                titleContent={this.props.titleContent}
                eyebrowContent={this.props.eyebrowContent}
                buttonContent={this.props.buttonContent}
                analyticsEvent={ this.props.analyticsEvent }
                sectionType={ this.props.sectionType }
                noMax
              />
            </Div>
        );
    }
}



