import * as React from 'react';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import { findDOMNode } from 'react-dom';
import { Dispatch } from '@types/react-redux';
import { analyticsEvent} from '../../../../state/actionCreators/data';
import { hideCookie } from '../../../../state/actionCreators/appCreator';

// Style & Util Imports
import * as colors from 'utils/styles/colors';
import { padding } from 'utils/styles';
import * as styles from './Section-styles';

// Component Imports
import { CardSet } from '../../atoms/Card';
import { Image } from '../../atoms/Image';
import { BannerImage } from '../../atoms/BannerImage';
import { Button } from '../../atoms/Button';
import { Video } from '../../atoms/Video';
import { Headline } from '../../molecules/Headline';
import { CopyWithImage } from '../../molecules/CopyWithImage';
import { DeviceMobileLockup } from '../../molecules/DeviceMobileLockup';
import { DevicesCollage } from '../../molecules/DevicesCollage';
import { LogoGarden } from '../../molecules/LogoGarden';
import { VideoProgressSection } from '../../molecules/VideoProgressSection';
import { TabOrganism } from '../../organisms/TabOrganism';
import { PreFooter } from '../../organisms/PreFooter';
import { StoriesGrid } from '../../organisms/StoriesGrid';
import { ErrorPage } from '../../organisms/ErrorPage';
import { LeadGenForm } from '../../molecules/LeadGenForm';
import { SubpageNav } from '../../molecules/SubpageNav';
import { CookieNotice } from '../../molecules/CookieNotice';
import { BlogGrid } from '../../organisms/BlogGrid';
import { QuoteBlock } from '../../molecules/QuoteBlock';
import { MetricCollection } from '../../molecules/MetricCollection';
import { NumberedList } from '../../atoms/NumberedList';
import { HeadlineCTA } from '../../molecules/HeadlineCTA';
import { BulletedList } from '../../atoms/BulletedList';
import { LeadInCopyBlock } from '../../molecules/LeadInCopyBlock';
import { MultiImage } from '../../molecules/MultiImage';
import { PageHeading } from '../../organisms/PageHeading';
import { CopyBlock } from '../../atoms/CopyBlock';
import { KeyLearningsBlock } from '../../molecules/KeyLearningsBlock';
import { SuccessStoryAdProduct } from '../../organisms/SuccessStoryAdProduct';
import { GridCardSet } from '../../atoms/GridCard';
import { AnnotationSection } from '../../atoms/AnnotationSection';
import { CopyWithStat } from '../../molecules/CopyWithStat';

const { Div } = glamorous;

// TODO: Create master component export file;
const components = {
  'error-page': ErrorPage,
  'home-page-headline': Headline,
  'sub-header': Headline,
  'tab-devices': TabOrganism,
  'image-with-copy': CopyWithImage,
  'pre-footer': PreFooter,
  'device-collage': DevicesCollage,
  'device-lockup-mobile': DeviceMobileLockup,
  'logo-garden': LogoGarden,
  'progress-video': VideoProgressSection,
  'lead-gen-form': LeadGenForm,
  'sub-nav': SubpageNav,
  'blog-grid': StoriesGrid,
  statsBesideText: CopyWithStat,
  cards: CardSet,
  image: Image,
  button: Button,
  video: Video,
  stories: StoriesGrid,
  blockQuote: QuoteBlock,
  stats: MetricCollection,
  numberedList: NumberedList,
  bulletedList: BulletedList,
  leadInCopyBlock: LeadInCopyBlock,
  multiImage: MultiImage,
  storiesGrid: StoriesGrid,
  gridCardSet: GridCardSet,
  headlineCTA: HeadlineCTA,
  textBlock: CopyBlock,
  keyLearnings: KeyLearningsBlock,
  adProductBlock: SuccessStoryAdProduct,
  metricCards: MetricCollection,
  metrics: MetricCollection,
  annotations: AnnotationSection,
  heroImage: BannerImage,
};

export interface ISectionProps {
  elements: Array<any>;
  index: number;
  title: string;
  color?: string;
  innerRef: any; // function
  routeId: string;
  analyticsEvent: any; // function
  pageColor: string;
  isMobile: boolean;
  pageType: string;
  params?: any;
}

export class Section extends React.PureComponent<ISectionProps, void> {

  el;

  componentDidMount() {
    this.handleRef(this.el);
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps !== this.props) {
      this.handleRef(this.el);
    }
  }

  generatePadding(index, total, type, formatting, props) {
    const prevElement = index !== 0 && this.props.elements[index - 1];
    const nextElement = index < this.props.elements.length - 1 && this.props.elements[index + 1];

    let top = 0;
    let bottom = 0;
    let left = padding.semi;
    let right = padding.semi;
    let align = 'inherit';

    // horizontal padding 0
    if (type === 'sub-header' ||
      type === 'home-page-headline' ||
      type === 'pre-footer' ||
      type === 'tab-devices' ||
      type === 'device-collage') {
      left = 0;
      right = 0;
    }
    // horizontal padding 0 - mobile
    if (type === 'adProductBlock') {
      left = this.props.isMobile ? 0 : padding.semi;
      right = this.props.isMobile ? 0 : padding.semi;
    }
    if (type === 'button') {
      align = 'center';
    }
    if (type === 'home-page-headline') {
      top = 0;
      bottom = 0;
    } else if (this.props.pageType === 'newsEntry' || this.props.pageType === 'insightsEntry') { // The blog demo pages have special rules not applied else where
      if (index !== 0 && prevElement.type === 'multiImage' && type === 'multiImage') {
        top = this.props.isMobile ? 0 : padding.small;
      } else if (index !== 0 && prevElement.type === 'multiImage' && type === 'textBlock') {
        top = this.props.isMobile ? padding.tiny : 40; // this is an very special edge case just for the everything pages
      } else if (index !== 0 && prevElement.type === 'keyLearnings' && type === 'annotations') {
        top = this.props.isMobile ? padding.normal : 56;
      } else if (index < this.props.elements.length - 1 && nextElement.type === 'textBlock' && type === 'leadInCopyBlock') {
        top = padding.semi;
      } else if (index !== 0) {
        top = this.props.isMobile ? padding.xlarge : padding.xxlarge;
      }
      if (type === 'storiesGrid') {
        top = 0;
        left = this.props.isMobile ? 0 : padding.xxlarge;
        right = this.props.isMobile ? 0 : padding.xxlarge;
      }
      if (type === 'heroImage') {
        left = 0;
        right = 0;
      }
    } else if (prevElement.type === 'sub-header' && type === 'headlineCTA') {
      top = 0;
      bottom = 0;
      left = this.props.isMobile ? padding.semi : padding.xxlarge;
      right = this.props.isMobile ? padding.semi : padding.xxlarge;
    } else if (type === 'metrics' && this.props.pageType !== 'insightsEntry' && this.props.pageType !== 'newsEntry') {
      top = padding.large;
      bottom = padding.large;
      left = this.props.isMobile ? padding.semi : padding.xxlarge;
      right = this.props.isMobile ? padding.semi : padding.xxlarge;
    } else if (type === 'headlineCTA') {
      top = padding.xlarge;
      bottom = 0;
      left = this.props.isMobile ? padding.semi : padding.xxlarge;
      right = this.props.isMobile ? padding.semi : padding.xxlarge;
    } else if (this.props.pageType === 'partners' && (type === 'storiesGrid' || type === 'heroImage')) {
      if (type === 'heroImage') {
        top = 0;
        bottom = 0;
        left = 0;
        right = 0;
      }
      if (type === 'storiesGrid') {
        bottom = padding.xlarge;
        top = padding.xlarge;
        left = padding.semi;
        right = padding.semi;
      }
    } else if (index === this.props.elements.length - 1 && !this.props.isMobile && (type === 'storiesGrid' || type === 'blog-grid')) {
      top = 0;
      bottom = padding.xlarge;
      left = this.props.isMobile ? 0 : padding.xxlarge;
      right = this.props.isMobile ? 0 : padding.xxlarge;
    } else if (type === 'storiesGrid') {
      top = 0;
      bottom = 0;
      left = this.props.isMobile ? 0 : padding.xxlarge;
      right = this.props.isMobile ? 0 : padding.xxlarge;
    } else if (type === 'blog-grid') {
      bottom = 0;
      left = this.props.isMobile ? 0 : padding.semi;
      right = this.props.isMobile ? 0 : padding.semi;
    } else if (this.props.pageType === 'home' && type === 'cards' && index === 0) {
      top = 0;
      bottom = 0;
    } else if (
      index < this.props.elements.length - 1 && nextElement.type === 'textBlock' && type === 'leadInCopyBlock' ||
      index !== 0 && prevElement.type === 'textBlock' && type === 'textBlock'
    ) {
      top = padding.semi;
      bottom = 0;
    } else if (index < this.props.elements.length - 1 && nextElement.type === 'cards' && nextElement.props.background === false && type === 'sub-header') {
      if (this.props.index === 2 && this.props.routeId === 'home') {
        top = padding.semi;
        bottom = 0;
      }
      else {
        top = padding.xxlarge;
        bottom = 0;
      }
    } else if (
      index < this.props.elements.length - 1 && nextElement.type === 'cards' && nextElement.props.background === false && type === 'sub-header' ||
      this.props.isMobile && index < this.props.elements.length - 1 && nextElement.type === 'progress-video' && type === 'sub-header'
      ) {
      top = padding.xlarge;
      bottom = 0;
    } else if (type === 'leadInCopyBlock' || type === 'textBlock' || type === 'metricCards') {
      top = padding.semi;
      bottom = padding.semi;
    } else if (index === 0 && this.props.pageType !== 'successStory' && type === 'heroImage') {
      bottom = padding.semi;
      top = 0;
      left = 0;
      right = 0;
    } else if (type === 'heroImage') {
      bottom = padding.semi;
      top = padding.semi;
      left = 0;
      right = 0;
    } else if (type === 'blockQuote') {
      if (this.props.pageType !== 'insightsEntry' && this.props.pageType !== 'newsEntry') {
        bottom = padding.large;
        top = padding.large;
        left = this.props.isMobile ? padding.semi : padding.xlarge;
        right = this.props.isMobile ? padding.semi : padding.xlarge;
      } else {
        bottom = padding.large;
        top = padding.large;
        left = 0;
        right = 0;
      }
    } else if (index !== 0 && prevElement.type === 'cards' && type === 'cards') {
      top = 0;
      bottom = padding.xlarge;
    } else if (index < this.props.elements.length - 1 && nextElement.type === 'cards' && type === 'cards') {
      top = 0;
      bottom = 0;
    } else if (
      index !== 0 && prevElement.type === 'sub-header' && type === 'video' ||
      index !== 0 && prevElement.type === 'sub-header' && type === 'device-collage' ||
      index !== 0 && prevElement.type === 'sub-header' && type === 'tab-devices' ||
      index !== 0 && prevElement.type === 'sub-header' && type === 'image-with-copy' ||
      index !== 0 && prevElement.type === 'sub-header' && type === 'image' ||
      index !== 0 && prevElement.type === 'sub-header' && type === 'progress-video' ||
      index !== 0 && prevElement.type === 'cards' && type === 'cards' ||
      index !== 0 && prevElement.type === 'cards' && type === 'sub-header' ||
      index < this.props.elements.length - 1 && nextElement.type !== 'storiesGrid' && index !== 0 && prevElement.type === 'image' && type === 'sub-header'
      ) {
      top = 0;
      bottom = padding.xlarge;
    } else if (index !== 0 && prevElement.type === 'sub-header' && type === 'cards') {
      if (this.props.index === 2 && this.props.routeId === 'home') {
        top = 0;
        bottom = padding.xlarge;
      }
      else {
        top = 0;
        bottom = padding.xxlarge;
      }
    } else if (
      index < this.props.elements.length - 1 && nextElement.type === 'sub-header' && this.props.pageType !== 'successStories' && type === 'image' ||
      this.props.pageType !== 'successStories' && this.props.pageType !== 'creativeSpotlight' && index < this.props.elements.length - 1 && nextElement.type === 'storiesGrid' && type === 'sub-header'
      ) {
      top = padding.xlarge;
      bottom = 0;
    } else if (
      type === 'pre-footer' ||
      this.props.routeId === 'detailPage' && type === 'image' ||
      this.props.pageType === 'successStories' && type === 'image'
      ) {
      top = 0;
      bottom = 0;
    } else if (index === total) {
      top = index === 0 ? padding.xxlarge : this.props.isMobile ? padding.large : padding.xlarge;
      bottom = padding.xxlarge;
    } else if (index === 0) {
      top = padding.xxlarge;
      bottom = index === total ? padding.xxlarge : padding.xlarge;
    } else {
      top = padding.xlarge;
      bottom = padding.xlarge;
    }

    if (type === 'pre-footer' && prevElement.type === 'storiesGrid' && this.props.isMobile) {
      top = 0;
    }

    return { top, bottom, left, right, align };
  }

  renderElements() {
    return this.props.elements.map((element, elementIndex) => {
      if (element && element.type && components[element.type]) {
        const padding = this.generatePadding(elementIndex, this.props.elements.length - 1, element.type, element.formatting, element.props);
        return (
          <Div className={element.type} css={[styles.formatHolder(element.formatting, element.type, elementIndex, padding, this.props.pageType, element)]} key={elementIndex}>
            {React.createElement(components[element.type], {
              ...element.props,
              sectionType: element.type,
              routeData: this.props.routeId,
              analyticsEvent: this.props.analyticsEvent,
              section: this.props.title,
              params: this.props.params,
            })}
          </Div>
        );
      } else {
        if (element && element.type) {
          console.log('no template for: ', element.type);
        }
      }
    });
  }

  @autobind
  handleRef(node) {
    this.props.innerRef(node, this.props.index);
  }

  render() {
    return (
      <Div
        innerRef={(el) => this.el = el}
        css={[
          styles.section(this.props.color),
          (this.props.routeId === 'home' || this.props.routeId === 'odg') && this.props.index === 0 && styles.navigationHeader(this.props.pageColor, 630),
        ]}
        className="section"
      >
        {this.renderElements()}
      </Div>
    );
  }
}



