import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as utilStyles from 'utils/styles';

const { mediaQueries } = utilStyles;

export const image = () => ({
  width: '100%',
  height: 'auto',
  display: 'block',
  [`${mediaQueries.phablet}`]: {
    width: 'auto',
    height: '430px',
    position: 'relative',
    transform: 'translateX(-50%)',
    left: '50%',
  },
  [`${mediaQueries.normal}`]: {
    width: '100%',
    height: 'auto',
    position: 'relative',
    transform: 'none',
    left: 'auto',
  },
});

export const bannerContainer = () => ({
  overflowX: 'hidden',
  [`${mediaQueries.normal}`]: {
    overflowX: 'visible',
  },
});



