import { autobind } from 'core-decorators';

// gsap & hammerjs use dom, they are ignored via webpack.
import { TweenMax } from 'gsap';
import * as Hammer from 'hammerjs';

import * as React from 'react';
import { CarouselProptypes, ICarouselItemPosition, ICarouselProps, ICarouselRefs } from './';
import { Carousel as Base } from './Carousel-base';

export interface ICarouselState {
  active: number;
}

export class Carousel extends React.Component<ICarouselProps, ICarouselState> {
  childRefs: ICarouselRefs;
  hammerManager: HammerManager;
  hammerRecognizer: PanRecognizer;
  hammerTapRecognizer: TapRecognizer;
  initialized: boolean = false;
  panStart: number = 0;
  panDeltaX: number = 0;
  draggingTimeout: number;
  snapTimeout: number;
  mounted: boolean = false;

  state = {
    active: this.props.initialIndex,
  };

  static propTypes = CarouselProptypes;

  static defaultProps = {
    initialIndex: 0,
  };

  componentDidMount() {
    this.mounted = true;
  }

  componentWillUnmount() {
    if (this.childRefs && this.childRefs.container) {
      TweenMax.killTweensOf(this.childRefs.container);
    }
    this.mounted = false;
  }

  initInteractions(): void {
    if (this.props.children.length > 1) {
      this.hammerManager = new Hammer.Manager(this.childRefs.outerContainer, { domEvents: true });
      this.hammerRecognizer = new Hammer.Pan();
      this.hammerRecognizer.set({ direction: Hammer.DIRECTION_HORIZONTAL });
      this.hammerManager.add(this.hammerRecognizer);
      this.hammerTapRecognizer = new Hammer.Tap();
      this.hammerManager.add(this.hammerTapRecognizer);

      this.hammerManager.on('panstart', this.handlePanStart);
      this.hammerManager.on('panleft', this.handlePan);
      this.hammerManager.on('panright', this.handlePan);

      this.hammerManager.on('panend', this.handlePanEnd);
      if (!((typeof window !== 'undefined') ? ('ontouchstart' in window) : false)) {
        this.hammerManager.on('tap', this.handleTap);
      }
    }
    this.initialized = true;
  }

  @autobind
  handleTap(e: HammerInput) {
    const x = e.center.x;
    const center = this.childRefs.outerContainerBounds.left +
      (this.childRefs.outerContainerBounds.width * 0.5);
    let snapTo = 0;
    if (x < center && this.state.active > 0) {
      if (this.props.onChange) { this.props.onChange(this.state.active - 1); }
      this.setState({ active: this.state.active - 1 });
    }

    if (x > center && this.state.active < this.childRefs.positions.length - 1) {
      if (this.props.onChange) { this.props.onChange(this.state.active + 1); }
      this.setState({ active: this.state.active + 1 });
    }

    if (this.props.snapCenter) {
      snapTo = center -
        this.childRefs.positions[this.state.active].left -
        (this.childRefs.positions[this.state.active].width * 0.5);
    } else {
      snapTo = 0 - this.childRefs.positions[this.state.active].left; // Note this is for left snap
    }

    if ((snapTo + this.childRefs.containerWidth < this.childRefs.outerContainerBounds.width) &&
      (this.childRefs.containerWidth > this.childRefs.outerContainerBounds.width)) {
      snapTo = this.childRefs.outerContainerBounds.width - this.childRefs.containerWidth;
    }

    if ((this.childRefs.containerWidth < this.childRefs.outerContainerBounds.width) && this.props.snapCenter) {
      snapTo = 0;
    }
    snapTo = !this.props.snapCenter ? Math.min(snapTo, 0) : snapTo;
    this.snap(0.001, snapTo, 0.5);
  }

  @autobind
  handlePanStart(e: HammerInput) {
    if (e.eventType === 2) {
      if (this.draggingTimeout) { clearTimeout(this.draggingTimeout); }
      TweenMax.killTweensOf(this.childRefs.container);
    }
    this.panStart = this.childRefs.container.getBoundingClientRect().left;
  }

  @autobind
  handlePan(e: HammerInput) {
    if (e.eventType === 2 && Math.abs(e.deltaY) < 30) {
      if ((this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left <= 0 && (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left >= -(this.childRefs.containerWidth - this.childRefs.outerContainerBounds.width)) {
        this.panDeltaX = e.deltaX;
        this.childRefs.container.style.left = `${ (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left }px`;
      }
    }
  }

  @autobind
  handlePanEnd(e: HammerInput) {
    if (Math.abs(e.deltaY) < 30) {
      if ((this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left <= 0 && (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left >= -(this.childRefs.containerWidth - this.childRefs.outerContainerBounds.width)) {
        this.panDeltaX = e.deltaX;
        this.childRefs.container.style.left = `${ (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left }px`;
        this.snap(e.velocityX, (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left);
      } else {
        this.panDeltaX = e.deltaX;
        if (this.state.active !== 0 && this.state.active !== this.childRefs.positions.length - 1) {
          this.snap(e.velocityX, (this.panStart + e.deltaX) - this.childRefs.outerContainerBounds.left);
        }
      }
      // AnalyticsStore.sendEvent(this.props.trackingCategory, 'swipe-carousel', this.props.trackingCarouselLabel);
      this.draggingTimeout = window.setTimeout(() => { this.stopDragging(); }, 10);
    }
  }

  @autobind
  stopDragging(): void {
    // InteractionStore.dragging = false;
  }

  @autobind
  snap(velocity: number, positionX: number, speed: number = 0.5) {
    if (this.mounted) {
      const multipliedVelocity: number = velocity * 200;
      const center: number = (this.childRefs.outerContainerBounds.width * 0.5)
        + this.childRefs.outerContainerBounds.left;
      let targetThrow: number = positionX + multipliedVelocity;
      let targetX: number;
      let targetIndex: number = 0;
      if (targetThrow > 0 && this.childRefs.containerWidth > this.childRefs.containerBounds.width) {
        targetX = 0;
        targetIndex = 0;
        targetThrow = 0;
      } else {
        this.childRefs.positions.forEach((position, index) => {
          let diff: number;
          if (this.props.snapCenter) {
            diff = Math.abs((position.left + targetThrow + (position.width * 0.5)) - center);
          } else {
            diff = Math.abs(position.left + targetThrow);
          }
          if (!targetX || diff < targetX) {
            targetX = diff;
            targetIndex = index;
          }
        });
        if (this.props.snapCenter) {
          targetThrow = center - this.childRefs.positions[targetIndex].left -
            (this.childRefs.positions[targetIndex].width * 0.5);
        } else {
          // Note this is for left snap
          targetThrow = 0 - this.childRefs.positions[targetIndex].left;
        }

        if ((targetThrow + this.childRefs.containerWidth) <
          this.childRefs.outerContainerBounds.width &&
          (this.childRefs.containerWidth > this.childRefs.outerContainerBounds.width)) {
          targetThrow = this.childRefs.outerContainerBounds.width - this.childRefs.containerWidth;
        }

        if ((this.childRefs.containerWidth < this.childRefs.outerContainerBounds.width) &&
          this.props.snapCenter) {
          targetThrow = 0;
        }
      }
      targetThrow = !this.props.snapCenter ? Math.min(targetThrow, 0) : targetThrow;
      TweenMax.killTweensOf(this.childRefs.container);
      TweenMax.to(this.childRefs.container, speed, { left: targetThrow });
      this.panStart = 0;
      if (this.props.onChange) { this.props.onChange(targetIndex); }
      if (targetIndex !== this.state.active) {
        this.setState({ active: targetIndex });
      }
    }
  }

  @autobind
  setActiveIndex() {
    const center = this.childRefs.outerContainerBounds.left + (this.childRefs.outerContainerBounds.width * 0.5);
    let snapTo;
    if (this.props.snapCenter) {
      snapTo = center -
        this.childRefs.positions[this.state.active].left -
        (this.childRefs.positions[this.state.active].width * 0.5);
    } else {
      snapTo = 0 - this.childRefs.positions[this.state.active].left;
    }

    if ((snapTo + this.childRefs.containerWidth < this.childRefs.outerContainerBounds.width) &&
      (this.childRefs.containerWidth > this.childRefs.outerContainerBounds.width)) {
      snapTo = this.childRefs.outerContainerBounds.width - this.childRefs.containerWidth;
    }

    if ((this.childRefs.containerWidth < this.childRefs.outerContainerBounds.width) && this.props.snapCenter) {
      snapTo = 0;
    }
    snapTo = !this.props.snapCenter ? Math.min(snapTo, 0) : snapTo;
    this.childRefs.container.style.left = snapTo + 'px';
    this.props.onChange(this.state.active);
  }

  @autobind
  refsReady(refs: ICarouselRefs) {
    this.childRefs = refs;
    if (!this.initialized) {
      this.initInteractions();
      this.childRefs.outerContainer.style.opacity = '1';
    } else {
      window.clearTimeout(this.snapTimeout);
      this.snapTimeout = window.setTimeout(() => {
        this.setActiveIndex();
      }, 0);
    }
  }

  render() {
    return (
      <Base { ...this.props } active={ this.state.active } refsReady={ this.refsReady } containerWidth={this.props.containerWidth}/>
    );
  }
}



