import glamorous from 'glamorous';
import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { hexToRGB } from 'utils/helpers';
import * as glamor from 'glamor';

const videoIconWhite = require('../assets/video_icon_white.svg');
const videoIconBlack = require('../assets/video_icon_black.svg');

const { mediaQueries, screenSizes, padding } = utils;
const fontSize = '19px';
const cardMargin = 8;
export const spanBottomMargin = 2;

const slideFade = glamor.keyframes({
  '100%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
});

const opacityFade = glamor.keyframes({
  '0%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
  '100%': {
    transform: 'translateY(0)',
    opacity: 0,
  },
});

export const cardWrapper = {
  cursor: `pointer`,
  position: 'relative',
  overflow: 'hidden',
  fontSize: fontSize,
  lineHeight: null,
  display: 'block',
  transition: 'transform .2s linear, opacity .2s linear',
  letterSpacing: utils.letterSpacing,
  marginBottom: cardMargin,
  backgroundSize: 'cover',
  backgroundRepeat: 'no-repeat',
  '&.animate': {
    transform: 'translateY(1em)',
    opacity: 0,
    animation: `${slideFade} 0.3s ease-in-out forwards`,
  },
  '&.animate.is-updating': {
    animation: `${opacityFade} 0.3s ease-in-out forwards`,
  },
  [`${mediaQueries.phablet}`]: {
    marginBottom: cardMargin,
  },
  '& .content-span:before': {
    content: `''`,
    opacity: 0,
    background: 'linear-gradient(to right, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 1) 50%)',
    backgroundSize: '200%',
    transition: 'background-position .4s ease-in-out, opacity .4s ease-in-out',
    backgroundPosition: '0 0',
    position: 'absolute',
    left: 0,
    height: '100%',
    width: '100%',
    zIndex: -1,
    borderRadius: 4,
  },
  [`${mediaQueries.normal}`]: {
    '&:hover': {
      '& .card-subhead-icon': {
        background: `url(${videoIconBlack})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'contain',
      },
      '& .cs-subhead': {
        opacity: 0,
      },
      '& .content-span': {
        color: colors.black,
      },
      '& .content-span:before': {
        opacity: 1,
        backgroundPosition: '-100% 0',
      },
      '& .card-cta': {
        opacity: 1,
      },
      '& .card-image': {
        '& .card-logo-image': {
          opacity: 1,
          transform: 'scale(1)',
          willChange: 'transform',
        },
        '& .card-featured-image': {
          opacity: 1,
          transform: 'scale(1)',
          willChange: 'transform',
        },
      },

      '&:not(.featured)': {
        '&.creativeSpotlightDevice .card-content': {
          opacity: 1,
        },
        '&.successStory .card-image': {
          '& .card-logo-image': {
            opacity: 0,
          },
        },
      },
    },
  },
};

export const cardContent = {
  position: 'absolute',
  bottom: 32,
  zIndex: 1,
  padding: '0 32px',

  [`${mediaQueries.phablet}`]: {
    padding: '0 44px',
  },

  [`${mediaQueries.normal}`]: {
    transition: 'transform .2s ease-out, opacity .4s ease-in-out',
    bottom: 8,
    ':not(.featured).creativeSpotlightDevice &': {
      opacity: 0,
    },
    '.grid-card:hover &': {
      transform: `translateY(-42px)`,
    },
  },
};

export const cardTitle = {
  fontSize: 24,
  lineHeight: '32px',
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-Medium',
  display: 'inline-block',
  padding: '8px 0 0',
  // Below are custom breakpoints for grid card titles to account for text overlapping logos at specific widths
  [`@media (max-width:899px) and (min-width:${screenSizes.phablet}px)`]: {
    fontSize: 20,
    lineHeight: '24px',
  },
  [`@media (max-width:1072px) and (min-width:${screenSizes.normal}px)`]: {
    fontSize: 18,
    lineHeight: '22px',
  },
  [`@media (max-width:1248px) and (min-width:1073px)`]: {
    fontSize: 20,
    lineHeight: '24px',
  },
};

export const cardSubheadIcon = {
  display: 'inline-block',
  height: 12,
  width: 12,
  background: `url(${videoIconWhite})`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  position: 'relative',
  marginRight: 8,
  top: 2,
};

export const cardSubhead = {
  padding: '1rem 0 0',
  fontSize: 12,
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-DemiBold',
  letterSpacing: '0.2px',
  '& .content-span-wrapper': {
    paddingBottom: 2,
  },
};

export const creativeSpotlightSubhead = {
  position: 'absolute',
  bottom: '10%',
  left: '50%',
  transform: 'translateX(-50%) translateY(-50%)',
  transition: 'opacity .3s ease-out',
};

export const cardCTA = {
  padding: '1rem 0 0',
  margin: '0 5px',
  fontSize: 12,
  fontWeight: 'normal',
  fontFamily: 'AvenirNext-Bold',
  letterSpacing: '1.4px',
  textTransform: 'uppercase',
  opacity: 1,
  [`${mediaQueries.phablet}`]: {
    opacity: 0,
  },
};

export const cardCtaIcon = {
  display: 'inline-block',
  height: 12,
  width: 12,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  position: 'relative',
  marginLeft: 16,
  top: 2,
};

export const mobileLogoImage = {
  display: 'none',
  width: 160,
};

export const cardImage = {
  backgroundRepeat: 'no-repeat',

  '& .card-featured-image': {
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundSize: 'contain',
    position: 'absolute',
    height: '80%',
    width: '100%',
    top: 0,
    transition: 'transform .3s ease-out, opacity .3s ease-out',
    opacity: 1,

    '&:not(.creativeSpotlightDevice)': {
      opacity: 0,
      transform: 'scale(.9)',
    },
  },

  '& .card-logo-image': {
    position: 'absolute',
    top: 0,
    height: '100%',
    width: '100%',
    backgroundRepeat: 'no-repeat',
    transition: 'transform .3s ease-out, opacity .3s ease-out',
  },
};

export const cardCopy = (article: boolean = false) => ({
  color: colors.regularCharcoal,
  fontSize: 16,
  lineHeight: '24px',
  padding: '0.5rem 2rem 0',
  boxSizing: 'border-box',
  paddingBottom: article && 42,
  [`${mediaQueries.phablet}`]: {
    fontSize: !article ? 19 : 16,
    lineHeight: !article ? '28px' : '24px',
  },
});

export const superscript = {
  transform: 'translateY(-20%)',
  display: 'inline-block',
  fontSize: '50%',
  verticalAlign: 'top',
};

export const contentContainer = (isLoaded = false) => ({
  display: 'block',
  opacity: isLoaded ? 1 : 0,
  transition: 'opacity .3s ease-out',
});

export const contentSpanWrapper = {
  display: 'table',
  position: 'relative',
  marginBottom: spanBottomMargin,
  padding: '0 4px',
};



