import { css } from 'glamor';
import * as colors from 'utils/styles/colors';

export const buttonStyles = ({color, outline, filled, isSmall}: any) => ({
    borderRadius: '100px',
    fontFamily: 'AvenirNext-Bold, sans-serif',
    letterSpacing: '1.4px',
    appearance: 'none',
    color: outline ? colors[color] : (filled && color !== 'regularYellow' && color !== 'white' && color !== 'regularWhite') ? colors.white : colors.black,
    textTransform: 'uppercase',
    cursor: 'pointer',
    height: isSmall ? null : '64px',
    minHeight: isSmall && 48,
    padding: isSmall ? '0 32px' : '0 64px',
    fontSize: isSmall ? '11px' : '14px',
    border: outline ? '2px solid transparent' : 'none',
    borderColor: outline ? colors[color] : 'transparent',
    position: 'relative',
    zIndex: outline ? 'initial' : '100',
    backgroundColor: filled ? 'none' : outline ? colors.white : colors[color],
    backgroundImage: filled ? colors[`${color}Gradient`] : 'none',
    backgroundClip: 'padding-box',
    transition: 'all .4s ease',
    '&:hover, &:focus': {
        boxShadow: (filled && color === 'regularYellow') ? '0 4px 24px 0 rgba(0,0,0,0.16)' : filled ? 'none' : '0 4px 24px 0 rgba(0,0,0,0.16)',

        color: outline ? colors[color] : (filled && color !== 'regularYellow' && color !== 'white' && color !== 'regularWhite') ? colors.white : colors.black,

        backgroundColor: filled ? 'transparent' : colors.white,
        '&::before': {
            opacity: '1',
        },
        '&::after': {
            opacity: '0',
        },
    },
    '&::before': {
        content: outline || filled ? `''` : 'none',
        position: 'absolute',
        backgroundImage: colors[`${color}GradientHover`],
        zIndex: '-1',
        top: outline ? '-2px' : '0',
        bottom: outline ? '-2px' : '0',
        left: outline ? '-2px' : '0',
        right: outline ? '-2px' : '0',
        opacity: '0',
        borderRadius: outline ? '100px' : 'inherit',
        transition: 'opacity .4s ease',

    },
    '&::after': {
        content: outline ? `''` : 'none',
        position: 'absolute',
        borderRadius: 'inherit',
        transition: 'opacity .4s ease',
        top: '-2px',
        bottom: '-2px',
        left: '-2px',
        right: '-2px',
        backgroundImage: colors[`${color}Gradient`],
        zIndex: '-1',
        opacity: '1',
    },
    outline: '0 dashed rgba(0,0,0,0.2)',
    '[data-whatinput="keyboard"] &:focus': {
        outlineWidth: '1px',
    },
});



