import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div, Ol, Li } = glamorous;

import * as styles from './NumberedList-styles';

export interface INumberedListProps {
  items: Array<string>;
  css?: any;
}

export const NumberedListPropTypes = {
  items: PropTypes.array.isRequired,
  css: PropTypes.object,
};

export class NumberedList extends React.Component<INumberedListProps, void> {

  static propTypes = NumberedListPropTypes;

  render() {
    return (
      <Ol css={[styles.listStyle(), this.props.css]}>
        {this.props.items.map((item, i) => {
          return <Li key={i} css={styles.listItem(i + 1)}>{item}</Li>;
        })}
      </Ol>
    );
  }
}



