import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

export const container = () => ({
  backgroundColor: 'transparent',
  maxWidth: `${utils.columns.desktopMaxWidth}px`,
  margin: `0 auto`,
  [`${utils.mediaQueries.phablet}`]: {
    display: `flex`,
    justifyContent: `space-between`,
    alignItems: `center`,
    padding: `0 ${utils.padding.tiny}px`,
  },
  [`${utils.mediaQueries.normal}`]: {
    padding: `0 ${utils.padding.semi}px`,
  },
});

export const confettiStatContainer = () => ({
  backgroundColor: '#f9f9f9',
  boxShadow: `0px 0px 50px 0px rgba(0, 0, 0, 0.05)`,
  borderRadius: utils.borderRadius.mobileContainer,
  [`${utils.mediaQueries.phablet}`]: {
    width: `58%`,
    maxWidth: `600px`,
  },
});

export const copyContainer = () => ({
  textAlign: 'center',
  marginBottom: `${utils.padding.large}px`,
  [`${utils.mediaQueries.phablet}`]: {
    width: `40%`,
    textAlign: `left`,
    marginBottom: `0`,
  },
});

export const headlineWrapper = () => ({
  marginBottom: `${utils.padding.small}px`,
});

export const headlineMarkdown = () => ({
  [' p']: {
    ...type.headline2,
  },
  [' h1']: {
    ...type.headline2,
  },
  [' h2']: {
    ...type.headline2,
  },
  [' h3']: {
    ...type.headline2,
  },
});

export const paragraphWrapper = () => ({
  marginBottom: `${utils.padding.small}px`,
});

export const buttonWrapper = () => ({
  width: `100%`,
  '& button': {
    width: '100%',
  },
  [`${utils.mediaQueries.phablet}`]: {
    '& button': {
      width: 'auto',
    },
  },
});

export const specLinkHolder = () => ({
    marginTop: '20px',
});

export const specsLink = (color) => ({
    fontWeight: 600,
    textTransform: 'capitalize',
    color: color,
    transition: 'opacity 0.2s ease-out',
    '& span': {
      borderBottom: `2px solid ${color}`,
      verticalAlign: 'middle',
      fontSize: '12px',
      lineHeight: '16px',
      paddingBottom: '2px',
    },
    '&:hover': {
        opacity: 0.5,
    },
});



