import * as React from 'react';
import * as glamorous from 'glamorous';

import * as styles from './LeadInCopyBlock-styles';
import { Markdown } from '../../atoms/Markdown';
const {H2} = glamorous;

export interface ILeadInCopyBlockProps {
  copy: string;
}

export class LeadInCopyBlock extends React.Component<ILeadInCopyBlockProps, void> {

  static propTypes = {
    copy: React.PropTypes.string,
  };

  render() {
    return (
      <Markdown copy={`${this.props.copy}`}
        styleOverrides={{
        [' h2']: styles.leadCopy(),
      }} />
    );
  }
}



