import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries, columns } = utils;

export const devicesCollageStyle = (twoDevices) => ({
  width: '100vw',
  overflow: 'hidden',
  display: 'block',
  [`${mediaQueries.phablet}`]: {
    position: 'relative',
    overflow: 'visible',
    width: '100%',
    maxWidth: twoDevices ? 980 : 1240,
    padding: `0 ${columns.desktopColumnWidth}%`,
    boxSizing: 'border-box',
    margin: '0 auto',
    display: 'flex',
    '& > div': {
      flex: '1 0 30%',
      '&:not(:last-child)': {
        marginRight: twoDevices ? null : `${columns.desktopColumnWidth}%`,
      },
    },
  },
});

export const carouselContainer = (clientWidth, twoDevices) => ({
  display: 'block',
  width: `calc(${clientWidth}px - 160px)`,
  maxWidth: '311px',
  height: 'auto',
  margin: twoDevices ? '0 auto' : null,
});



