import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries } = utils;

export const footerWrapper = () => ({
  backgroundColor: colors.white,
  padding: '0px 24px',
});

export const footerContainer = () => ({
  maxWidth: utils.columns.desktopMaxWidth,
  margin: '0 auto',
  display: 'flex',
  flexDirection: 'column',
  textAlign: 'center',
  padding: '13px 0 0 0',
  [`${mediaQueries.phablet}`]: {
    flexDirection: 'row',
    textAlign: 'left',
    padding: '29px 0 0 0',
  },
});

export const innerWrapper = () => ({
  display: 'flex',
  flex: 4,
  flexWrap: 'no-wrap',
  flexDirection: 'column',
  paddingTop: 35,
  margin: '0 -2%',
  paddingBottom: 48,
  [`${mediaQueries.phablet}`]: {
    flexWrap: 'wrap',
    flexDirection: 'row',
    paddingTop: 0,
    paddingBottom: 64,
  },
});

export const logoWrapper = () => ({
  width: '100%',
  paddingTop: 35,
  [`${mediaQueries.phablet}`]: {
    width: '20%',
    display: 'table',
    paddingLeft: '8px',
    paddingRight: '32px',
    textAlign: 'center',
  },
});

export const linkWrapper = () => ({
  flex: 'auto',
});

export const header = () => ({
  fontSize: 19,
  paddingBottom: 8,
});

export const list = () => ({
    flex: '1 0 auto',
    padding: '35px 2% 0 2%',
});

export const listItem = () => ({
  fontSize: 19,
  lineHeight: '32px',
  color: colors.regularCharcoal,
  [`${mediaQueries.phablet}`]: {
    fontSize: 16,
    lineHeight: '28px',
    color: colors.regularCharcoal,
  },
});

export const annotationSection = () => ({
  flex: '0 1 100%',
  marginTop: 16,
  paddingTop: 0,
  paddingBottom: 64,
  [`${mediaQueries.phabletMax}`]: {
    paddingBottom: 48,
  },
});

export const adManager = () => ({
  width: '100%',
  textAlign: 'center',
  padding: '27px 0',
  backgroundColor: colors.backgroundGray,
  '& a': {
    fontWeight: 'normal',
    fontFamily: 'AvenirNext-DemiBold',
    fontSize: '12px',
    textDecoration: 'underline',
  },
  '& .ad-manager-copy': {
    fontSize: '19px',
    paddingRight: '1rem',
    [utils.mediaQueries.phabletMax]: {
      paddingBottom: '1rem',
    },
  },
  [utils.mediaQueries.phabletMax]: {
    '& span': {
      display: 'block',
    },
  },
});

export const localeSelectWrapper = () => ({
  flex: '1 0 100%',
});

export const localeSelectContainer = () => ({
  display: 'inline-block',
  position: 'relative',
});

export const languageSelect = {
  appearance: 'none',
  border: '1px solid #CCCCCC',
  borderRadius: 5,
  background: colors.white,
  marginTop: 10,
  padding: '5px 25px 5px 10px',
  fontSize: 14,
  color: 'rgba(0, 0, 0, 0.6)',
  height: 34,
  cursor: 'pointer',
  '&::-ms-expand': {
    display: 'none',
  },
};

export const upArrow = () => ({
  position: 'absolute',
  right: 10,
  width: 0,
  height: 0,
  pointerEvents: 'none',
  borderStyle: 'solid',
  bottom: 18,
  borderWidth: '0 3px 5px 3px',
  borderColor: 'transparent transparent #666666 transparent',
});

export const downArrow = () => ({
  position: 'absolute',
  right: 10,
  width: 0,
  height: 0,
  pointerEvents: 'none',
  borderStyle: 'solid',
  bottom: 10,
  borderWidth: '5px 3px 0 3px',
  borderColor: '#666666 transparent transparent transparent',
});



