import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries } = utils;

export const filtersContainer = (centered) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'stretch',
  justifyContent: 'stretch',
  [`${mediaQueries.normal}`]: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: centered ? 'center' : 'flex-end',
  },
});

export const filtersLabel = () => ({
  display: 'none',
  whiteSpace: 'nowrap',

  [`${mediaQueries.normal}`]: {
    display: 'inherit',
  },
});

export const selectStyles = () => ({
  marginBottom: 16,
  marginLeft: 0,

  [`${mediaQueries.normal}`]: {
    marginBottom: 0,
    marginLeft: 8,
  },
});



