import * as React from 'react';
import { Section } from '../../atoms/Section';

import glamorous from 'glamorous';
const { Div } = glamorous;

export interface ISectionListProps {
  sections: Array<any>;
  sectionRef: any; // function returning node
  routeId: string;
  analyticsEvent: any; // function
  pageColor: string;
  isMobile: boolean;
  pageType: string;
  params?: any;
}

export class SectionList extends React.PureComponent<ISectionListProps, void> {
  renderSections() {
    return this.props.sections.map((section, sectionIndex) => {
      return (
        <Section
          key={sectionIndex}
          index={sectionIndex}
          title={section.title}
          color={section.color}
          elements={section.elements}
          innerRef={this.props.sectionRef}
          routeId={this.props.routeId}
          analyticsEvent={this.props.analyticsEvent}
          pageColor={this.props.pageColor}
          isMobile={this.props.isMobile}
          pageType={this.props.pageType}
          params={this.props.params}
        />
      );
    });
  }

  render() {
    return (
      <div>
        {this.renderSections()}
      </div>
    );
  }
}



