import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
const { mediaQueries, columns } = utils;

export const container = (isArticle) => ({
  display: 'flex',
  flexDirection: 'column',
  margin: '0 auto',
  paddingLeft: 32,
  paddingRight: 32,
  paddingTop: isArticle ? 64 : 40,

  [`${mediaQueries.phablet}`]: {
    paddingTop: 96,
    maxWidth: (columns.desktopMaxWidth / 17) * 13,
  },
});

export const image = () => ({
  width: 58,
  height: 58,
  margin: '0 auto',
  marginBottom: 8,
  borderRadius: '50%',

  [`${mediaQueries.phablet}`]: {
    width: 88,
    height: 88,
    marginBottom: 10,
  },
});

export const subtext = (isArticle) => ({
  marginBottom: isArticle ? 40 : 25,
  fontSize: 14,
  lineHeight: '19px',
  textAlign: isArticle ? 'left' : 'center',

  [`${mediaQueries.phablet}`]: {
    marginBottom: isArticle ? 64 : 40,
    textAlign: 'center',
  },
});

export const headline = (isArticle) => ({
  marginBottom: 64,
  fontFamily: 'AvenirNext-Bold',
  fontSize: 36,
  lineHeight: '48px',
  textAlign: isArticle ? 'left' : 'center',

  [`${mediaQueries.phablet}`]: {
    marginBottom: 64,
    fontSize: 60,
    lineHeight: '64px',
    letterSpacing: '-0.5px',
    textAlign: 'center',
  },
});



