import { autobind, debounce } from 'core-decorators';
import * as React from 'react';
import { css } from 'glamor';
import { CarouselBaseProptypes, ICarouselBaseProps, ICarouselItemPosition, ICarouselRefs } from './';
import { throttle } from 'lodash';
import { DotNav } from '../../atoms/DotNav';
import * as styles from './Carousel-styles';

export class Carousel extends React.Component<ICarouselBaseProps, void> {
  container: HTMLElement;
  outerContainer: HTMLElement;
  positions: Array<ICarouselItemPosition> = [];
  elements: Array<any> = [];
  layoutTimeout: number;

  static propTypes = CarouselBaseProptypes;

  updateDimensionsCallback: any;
  updateDimensionsTimeout: any;

  componentDidMount() {
    setTimeout(() => {
      this.updateDimensions();
    }, 0);
  }

  componentWillUpdate(newProps: any) {
    if (newProps.isMobile !== this.props.isMobile) {
      this.updateDimensions();
    }

    if (newProps.containerWidth !== this.props.containerWidth) {
      window.clearTimeout(this.updateDimensionsTimeout);
      this.updateDimensionsTimeout = window.setTimeout(() => {
        this.updateDimensions();
      }, 0);

    }
  }

  @autobind
  setupLayout() {
    if (this.container) {
      this.container.style.minWidth = '0';
      const padding = this.props.padding || 0;
      let containerWidth: number = 0;
      let offsetX: number = this.props.leftOffset || 0;
      this.elements.forEach((child, index) => {
        const childWidth = child.children[0].offsetWidth;
        this.positions[index] = {
          left: offsetX,
          center: offsetX + (childWidth * 0.5),
          width: childWidth,
        };
        containerWidth = offsetX + childWidth;
        offsetX += childWidth + padding;
      });

      if (containerWidth < this.outerContainer.getBoundingClientRect().width
        && this.props.snapCenter) {
        this.container.style.left = '0px';
      }
      containerWidth += this.props.leftOffset ? this.props.leftOffset : 0;
      this.props.refsReady(
        {
          container: this.container,
          containerBounds: this.container.getBoundingClientRect(),
          containerWidth,
          outerContainer: this.outerContainer,
          outerContainerBounds: this.outerContainer.getBoundingClientRect(),
          positions: this.positions,
        },
      );
    } else {
      this.setupLayout();
    }
  }

  @autobind
  updateDimensions() {
    if (this.outerContainer) {
      this.setupLayout();
    }
  }

  render() {
    const children = React.Children.map(this.props.children, (child, key) => {
      const length = (this.props.children.length - 1)
        + (this.props.children[1] && this.props.children[1].length > 0 ?
        this.props.children[1].length : 0);
      const paddingStyles = () => ({ paddingRight: key < length ? `${ (this.props.padding || 0) }px` : 0 });
      const oneItem = this.props.children.length === 1;
      return (
        <div className={ css(styles.element(), paddingStyles(), oneItem && styles.centerItem(), this.props.alignTop && styles.alignTop(), this.props.flex && styles.flexItem()).toString() } ref={ (item) => this.elements[key] = item }>{ child }</div>
      );
    });
    const modifiedStyles = () => ({
      padding: `0 ${ this.props.leftOffset || 0 }px`,
      marginBottom: 16,
    });

    return (
      <div>
        <div
          className={ css(styles.defaults(), this.props.allowOverflow && styles.allowOverflow()).toString() }
          ref={ (outerContainer) => this.outerContainer = outerContainer }
        >
          <div
            className={
              css(styles.container(),
                modifiedStyles(),
                this.props.flex && styles.flex(),
              ).toString()
            }
            ref={ (container) => this.container = container }
          >
            { children }
          </div>
        </div>
        {
          this.props.dotNav && this.props.children.length > 1 &&
          <div className={ css(styles.dotNav()).toString() }>
            <DotNav length={ this.props.children.length } active={ this.props.active } color={this.props.color}/>
          </div>
        }
      </div>
    );
  }
}



