import { css } from 'glamor';
import * as utilStyles from 'utils/styles';
import * as type from 'utils/styles/type';
const { mediaQueries, padding } = utilStyles;

export const headerBlock = () => ({
    textAlign: 'center',
    maxWidth: utilStyles.columns.desktopMaxWidth,
    margin: '0 auto',
});

export const headerButtonBlockWrapper = (primary) => ({
    paddingTop: primary ? 40 : 22,
    paddingLeft: utilStyles.padding.semi,
    paddingRight: utilStyles.padding.semi,
    boxSizing: 'border-box',
    '& button+button': {marginLeft: 16},
    [`${mediaQueries.phablet}`]: {
        paddingLeft: 0,
        paddingRight: 0,
    },
});

export const headlineBodyCopy = () => ({
    padding: '10px 32px 0 32px',
    fontSize: 16,
    lineHeight: '24px',
    [`${mediaQueries.phablet}`]: {
        fontSize: 19,
        lineHeight: '28px',
        maxWidth: `${utilStyles.columns.desktopColumnWidth * 9}%`,
        margin: '0 auto',
    },
});

export const headlineSubheadCopy = () => ({
    padding: '32px 32px 0 32px',
    fontSize: '24px',
    lineHeight: '32px',
    [`${mediaQueries.phablet}`]: {
        fontSize: '32px',
        lineHeight: '40px',
        maxWidth: `${utilStyles.columns.desktopColumnWidth * 9}%`,
        margin: '0 auto',
    },
});

export const headlineImageWrapper = (height = 250, width = 250) => ({
    position: 'relative',
    margin: `0 auto ${utilStyles.padding.semi}px auto`,
    height: `${height}`,
    width: `${width}`,
});

export const specLinkHolder = () => ({
    marginTop: '20px',
});

export const specsLink = (color) => ({
    fontWeight: 600,
    textTransform: 'capitalize',
    color: color,
    transition: 'opacity 0.2s ease-out',
    '& span': {
      borderBottom: `2px solid ${color}`,
      verticalAlign: 'middle',
      fontSize: '12px',
      lineHeight: '16px',
      paddingBottom: '2px',
    },
    '&:hover': {
        opacity: 0.5,
    },
});

export const markdownStyles = () => ({
    [' p']: {
        padding: '10px 32px 0 32px',
        [`${mediaQueries.phablet}`]: {
            ...type.large,
            maxWidth: `${utilStyles.columns.desktopColumnWidth * 9}%`,
            margin: '0 auto',
        },
    },
});
export const markdownStylesSubHead = () => ({
    [' p']: {
        ...type.headline3,
        padding: '32px 32px 0 32px',
        [`${mediaQueries.phablet}`]: {
            ...type.headline3,
            maxWidth: `${utilStyles.columns.desktopColumnWidth * 9}%`,
            margin: '0 auto',
        },
    },
});



