import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div, Img } = glamorous;
import { connect } from 'react-redux';

import { CaptionAndDownload } from '../../molecules/CaptionAndDownload';
import * as styles from './Image-styles';

export interface IImageProps {
  imageUrl?: string;
  imageUrl2x?: string;
  mobileImageUrl?: string;
  title: string;
  maxHeight?: string;
  maxWidth?: boolean;
  fullWidth?: boolean;
  valign?: boolean;
  halign?: boolean;
  natural?: boolean;
  rounded?: boolean;
  isMobile?: boolean;
  noPadding?: boolean;
  isBlog?: boolean;
  isLast?: boolean;
  noMargin?: boolean;
  caption?: string;
  downloadUrl?: string;
  isWide?: boolean;
  width?: number;
  height?: number;
  isHighDensity?: boolean;
  analyticsEvent?: Function;
  type?: string;
}

export const ImagePropTypes = {
  imageUrl: PropTypes.string.isRequired,
  imageUrl2x: PropTypes.string,
  mobileImageUrl: PropTypes.string,
  title: PropTypes.string.isRequired,
  maxWidth: PropTypes.bool,
  maxHeight: PropTypes.string,
  fullWidth: PropTypes.bool,
  valign: PropTypes.bool,
  halign: PropTypes.bool,
  natural: PropTypes.bool,
  rounded: PropTypes.bool,
  noPadding: PropTypes.bool,
  isBlog: PropTypes.bool,
  isLast: PropTypes.bool,
  noMargin: PropTypes.bool,
  caption: PropTypes.string,
  downloadUrl: PropTypes.string,
  isWide: PropTypes.bool,
  width: PropTypes.number,
  height: PropTypes.number,
  isHighDensity: PropTypes.bool,
};

const mapStateToProps = (state: any, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  isHighDensity: state.app.isHighDensity,
});

export class ImageImpl extends React.Component<IImageProps, void> {
  static propTypes = ImagePropTypes;
  render() {
    const { isMobile, imageUrl, imageUrl2x, mobileImageUrl, noPadding, isBlog, isLast, noMargin, width, isHighDensity} = this.props;
    const useMobileSrc = isMobile && mobileImageUrl;
    const hasCaptionOrDownload = this.props.caption || this.props.downloadUrl;
    let imgSrc = useMobileSrc ? mobileImageUrl : imageUrl;
    if (this.props.natural) {
      const imgStyles = [
        { width: '100%', height: 'auto', },
        this.props.maxHeight && { height: this.props.maxHeight, width: 'auto', },
        this.props.valign && { verticalAlign: this.props.valign, },
        this.props.rounded && { borderRadius: '33px'},
      ];
      if (isHighDensity && imageUrl2x) {
        imgSrc = imageUrl2x;
      }
      return (
        <Div css={ styles.naturalImageContainer(noPadding, isBlog, isLast, noMargin, this.props.type)}>
          <Div css={ styles.maxWidth(this.props.maxWidth, noPadding, width)}>
            <Img
              src={imgSrc}
              alt={this.props.title}
              css={imgStyles}
            />
            {hasCaptionOrDownload && !this.props.isMobile && (
              <CaptionAndDownload
                caption={this.props.caption}
                downloadUrl={this.props.downloadUrl}
                analyticsEvent={ this.props.analyticsEvent }
              />
            )}
          </Div>
        </Div>
      );
    } else {
      const imageStyles = [
        styles.defaultStyles(),
        { backgroundImage: `url(${imgSrc})`},
        this.props.maxHeight ? styles.maxHeight(this.props.maxHeight, this.props.fullWidth, this.props.valign, this.props.halign) : null,
        this.props.rounded && { borderRadius: '33px' },
      ];
      return (
        <Div css={imageStyles} title={this.props.title}></Div>
      );
    }
  }
}

export const Image = connect(mapStateToProps)(ImageImpl);



