import * as React from 'react';
import glamorous from 'glamorous';
const { H3 } = glamorous;

import * as styles from './EyebrowTitle-styles';

export interface IEyebrowTitleProps {
  color?: string;
  children: string;
  css?: any;
}

export class EyebrowTitle extends React.Component<IEyebrowTitleProps, void> {
  render() {
    return (
      <H3 css={[styles.defaultStyles(this.props.color), this.props.css]}>
        { this.props.children }
      </H3>
    );
  }
}



