import * as React from 'react';
import * as glamorous from 'glamorous';
import * as Router from 'react-router';
import * as utils from 'utils/styles';
import { debounce } from 'lodash';
import { autobind } from 'core-decorators';
import { connect } from 'react-redux';
import { GridCard } from './GridCard';
import {
  cardSetWrapper,
  cardLayoutWrapper,
  cardSetChild,
} from './base/GridCardSet-base-styles';

import {desktopGridRadius, tabletGridRadius} from './base/GridCardSet-radius-styles';

import { ChillahTooltip } from '../../molecules/ChillahTooltip';

const { Div } = glamorous;
const { Link } = Router;

export interface ICardProps {
  id?: string;
  subhead?: string;
  title?: string;
  image: string;
  image2x?: string;
  copy: string;
  link?: string;
  target?: string;
  logoImage?: string;
  featuredImage?: string;
}

export interface IGridCardSetProps {
  cards?: Array<ICardProps>;
  rows?: Array<Array<ICardProps>>;
  background?: boolean;
  article?: boolean;
  titleStyle?: any;
  animate?: boolean;
  isUpdating?: boolean;
  color: string;
  isMobile?: boolean;
  locale?: string;
  openModal?: any;
  isThreeUp?: boolean;
  analyticsEvent?: Function;
  routeId?: string;
}
export interface ICardSetState {
  isDesktop: boolean;
  windowWidth: number;
}

const mapStateToProps = (state: any, ownProps) => {
  return {
    ...ownProps,
    isMobile: state.app.mobile,
    locale: state.data.locale,
    routeId: state.data.current.id,
  };
};

export class GridCardSetImpl extends React.Component<IGridCardSetProps, ICardSetState> {

  state = {
    isDesktop: (typeof window !== 'undefined') ? window.innerWidth >= utils.screenSizes.normal : true,
    windowWidth: (typeof window !== 'undefined') ? window.innerWidth : 1024, // arbitruary...
  };

  resizeCallback: any;

  componentDidMount() {
    this.resizeCallback = debounce(this.onResize, 250);
    window.addEventListener('resize', this.resizeCallback);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeCallback);
  }

  generateMobileImageHeight(type) {

    let mobileBase = '50%';
    let mobileFeatured = '50%';
    let mobileSpacing = '50%';

    if (type === 'successStory') {
      mobileBase = '40%';
      mobileFeatured = '388px';
      mobileSpacing = '0%';
    } else if (type === 'creativeSpotlightDevice') {
      mobileBase = '70%';
      mobileFeatured = '70%';
      mobileSpacing = '0%';
    }

    return { mobileBase, mobileFeatured, mobileSpacing };

  }

  @autobind
  analytics(index) {
    if (this.props.analyticsEvent) {
      this.props.analyticsEvent({
        action: 'click',
        label: `${this.props.routeId}-${this.props.isThreeUp ? 'card-relatedEntries' : 'card'}-${ this.props.cards[index].title }`,
        category: `${this.props.routeId}-${this.props.isThreeUp ? 'card-relatedEntries' : 'card'}-${ this.props.cards[index].title }`,
      });
    }
  }

  renderIndividualCard = (card, index) => {
    const {
      cards,
      background,
      article,
      titleStyle,
      animate,
      isUpdating,
      color,
    } = this.props;

    const { isDesktop } = this.state;

    let isFeatured = false;

    if (isDesktop) {
      if (index === 0 || index % 10 === 0 || index - 6 % 10 === 0) {
        isFeatured = true;
      }
    } else {
      if (index === 0 || index % 5 === 0) {
        isFeatured = true;
      }
    }

    return (
      <GridCard
        {...card}
        titleStyle={titleStyle}
        key={card.id}
        animate={animate}
        isUpdating={isUpdating}
        isDesktop={this.state.isDesktop}
        windowWidth={this.state.windowWidth}
        isFeatured={isFeatured}
        cardIndex={index}
        openModal={this.props.openModal}
        isThreeUp={this.props.isThreeUp} />
    );
  }

  renderCards (cards) {
    const renderCard = this.renderIndividualCard;
    return cards.map((card, index) => {
      const cardHeight = this.generateMobileImageHeight(card.type);
      return (
        <Div
          className={'grid-card-wrapper'}
          css={cardSetChild(cardHeight, card.type, this.props.isMobile, this.props.isThreeUp)}
          key={index}
          tabIndex="0"
        >
          { card.link ?
            <Link to={`${(this.props.locale === '' || this.props.locale === 'en-us') ? card.link : '/l/' + this.props.locale + card.link }`  } onClick={ () => this.analytics(index) }>
              { renderCard(card, index) }
            </Link>
            :
            renderCard(card, index)
          }
        </Div>
      );
    });
  }

  @autobind
  onResize() {

    this.setState((state) => {
      return {
        isDesktop: window.innerWidth >= utils.screenSizes.normal,
        windowWidth: window.innerWidth,
      };
    });

  }

  render () {
    const {
      background,
      article,
      rows,
      cards,
      isMobile,
      isThreeUp,
    } = this.props;

    const cardCount = cards.length;
    const cardGrid = this.state.isDesktop ? desktopGridRadius(cardCount, isThreeUp) : !isMobile ? tabletGridRadius(cardCount) : null;
    return (
      <Div className={'card-set-wrapper'} css={cardLayoutWrapper()}>
        <Div className={'card-set'} css={[cardSetWrapper({article, background, isThreeUp}), cardGrid]}>
          { this.renderCards(cards) }
        </Div>
      </Div>
    );
  }
}
export const GridCardSet = connect(mapStateToProps)(GridCardSetImpl);



