import * as actions from '../actionCreators/data';
import { parseData, IDataStore } from 'utils/helpers/parseModules';
import { URIs, routes } from 'utils/constants';
import axios from 'axios';
import { languages } from 'utils/constants';
import { safeName } from 'utils/helpers';
type StateObject = {
  entries: IDataStore;
};

export function getData(state: StateObject, route: string) {
  return async (dispatch) => {
    dispatch(actions.dataRequest());
    try {
      const response = await axios.get('');
      const parsedResponse = parseData(response);
      dispatch(actions.dataReceived(parsedResponse));
    } catch (ex) {
      dispatch(actions.requestError(ex));
    }
  };
}

export function loadJson(state: StateObject, json: any) {
  const parsedResponse = parseData(json);
  return {
    ...state,
    loading: false,
    entries: parsedResponse,
    current: parsedResponse.pages[0],
    keys: parsedResponse.keys,
  };
}

export function setLocale(state: StateObject, locale: string) {
  return {
    ...state,
    locale,
  };
}

export function setCurrentEntry(state: StateObject, route: string) {
  let lang;
  if (typeof document !== 'undefined') {
    const uri = (document.location.pathname.slice(1)).split('/');
    lang = uri[0] === 'l' ? uri[1] : 'en-us';
  }
  else {
    lang = 'en-us';
  }

  const { current, currentId } = getCurrent(state.entries, route, lang);

  return {
    ...state,
    current: current ? current : state.entries.pagesById[currentId],
  };
}

export function getCurrent(entries, route, lang) {
  const splitRoute = route.split('/');
  if (splitRoute[1] === 'l') {
    route = '/';
    for (let i = 3; i < splitRoute.length; i++) {
      route += splitRoute[i];
      route += i < splitRoute.length - 1 ? '/' : '';
    }
  }

  const isLanguageMatch = languages.filter((language) => {
    return language === lang;
  });

  const currentId = (route === '/' || route === '/index.html') && (isLanguageMatch.length || lang === 'en-us') ? 'home' : '*';
  let current;

  const parsedRoute = route.substring(1).split('/');
  if (parsedRoute[0] === URIs.BLOG && parsedRoute[1]) {
    const blogId = safeName(parsedRoute[1]);
    if (entries.blogPosts[blogId]) {
      current = entries.blogPosts[blogId];
      current['color'] = entries.pagesById[URIs.BLOG].color;
    }
  } else if (parsedRoute[0] === URIs.SUCCESS_STORIES && parsedRoute[1]) {
    const storyId = safeName(parsedRoute[1]);
    if (entries.successPosts[storyId]) {
      current = entries.successPosts[storyId];
      current['color'] = entries.pagesById['success_stories'].color;
    }
  } else {
    Object.keys(entries.pagesById).forEach((key) => {
      if (key === safeName(parsedRoute[0])) {
        current = entries.pagesById[safeName(parsedRoute[0])];
      }
    });
    const isDraft = (parsedRoute[0] === 'draftpreview' || parsedRoute[0] === 'draftpreviewblog');
    if (isDraft) {
      current = entries.pages[0];
    }
    if (!current) {
      current = entries.pagesById[currentId];
    }
    if (!current) {
      current = entries.pagesById['*'];
    }
  }

  return {
    current,
    currentId,
  };
}



