import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

const { mediaQueries, screenSizes } = utils;

export const listStyle = () => ({
  ...type.large,
  paddingLeft: 24,
  listStyleType: 'none',
});

export const listItem = (i) => ({
  paddingLeft: 22,
  paddingBottom: 16,
  position: 'relative',
  color: colors.regularCharcoal,
  '&:before': {
    content: `''`,
    position: 'absolute',
    left: 2,
    top: 10,
    width: 6,
    height: 6,
    backgroundColor: colors.lightYellow,
    border: `1px solid ${colors.black}`,
    borderRadius: '50%',
  },
});



