import { black } from 'utils/styles/colors';

export const paginationList = (activeIndex) => ({
  margin: 0,
  padding: 0,
  position: 'fixed',
  top: '50%',
  transform: 'translateY(-50%)',
  left: 38,
  opacity: activeIndex > 0 ? 1 : 0,
  transition: 'all .2s ease',
  pointerEvents: activeIndex > 0 ? 'all' : 'none',
});

export const listItemContainer = (color: string, active: boolean, titleWidth: number, isTouchEnabled: boolean) => ({
  display: 'block',
  margin: 0,
  padding: 0,
  marginBottom: 8,
  cursor: 'pointer',
  '&:hover .list-item': !isTouchEnabled && {
    opacity: 1,
    width: titleWidth + 32,
  },
  '&:hover .item-title': {
    opacity: 1,
  },
  '&:hover .dot': !active && !isTouchEnabled && {
    '&:after': {
      background: black,
      border: '2px solid black',
    },
  },
});

export const listItem = (color: string, active: boolean, titleWidth: number, isTouchEnabled: boolean) => ({
  pointerEvents: 'none',
  verticalAlign: 'top',
  fontFamily: '\'AvenirNext-DemiBold\', sans-serif',
  fontSize: 12,
  lineHeight: '16px',
  color: black,
  backgroundColor: color,
  borderRadius: '100px',
  boxSizing: 'border-box',
  opacity: 0,
  transition: 'opacity 0.2s ease-out, width 0.25s ease-out',
  display: 'inline-block',
  width: 0,
  height: 30,
  overflow: 'hidden',
  whiteSpace: 'nowrap',
  padding: '7px 16px',
  marginTop: -7,
});

export const listItemTitle = (active: boolean) => ({
  opacity: active ? 1 : 0,
  display: 'inline-block',
  transition: 'opacity 0.6s ease-out',
});

export const dotHolder = (active: boolean) => ({
  width: 17,
  height: 20,
  verticalAlign: 'top',
  display: 'inline-block',
  cursor: 'pointer',
  position: 'relative',
  marginRight: 5,
  '& .dot': {
    backgroundColor: !active && black,
    width: active ? 8 : 6,
    height: active ? 8 : 6,
    position: 'relative',
    margin: '0 auto',
    top: '50%',
    marginTop: -4,
    borderRadius: '100%',
    boxSizing: 'border-box',
    transition: 'all 0.25s ease',
    '&:after ': {
      position: 'absolute',
      content: `''`,
      left: -2,
      right: -2,
      top: -2,
      bottom: -2,
      borderRadius: '100%',
      background: 'transparent',
      border: active ? '2px solid black' : '2px solid transparent',
      transition: 'all .25s ease',
    },
  },
});



