import * as React from 'react';
import LazyLoad from 'react-lazyload';
import glamorous from 'glamorous';
import * as styles from './LazyloadFade-styles';

const { Div } = glamorous;

export type ILazyloadFadeProps = {
  children?: React.ReactNode;
};

export class LazyloadFade extends React.Component<ILazyloadFadeProps, void> {
  render() {
    return (
        <LazyLoad offset={1500} height={0}>
            <Div css={ styles.fadeIn }>
                {this.props.children}
            </Div>
        </LazyLoad>
    );
  }
}



