import * as colors from 'utils/styles/colors';
import * as utils from 'utils/styles';
const { mediaQueries, screenSizes, padding } = utils;

export const defaultStyle = (gradientColor = 'Purple', id) => ({
  background: id !== 'home' && colors[`regular${gradientColor}Gradient`],
});

export const blogHeadlineWrapper = () => ({
    textAlign: 'center',
});

export const blogTitle = () => ({
    fontFamily: 'AvenirNext-DemiBold',
    fontWeight: 600,
});

export const linkBlock = () => ({
    padding: '70px 32px 20px 32px',
    fontSize: 11,
    fontFamily: 'AvenirNext-Bold',
    letterSpacing: '1px',
    fontWeight: 700,
    textTransform: 'uppercase',
    [`${mediaQueries.phablet}`]: {
        padding: '70px 64px 20px 64px',
    },
});

export const backArrow = () => ({
    position: 'relative',
    top: 5,
    paddingRight: 18,
});

export const blogContentWrapper = () => ({
    maxWidth: 800,
    margin: '0 auto',
    paddingBottom: 55,
});

export const formatHolder = (marginTop: number = 0, marginBottom: number = 0, marginLeft: number = 0, marginRight: number = 0, small: boolean = false) => ({
  marginTop,
  marginBottom,
  marginLeft,
  marginRight,
  [`${mediaQueries.phabletMax}`]: small && {
    marginLeft: padding.normal,
    marginRight: padding.normal,
  },
});



