import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';
import { hexToRGB } from 'utils/helpers';

export const container = () => ({
  backgroundColor: 'white',
  position: 'fixed',
  bottom: '0',
  width: '100vw',
  boxShadow: `0px -5px 5px rgba(${hexToRGB(colors.shadowGray)}, 0.5)`,
  zIndex: 20000,
  padding: `32px ${utils.columnsByPercent(1)}`,
  display: 'flex',
  alignItems: `center`,
  justifyContent: `center`,
  overflow: 'hidden',
});

export const innerContainer = () => ({
  display: 'flex',
  alignItems: `center`,
  maxWidth: '1200px',
  width: '100%',
});

export const image = () => ({
    maxWidth: `100%`,
});

export const imageContainer = () => ({
  display: `none`,
  [`${utils.mediaQueries.phablet}`]: {
    display: `block`,
    flex: `1 1 10%`,
  },
});

export const content = () => ({
  flex: `4`,
  textAlign: 'center',
  maxHeight: '80%',
  [`${utils.mediaQueries.phablet}`]: {
    textAlign: 'left',
    padding: `0 ${utils.padding.normal}px`,
  },
});

export const markdownStyles = () => ({
  [' p']: {
    ...type.stat,
  },
});

export const close = () => ({
  display: 'inline-block',
  verticalAlign: 'middle',
  position: 'relative',
  height: '32px',
  width: '32px',
  borderRadius: '16px',
  border: 'none',
  backgroundColor: `${colors.shadowGray}`,
  color: 'white',
  cursor: 'pointer',
  marginLeft: `${utils.columnsByPercent(0.5)}`,
  '&::before': {
    display: 'block',
    position: 'absolute',
    left: '50%',
    top: 'calc(50% - 1px)',
    height: '2px',
    width: '18px',
    backgroundColor: `${colors.white}`,
    content: '""',
    fontSize: 0,
    transform: 'translate(-50%, 0) rotate(45deg)',
  },
  '&::after': {
    display: 'block',
    position: 'absolute',
    left: '50%',
    top: 'calc(50% - 1px)',
    height: '2px',
    width: '18px',
    backgroundColor: `${colors.white}`,
    content: '""',
    fontSize: 0,
    transform: 'translate(-50%, 0) rotate(-45deg)',
  },
});



