import * as types from '../constants/data';
import * as actions from '../actions/data';
import { IDataStore } from 'utils/helpers/parseModules';

const INITIAL_STATE = {
  current: {
    sections: [],
  },
  loading: true,
  entries: {},
  locale: '',
  keys: {},
};

export function dataReducer(state = INITIAL_STATE, action: any) {
  switch (action.type) {
    case types.ANALYTICS_EVENT:
      return {
        ...state,
        event: action.event,
      };
    case types.SET_LOCALE:
      return {
        ...state,
        locale: action.locale,
      };
    case types.DATA_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case types.DATA_RECEIVED:
      return {
        ...state,
        loading: false,
        data: action.data,
      };
    case types.GET_DATA:
      return actions.getData(state, action.route);
    case types.LOAD_JSON:
      return actions.loadJson(state, action.data);
    case types.REQUEST_ERROR:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    case types.SET_CURRENT_ENTRY:
      return actions.setCurrentEntry(state, action.route);
    default:
      return state;
  }
}



