import * as React from 'react';
import glamorous from 'glamorous';
const { Div } = glamorous;
import * as styles from './SelectFilters-styles';
import { connect } from 'react-redux';
import { DynamicSelect as Select } from './components/DynamicSelect';
import { StatefulContainer } from 'utils/helpers';

export interface ISelectFiltersProps {
  selects?: Array<any>;
  centered?: boolean;
  onChange?: Function;
  pageId?: string;
}

const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    pageId: state.data.current.id,
  };
};

export class SelectFiltersClass extends React.Component<ISelectFiltersProps, void> {

  renderSelects = () => {
    return this.props.selects.map((option, i) => {
      return (
        <Select
          key={i}
          css={styles.selectStyles()}
          placeholder={option.placeholder}
          options={option.options}
          onChange={this.props.onChange}
          pageId={this.props.pageId}
          instanceId={`${i}`} // Needed for SSR - https://github.com/JedWatson/react-select/issues/1970
        />
      );
    });
  }

  render() {
    return (
      <Div css={styles.filtersContainer(this.props.centered)}>
        <Div css={styles.filtersLabel()}>
          {'Filter by:'}
        </Div>
        {this.renderSelects()}
      </Div>
    );
  }
}

export const SelectFilters = connect(mapStateToProps)(SelectFiltersClass);



