import * as React from 'react';
import './react-select.css'; // Global CSS for react-select
import './react-select-custom.css'; // CSS Override for react-select
import { ReactSelectGlam } from './Select-styles';

export type ISelectProps = {
  // Allow any additional props to be passed along to react-select
  [propertyName: string]: any;
  instanceId?: string;
};

export class Select extends React.Component<ISelectProps, void> {
  render() {
    const { ...restProps } = this.props;
    return (
      <ReactSelectGlam className="Select-custom" {...restProps} searchable={false} />
    );
  }
}



