import * as React from 'react';
import * as glamorous from 'glamorous';
import { LogoCard } from './LogoCard';
import * as styles from './LogoGarden-styles';
const { Div } = glamorous;
const { PropTypes, Component } = React;

export interface ILogoGardenProps {
  logos: Array<{
    title: string;
    link: string;
    image: string;
    image2x?: string;
    imageAlt?: string;
  }>;
}

export class LogoGarden extends Component<ILogoGardenProps, void> {
  static propTypes = {
    logos: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string,
      link: PropTypes.string,
      image: PropTypes.string,
      imageAlt: PropTypes.string,
    })),
  };
  renderLogos() {
    const { logos } = this.props;
    return logos.map((logo, index) => {
      return <LogoCard {...logo} key={`logo-${index}`} />;
    });
  }
  render() {
    return (
      <Div css={styles.logoGardenWrapper()}>
        {this.renderLogos()}
      </Div>
    );
  }
}



