import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import * as glamorous from 'glamorous';
import * as styles from './LeadGenForm-styles';
import axios, { AxiosRequestConfig } from 'axios';
import { fields } from './leadGenFields';
import { CopyBlock } from '../../atoms/CopyBlock';
import ReCAPTCHA  from 'react-google-recaptcha';

const {
  Div,
  Span,
  Input,
  Button,
  P,
  Form,
} = glamorous;
const { Component } = React;
const { findDOMNode } = ReactDOM;

export interface ILeadGenFormProps {
  successMsg: string;
  intro?: string;
  analyticsEvent?: Function;
}

type LeadGenState = {
  fields: {
    [key: string]: any;
  },
  valid: boolean;
  submitted: boolean;
  active: string;
  isNotRobot: boolean;
  ctaMessage: string;
};

export class LeadGenForm extends Component<ILeadGenFormProps, LeadGenState> {
  state = {
    fields: fields,
    valid: false,
    submitted: false,
    active: '',
    isNotRobot: false,
    ctaMessage: this.props.intro,
  };
  validateField(field, val) {
     if (!field.touched) { return false; }
     if (field.required && !val) {
       return 'Required';
     } else if (field.minLength && val.length < 2) {
       return 'Too short';
     } else if (field.number) {
       const numRegEx = /^\$?(\d+|\d{1,3}(,\d{3})*)(\.\d+)?$/;
       return !numRegEx.test(val);
     } else if (field.type === 'phone') {
       const phoneRegEx = /^(?:(?:\+?1\s*(?:[.-]\s*)?)?(?:\(\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\s*\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\s*(?:[.-]\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\s*(?:[.-]\s*)?([0-9]{4})(?:\s*(?:#|x\.?|ext\.?|extension)\s*(\d+))?$/;
       return !phoneRegEx.test(val);
     } else if (field.type === 'email') {
       const regEx = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
       return !regEx.test(val);
     }
     return false;
  }
  validateForm() {
    const {fields} = this.state;
    let formValid = true;
    Object.keys(fields).map((field) => {
      const error = this.validateField(fields[field], fields[field].value);

      fields[field].touched = true;
      fields[field].error = error;

      if (error) { formValid = false; }
    });
    this.setState({
      fields,
      valid: formValid,
    });
    return formValid;
  }

  onChange(key: string, e: {[key: string]: any; }) {
    const value = e.target.value;
    const {fields} = this.state;
    const error = this.validateField(fields[key], value);
    fields[key] = {
      ...fields[key],
      error,
      touched: true,
      value,
    };
    this.setState({
      fields,
    });
  }

  toggleActive(e, field) {
    e.preventDefault();
    this.setState({
      active: (field.key === this.state.active ? '' : field.key),
      fields: {
        ...this.state.fields,
        [field.key]: {
          ...field,
        },
      },
    });
  }
  renderInput(key: any) {
    if (key === 'captchaField') { return false; }
    const field = this.state.fields[key];
    field['key'] = key;
    const {
      type,
      label,
      name,
      error,
     } = field;
    const active = this.state.active === key;

    let component;

    if (type === 'select') {
      component = this.renderSelect(field, active);
    } else if (type === 'textarea') {
      component = this.renderTextarea(field, active);
    } else if (type === 'hidden') {
      component = this.renderHidden(field);
    } else {
      component = <input
          required={field.required}
          placeholder={label}
          onChange={(e) => this.onChange(key, e)}
          type={type}
          maxLength={field.maxLength || null}
          onFocus={(e) => this.setActive(key)}
          onBlur={(e) => this.setActive('')}
          name={name}/>;
    }
    return (
      <Div
        key={label}
        className={key}
        css={styles.inputStyle(error, active, field.type)}>
        {component}
      </Div>
    );
  }
  renderHidden(field) {
    return <input
       type={field.type}
       name={field.name}
       value={field.value}/>;
  }
  renderTextarea(field, active) {
    return <textarea
          maxLength={field.maxLength || null}
          required={field.required}
          rows={1}
          style={{ maxWidth: '100%' }}
          placeholder={field.label}
          type={field.type}
          onChange={(e) => this.onChange(field.key, e)}
          onFocus={(e) => this.setActive(field.key)}
          onBlur={(e) => this.setActive('')}
          name={field.name}/>;
  }
  renderSelect(field, active) {
    return (
      <Div
        className="select-wrapper"
        css={styles.selectStyle()}
        tabIndex="0">
        <Div css={styles.selectedOption(field.value, active)}
        onClick={(e) => this.toggleActive(e, field)}>
          {field.value ? field.value.label : field.label}
        </Div>
        <Div
          css={styles.optionsWrapper(active)}>
          {field.options.map((option) => (
            <Div
              className="select-option"
              onClick={(e) => this.setValue(e, field, option)}
              css={styles.selectOption()}
              key={`option-${option.value}`}>
              {option.label}
            </Div>
          ))}
        </Div>
        <select
          required={field.required}
          ref={field.name}
          value={this.state.fields[field.key].value}
          name={field.name}>
          {field.options.map((option) => (
              <option
                value={option.value}
                key={option.value}>
                {option.label}
              </option>
            ))
          }
         </select>
      </Div>
    );
  }
  renderFields() {
    return Object.keys(this.state.fields).map((field) => {
      return this.renderInput(field);
    });
  }
  setActive(key) {
    this.setState({
      active: key,
    });
  }
  setValue(e, field, option) {
    e.preventDefault();
    const select = findDOMNode(this.refs[field.name]);
    select.setAttribute('value', option.value);
    this.setState({
      active: '',
      fields: {
        ...this.state.fields,
        [field.key]: {
          ...field,
          value: option,
          touched: true,
          error: false,
        },
      },
    });
  }
  handleSubmit(e) {
    e.preventDefault();
    this.props.analyticsEvent({
      action: 'click',
      page: 'get-started',
      label: 'lead-gen-form-submit',
      category: 'get-started:button-submit',
    });
    if (this.validateForm()) {
      const data = new FormData();
      const { fields } = this.state;
      Object.keys(fields).map((field) => {
        let value = fields[field].value;
        if (fields[field].type === 'select') {
          value = value.value;
        }
        data.append(fields[field].name, value);
      });
      const send = axios.create();
      send.interceptors.response.use((res) => {
        return res;
      }, (error) => {
        return error;
      });
      send.request({
        url: 'https://businesshelp.snapchat.com/en-US/api/v2/send',
        method: 'post',
        data,
        headers: {'Content-Type': 'multipart/form-data'},
        maxRedirects: 0,
      })
      .then((res) => {
        console.log(res);
        this.setState((state) => {
          return {
            ...state,
            submitted: true,
            ctaMessage: this.props.successMsg,
          };
        });
      });
    }
  }
  componentDidMount() {
    const form = findDOMNode(this.refs.formNode);
  }

  onCaptchaChange() {
    this.setState({isNotRobot: true});
  }

  render() {
    const { valid, submitted } = this.state;
    return (
      <Div
        css={styles.formWrapper()}>
        <CopyBlock css={styles.message()}>
          {this.state.ctaMessage}
        </CopyBlock>
        <Form
          css={styles.formNode(submitted)}
          ref="formNode"
          onSubmit={(e) => this.handleSubmit(e)}>
          { this.renderFields() }
          <P css={styles.disclaimer()}>
            By submitting your contact information, you are authorizing Snap to send you information about our products, services, and promotional offers that we think may interest to you.
          </P>
          <Div css={styles.formFooter()}>
            <ReCAPTCHA
              ref="recaptcha"
              sitekey="6LfaGgkTAAAAALkkeRWFbPA1uALybA5Nj3rpoHdm"
              onChange={this.onCaptchaChange.bind(this)}
            />
            <Button type="submit" css={styles.submitStyle(valid)} onClick={this.validateForm.bind(this)} disabled={!this.state.isNotRobot}>
              <p>Submit</p>
            </Button>
          </Div>
        </Form>
      </Div>
    );
  }
}



