import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

export const container = () => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: `center`,
  [`${utils.mediaQueries.phablet}`]: {
    maxWidth: utils.columns.desktopMaxWidth,
    margin: '0 auto',
  },
});

export const header = () => ({
  fontSize: `24px`,
  lineHeight: `32px`,
  flex: `1`,
  paddingRight: utils.padding.xtiny,
  [`${utils.mediaQueries.phablet}`]: {
    paddingRight: utils.padding.small,
  },
});

export const buttonWrapper = () => ({
  flex: `1`,
  display: `flex`,
  justifyContent: `flex-end`,
  paddingLeft: utils.padding.xtiny,
  [`${utils.mediaQueries.phablet}`]: {
    paddingLeft: utils.padding.small,
  },
});



