import * as React from 'react';
import * as PropTypes from 'prop-types';
import { autobind } from 'core-decorators';
import glamorous from 'glamorous';
const { Div } = glamorous;

import { VIDEO_DIMENSIONS } from '../../../../utils/constants';

import { MediaElement } from './components/Media.element';

interface IMediaPropsImageType {
  imageUrl?: string;
  imageUrl2x?: string;
  title?: string;
  ctaText?: string;
  natural?: boolean;
}

export const MediaPropsImageType = {
  imageUrl: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  ctaText: PropTypes.string,
};
/*
  IMediaPropsVideoType mostly overlaps with the Video component's props Interface,
  but we use width and height on the top level of the IMediaProps Interface,
  so we need to spec out IMediaPropsVideoType separately.
*/
export interface IMediaPropsVideoType {
  videoSrc?: string;
  posterImageUrl?: string;
  posterVideoSrc?: string;
  ctaText?: string;
  showCTA?: boolean;
  hasHeadline?: boolean;
  smallLayout?: boolean;
  heroMediaStyle?: boolean;
}

export const MediaPropsVideoType = {
  videoSrc: PropTypes.string.isRequired,
  posterImageUrl: PropTypes.string.isRequired,
  posterVideoSrc: PropTypes.string,
  ctaText: PropTypes.string,
  showCTA: PropTypes.bool,
  smallLayout: PropTypes.bool,
  heroMediaStyle: PropTypes.bool,
};

export interface IMediaProps {
  ratioHeight?: number;
  ratioWidth?: number;
  aspectRatio?: number;
  mediaWidthRelativeToContainer: string;
  image?: IMediaPropsImageType;
  video?: IMediaPropsVideoType;
  bottomCTA?: boolean;
  bottomCTAInset?: boolean;
  minHeight?: number;
  containerStyle?: any;
  heroMediaStyle?: boolean;
  maxHeight?: string;
  fullWidth?: boolean;
  mute?: boolean;
  autorun?: boolean;
  controls?: boolean;
  handlePlayback?: Function;
  roundedCorner?: boolean;
  seekTo?: number;
  dragging?: boolean;
  noShadow?: boolean;
  loop?: boolean;
  isVideoProgress?: boolean;
  isFocused?: boolean;
  autosize?: boolean;
}

export const MediaPropTypes = {
  ratioWidth: PropTypes.number,
  ratioHeight: PropTypes.number,
  aspectRatio: PropTypes.number,
  bottomCTA: PropTypes.bool,
  bottomCTAInset: PropTypes.bool,
  mediaWidthRelativeToContainer: PropTypes.string.isRequired,
  heroMediaStyle: PropTypes.bool,
  image: {
    imageUrl: PropTypes.string,
    title: PropTypes.string,
    ctaText: PropTypes.string,
    maxHeight: PropTypes.bool,
  },
  video: {
    videoSrc: PropTypes.string,
    posterImageUrl: PropTypes.string,
    posterVideoSrc: PropTypes.string,
    ctaText: PropTypes.string,
    showCTA: PropTypes.bool,
    smallLayout: PropTypes.bool,
  },
  useColor: PropTypes.bool,
  mute: PropTypes.bool,
  autorun: PropTypes.bool,
  controls: PropTypes.bool,
  handlePlayback: PropTypes.func,
  roundedCorner: PropTypes.bool,
  seekTo: PropTypes.number,
  isVideoProgress: PropTypes.bool,
};

export interface IMediaInnerProps {
  ratioHeight?: number;
  ratioWidth?: number;
  aspectRatio?: number;
  mediaWidthRelativeToContainer: string;
  image?: IMediaPropsImageType;
  video?: IMediaPropsVideoType;
  useColor?: Boolean;
}

export interface IMediaState {
  aspectRatio: number;
  containerWidth: number;
  mediaHeight: number;
}

export class Media extends React.Component<IMediaProps, IMediaState> {
    static defaultProps = {
        minHeight: 0,
        isFocused: true,
    };

    state = {
        aspectRatio: 1,
        containerWidth: 0,
        mediaHeight: 0,
    };

    refs: {
        main: HTMLDivElement,
    };

    componentDidMount() {
        this.setState({
            aspectRatio: this.props.ratioHeight / this.props.ratioWidth,
        }, () => {
            if (this.props.video) {
                window.addEventListener('resize', this.updateDimensions.bind(this));
                setTimeout(() => {
                    this.updateDimensions();
                }, 0);
            }
        });
    }

    componentDidUpdate(oldProps: IMediaProps) {
        if (!this.props.video) {
            return;
        }

        if (this.props.mediaWidthRelativeToContainer !== oldProps.mediaWidthRelativeToContainer) {
            this.updateDimensions();
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }

    updateDimensions() {
        if (this.refs.main && this.refs.main.getBoundingClientRect().width) {
            const widthToUse = this.refs.main.getBoundingClientRect().width * (Number(this.props.mediaWidthRelativeToContainer.split('%')[0]) / 100);
            this.setState({
                containerWidth: widthToUse,
                mediaHeight: Math.max(this.props.minHeight, Math.floor(this.state.aspectRatio * widthToUse)),
            });
        }
    }

    renderMedia() {
        const { containerWidth, aspectRatio, mediaHeight } = this.state;
        if (this.props.image) {

            const { image, mediaWidthRelativeToContainer, ...rest } = this.props;

            const minHeightStyle = !!this.props.minHeight ? {
                display: 'flex',
                flexDirection: 'column',
                minHeight: this.props.minHeight,
            } : null;

            return (
                <MediaElement
                    {...rest}
                    containerStyle={minHeightStyle}
                    image={image}
                    mediaWidthRelativeToContainer={mediaWidthRelativeToContainer}
                    aspectRatio={aspectRatio}
                />
            );
        }

        const { video, mediaWidthRelativeToContainer, ...rest } = this.props;
        return (
            <div ref="main">
                <MediaElement
                    {...rest}
                    ratioWidth={containerWidth}
                    ratioHeight={mediaHeight}
                    video={video}
                    autosize={this.props.autosize}
                    mediaWidthRelativeToContainer={mediaWidthRelativeToContainer}
                />
            </div>
        );
    }

    render() {
        return this.renderMedia();
    }
}



