export const REGULAR_FONT = `'Avenir Next', sans-serif`;
export const BOLD_FONT = `'AvenirNext-Bold', sans-serif`;
export const DEMI_BOLD_FONT = `'AvenirNext-DemiBold', sans-serif`;
export const ITALIC_REGULAR_FONT = `'AvenirNext-Italic', sans-serif`;

export const LETTER_SPACE = `0.2px`;
const SMALL_LETTER_SPACE = `-0.5px`;

function typeStyles(
    fontSize: string,
    lineHeight: string,
    letterSpacing: string | number = 0,
    fontFamily: string = REGULAR_FONT,
    ) {
        return ({
            fontFamily: fontFamily,
            fontSize: fontSize,
            lineHeight: lineHeight,
            letterSpacing: letterSpacing,
            width: '100%',
        });
}

export const headline1 = typeStyles( `48px`, `56px` );
export const headline1Mobile = typeStyles( `32px`, `36px` );
export const headline2 = typeStyles( `32px`, `36px` );
export const headline3 = typeStyles( `24px`, `32px` );
export const headline3Bold = typeStyles( `24px`, `32px`, 0, DEMI_BOLD_FONT );

export const large = typeStyles( `19px`, `28px`, LETTER_SPACE );
export const largeBold = typeStyles( `19px`, `28px`, LETTER_SPACE, DEMI_BOLD_FONT );

export const regular = typeStyles( `16px`, `24px`, LETTER_SPACE );
export const regularBold = typeStyles( `16px`, `24px`, LETTER_SPACE, DEMI_BOLD_FONT );
export const stat = typeStyles( `16px`, `19px`, LETTER_SPACE );

export const small = typeStyles( `12px`, `16px`, LETTER_SPACE );
export const smallBold = typeStyles( `12px`, `16px`, LETTER_SPACE, DEMI_BOLD_FONT );

export const tiny = typeStyles( `8px`, `12px`, LETTER_SPACE );

export const bold1 = typeStyles( `60px`, `66px`, SMALL_LETTER_SPACE, BOLD_FONT );
export const bold2 = typeStyles( `48px`, `56px`, SMALL_LETTER_SPACE, BOLD_FONT );
export const bold3 = typeStyles( `40px`, `48px`, SMALL_LETTER_SPACE, BOLD_FONT );
export const bold4 = typeStyles( `32px`, `40px`, SMALL_LETTER_SPACE, BOLD_FONT );
export const bold5 = typeStyles( `24px`, `32px`, SMALL_LETTER_SPACE, BOLD_FONT );

export const superscript = {
    transform: 'translateY(-20%)',
    display: 'inline-block',
    fontSize: '50%',
    verticalAlign: 'top',
};

export const subscript = {
    transform: 'translateY(20%)',
    display: 'inline-block',
    fontSize: '50%',
    verticalAlign: 'top',
};



