import * as React from 'react';
import * as glamorous from 'glamorous';
import { Markdown } from '../../atoms/Markdown';
import { Header } from '../../atoms/Header';

import * as styles from './BodyCopyBlock-styles';

const {Div} = glamorous;

export interface IBodyCopyBlockProps {
  headerCopy?: string;
  marginTop?: string;
  fullWidth?: boolean;
  tag?: 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';
}

export class BodyCopyBlock extends React.Component<IBodyCopyBlockProps, void> {
  static defaultProps = {
    marginTop: '40px',
    tag: 'h2',
  };

  static propTypes = {
    headerCopy: React.PropTypes.string,
  };

  render() {
    return (
      <Div css={styles.container(this.props.fullWidth)} >
        {
          this.props.headerCopy &&
          <Header tag={this.props.tag} css={styles.header(this.props.marginTop)}> {this.props.headerCopy} </Header>
        }
        <Markdown copy={`${this.props.children}`} styleOverrides={styles.markdownStyles(this.props.marginTop)} />
      </Div>
    );
  }
}



