import { css } from 'glamor';
import * as colors from 'utils/styles/colors';
import * as utilStyles from 'utils/styles';
const { mediaQueries, padding } = utilStyles;

export const colorWashBlock = (backgroundColor, color = colors.black, height = '100vh') => ({
    backgroundImage: colors[backgroundColor],
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    height: 'auto',
    boxSizing: 'border-box',
    padding: `${padding.xxlarge}px 0`,
    color: color,
    [`${mediaQueries.phablet}`]: {
        height: `${height}`,
        padding: `0 ${padding.xlarge}px`,
    },
});



