import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';
import { Z_INDEXES } from '../../../../utils/constants';

export const myStyleOne = (props) => ({
  backgroundColor: 'red',
});

export const myStyleTwo = () => ({
  backgroundColor: 'blue',
});

function generateDetailPageContainerWidth(type, element) {
  let maxWidth;

  switch (type) {
    case 'textBlock':
    case 'leadInCopyBlock':
    case 'annotations':
    case 'bulletedList':
    case 'numberedList':
      maxWidth = (utils.columns.desktopMaxWidth / 17) * 9;
      break;
    case 'image':
      maxWidth = element.props.isWide ? '100%' : (utils.columns.desktopMaxWidth / 17) * 9;
      break;
    case 'blockQuote':
      maxWidth = `1200px`;
      break;
    default:
      maxWidth = '100%';
  }

  return maxWidth;

}

export const formatHolder = (formatting: string = 'none', type: string, index: number, {...padding}, pageType, element) => ({
  boxSizing: 'border-box',
  position: 'relative',
  top: formatting === 'vertically-center' && '50%',
  marginTop: pageType === 'home' && type === 'cards' && index === 0 ? '-80px' : formatting === 'half-padding' ? '-64px' : null,
  transform: formatting === 'vertically-center' ? 'translateY(-50%)' : null,
  paddingTop: formatting === 'no-padding' ? 0 : padding.top,
  paddingBottom: formatting === 'no-padding' ? 0 : formatting === 'negative-top' ? 0 : padding.bottom,
  paddingLeft: padding.left,
  paddingRight: padding.right,
  textAlign: padding.align,
  margin: pageType === 'newsEntry' || pageType === 'insightsEntry' || pageType === 'successStory' ? '0 auto' : null,
  maxWidth: pageType === 'newsEntry' || pageType === 'insightsEntry' || pageType === 'successStory' ? generateDetailPageContainerWidth(type, element) : null,
});

export const navigationHeader = (gradientColor = 'Purple', height = 120, absolute: boolean = false) => ({
  height: `${height}px`,
  width: '100%',
  background: colors[`regular${gradientColor}Gradient`],
  position: absolute ? 'absolute' : 'relative',
  top: 0,
  left: 0,
});

export const section = (backgroundColor: string = 'transparent') => ({
  backgroundColor,
  position: 'relative',
  boxSizing: 'border-box',
  width: '100%',
  '& button.outline-button': {
    backgroundColor: (backgroundColor === 'transparent' || !backgroundColor) ? colors.backgroundGray : colors.white,
    '&:hover': {
      backgroundColor: (backgroundColor === 'transparent' || !backgroundColor) ? colors.backgroundGray : colors.white,
    },
  },
});



