import * as React from 'react';
import glamorous from 'glamorous';
import * as Router from 'react-router';
const { Link } = Router;
const { Div, Ul, Li } = glamorous;

import * as styles from './MobileMenu-styles';

export interface IMobileMenuProps {
    active: boolean;
    pageColor?: string;
    selected?: number;
    menuItems: {
        menuItems: Array<{
            type?: string;
            label: string;
            link: string;
        }>;
        subMenuItems: Array<{
            type?: string;
            label: string;
            link: string;
        }>;
    };
    isVisible?: boolean;
    onClick?: Function;
}

export class MobileMenu extends React.Component<IMobileMenuProps, void> {

    render() {
        const { active, isVisible } = this.props;

        return (
            <Div className={active ? 'mobile-nav active' : 'mobile-nav'} css={styles.navWrapper(active, isVisible)}>
                <Div css={styles.innerNavContainer()}>
                    <Div css={styles.navContainer()}>
                        <Ul>
                            {this.props.menuItems.menuItems.map((item, index) => {
                                return (
                                    <Li css={styles.navItem(index, this.props.selected, this.props.pageColor)} key={index}><a href="#" onClick={((evt) => this.props.onClick(evt, item.link))}>{item.label}</a></Li>
                                );
                            })}
                        </Ul>
                    </Div>
                    <Div css={styles.subNavContainer()}>
                        <Ul>
                            {this.props.menuItems.subMenuItems.map((item, index) => {
                                return (
                                    <Li key={index}><a href="#" onClick={((evt) => this.props.onClick(evt, item.link))}>{item.label}</a></Li>
                                );
                            })}
                        </Ul>
                    </Div>
                </Div>
            </Div>
        );
    }
}



