import * as React from 'react';
import glamorous from 'glamorous';
const { Div, Img } = glamorous;

import { LazyloadFade } from '../../atoms/LazyloadFade';

import * as styles from './MultiImage-styles';

export interface IMultiImageProps {
  images?: Array<{
    imageUrl: string;
    altText: string;
  }>;
  caption?: string;
}

export class MultiImage extends React.Component<IMultiImageProps, void> {

  renderImages() {
    return this.props.images.map((image, index, array) => (
      <Div
        key={index}
        css={styles.imageWrapper()}
      >
        <LazyloadFade>
          <Img
            src={image.imageUrl}
            alt={image.altText}
            css={styles.image()}
          />
        </LazyloadFade>
      </Div>
    ));
  }

  render() {
    return (
      <Div css={styles.container}>
        <Div css={styles.rowStyle}>
          {this.renderImages()}
        </Div>
        <Div css={styles.caption}>
          {this.props.caption}
        </Div>
      </Div>
    );
  }
}



