import * as utils from 'utils/styles';
import * as colors from 'utils/styles/colors';

const { mediaQueries, borderRadius } = utils;

export const container = (fullWidth: boolean, isSingleMetric: boolean) => ({
  display: 'inline-flex',
  border: `3px solid ${colors.snapYellow}`,
  borderRadius: borderRadius.tabletContainer,
  maxWidth: fullWidth && !isSingleMetric ? 'none' : 311,
  width: `100%`,
  [`${mediaQueries.normal}`]: {
    width: fullWidth && '100%',
    border: `5px solid ${colors.snapYellow}`,
    maxWidth: !isSingleMetric ? '495px' : '248px',
  },
});

export const metricContainer = (isFirst, isLast, fullWidth: boolean, isSingleMetric: boolean) => ({
  display: 'inline-flex',
  flex: 1,
  borderLeft: !isFirst && `1.5px solid ${colors.snapYellow}`,
  borderRight: !isLast && `1.5px solid ${colors.snapYellow}`,
  [`${mediaQueries.phablet}`]: {
    width: isSingleMetric ? `248px` : 0,
  },
  [`${mediaQueries.normal}`]: {
    borderLeft: !isFirst && `2.5px solid ${colors.snapYellow}`,
    borderRight: !isLast && `2.5px solid ${colors.snapYellow}`,
  },
});



