import * as types from '../constants/appConstants';
import * as styles from '../../utils/styles';

const hasWindow = (typeof window !== 'undefined');
const isMobile = hasWindow && window.innerWidth < styles.screenSizes.phablet;
let isHighDensity = false;
if (hasWindow) {
  isHighDensity = window.matchMedia && window.matchMedia('only screen and (min-resolution: 124dpi), only screen and (min-resolution: 1.3dppx), only screen and (min-resolution: 48.8dpcm)').matches ||
    window.matchMedia && window.matchMedia('only screen and (-webkit-min-device-pixel-ratio: 1.3), only screen and (-o-min-device-pixel-ratio: 2.6/2), only screen and (min--moz-device-pixel-ratio: 1.3), only screen and (min-device-pixel-ratio: 1.3)').matches ||
    window.matchMedia && window.matchMedia('only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx), only screen and (min-resolution: 75.6dpcm)').matches ||
    window.matchMedia && window.matchMedia('only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2/1), only screen and (min--moz-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2)').matches ||
    (window.devicePixelRatio && window.devicePixelRatio >= 2) && /(iPad|iPhone|iPod)/g.test(navigator.userAgent);
}

export const INITIAL_STATE = {
  mobile: isMobile,
  previousRoute: null,
  navIndex: 0,
  blogIndex: 0,
  cookieHidden: true,
  isLoaded: false,
  modalIsOpen: false,
  isHighDensity,
};

export function appReducer(state = INITIAL_STATE, action: any) {
  switch (action.type) {
    case types.IS_MOBILE:
      return {
        ...state,
        mobile: true,
      };
    case types.NOT_MOBILE:
      return {
        ...state,
        mobile: false,
      };
    case types.PREVIOUS_ROUTE:
      return {
        ...state,
        previousRoute: action.route,
      };
    case types.NAV_INDEX:
      return {
        ...state,
        navIndex: action.navIndex,
      };
    case types.BLOG_INDEX:
      return {
        ...state,
        blogIndex: action.blogIndex,
      };
    case types.SHOW_COOKIE:
      return {
        ...state,
        cookieHidden: false,
      };
    case types.HIDE_COOKIE:
      return {
        ...state,
        cookieHidden: true,
      };
    case types.IS_LOADED:
      return {
        ...state,
        isLoaded: true,
      };
    case types.MODAL_IS_OPEN:
      return {
        ...state,
        modalIsOpen: action.isOpen,
      };
    case types.VIDEO_IS_PLAYING:
      return {
        ...state,
        videoIsPlaying: action.videoIsPlaying,
      };
    default:
      return state;
  }
}



