import { css } from 'glamor';

const fadeAnimation = (css as any).keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
});

export const fadeIn = {
  animation: `${fadeAnimation} 0.5s 1 ease-out`,
};



