import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { throttle } from 'lodash';
import { isMobile } from '../../../../state/actionCreators/appCreator';
import { ContentContainer } from './components/ContentContainer';

import * as colors from 'utils/styles/colors';

import { autobind } from 'core-decorators';

const arrowWhite = require('./assets/cta_arrow_white.svg');
const arrowBlack = require('./assets/cta_arrow_black.svg');

const { findDOMNode } = ReactDOM;
const {
  Div,
  H4,
  H6,
  P,
  Span,
  Img,
} = glamorous;

import {
  cardTitle,
  cardWrapper,
  cardImage,
  cardSubhead,
  creativeSpotlightSubhead,
  cardCTA,
  cardCtaIcon,
  cardContent,
  mobileLogoImage,
  cardSubheadIcon,
  superscript,
} from './base/GridCard-base-styles';

export interface IGridCardProps {
  type: string;
  subhead?: string;
  date?: string;
  title?: string;
  cta: string;
  image: string;
  image2x?: string;
  logoImage?: string;
  logoImage2x?: string;
  featuredImage?: string;
  featuredImage2x?: string;
  titleStyle?: any;
  animate?: boolean;
  isMobile?: any;
  isUpdating?: boolean;
  color?: string;
  lightText?: boolean;
  locale?: string;
  isDesktop?: boolean;
  windowWidth?: number;
  isFeatured?: boolean;
  cardIndex?: number;
  modalId?: number;
  openModal?: any;
  isThreeUp?: any;
  isHighDensity: boolean;
}

export interface IGridCardState {
  title: string;
  titleIndex: number;
  subhead: string;
  subHeadIndex: number;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  locale: state.data.locale,
  isHighDensity: state.app.isHighDensity,
});

const cn = (bool, className) => bool ? className : '';

export class GridCardClass extends React.Component<IGridCardProps, IGridCardState> {

  state = {
    title: this.props.title ? this.props.title.split(' ')[0] : '',
    titleIndex: 0,
    subhead: this.props.subhead ? this.props.subhead.split(' ')[0] : '',
    subHeadIndex: 0,
  };

  card;
  titleEl;
  cardEl;

  modalCallback: any;

  componentDidMount() {

    if (this.props.type === 'creativeSpotlightVideo' && this.cardEl || this.props.type === 'creativeSpotlightDevice' && this.cardEl) {
      this.modalCallback = this.props.openModal.bind(this, this.props.modalId, this.props.type);
      this.cardEl.addEventListener('click', this.modalCallback);
    }

  }

  componentWillUnmount() {
    if (this.props.type === 'creativeSpotlightVideo' || this.props.type === 'creativeSpotlightDevice') {
      this.cardEl.removeEventListener('click', this.modalCallback);
    }
  }

  getInsightsColor(color: string) {
    switch (color) {
      case 'yellow':
        return colors.snapYellow;
      case 'lightYellow':
        return colors.lightYellow;
      case 'orange':
        return colors.regularOrange;
      case 'lightOrange':
        return colors.lightOrange;
      case 'blue':
        return colors.regularBlue;
      case 'lightBlue':
        return colors.lightBlue;
      case 'purple':
        return colors.regularPurple;
      case 'lightPurple':
        return colors.lightPurple;
      case 'red':
        return colors.snapRed;
      case 'lightRed':
        return colors.lightRed;
      case 'green':
        return colors.regularGreen;
      case 'lightGreen':
        return colors.lightGreen;
      default:
        return colors.white;
    }
  }

  generateBackground(type, color, image, logoImage) {
    let background;
    let fontColor;
    let arrowColor;
    let position = 'center';
    if (type === 'newsEntry' || type === 'creativeSpotlightVideo') {
      background = `linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(${image})`;
      fontColor = colors.white;
      arrowColor = arrowWhite;
      if (type === 'newsEntry') {
        position = 'right center';
      }
    } else if (type === 'successStory') {
      background = `linear-gradient(${color}, ${color})`;
      fontColor = this.props.lightText ? colors.white : colors.black;
      arrowColor = this.props.lightText ? arrowWhite : arrowBlack;
    } else if (type === 'insightsEntry') {
      background = this.getInsightsColor(color);
      fontColor = this.props.lightText ? colors.white : colors.black;
      arrowColor = this.props.lightText ? arrowWhite : arrowBlack;
    } else {
      background = `linear-gradient(${color}, ${color})`;
      fontColor = this.props.lightText ? colors.white : colors.black;
      arrowColor = this.props.lightText ? arrowWhite : arrowBlack;
    }

    return {background, fontColor, arrowColor, position};
  }

  generateCardImage(type, backgroundImage, featuredCardImage, logoCardImage) {
    let image = null;
    let logoImage = null;
    let featuredImage = null;
    let position = 'center center';
    let size = 'auto';
    if (type === 'insightsEntry') {
      image = backgroundImage;
      size = 'cover';
    } else if (type === 'successStory' || type === 'creativeSpotlightDevice') {
      logoImage = logoCardImage;
      featuredImage = featuredCardImage;
      position = this.props.isMobile ? '32px 2rem' : '44px 2rem';
      size = '160px auto';
    } else if (type === 'creativeSpotlightVideo') {
      logoImage = logoCardImage;
      position = this.props.isMobile ? '32px 2rem' : '44px 2rem';
      size = '160px auto';
    }
    return {image, featuredImage, logoImage, position, size, type};
  }

  render () {
    const {
      type,
      subhead,
      date,
      title,
      cta,
      image,
      image2x,
      logoImage,
      logoImage2x,
      featuredImage,
      featuredImage2x,
      titleStyle,
      animate,
      isUpdating,
      color,
      isMobile,
      locale,
      isDesktop,
      windowWidth,
      isFeatured,
      isThreeUp,
      isHighDensity,
    } = this.props;

    let imageSrc = image;
    let logoImageSrc = logoImage;
    let featuredImageSrc = featuredImage;

    if (isHighDensity && image2x) {
      imageSrc = image2x;
    }

    if (isHighDensity && logoImage2x) {
      logoImageSrc = logoImage2x;
    }

    if (isHighDensity && featuredImage2x) {
      featuredImageSrc = featuredImage2x;
    }

    const background = this.generateBackground(type, color, image, logoImageSrc);
    const featuredCardImage = this.generateCardImage(type, image, featuredImageSrc, logoImageSrc);
    const showLogoImage = featuredCardImage.logoImage && !(type === 'creativeSpotlightDevice' && !isFeatured);

    return (
      <Div
        innerRef={(el) => { this.cardEl = el; }}
        className={`grid-card ${cn(isFeatured && !isThreeUp, 'featured')} ${cn(animate, 'animate')} ${cn(isUpdating, 'is-updating')} ${type} ${cn(isThreeUp, 'three-up')}`}
        css={cardWrapper}
        style={{
          background: background.background,
          color: background.fontColor,
          backgroundPosition: background.position,
          backgroundSize: `cover`,
        }}
      >
        <Div
          className={'card-image'}
          css={cardImage}
          style={featuredCardImage.image && {
            backgroundImage: `url(${featuredCardImage.image})`,
            backgroundPosition: featuredCardImage.position,
            backgroundSize: featuredCardImage.size,
          }}
        >
          {showLogoImage && (
              <div
                className={`card-logo-image ${featuredCardImage.type}`}
                style={{
                  backgroundImage: `url(${featuredCardImage.logoImage})`,
                  backgroundPosition: featuredCardImage.position,
                  backgroundSize: featuredCardImage.size,
                }}
              />
            )
          }
          {featuredCardImage.featuredImage && (
            <div
              className={`card-featured-image ${featuredCardImage.type}`}
              style={{
                backgroundImage: `url(${featuredCardImage.featuredImage})`,
              }}
            />
          )}
        </Div>
        <Div className={'card-content'} css={cardContent}>
          {isMobile && type === 'successStory' && logoImageSrc &&
            <Img className={'mobile-logo-image'} src={logoImageSrc} css={mobileLogoImage} />
          }
          {subhead &&
            <H6 className={'card-subhead'} css={cardSubhead}>
              <ContentContainer content={date ? `${subhead} / ${date}` : subhead} windowWidth={windowWidth}>
                {type === 'creativeSpotlightVideo' && <Span className={'card-subhead-icon'} css={cardSubheadIcon}/> }
              </ContentContainer>
            </H6>
          }
          {title &&
            <H4 className={'card-title'} css={[titleStyle ? titleStyle(locale) : cardTitle]}>
              <ContentContainer content={title} windowWidth={windowWidth} />
            </H4>
          }
          {cta &&
            <H6 className={'card-cta'} css={cardCTA}>
              { cta }
              <Span css={cardCtaIcon} style={background.arrowColor && {background: `url(${background.arrowColor})`}}/>
            </H6>
          }
        </Div>
        {!isFeatured && type === 'creativeSpotlightDevice' && isDesktop &&
          <Span className={'cs-subhead'} css={[cardSubhead, creativeSpotlightSubhead]}>{ subhead }</Span>
        }
      </Div>
    );
  }
}

export const GridCard = connect(mapStateToProps)(GridCardClass);



