import * as React from 'react';
import glamorous from 'glamorous';
import { Markdown } from '../../atoms/Markdown';
const { Div } = glamorous;

import * as styles from './CopyBlock-styles';

export interface ICopyBlockProps {
  css?: any;
}

export class CopyBlock extends React.Component<ICopyBlockProps, void> {
  render() {
    return (
      <Div css={[styles.containerStyle(), this.props.css]}>
        <Markdown copy={ `${this.props.children}` } styleOverrides={styles.markdownStyles()} />
      </Div>
    );
  }
}



