import * as React from 'react';
import { browserHistory, Router, Route, IndexRoute, hashHistory, applyRouterMiddleware, Redirect } from 'react-router';
import { useScroll } from 'react-router-scroll';
import { URIs, languages, routes } from '../../utils/constants';

import { App } from '../Shared/templates/App';
import { PageTemplate } from '../Shared/templates/PageTemplate';
import { BlogPageTemplate } from '../Shared/templates/BlogPageTemplate';

import { store } from '../../state/createStore';
import { navIndex } from '../../state/actionCreators/appCreator';
import { previousRoute } from '../../state/actionCreators/appCreator';

// Set prevRoute on Redux store for 404 page route direction
const onChange = (prevState, nextState, replace, callback) => {
  if (typeof window === 'undefined') {
    return;
  }
  store.dispatch(previousRoute(prevState.location.pathname));
  callback();
};

const onEnter = (router) => {
  /*
  * There is a mobile bug where the document body initialy miscalculated its width...resulting in the fixed header
  * incorrectly calculating its postioning and size. Scrolling 1px in the x or y axis causes it to recalculate.
  */
  if (typeof window === 'undefined') {
    return;
  }

  window.setTimeout(() => {
    window.scrollTo(1, 0);
  }, 0);

  let pageIndex = -1;
  const splitRoute = router.location.pathname.split('/');
  const parsedRoute = splitRoute[1] === 'l' ? `/${router.location.pathname.split('/')[3] || ''}` : `/${router.location.pathname.split('/')[1]}`;
  store.getState().data.entries.navigation.map((item, index) => {
    if (parsedRoute === item.link) {
      pageIndex = index;
    }
  });
  store.dispatch(navIndex(pageIndex));
};

export const SnapchatRoutes = (
  <Router history={ browserHistory } render={applyRouterMiddleware(useScroll())}>
    <Route path="/" component={App} onEnter={onEnter} onChange={onChange}>
      <IndexRoute component={PageTemplate} />
      {
        routes.map((uri, key) => {
          return <Route path={uri} component={PageTemplate} key={key}/>;
        })
      }
      <Route path={`success-stories/:storName`} component={ PageTemplate } />
      <Route path={`creative-spotlight/:spotName`} component={ PageTemplate } />
      <Route path={`inspiration/:spotName`} component={ PageTemplate } />
      <Route path={`blog/:blogName`} component={ BlogPageTemplate } />
      <Route path={`draftpreviewblog/:Entry/:id/:locale/:id2`} component={ BlogPageTemplate } />
      <Route path={`draftpreview/:Entry/:id/:locale/:id2`} component={ PageTemplate } />
      <Route path="l">
      {
        languages.map((lang, index) => {
          return (
             <Route key={lang} path={`${lang}`}>
              <IndexRoute component={ PageTemplate  } />
              {
                routes.map((uri, key) => {
                  return <Route path={uri} component={PageTemplate} key={key}/>;
                })
              }
              <Route path={`blog/:blogName`} component={ BlogPageTemplate } />
            </Route>
          );
        })
      }
      </Route>
      <Route path={`*`} component={ PageTemplate } />
    </Route>
  </Router>
);



