import * as utils from 'utils/styles';
import { white, black } from 'utils/styles/colors';

export const globalNavStyle = (fontColor = white) => ({
  color: fontColor,
  fontWeight: 600,
  fontSize: 16,
  height: 25,
  justifyContent: 'flex-end',
  position: 'relative',
  top: 41,
  transition: 'color 0.2s ease-out',
});

export const globalNavWrapper = (bgColor = 'transparent') => ({
  backgroundColor: bgColor,
  height: 96,
  display: 'inline-block',
  paddingRight: 13,
});

export const globalNavTab = (fontColor = white) => ({
  padding: '0 2px',
  margin: `0 ${utils.padding.semi / 2}px`,
  fontWeight: 600,
  fontFamily: 'AvenirNext-DemiBold',
  color: fontColor,
  '&:hover': {
    opacity: 0.5,
  },
  '&:focus': {
    opacity: 1,
  },
  '&::after': {
    bottom: 1,
  },
});



