import * as utils from 'utils/styles';
import * as glamor from 'glamor';
import * as colors from 'utils/styles/colors';

export const container = () => ({
  display: 'flex',
  flexDirection: 'row',
  maxWidth: 750,
  width: '100%',
  margin: '0 auto',
  position: 'relative',
});

const slideFade = glamor.keyframes({
  '100%': {
    transform: 'translateY(0)',
    opacity: 1,
  },
});

export const copyContainer = () => ({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  flex: 2,
  paddingLeft: 75,
});

export const animateIn = (timing: number) => ({
  transform: 'translateY(-.5em)',
  opacity: 0,
  animation: `${slideFade} ${(timing / 1000)}s ease-in-out forwards`,
});

export const eyebrow = () => ({
  fontWeight: 500,
  fontSize: 19,
});

export const buttonContainer = () => ({
  display: 'flex',
  flexDirection: 'row',
  justifyContent: 'flex-start',
  marginTop: 25,
  '& > :first-child': {
    marginRight: 15,
  },
});



