import * as React from 'react';
import * as glamorous from 'glamorous';

import * as styles from './MetricCollection-styles';
import  { MetricBlock }  from '../../molecules/MetricBlock';
import { Header } from '../../atoms/Header';

const { Div } = glamorous;

export interface IStatMetric {
  headline?: string;
  subhead: string;
}

export interface IMetricCollectionProps {
  headline: string;
  metrics: Array<IStatMetric> ;
}

export class MetricCollection extends React.Component<IMetricCollectionProps, void> {

  countProps(prop) {
    if (prop.length === 4) {
      return ({
        container: styles.container,
        wrapper: styles.wrapper,
        metricWrapper: styles.metricWrapper,
      });
    } else if (prop.length === 3) {
      return ({
        container: styles.container3Up,
        wrapper: styles.wrapper3Up,
        metricWrapper: styles.metricWrapper3Up,
      });
    } else if (prop.length === 2) {
      return ({
        container: styles.container2Up,
        wrapper: styles.wrapper2Up,
        metricWrapper: styles.metricWrapper2Up,
      });
    } else if (prop.length === 1) {
      return({
        container: styles.container1Up,
        wrapper: styles.wrapper1Up,
        metricWrapper: styles.metricWrapper1Up,
      });
    }
  }

  computedStyles = this.countProps(this.props.metrics);

  items = this.props.metrics.map((metric, i) => (
    <Div
      key={i}
      css={this.computedStyles.metricWrapper()}
    >
      <MetricBlock
        numbMetricStat={{
          headline: metric.headline,
          subhead: metric.subhead,
        }}
      />
    </Div>
  ));

  render() {
    return (
      <Div css={this.computedStyles.wrapper}>
        {
          this.props.headline &&
          <Header tag={'h3'} css={styles.metricHeadline} > {this.props.headline} </Header>
        }
        <Div css={this.computedStyles.container()} >
          {this.items}
        </Div>
      </Div>
    );
  }
}



