import * as utils from 'utils/styles';
import * as glamor from 'glamor';
import * as colors from 'utils/styles/colors';

const activeCaret = require('./active-caret.svg');

const fadeEnter = glamor.keyframes({
  '0%': {
    opacity: 0,
  },
  '100%': {
    opacity: 1,
  },
});

const fadeLeave = glamor.keyframes({
  '0%': {
    opacity: 1,
  },
  '100%': {
    opacity: 0,
  },
});

export const deviceContainer = () => ({
  display: 'inline-block',
  position: 'relative',
  width: utils.columnsByPercent(5),
  marginRight: utils.columnsByPercent(1),
  verticalAlign: 'top',
  [`${utils.mediaQueries.normal}`]: {
    width: utils.columnsByPercent(4),
    marginLeft: utils.columnsByPercent(3),
  },
});

export const rightColumnContainer = () => ({
  display: 'inline-block',
  width: utils.columnsByPercent(11),
  [`${utils.mediaQueries.normal}`]: {
    width: utils.columnsByPercent(7),
  },
});

export const tabsContainer = () => ({
  display: 'block',
  position: 'relative',
  width: '100%',
  height: 'auto',
  zIndex: 4,
  marginTop: '48px',
  [`${utils.mediaQueries.normal}`]: {
    marginTop: '64px',
  },
});

export const copyContainer = (animating = false, timing = 300) => ({
  display: 'block',
  position: 'relative',
  width: '100%',
  maxWidth: '400px',
  opacity: 0,
  animation: animating ? `${fadeLeave} ${timing / 1000}s ease-in-out forwards` : `${fadeEnter} ${timing / 1000}s ease-in-out forwards ${timing / 1000}s`,
  [`${utils.mediaQueries.normal}`]: {
    maxWidth: '496px',
  },
});

export const tabOrganismStyle = () => ({
  width: '100%',
  margin: '0 auto',
  display: 'block',
  [`${utils.mediaQueries.phablet}`]: {
    padding: '0 32px',
    maxWidth: '704px',
  },
  [`${utils.mediaQueries.normal}`]: {
    maxWidth: `${utils.maxContentWidth - utils.padding.semi * 2}px`,
  },
});

export const tabs = () => ({
  display: 'block',
  position: 'relative',
});

export const tabWrapperStyle = () => ({
  textAlign: 'center',
  marginBottom: '32px',
  display: 'flex',
  [`${utils.mediaQueries.normal}`]: {
    marginBottom: '48px',
  },
});

export const tabIconStyle = (selected) => ({
  padding: '20px 8px 8px 8px',
  '&::after': {
    backgroundColor: 'none',
  },
});

export const tabsPadding = () => ({
  display: 'inline-block',
  flexDirection: 'column',
  content: '""',
});

export const tabLabelStyle = (selected, text) => ({
  fontWeight: 'normal',
  fontSize: '12px',
  lineHeight: '16px',
  position: 'relative',
  color: 'rgba(0,0,0,0)',
  display: 'block',
  '&::after': {
    position: 'absolute',
    width: '100%',
    content: `"${text}"`,
    right: 0,
    top: 0,
    fontFamily: selected ? 'AvenirNext-DemiBold' : 'AvenirNext',
    color: 'black',
  },
});

export const tabContainer = (selected, color: any = false) => ({
  boxSizing: 'border-box',
  border: `${ selected ? 3 : 2}px solid ${ selected ? color === 'Yellow' ? colors.snapYellow : ((color && colors['medium' + color]) || colors.mediumBlue) : colors.lightGray}`,
  borderRadius: '8px',
  flexDirection: 'column',
  flex: '1 0 auto',
  width: '96px',
  maxWidth: 128,
  marginRight: '16px',
  transition: 'border .2s ease-out',
  '&:last-child': {
    marginRight: 0,
  },
  [`${utils.mediaQueries.normal}`]: {
    maxWidth: 170,
  },
});

export const eyebrow = () => ({
  fontWeight: 500,
  fontSize: 19,
});

export const buttonContainer = (specLink = null) => ({
  display: 'block',
  width: '100%',
  marginTop: '8px',
  '& > :first-child': {
    marginBottom: '24px',
  },
  [`${utils.mediaQueries.phablet}`]: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
    marginTop: 25,
    '& > :first-child': {
      marginBottom: 0,
      marginRight: specLink ? 24 : 15,
    },
  },
});

export const carouselContainer = (clientWidth) => ({
  display: 'block',
  width: `calc(${clientWidth}px - 160px)`,
  maxWidth: '311px',
  height: 'auto',
});

export const content = () => ({
  display: 'block',
  width: `calc(100vw - 64px)`,
  maxWidth: '311px',
  margin: '0 auto',
  textAlign: 'center',
  marginTop: '4px',
});

export const headline = () => ({
  fontFamily: '\'AvenirNext-DemiBold\', sans-serif',
  fontSize: '16px',
  lineHeight: '24px',
  marginTop: '8px',
  [`${utils.mediaQueries.phablet}`]: {
    fontSize: '14px',
    marginTop: '0px',
  },
  [`${utils.mediaQueries.normal}`]: {
    fontSize: '19px',
    lineHeight: '28px',
  },
});

export const buttonMobile = () => ({
  [`${utils.mediaQueries.phabletMax}`]: {
    margin: '0 auto',
    display: 'block',
  },
});

export const desktopButton = (specLink = null) => ({
  marginRight: specLink && 24,
});

export const specsLink = () => ({
  fontWeight: 'normal',
  textTransform: 'capitalize',
  color: colors.regularBlue,
  alignSelf: 'center',
  '& span': {
    borderBottom: `2px solid ${colors.regularBlue}`,
    verticalAlign: 'middle',
    fontFamily: 'AvenirNext-DemiBold',
    fontSize: '12px',
    lineHeight: '16px',
    letterSpacing: '0.2px',
    paddingBottom: '2px',
    [utils.mediaQueries.phabletMax]: {
      display: 'inline-block',
      margin: '24px auto 0',
    },
    [utils.mediaQueries.phablet]: {
      margin: '0',
      display: 'block',
    },
  },
});



